<template>
    <section class="electric_game">
        <div class="background">
        </div>
        <div class="game">
            <div class="cell" v-for="(item, index) in cells" :key="index" :style="'transform: rotate(' + item.rotate + 'deg)'" @click="rotateMe(index)" :class="[item.dir, {active: item.active}]">
            </div>
        </div>
        <button class="btn btn-reset" @click="resetGame()" :disabled="resetDisabled">Сбросить</button>
    </section>
</template>

<script>
require('@/assets/css/games/electric.css')

function checkDir(cell, cells) {
    var thisBoxDirections = getObjectDirections(cell);
    var x = cell.posX;
    var y = cell.posY;

    thisBoxDirections.forEach(el => {
        if (el == 't') {
            var objectA = cells.find(elem => elem.posX == x - 1 && elem.posY == y);
            if ((objectA) && !objectA.active) {
                var newDirectionsA = getObjectDirections(objectA);
                if (newDirectionsA.includes('b')) {
                    objectA.active = true;
                    checkDir(objectA, cells)
                }
            }
        }
        if (el == 'r') {
            var objectB = cells.find(elem => elem.posX == x && elem.posY == y + 1);
            if ((objectB) && !objectB.active) {
                var newDirectionsB = getObjectDirections(objectB);
                if (newDirectionsB.includes('l')) {
                    objectB.active = true;
                    checkDir(objectB, cells)
                }
            }
        }
        if (el == 'b') {
            var objectC = cells.find(elem => elem.posX == x+1 && elem.posY == y);
            if ((objectC) && !objectC.active) {
                var newDirectionsC = getObjectDirections(objectC);
                if (newDirectionsC.includes('t')) {
                    objectC.active = true;
                    checkDir(objectC, cells)
                }
            }
        }
        if (el == 'l') {
            var objectD = cells.find(elem => elem.posX == x && elem.posY == y - 1);
            if ((objectD) && !objectD.active) {
                var newDirectionsD = getObjectDirections(objectD);
                if (newDirectionsD.includes('r')) {
                    objectD.active = true;
                    checkDir(objectD, cells)
                }
            }
        }
    });
    
}

function getObjectDirections(object) {
    var directions = object.dir;
    var list = directions ? directions.split('') : [];
    return list;
}
import axios from "axios";
export default {
    name: "ElectricGame",
    props: {
        
    },
    components: {
        
    },
    data() {
        return {
            cells: [],
            activeCell: {},
            isWinGame: false,
            resetDisabled: false,
        }
    },
    methods: {
        rotateMe(index) {
            this.activeCell = this.cells[index];
            this.activeCell.rotate += 90;

            var directions = this.getDirections();
            var newdirections = ``;
            for (var i=0; i < directions.length; i++) {
                if (directions[i] == 't') {
                    newdirections = newdirections+'r';
                }
                if (directions[i] == 'r') {
                    newdirections = newdirections+'b';
                }
                if (directions[i] == 'b') {
                    newdirections = newdirections+'l';
                }
                if (directions[i] == 'l') {
                    newdirections = newdirections+'t';
                }
            }
            this.activeCell.dir = newdirections;
            this.checkGame();
        },
        checkGame() {
            this.cells.forEach(item => item.active = false);
            this.checkFirstElement();
            if (this.cells[0].active) {
                checkDir(this.cells[0], this.cells);
            }
            
            if (this.cells[24].active) {
                var lastElemDirections = getObjectDirections(this.cells[24]);
                for (var j=0; j < lastElemDirections.length; j++) {
                    if (lastElemDirections[j] == 'r') {
                        this.winGame();
                    }
                }
            }
        },
        checkFirstElement() {
            var firstElemDirections = getObjectDirections(this.cells[0]);
            this.cells[0].active = false;
            for (var j=0; j < firstElemDirections.length; j++) {
                if (firstElemDirections[j] == 'l') {
                    this.cells[0].active = true;
                }
            }
        },
        getDirections() {
            var directions = this.activeCell.dir;
            var list = directions ? directions.split('') : [];
            return list;
        },
        generateFiled() {
            this.cells = [];
            for (let i = 0; i < 5; i++) {
                for (let j = 0; j < 5; j++) {
                    var rnd = Math.floor((Math.random() * 4) + 1);
                    var elem = {"rotate": 0, "posX": i, "posY": j};
                    if (rnd == 1) {elem.dir="lr"}
                    if (rnd == 2) {elem.dir="lt"}
                    if (rnd == 3) {elem.dir="ltr"}
                    if (rnd == 4) {elem.dir="ltrb"}
                    elem.active = false;
                    this.cells.push(elem);
                }
            }
            this.activeCell = this.cells[0];
            this.checkGame();
        },
        resetGame() {
            this.isWinGame = false;
            this.generateFiled();
            this.resetDisabled = true;
        
        },
        winGame() {
            if (!this.isWinGame) {
                this.isWinGame = true;
                setTimeout(() => this.sendData(), 500);
                console.log('win game')
            }
        },
        sendData() {
                axios.post('https://eclipse/ElectricWinGame', {
                }).then(() => { return; }).catch(function () { console.log() });
                this.$parent.activeGame = ""
        }
    },
    mounted() {
        this.generateFiled();
    }
}
</script>