<template>
    <svg
        :height="radius * 2"
        :width="radius * 2"
    >
        <linearGradient id="grad" gradientTransform="rotate(25 2 1.5)" >
            <stop offset="0%" stop-color="#68028a" />
            <stop offset="80%" stop-color="#e9117e" />
        </linearGradient>

        <linearGradient id="grad2" gradientTransform="rotate(25 2 1.5)" >
            <stop offset="0%" stop-color="#00000055" />
            <stop offset="80%" stop-color="#00000055" />
        </linearGradient>

        <circle
            v-if=gradient
            fill="transparent"
            :stroke-dasharray="circumference + ' ' + circumference"
            :style="{ strokeDashoffset }"
            :stroke-width="stroke"
            :r="normalizedRadius"
            :cx="radius"
            :cy="radius"
            style="stroke: url(#grad); transition: all .250s ease-in;"
        />
        <circle
            v-if=!gradient
            fill="transparent"
            :stroke-dasharray="circumference + ' ' + circumference"
            :style="{ strokeDashoffset }"
            :stroke-width="stroke"
            :r="normalizedRadius"
            :cx="radius"
            :cy="radius"
            style="stroke: url(#grad2)"
        />


    </svg>
</template>

<script>
    export default {
        name: 'HudSpeedometer',
        props: {
            radius: Number,
            progress: Number,
            stroke: Number,
            gradient: Boolean,
        },
        data() {
            const normalizedRadius = this.radius - this.stroke * 2;
            const circumference = normalizedRadius * 2 * Math.PI;

            return {
            normalizedRadius,
            circumference
            };
        },
        computed: {
            strokeDashoffset() {
                var speed = this.progress;
                if (speed > 75) {
                    speed = 75
                }
                return this.circumference - speed / 100 * this.circumference;
            }
        }
    }
</script>