<template>
    <div class="app_fullscreen content_page app_fleeca">

        <div class="fleeca_bg_logo"></div>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==1">
                <h4 class="app_name">FLEECA</h4>
                <div class="main_menu">
                    <button class="btn btn-block btn_fleeca" @click="changePage(2)">Баланс</button>
                    <button class="btn btn-block btn_fleeca" @click="changePage(3)">Трансфер</button>
                    <button class="btn btn-block btn_fleeca" @click="closeApp()">Выход</button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==2">
                <div class="contact_heading bottom-fix">
                    <button class="btn btn_fleeca" @click="changePage(1)">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Баланс</h4>
                    <div class="fleeca_text">
                        <h5>Баланс</h5>
                        <h4>{{getPrice(playerData.money)}}</h4>
                    </div>
                    
                    
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page == 3">
                <div class="contact_heading bottom-fix">
                    <button class="btn btn_fleeca" @click="changePage(1)" >Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Трансфер</h4>
                    <div class="fleeca_text">
                        <h5>Введите количество</h5>
                        <input class="form-control mb-0" v-model="amount">
                        <h5>Введите номер</h5>
                        <input class="form-control" v-model="phoneNumber">
                        <button class="btn btn-block btn_fleeca" @click="sendMoney()">Отправить</button>
                    </div>
                    
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page == 4">
                <div class="contact_heading bottom-fix">
                    <button class="btn btn_fleeca" @click="changePage(1)">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Успешно</h4>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page == 5">
                <div class="contact_heading bottom-fix">
                    <button class="btn btn_fleeca" @click="changePage(1)">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Ошибка</h4>
                </div>
            </div>
        </transition>

           <transition name="transition_03">
            <div class="content_page_l2" v-show="page == 6">
                <div class="contact_heading bottom-fix">
                    <button class="btn btn_fleeca" @click="changePage(1)">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Ошибка онлайна</h4>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'App_fleeca',
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            amount: "",
            phoneNumber: "",
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.page=1;
        },
        changePage(page) {
            this.page = page;
            if(page == 2) {
                  axios.post('https://eclipse/GetPhoneBankData', {

                }).then(() => { return; }).catch(function () { console.log() });  
            }
        },
        getPrice(numb) {
            var elem = String(numb);
            var result = elem.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
            result = result + '$'
            return result;
        },
        sendMoney() {
            axios.post('https://eclipse/TransferMoney', {
                TargetName: this.phoneNumber,
                TargetSum: this.amount
            }).then(() => { return; }).catch(function () { console.log() });
        }

    },
    mounted() {
        window.addEventListener(
        "message",
        (event) => {
            if (event.data.type == "LoadPhoneBankData") {
                this.$parent.playerData.money = event.data.data;
            }
            else if(event.data.type == "TransferSuccess") {
                this.amount = "",
                this.phoneNumber = "",
                this.changePage(4);
            }
            else if(event.data.type == "OnlineError") {
                this.amount = "",
                this.phoneNumber = "",
                this.changePage(6);
            }
            else if(event.data.type == "TransferError") {
                this.amount = "",
                this.phoneNumber = "",
                this.changePage(5);
            }
        },
            false
        );
    }
}
</script>

