<template>
    <section class="documents">
        <transition name="list">
            <div class="id_card" v-if="playerIdCardData && this.showIdCard" v-dragged="onDragged" :class="{bc: playerIdCardData.ResidentPlace == 2, na: playerIdCardData.ResidentPlace == 0}">
                <div class="id_image" :style="'background-image: url(' + playerIdCardData.image + ');'"></div>

                <h2 class="id_heading">{{getHeading(playerIdCardData.ResidentPlace)}}</h2>
                <h3 class="id_descr">IDENTIFICATION<br>CARD</h3>

                <h3 class="id_id red">ID: SA55934-{{getId(playerIdCardData.id)}}</h3>
                <h3 class="id_name">{{playerIdCardData.name}}</h3>
                <h3 class="id_lastname">{{playerIdCardData.lastname}}</h3>
                <h3 class="id_dob">DOB: <span class="red">{{getDate(playerIdCardData.dob)}}</span></h3>
                <h3 class="id_sex">SEX: {{getSex(playerIdCardData.sex)}}</h3>
                <h3 class="id_status">STATUS: <span v-if="playerIdCardData.ResidentPlace == 0">N/A</span><span v-else>Resident</span></h3>

                <!-- 
                <h3 class="id_address">{{playerIdCardData.address}}</h3>
                <h3 class="id_hgt">5' 11''</h3>
                <h3 class="id_hair">N/A</h3>
                <h3 class="id_wgt">{{getWeight(playerIdCardData.sex)}}</h3>
                <h3 class="id_eyes">N/A</h3>
                <h3 class="id_dd">06/11/201660221/21FD/{{playerIdCardData.id}}</h3>
                <h3 class="id_iss">09/30/2010</h3> -->

                <h3 class="id_sign">{{playerIdCardData.name}} {{playerIdCardData.lastname}}</h3>
                <button class="btn btn-primary btn-close" @click="closeIdCard()">Закрыть</button>
            </div>
        </transition>

        <transition name="list">
            <div class="driver_license" v-if="playerDriverLicense && this.showDriverLicense" v-dragged="onDragged">
                <div class="id_image" :style="'background-image: url(' + playerDriverLicense.image + ');'"></div>
                <h3 class="id_id red">SA55934-{{getId(playerDriverLicense.id)}}</h3>
                <h3 class="id_expdate red">{{getDate(playerDriverLicense.expdate)}}</h3>
                <h3 class="id_name">{{playerDriverLicense.name}}</h3>
                <h3 class="id_lastname">{{playerDriverLicense.lastname}}</h3>
                <h3 class="id_address">{{playerDriverLicense.address}}</h3>
                <h3 class="id_dob red">{{getDate(playerDriverLicense.dob)}}</h3>
                <h3 class="id_sex">{{getSex(playerDriverLicense.sex)}}</h3>
                <h3 class="id_hgt">5' 11''</h3>
                <h3 class="id_hair">N/A</h3>
                <h3 class="id_wgt">{{getWeight(playerDriverLicense.sex)}}</h3>
                <h3 class="id_eyes">N/A</h3>
                <h3 class="id_dd">06/11/201660221/21FD/{{playerDriverLicense.id}}</h3>
                <h3 class="id_iss">09/30/2010</h3>
                <h3 class="id_class">{{playerDriverLicense.class}}</h3>
                <h3 class="id_enddate">{{getDate(playerDriverLicense.enddate)}}</h3>

                <h3 class="id_sign">{{playerDriverLicense.name}} {{playerDriverLicense.lastname}}</h3>
                <button class="btn btn-primary btn-close" @click="closeDriverLicense()">Закрыть</button>
            </div>
        </transition>

        <transition name="list">
            <div class="police_badge">
                
            </div>
        </transition>
    </section>
    
</template>

<script>
import axios from "axios";
export default {
    name: 'HudDocuments',
    props: {
        playerData: Object,
    },
     data() {
        return {
            playerIdCardData: undefined,
            playerDriverLicense: undefined,
            playerPoliceBadge: undefined,

            showIdCard: false,
            showDriverLicense: false,
            showPoliceBadge: false,

            elWIdth: 600,
            elHeight: 300
        }
    },
    methods: {
        onDragged({ el, deltaX, deltaY, first, last }) {
            if (first) {
                this.isDragging = true
                return
            }
            if (last) {
                this.isDragging = false
                return
            }
            var l = +window.getComputedStyle(el)['left'].slice(0, -2) || 0;
            var t = +window.getComputedStyle(el)['top'].slice(0, -2) || 0;
            el.style.left = l + deltaX + 'px';
            el.style.top = t + deltaY + 'px';
        },
        getHeading(numb) {
            if (numb == 0) {
                return "State of San Andreas"
            } else if (numb == 1) {
                return "City of Los Santos"
            } else {
                return "Blaine County"
            }
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "/" + month + "/" + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getSex(sex) {
            if (sex) {
                return "M"
            } else {
                return "F"
            }
        },
        getWeight(sex) {
            if (sex) {
                return "155 lbs"
            } else {
                return "110 lbs"
            }
        },
        getId(id) {
            var newid = ("00" + id).slice(-4);
            return newid
        },
        closeIdCard() {
            this.showIdCard = false;
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        closeDriverLicense() {
            this.showDriverLicense = false;
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        doCommand(e) {
            if(e.keyCode == 27) {
                if (this.showIdCard || this.showDriverLicense || this.showPoliceBadge) {
                    this.closeIdCard();
                }
            }
        },
    },
    created() {
        window.addEventListener("keydown", this.doCommand);
    },
    destroyed() {
        window.addEventListener("keydown", this.doCommand);
    },
    mounted() {
        window.addEventListener('message',(event) => {
            if (event.data.type == "LoadIdCardData") {
                this.playerIdCardData = {
                    id: event.data.IdCard.Id,
                    image: event.data.IdCard.Image,
                    phone: event.data.IdCard.Phone,
                    name: event.data.IdCard.Name,
                    lastname: event.data.IdCard.Lastname,
                    age: event.data.IdCard.Age,
                    dob: event.data.IdCard.Dob,
                    sex: event.data.IdCard.Sex,
                    expdate: event.data.IdCard.Expdate,
                    address: event.data.IdCard.Address,
                    ResidentPlace: event.data.IdCard.ResidentPlace
                }
                this.showIdCard = true;
            } else if(event.data.type == "DisableIdCard") {
                this.showIdCard = false;
            } 
        }, false);

        // /* Test DATA */
        // this.playerIdCardData = {
        //     id: 51,
        //     image: "http://gta5.space/archive/test/player.jpg",
        //     phone: "8004455",
        //     name: "Douglas",
        //     lastname: "Jhonson",
        //     age: "33",
        //     reg: 1,
        //     dob: "Mon Sep 11 1990 16:45:44 GMT+0300 (Москва, стандартное время)",
        //     sex: true,
        //     expdate: "Mon Oct 18 2024 16:45:44 GMT+0300 (Москва, стандартное время)",
        //     address: "San Andreas, Los Santos, Vespucci Boulevard 24, 3",
        // }
        // this.playerDriverLicense = {
        //     id: 51,
        //     image: "http://gta5.space/archive/test/player.jpg",
        //     phone: "8004455",
        //     name: "Douglas",
        //     lastname: "Jhonson",
        //     age: "33",
        //     dob: "Mon Sep 11 1990 16:45:44 GMT+0300 (Москва, стандартное время)",
        //     sex: true,
        //     expdate: "Mon Oct 18 2024 16:45:44 GMT+0300 (Москва, стандартное время)",
        //     address: "San Andreas, Los Santos, Vespucci Boulevard 24, 3",
        //     class: "A",
        //     enddate: "Mon Jun 30 2022 16:45:44 GMT+0300 (Москва, стандартное время)"
        // }
        
    }
}

</script>

