<template>
    <section>
        <transition name="list">
            <div class="vehicle_sell bg_primary" v-if="page == 0">
                <h3 class="text-center">ТС №{{carData.id}}</h3>
                <div class="d-flex justify-content-between">
                    <h6>Модель:</h6>
                    <h6 class="color">{{carData.modelName}}</h6>
                </div>
                <div class="d-flex justify-content-between">
                    <h6>Марка:</h6>
                    <h6 class="color">{{carData.vehicleMakeName}}</h6>
                </div>
                <div class="d-flex justify-content-between">
                    <h6>Владелец:</h6>
                    <h6 class="color">{{playerData.name}} {{playerData.surname}}</h6>
                </div>

                <div class="controls">
                    <button @click="changePage(1)" class="btn btn-secondary">Передать транспорт</button>
                    <button @click="closePage()" class="btn btn-primary">Закрыть окно</button>
                </div>
            </div>
        </transition>

        <transition name="list">
            <div class="vehicle_sell bg_primary" v-if="page == 1">

                <h3>Передать транспорт:</h3>
                <label>Продать игроку по номеру паспорта:</label>
                <div class="d-flex justify-content-between">
                    <input class="form-control mr-2" v-model="passToPlayerById" placeholder="Введите номер">
                    <button @click="passToPlayerByPhone()" class="btn btn-secondary">Передать</button>
                </div>
                <hr>
                <div class="controls mt-2">
                    <button @click="passToNearestPlayer()" class="btn btn-secondary">Передать ближайшему игроку</button>
                    <button @click="passToGoverment()" class="btn btn-secondary">Передать правительству</button>
                    <button @click="changePage(0)" class="btn btn-primary">Назад</button>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    name: 'VehicleSell',
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 0,
            carData: {
                "id": 34224,
                "modelName": "chino2",
                "vehicleMakeName": "VAPID",
                "plateName": "BIGFUCK",
            },
            passToPlayerById: undefined,

        }
    },
    methods: {
         closePage() {
            this.$parent.showVehicleSell = false;
            axios.post('https://eclipse/Tilda', {}).then(() => { return; }).catch(function () { console.log() });
        },
        changePage(numb) {
            if (this.page != numb) {
                this.page = numb;
                this.passToPlayerById = undefined;
            }
        },
        passToPlayerByPhone() {
            var id = this.passToPlayerById;
            if (!id) {
                // высираем ошибку: "Введите правильный номер телефона"
                return
            }
            if (id.length >= 1) {
                axios.post('https://eclipse/passVehicleToPlayerById', {
                    Id: id,
                    VehicleId: this.carData.id
                }).then(() => { return; }).catch(function () { console.log() });
                this.closePage()
            } else {
                // высираем ошибку "Введите правильный номер телефона"
            }
        },
        passToNearestPlayer() {
            this.closePage()
        },
        passToGoverment() {
            this.closePage()
        },
        

    },
    mounted() {
        window.addEventListener('message',(event) => {
            if (event.data.type == "LoadVehicleSellData") {
                this.carData = {
                    "id": event.data.id,
                    "modelName": event.data.modelName,
                    "vehicleMakeName": event.data.vehicleMakeName,
                    "plateName": event.data.Plate,
                }
            }
        }, false);
    }
}
</script>

