<template>
    <div :method='method'>
        <div class="category d-flex justify-content-between mb-1">
            <div class="d-flex align-items-center" style="width: 35%">
                <span>{{categoryName}}</span>
            </div>
            <div class="d-flex justify-content-end" style="width: 65%">
                <div class="maxColors"><span>{{maxIndex}}</span></div>

                <button class="minus" @click="minus()">-</button>
                <input class="changeTest" type="number" :max="maxIndex" v-model="result" @change="changeTo()" @mousewheel="wheelIndexMove">
                <button class="plus" @click="plus()">+</button>

                <button class="minus" @click="minusColor()">-</button>
                <input class="changeTest" type="number" :max="maxColors" v-model="colorResult" @change="changeTo()" @mousewheel="wheelColorMove">
                <button class="plus" @click="plusColor()">+</button>

                <div class="maxColors"><span v-if="result!= 0">{{maxColors}}</span></div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'carouselCloth',
    props: {
        list: Array,
        category: Number,
        categoryName: String,
        id: String,
        selected: Number,
        method: String,
        drawable: Number,
        texture: Number
    },
    methods: {
        minus() {
            this.result -= 1;
            if (this.result < 0) {
                this.result = this.maxIndex;
            }
            this.colorResult = 1;
            if (this.result != 0) {
                this.maxColors = this.list[this.result-1].Texture;
            }
            
            this.sendData(this.result, this.colorResult);
        },
        plus() {
            this.result += 1;
            if (this.result > this.maxIndex - 1) {
                this.result = 0;
            }
            this.colorResult = 1;
            if (this.result != 0) {
                this.maxColors = this.list[this.result-1].Texture;
            }
            this.sendData(this.result, this.colorResult);
        },
        minusColor() {
            this.colorResult -= 1;
            if (this.colorResult < 1) {
                this.colorResult = this.maxColors;
            }
            this.sendData(this.result, this.colorResult);
        },
        plusColor() {
            this.colorResult += 1;
            if (this.colorResult > this.maxColors) {
                this.colorResult = 1;
            }
            this.sendData(this.result, this.colorResult);
        },
        sendData(index, colorindex) {
            if (index == 0) {
                this.colorResult = 0;
                colorindex = 0;
            }
            this.$emit('sendData', this.category, index, colorindex-1);
        },
        isNumber(num) {
            return typeof num === 'number' && !isNaN(num);
        },
        changeTo() {
            var bool = this.isNumber(parseInt(this.result))
            if (!bool) {
                this.result = 0;
            }
            bool = Number.isInteger(this.colorResult)
            if (!bool) {
                this.colorResult = 0;
            }
            if (this.result > this.maxIndex) {
                this.result = this.maxIndex;
            }
            if (this.result < 0) {
                this.result = 0;
            }
            if (this.colorResult > this.maxColors) {
                this.colorResult = this.maxColors;
            }
            if (this.colorResult < 0) {
                this.colorResult = 0;
            }
            this.sendData(this.result, this.colorResult);
        },
        wheelIndexMove(e) {
            e.preventDefault();
            if (e.deltaY < 0) {
                this.plus()
            } else {
                this.minus()
            }
        },
        wheelColorMove(e) {
            e.preventDefault();
            if (e.deltaY < 0) {
                this.plusColor()
            } else {
                this.minusColor()
            }
        }
    },
    data() {
        return {
            result: 0,
            colorResult: 0,
            maxIndex: 1,
            maxColors: 1,
            functionName: '',
        }
    },
    mounted() {
        this.maxIndex = this.list.length;
        this.maxColors = this.list[this.result].Texture;
        this.result = this.drawable;
        this.colorResult = this.texture;
    }
}
</script>

<style scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

</style>