<script>
import axios from "axios";
import click from "../../../assets/sounds/pad_click.wav"
import walkstyles from "../../../json/walkstyles.json"

export default {
    name: "AppSettings",
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            wallpaperList: [
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper1.jpg",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper2.jpg",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper3.jpg",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper4.png",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper5.jpg",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper6.jpg",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper7.png",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper8.png",
                "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper9.jpg",
            ],
            padData: this.playerData.playerPadData,
            customImageUrl: undefined,
            wallpaperIndex: 0,
            buttonanimated: false,
            helpFormModel :{
                subject: "",
                message: "",
            },
            walkstyles: walkstyles, 
        }
    },
    methods: {
        playSound() {
            var audio = new Audio(click);
            audio.play();
        },
        changePage(index) {
            if (this.page != index) {
                this.page = index;
            }
            this.playSound();
        },
        closePage() {
            this.$parent.closeAllApps();
            this.findWallpaperIndex();
            this.playSound();
        },
        changeWallpaper(index) {
            this.playSound();
            this.customImageUrl = undefined;
            this.wallpaperIndex = index;
        },
        buttonChangeWallpaper() {
            this.playSound();
            this.padData.Wallpaper = this.wallpaperList[this.wallpaperIndex];
            if (this.customImageUrl) {
                this.padData.Wallpaper = this.customImageUrl;
            } else {
                this.padData.Wallpaper = this.wallpaperList[this.wallpaperIndex];
            }
            axios.post('https://eclipse/PlayerCadChangeWallpaper', {
                Wallpaper: this.padData.Wallpaper,
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changeParameter(index) {
            this.playSound();
            switch (index) {
                case 0:
                    this.padData.ShowHud = !this.padData.ShowHud;
                    break;
                case 1:
                    this.padData.ShowMarker = !this.padData.ShowMarker;
                    break;
                case 2:
                    this.padData.ShowTips = !this.padData.ShowTips;
                    break;
                case 3:
                    this.padData.ShowBlipsA = !this.padData.ShowBlipsA;
                break;
                case 4:
                    this.padData.ShowBlipsB = !this.padData.ShowBlipsB;
                break;
                case 5:
                    this.padData.ShowBlipsC = !this.padData.ShowBlipsC;
                break;
                case 6:
                    this.padData.ShowBlipsD = !this.padData.ShowBlipsD;
                break;
                default:
                    break;
            }
            axios.post('https://eclipse/UpdatePlayerCadSettings', {
                ShowHud: this.padData.ShowHud,
                ShowMarker: this.padData.ShowMarker,
                ShowTips: this.padData.ShowTips,
                ShowBlipsA: this.padData.ShowBlipsA,
                ShowBlipsB: this.padData.ShowBlipsB,
                ShowBlipsC: this.padData.ShowBlipsC,
                ShowBlipsD: this.padData.ShowBlipsD,
                WalkStyle: this.padData.Walkstyle
            }).then(() => { return; }).catch(function () { console.log() });
        },
        checkData() {
            if (!this.padData.ShowHud) {
                this.$set(this.padData, "ShowHud", false);
            }
            if (!this.padData.ShowMarker) {
                this.$set(this.padData, "ShowMarker", false);
            }
            if (!this.padData.ShowTips) {
                this.$set(this.padData, "ShowTips", false);
            }
            if (!this.padData.ShowBlipsA) {
                this.$set(this.padData, "ShowBlipsA", false);
            }
            if (!this.padData.ShowBlipsB) {
                this.$set(this.padData, "ShowBlipsB", false);
            }
            if (!this.padData.ShowBlipsC) {
                this.$set(this.padData, "ShowBlipsC", false);
            }
            if (!this.padData.ShowBlipsD) {
                this.$set(this.padData, "ShowBlipsD", false);
            }
        },
        findWallpaperIndex() {
            this.wallpaperIndex = this.wallpaperList.findIndex(item=> item == this.padData.Wallpaper)
        },
        sendHelpMessage() {
            this.playSound();
            if ((this.helpFormModel.subject.length >= 3) && (this.helpFormModel.message.length >= 3)) {
                console.log('отправляем сообщение о помощи на бэк');
            }
        },
        changeGait(index) {
            this.$set(this.padData, "Walkstyle", index);
            var walkstyle = null;
            this.walkstyles.forEach(element => {
              if(element.Id == index) {
                walkstyle = element.Name
              }  
            });
            axios.post('https://eclipse/UpdatePlayerCadSettings', {
                ShowHud: this.padData.ShowHud,
                ShowMarker: this.padData.ShowMarker,
                ShowTips: this.padData.ShowTips,
                ShowBlipsA: this.padData.ShowBlipsA,
                ShowBlipsB: this.padData.ShowBlipsB,
                ShowBlipsC: this.padData.ShowBlipsC,
                ShowBlipsD: this.padData.ShowBlipsD,
                WalkStyle: walkstyle
            }).then(() => { return; }).catch(function () { console.log() });
        }
    },
    mounted() {
        this.checkData();
        this.findWallpaperIndex();    
    }
}
</script>