<template>
    <section class="vehicle">
        <VehicleSell v-if="showVehicleSell" :playerData="playerData" />
        <VehicleFuel v-if="showFuel" :playerData="playerData" />
    </section>
</template>

<script>
require('@/assets/css/vehicle.css')
import VehicleSell from './components/Vehiclesell.vue'
import VehicleFuel from './components/VehicleFuel.vue'

export default {
    name: 'VehicleComponent',
    props: {
        playerData: Object,
    },
    components: {
        VehicleSell,
        VehicleFuel,
    },
    data() {
        return {
            showVehicleSell: false,
            showFuel: false,
        }
    },
    methods: {

    },
    mounted() {
        window.addEventListener('message',(event) => {
            if (event.data.type == "ShowVehicleSellMenu") {
                this.showVehicleSell = true;
            }
            else if(event.data.type == "ShowGasStationMenu") {
                this.showFuel = true;
            }
        }, false);
    }
}
</script>

