<template>
    <div class="page page_1 app_casino">
        <div class="content">

            <transition name="transition_02">
                <div class="page_2 loader" v-if="page == 0">
                    <img :src="require(`../../../assets/img/casino/logo.svg`)">
                    <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
                </div>
            </transition>

            <transition name="transition_03">
                <div class="page_2 mainpage" v-if="page == 1">
                    <div class="logo"></div>
                    <div class="bg_man"></div>
                    <div class="casino_menu">
                        <button class="btn btn-casino" @click="openLootBoxPage()">Подарки <span class="presents" v-if="playerGiftsList.length > 0"><i class="icon_present"></i><span>{{playerGiftsList.length}}</span></span></button>
                        <button class="btn btn-casino" @click="changePage(3)">Колесо удачи</button>
                        <button class="btn btn-casino" @click="changePage(4)">Слот-машины</button>
                        <button class="btn btn-casino" @click="showInfoPage = true">Информация</button>
                        <button class="btn btn-casino" @click="exitApp()">Выход</button>
                    </div>
                    <div class="bg_female"></div>
                </div>
            </transition>

            <div class="page_2 bg_stars">
                <div class="stars"></div>
                <div class="twinkling"></div>
                <div class="beam"></div>
            </div>

            <transition name="list">
                <div class="page_3 information" v-if="showInfoPage">
                    <p class="italic">Уважаемый {{playerData.name}} {{playerData.surname}}</p>
                    <p>Добро пожаловать в приложение роскошного казино-отеля, находящегося в сердце вайнвуда!</p>
                    <p>До вас дошли слухи. Им хочется верить, и есть лишь один способ узнать, правдивы ли они. Отбросьте предрассудки. Забудьте про свои комплексы, сомнения, чувство меры и кредитный рейтинг. Слухи не способны передать все величие этого заведения. Добро пожаловать в Diamond.</p>
                    <p>Если жизнь – игра, то это джекпот. Здесь ваши мечты станут реальностью, а ваша реальность – мечтой. Здесь осуществляются все желания, воплощаются в жизнь все фантазии. Здесь нет запретов, ограничений, окон, часов и явно обозначенных выходов. Добро пожаловать: перед вами алмазный стандарт Diamond.</p>
                    <p>Хватит потирать ручки: пополняй баланс своего счета и играй в свои любимые игры на своём планшете виртуально прямо сейчас.</p>
                    <div class='text-center'>
                        <button class="btn btn-casino-pink" @click="showInfoPage = false">Конечно!</button>
                    </div>
                </div>
            </transition>

            <transition name="transition_03">
                <div class="page_2 lootbox" v-if="page == 2">
                    <h2 class="text-center">ПОДАРКИ</h2>
                    <p style="text-align: center">Подарки всем и каждому: получите бесплатное членство VIP, уникальную одежду, деньги или автомобиль своей мечты.<br>И пусть кто-то скажет, что это лутбоксы - не верьте! Разве не стоят зеленые бумажки этих секунд предвкушения и неопределенности? </p>

                    <div class="loot_area" v-if="isGiftListNull">
                        <div class="box" :class="{stoprotate: stopRotate}">
                            <div class="basis">
                                <div class="side top"></div>
                                <div class="side left"></div>
                                <div class="side right"></div>
                                <div class="side front"></div>
                                <div class="side back"></div>
                                <div class="side bottom"></div>
                            </div>
                        </div>
                        <transition name="present">
                            <div class="present" v-if="showWin">
                                <div class="background_shape"></div>
                                <h5 class="wintext">{{activeGift.Name}}</h5>
                            </div>
                        </transition>
                    </div>

                    <div class="controls">
                        <div class="row">
                            <div class="col-3">
                                <button class="btn btn-casino" @click="closeLootBoxPage()">Назад</button>
                            </div>
                            <div class="col-6 d-flex justify-content-center align-items-center">
                                <button class="btn btn-opengift btn-casino" @click="openGift()" :disabled="giftDisabled" v-if="!resetGift && playerGiftsList.length != 0">Открыть</button>
                                <button class="btn btn-opengift btn-casino" @click="nextGift()" :disabled="giftDisabled" v-if="resetGift && playerGiftsList.length != 0" >Следующий</button>
                            </div>
                            <div class="col-3 d-flex justify-content-end align-items-center casino_style">
                                Boxes: <span>{{playerGiftsList.length}}</span>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </transition>

            <transition name="transition_03">
                <div class="page_2 luckwheel" v-if="page == 3">
                    <h2 class="text-center">КОЛЕСО УДАЧИ</h2>
                    <p style="text-align: center">Принято говорить, что удача благоволит храбрецам. Мы же в Diamond считаем, что удача благоволит клиентам с ВИП-статусом.<br>Смело крутите колесо: каждый день вам гарантирован один спин – и солидная прибавка к самомнению.</p>

                    <div class="wheel_container">
                        <div class="wheel" :style="'transform: rotate('+ rotation +'deg); transition: transform '+ rotationSpeed + 's ease-in-out;'">
                            <div class="element" v-for="(item, index) in wheelOfFortune" :key="'wh_el_'+index">
                                <span>{{item.Text}}</span>
                                <i :class="'icon icon_'+ item.Icon"></i>
                            </div>
                        </div>
                        <div class="circle"></div>
                        <div class="center">
                            <div class="clogo"></div>
                        </div>
                        <div class="arrow"></div>
                    </div>

                    
                    <div class="controls">
                        <div class="row">
                            <div class="col-3">
                                <button class="btn btn-casino align-items-center" @click="changePage(1)">Назад</button>
                            </div>
                            <div class="col-6 d-flex justify-content-center align-items-center">
                                <button class="btn btn-run" @click="rotateWheel()" :disabled="disabled">Крутить <span v-if="freeSpins >0">(бесплатно!)</span><span v-else>(1000$)</span></button>
                            </div>
                            <div class="col-3 d-flex justify-content-end align-items-center casino_style">
                                Free spins: <span>{{freeSpins}}</span>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </transition>

            <transition name="transition_03">
                <div class="page_2 lootbox" v-if="page == 4">
                    <h2 class="text-center">СЛОТ-МАШИНЫ</h2>
                    <p style="text-align: center">Мы в Diamond ценим умение обращаться со слот-машиной. Мы понимаем: чтобы стать настоящим мастером этого дела, требуются молниеносная реакция, годы исследований и тренировок, чуткая интуиция и зашкаливающий IQ. Да-да, именно вас мы и ждали.</p>

                    <div class="slot_container">
                        <div class="slot_col">
                            <div class="slot_window">
                                <div class="slot_items" :style="'top: ' + slotAPos + 'px; transition-duration: ' + slotsRotationSpeed + 's'">
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotAA_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotAB_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotAC_'+index" :class="item.Class"></div>
                                    <div class="slot_item seven"></div>
                                </div>
                            </div>
                        </div>
                        <div class="slot_col">
                            <div class="slot_window">
                                <div class="slot_items" :style="'top: ' + slotBPos + 'px; transition-duration: ' + slotsRotationSpeed + 's'">
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotBA_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotBB_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotBC_'+index" :class="item.Class"></div>
                                    <div class="slot_item seven"></div>
                                </div>
                            </div>
                        </div>
                        <div class="slot_col">
                            <div class="slot_window">
                                <div class="slot_items" :style="'top: ' + slotCPos + 'px; transition-duration: ' + slotsRotationSpeed + 's'">
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotCA_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotCB_'+index" :class="item.Class"></div>
                                    <div class="slot_item" v-for="(item, index) in slotItemList" :key="'slotCC_'+index" :class="item.Class"></div>
                                    <div class="slot_item seven"></div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="controls">
                        <div class="row">
                            <div class="col-3">
                                <button class="btn btn-casino align-items-center" @click="changePage(1)">Назад</button>
                            </div>
                            <div class="col-6 d-flex justify-content-center align-items-center">
                                <button class="btn btn-run" @click="runSlotMachine()" :disabled="disabled">Крутить <span v-if="freeSpins >0">(бесплатно!)</span><span v-else>(1000$)</span></button>
                            </div>
                            <div class="col-3 d-flex justify-content-end align-items-center casino_style">
                                Free spins: <span>{{freeSpins}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
            

        </div>
    </div>
</template>

<script>
export default {
    name: "AppCasino",
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 0,
            showInfoPage: false,
            playerGiftsList: [],
            giftDisabled: false,
            stopRotate: false,
            showWin: false,
            activeGift: undefined,
            resetGift: false,
            isGiftListNull: false,

            wheelOfFortune: [
                {"Text": "$5.000", "Icon": "money"},
                {"Text": "Vehicle", "Icon": "vehicle"},
                {"Text": "Clothing", "Icon": "clothing"},
                {"Text": "Mystery", "Icon": "mystery"},
                {"Text": "$100.000", "Icon": "banknote"},
                {"Text": "$3.000", "Icon": "money"},
                {"Text": "Discount", "Icon": "discount"},
                {"Text": "$5,000", "Icon": "money"},
                {"Text": "$15,000", "Icon": "money"},
                {"Text": "Clothing", "Icon": "clothing"},
                {"Text": "$3.500", "Icon": "money"},
                {"Text": "Discount", "Icon": "discount"},
                {"Text": "Clothing", "Icon": "clothing"},
                {"Text": "$35.000", "Icon": "money"},
                {"Text": "$150.000", "Icon": "banknote"},
                {"Text": "$5.000", "Icon": "money"},
                {"Text": "Discount", "Icon": "discount"},
                {"Text": "Clothing", "Icon": "clothing"},
                {"Text": "$50.000", "Icon": "money"},
                {"Text": "$200.000", "Icon": "banknote"},
            ],
            rotation: 0,
            rotationSpeed: 0,
            freeSpins: 4,
            disabled: false,

            slotItemList: [
                {"Id": 1, "Class": "seven"},
                {"Id": 2, "Class": "bell"},
                {"Id": 3, "Class": "watermelon"},
                {"Id": 4, "Class": "plum"},
                {"Id": 5, "Class": "diamiond"},
                {"Id": 6, "Class": "brilliant"},
                {"Id": 7, "Class": "cherry"},
            ],
            slotAPos: 0,
            slotBPos: 0,
            slotCPos: 0,
            slotsRotationSpeed: 0,
        }
    },
    methods: {
        resetPage() {
            this.page = 0;
            setTimeout(() => {
                this.page = 1;
            }, 2000);
        },
        exitApp() {
            this.$parent.closeAllApps();
        },
        changePage(index) {
            if (index != this.page) {
                this.page = index;
            }
        },
        rotateWheel() {
            if (this.disabled) {
                return
            }
            this.disabled = true;
            this.rotationSpeed = 3;
            this.freeSpins -= 1;
            if (this.freeSpins < 0) {
                this.freeSpins = 0;
            }
            this.rotation = Math.floor((Math.random() * 1440) + 720);
            setTimeout(() => {
                this.disabled = false;
                var result = this.rotation - (Math.trunc(this.rotation / 360) * 360);
                var winner = parseInt(Math.ceil((result) / 18));
                console.log('Выиграло колесо, номер: ' + winner);
                console.log(this.wheelOfFortune[winner - 1].Text);
                this.rotationSpeed = 0;
                this.rotation = result;
            }, 3000);

        },
        getRandomIntInclusive(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min + 1)) + min; //Максимум и минимум включаются
        },
        runSlotMachine() {
            if (this.disabled) {
                return
            }
            
            this.slotsRotationSpeed = 0;
            this.slotAPos = 0;
            this.slotBPos = 0;
            this.slotCPos = 0;


            this.disabled = true;
            this.rotationSpeed = 3;
            this.freeSpins -= 1;
            if (this.freeSpins < 0) {
                this.freeSpins = 0;
            }
            console.log('run slot machine')

            var randA = Math.floor((Math.random() * 100) + 1);
            var randB = Math.floor((Math.random() * 100) + 1);
            var randC = Math.floor((Math.random() * 100) + 1);

            var pixelA;
            var pixelB;
            var pixelC;

            if (randA == 1) {
                // 2% на brilliant
                pixelA = 5;
            } else if ( 4 >= randA && randA >= 2) {
                pixelA = 4;
                // 3% на diamond
            } else if (9 >= randA  && randA >= 5) {
                pixelA = 0;
                // 5% на seven
            } else if (19 >= randA  && randA >= 10) {
                pixelA = 1;
                // 10% на bell
            } else if (34 >= randA  && randA >= 20) {
                pixelA = 2;
                // 15% на watermelon
            } else if (54>= randA  && randA >= 35) {
                pixelA = 3;
                // 20% на plum
            } else if (79 >= randA  && randA >= 55) {
                pixelA = 6;
                // 25% на cherry
            } else {
                var randNoA = this.getRandomIntInclusive(1,6);
                pixelA = randNoA + 0.5;
            }

            if (randB == 1) {
                // 2% на brilliant
                pixelB = 5;
            } else if ( 4 >= randB && randB >= 2) {
                pixelB = 4;
                // 3% на diamond
            } else if (9 >= randB  && randB >= 5) {
                pixelB = 0;
                // 5% на seven
            } else if (19 >= randB  && randB >= 10) {
                pixelB = 1;
                // 10% на bell
            } else if (34 >= randB  && randB >= 20) {
                pixelB = 2;
                // 15% на watermelon
            } else if (54>= randB  && randB >= 35) {
                pixelB = 3;
                // 20% на plum
            } else if (79 >= randB  && randB >= 55) {
                pixelB = 6;
                // 25% на cherry
            } else {
                var randNoB = this.getRandomIntInclusive(1,6);
                pixelB = randNoB + 0.5;
            }

            if (randC == 1) {
                // 2% на brilliant
                pixelC = 5;
            } else if ( 4 >= randC && randC >= 2) {
                pixelC = 4;
                // 3% на diamond
            } else if (9 >= randC  && randC >= 5) {
                pixelC = 0;
                // 5% на seven
            } else if (19 >= randC  && randC >= 10) {
                pixelC = 1;
                // 10% на bell
            } else if (34 >= randC  && randC >= 20) {
                pixelC = 2;
                // 15% на watermelon
            } else if (54>= randC  && randC >= 35) {
                pixelC = 3;
                // 20% на plum
            } else if (79 >= randC  && randC >= 55) {
                pixelC = 6;
                // 25% на cherry
            } else {
                var randNoC = this.getRandomIntInclusive(1,6);
                pixelC = randNoC + 0.5;
            }
            console.log(randA, randB, randC);
            console.log(pixelA, pixelB, pixelC)

            setTimeout(() => {
                this.slotsRotationSpeed = 1;
            }, 50);
            setTimeout(() => {
                this.slotAPos = pixelA * -168 - 1176;
                this.slotBPos = pixelB * -168 - 1176;
                this.slotCPos = pixelC * -168 - 1176;
            }, 100);

            if (pixelA == 5 && pixelB == 5 && pixelC == 5) {
                console.log('brilliant вин')
            }
            if (pixelA == 4 && pixelB == 4 && pixelC == 4) {
                console.log('diamond вин')
            }
            if (pixelA == 0 && pixelB == 0 && pixelC == 0) {
                console.log('seven вин')
            }
            if (pixelA == 1 && pixelB == 1 && pixelC == 1) {
                console.log('bell вин')
            }
            if (pixelA == 2 && pixelB == 2 && pixelC == 2) {
                console.log('watermelon вин')
            }
            if (pixelA == 3 && pixelB == 3 && pixelC == 3) {
                console.log('plum вин')
            }
            if (pixelA == 4 && pixelB == 4 && pixelC == 4) {
                console.log('cherry вин')
            }
            if (pixelA == 4 && pixelB != 4 && pixelC != 4) {
                console.log('1 diamond вин')
            }
            if (pixelA != 4 && pixelB == 4 && pixelC != 4) {
                console.log('1 diamond вин')
            }
            if (pixelA != 4 && pixelB != 4 && pixelC == 4) {
                console.log('1 diamond вин')
            }
            if (pixelA == 4 && pixelB == 4 && pixelC != 4) {
                console.log('2 diamond вин')
            }
            if (pixelA != 4 && pixelB == 4 && pixelC == 4) {
                console.log('2 diamond вин')
            }
            if (pixelA == 4 && pixelB != 4 && pixelC == 4) {
                console.log('2 diamond вин')
            }

            setTimeout(() => {
                this.disabled = false;
            }, 2000);
        },
        openLootBoxPage() {
            if (this.playerGiftsList.length == 0) {
                this.isGiftListNull = false;
            } else {
                this.isGiftListNull = true;
            }
            this.changePage(2);
        },
        closeLootBoxPage() {
            this.changePage(1);
            this.nextGift();
        },
        openGift() {
            this.giftDisabled = true;
            this.stopRotate = true;
            this.activeGift = this.playerGiftsList.shift();
            setTimeout(() => {
                this.showWin = true;
            }, 3000);
            setTimeout(() => {
                this.giftDisabled = false;
                this.resetGift = true;
            }, 4000); 
        },
        nextGift() {
            this.stopRotate = false;
            this.showWin = false;
            this.resetGift = false;
        },
    },
    mounted() {
        // test
        this.playerGiftsList = [
            {
                "Name": "X2 опыта на 7 дней.",
                Id: 21,
            },
            {
                "Name": "Автомобиль!",
                Id: 4
            }
        ]
    }
}

</script>

