<template>
    <div class="app_fullscreen content_page app_police">
        <button class="app_close" @click="closeApp"></button>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==1">
                 <div class="app_logo"></div>
                <h4 class="app_name">911 CALL POLICE</h4>
                <div class="text-center mt-3 mb-3">
                    <button class="panic_button" @click="panicbutton()">
                        Panic button
                    </button>
                </div>
                <p class="pt-2">Близжайший патруль направился к вашей позиции в режиме повышенной готовности.</p>

                <button class="btn btn-police absolute btn-block" @click="changepage(3)">Обычный вызов</button>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==2">
                <h4 class="text-center">Вызов отправлен!</h4>
                <p>Близжайший патруль направился к вашей позиции в режиме повышенной готовности.<p>
                <p>При прибытии сотрудников полиции не совершайте резких движений, грубых оскорблений и других действий, которые могут ошибочно указать на вас в качестве преступника.</p>
                <p class="text-center slogan">LSPD: TO PROTECT AND TO SERVE</p>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==3">
                <h4 class="text-center">Вызвать полицию</h4>
                <p>Кратко опишите вызов. Первый освободившийся сотрудник полиции займется вашей проблемой. Не более 100 символов.</p>
                <textarea type="text" rows="5" maxlength="100" v-model="message"></textarea>
                <button class="btn btn-police btn-block mt-3" @click="sendMessage()">Отправить</button>
                <p class="text-center slogan">LSPD: TO PROTECT AND TO SERVE</p>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==4">
                <h4 class="text-center">Спасибо!</h4>
                <p class="text-center">Ваше сообщение отправлено!</p>
                <p class="text-center slogan">LSPD: TO PROTECT AND TO SERVE</p>
            </div>
        </transition>


       

    </div>
</template>

<script>

import axios from 'axios';

export default {
    name: 'App_police',
    components: {
        
    },
    data() {
        return {
            page: 1,
            message: '',
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.page = 1;
        },
        panicbutton() {
            this.page = 2;
            axios.post('https://eclipse/PolicePanicButton', {
                from: this.$parent.$parent.playerData.phone,
                    message: this.message,
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        changepage(page) {
            this.page = page;
        },
        sendMessage() {
            if (this.message) {
                this.changepage(4);
                axios.post('https://eclipse/PoliceCall', {
                    from: this.$parent.$parent.playerData.phone,
                    message: this.message,
                }).then(() => { return; }).catch(function () { console.log() });  
                this.message='';
            }
        }
    }
}

</script>

