<template>
    <div class="app_fullscreen content_page app_settings">
        <transition name="transition_03">
            <div class="content_page_l2 fw page_1" v-show="page==1">
                <div class="contact_heading">
                    <h4 class="app_name">Настройки</h4>
                    <button @click="closeApp" class="blue_button_back">Выйти</button>
                </div>
                <div class="content">
                    <hr class="iphone">
                    <div class="menu_block">
                        <div class="menu" @click="changePage(2)"><span class="icon "><b-icon icon="person-fill"></b-icon></span><span>Настройки аккаунта</span> <span class="right-icon"><b-icon icon="chevron-right"></b-icon></span></div>
                        <div class="menu" @click="changePage(3)"><span class="icon gear"><b-icon icon="gear-fill"></b-icon></span><span>Настройки дизайна</span><span class="right-icon"><b-icon icon="chevron-right"></b-icon></span></div>
                        <div class="menu" @click="changePage(4)"><span class="icon phone"><b-icon icon="phone-fill"></b-icon></span><span>Выбор телефона</span><span class="right-icon"><b-icon icon="chevron-right"></b-icon></span></div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_2" v-show="page==2">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back">Назад</button>
                    <h4 class="app_name">Мой аккаунт</h4>
                </div>
                <div class="content">
                    <div class="image">
                        <img :src=playerData.image class="img-fluid">
                    </div>
                    <label>Моё имя:</label>
                    <h4 class="myphone">{{playerData.name}}</h4>
                    <label>Номер телефона:</label>
                    <h4 class="myphone">{{playerData.phone}}</h4>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page==3">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back">Назад</button>
                    <h4 class="app_name">Дизайн</h4>
                </div>
                <div class="content">
                    <label>Фоновое изображение:</label>
                    <input class="form-control" v-model="newimage">
                    <span class="help">*Оставьте поле пустым для стандартного изображения.</span>
                </div>
                <div class="controls">
                    <div class="btn" @click="save">Сохранить</div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page==4">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back">Назад</button>
                    <h4 class="app_name">Вид телефона</h4>
                </div>
                <div class="content">
                    <hr class="iphone">
                    <div class="menu_block">
                        <div :class="{active: newPhoneType == 0}" class="menu phonetype"  @click="changePhoneType(0)"><span>iPhone 12</span></div>
                        <div :class="{active: newPhoneType == 1}" class="menu phonetype" @click="changePhoneType(1)"><span>iPhone 13</span></div>
                        <div :class="{active: newPhoneType == 2}" class="menu phonetype" @click="changePhoneType(2)"><span>Samsung Galaxy S20</span></div>
                        <div :class="{active: newPhoneType == 3}" class="menu phonetype" @click="changePhoneType(3)"><span>Samsung Galaxy Note 20 Ultra</span></div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page==5">
                <div class="contact_heading">
                    <button @click="changePage(2)" class="blue_button_back">Назад</button>
                    <h4 class="app_name">Изменить изображение</h4>
                </div>
                <div class="content">
                    <label>Изображение аккаунта</label>
                    <input class="form-control" v-model="newPlayerImage">
                    <span class="help">*Оставьте поле пустым для стандартного изображения.</span>
                </div>
                <div class="controls">
                    <div class="btn" @click="savePlImg()">Сохранить</div>
                </div>
            </div>
        </transition>


    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'App_settings',
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            newimage: undefined,
            newPlayerImage: undefined,
            newPhoneType: this.playerData.phoneType,
            images: {
                male: require('@/assets/img/phone/icons/icon_male.jpg'),
                female: require('@/assets/img/phone/icons/icon_female.jpg')
            }
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.$parent.whiteColor = true;
        },
        changePage(page) {
            this.page = page;
        },
        save() {
            this.$parent.$parent.playerData.bgImage = this.newimage;
            axios.post('https://eclipse/ChangeBgImage', {
                    bgImage: this.newimage
                }).then(() => { return; }).catch(function () { console.log() });  
            this.page = 1;
        },
        savePlImg() {
            if (this.newPlayerImage) {
                axios.post('https://eclipse_phone/ChangeUserImage', {
                    Image: this.newPlayerImage
                }).then(() => { return; }).catch(function () { console.log() });  
                this.$parent.$parent.playerData.image = this.newPlayerImage;
            } else {
                if (this.playerData.sex == true) {
                    this.$parent.$parent.playerData.image = this.images.male;
                } else {
                    this.$parent.$parent.playerData.image = this.images.female;
                }
            }
            this.page = 1;
        },
        changePlayerImage() {
            this.page = 5;
        },
        changePhoneType(index) {
            this.newPhoneType = index;
            axios.post('https://eclipse/UpdatePhoneType', {
                    PhoneType: index
            }).then(() => { return; }).catch(function () { console.log() });  
            this.$parent.$parent.playerData.phoneType = index;
        }
    }
}
</script>