<template>
    <section class="player_garage bg_stylish auto" v-if="vehicleData" :class="'garagetype_'+garageType">
        <h1 class="primary">{{garageNames[garageType]}}</h1>
        <div class="wheel_control" @wheel="onWheel">
            <swiper class="vehicle_list" :options="swiperOptions" ref="vehSwiper">
                <swiper-slide class="vehicle_item" v-for="(vehicle, index) in renderList" :key="'veh'+index">
                    <h2>{{vehicle.Vehicle}}</h2>
                    <h3>{{vehicle.VehicleClass}}</h3>
                    <button class="btn btn-primary btn-block" @click="chooseAuto(index)">Выбрать</button>
                </swiper-slide>
            </swiper>
        </div>
        <div class="menu_control">
            <button class="btn btn-primary" @click="close()">Закрыть</button>
        </div>
    </section>
</template>

<script>
// import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import allCarList from '../../json/vehicles.json'
// import "swiper/swiper-bundle.min.css"
import axios from "axios";
export default {
    name: 'GarageMain',
    components: {
        // Swiper,
        // SwiperSlide,
    },
    data() {
        return {
            swiperOptions: {
                loop: false,
                autoplay: false,
                slidesPerView: 1,
                spaceBetween: 10,
                mousewheel: true,
                direction: 'horizontal'
            },
            allCarList: allCarList,
            vehicleData: undefined,
            renderList: undefined,
            garageNames: [
                "Личный гараж",
                "Общественный гараж",
                "Полицейский гараж",
                "Медицинский гараж",
                "Ещё какой-нибудь гараж",
            ],
            garageType: 2,
        }
    },
    methods: {
        onWheel(e) {
            var direction = e.deltaY; 
            if (direction > 0) {
                this.$refs.vehSwiper.$swiper.slideNext()
            } else {
                this.$refs.vehSwiper.$swiper.slidePrev()
            }
        },
        getVehicleClass(name) {
            switch (name) {
                case "VC_SUPER":
                    return "Суперкар"
                case "VC_MUSCLE":
                    return "Маслкар"
                case "VC_SPORT_CLASSIC":
                    return "Спортивная классика"
                case "VC_OFF_ROAD":
                    return "Внедорожник"
                case "VC_VAN":
                    return "Фургон"
                case "VC_SEDAN":
                    return "Седан"
                case "VC_EMERGENCY":
                    return "Служебный"
                default:
                    return "Неизвестно"
            }
        },
        generateList() {
            this.vehicleData = [];
            this.renderList.forEach(element => {
                this.vehicleData.push( this.allCarList.find((item) => element.name == item.modelName));
            });
        },
        chooseAuto(index) {
            var vehicle = this.renderList[index];
            axios.post('https://eclipse/ParkingTakeVehicle', {
               Id: vehicle.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        close() {
            this.$parent.showPage = "";
        }
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "OpenGarage") {
                this.renderList = event.data.vehicles;
                this.garageType = 1;
                this.generateList();
            }
        }, false);


        this.renderList = [{"Id":84,"Vehicle":"osiris","VehicleClass":"Super", "Image":"http://placehold.it/256x256"}]
        // Указываем тип гаража
        this.garageType = 1;
        // Хуяк и впродакшн
        this.generateList();
        

    }
}
</script>

