<template>
    <div class="page page_1 app_estate">
        <div class="content">

            <transition name="list">
                <div class="page_2 mainpage" v-if="page == 0">
                    <div class="logo">

                    </div>

                    <div class="main_menu">
                        <h3>Выберите фирму:</h3>
                        <button class="btn btn-maze btn-block" @click="chooseBusiness(index)" v-for="(item, index) in businessList" :key="index + 'bsnz'">{{item.Name}}</button>

                        <button class="btn btn-primary btn-block btn-close" @click="exitApp()">Закрыть</button>
                    </div>

                </div>
            </transition>

            <transition name="list">
                <div class="page_2 page_style_1" v-if="page == 1">
                    
                    <div class="main_image" :style="'background-image: url(' + activeBusiness.Image + ');'">
                        <h2>{{activeBusiness.Name}}</h2>
                        <span v-html="activeBusiness.Description"></span>
                    </div>
                    <button class="btn btn-maze" @click="changePage(0)">Назад</button>

                </div>
            </transition>

        </div>
    </div>
</template>

<script>
export default {
    name: "AppRealEstate",
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 0,
            businessList: [
                {
                    Name: "Bayview Lodge Motel",
                    Description: "Великолепный лаунж с прекрасным видом: северные леса Paleto с одной стороны, великолепный морской пейзаж с другой.<br>Ни одна поездка в Procopio Promenade, не обходится без ночи в мотеле Bayview.",
                    Image: "http://gta5.space/archive/estate/bayview.jpg",
                    Rooms: 10,
                }
            ],
            activeBusiness: undefined,
        }
    },
    methods: {
        exitApp() {
            this.$parent.closeAllApps();
        },
        changePage(index) {
            if (index != this.page) {
                this.page = index;
            }
        },
        chooseBusiness(index) {
            this.activeBusiness = this.businessList[index];
            this.changePage(1)
        }
    }
}
</script>