<template>
    <transition name="list">
        <div v-if="showThis" class="item_guidecard" v-dragged="onDragged">

            <h3 class="text-center">Путеводитель по San Andreas</h3>

            <div class="heading_image"></div>

            <div class="content">
                <p class="justify">Западное побережье - лучшее побережье, это факт. Сан-Андреас мечтает, Сан-Андреас мечтает. На Сан-Андреасской королевской кровати. Я мечтал о Сан-Андреасе. Сан-Андреасские сны, Сан-Андреасские сны.</p>
                <ul>
                    <li>Арендуй велосипед</li>
                    <li>Купи телефон</li>
                    <li>Зарегистрируй id-карту</li>
                    <li>Не сиди на шее у матери</li>
                    <li>Найди работу</li>
                </ul>
            </div>

            <button class="btn btn-primary btn-close" @click="closeThisItem()">Закрыть</button>
        </div>
    </transition>
</template>

<script>
import axios from "axios";
export default {
    name: 'guideCard',
    props: {
        playerData: Object,
    },
    data() {
        return {
            showThis: false,
        }
    },
    methods: {
        closeThisItem() {
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.showThis = false;
        },
        onDragged({ el, deltaX, deltaY, first, last }) {
            if (first) {
                this.isDragging = true
                return
            }
            if (last) {
                this.isDragging = false
                return
            }
            var l = +window.getComputedStyle(el)['left'].slice(0, -2) || 0;
            var t = +window.getComputedStyle(el)['top'].slice(0, -2) || 0;
            el.style.left = l + deltaX + 'px';
            el.style.top = t + deltaY + 'px';
        },
        doCommand(e) {
            if(e.keyCode == 27 & this.showThis) {
                this.closeThisItem();
            }
        },
    },
    created() {
        window.addEventListener("keydown", this.doCommand);
    },
    destroyed() {
        window.addEventListener("keydown", this.doCommand);
    },
    mounted() {
    window.addEventListener(
      "message",
      (event) => {
        if (event.data.type == "ShowGuideCard") {
            this.showThis = true;
        }
      },
      false
    );
    }
}

</script>