<template>
    <section class="notifications">

        <!-- <div style="position: absolute; bottom: 0; left: 0;">
            <b-button @click="makeToast(1, 'primary','test', 'test text', 900000)">Default</b-button>
            <b-button variant="primary" @click="makeToast(0, 'primary','test', 'test text', 900000)" >Primary</b-button>
            <b-button variant="secondary" @click="makeToast(1, 'secondary','test', 'test text', 900000)">Secondary</b-button>
            <b-button variant="danger" @click="makeToast(2, 'danger','test', 'test text', 900000)" >Danger</b-button>
            <b-button variant="warning" @click="makeToast(3, 'warning','test', 'test text', 900000)">Warning</b-button>
            <b-button variant="success" @click="makeToast(4, 'success','test', 'test text', 900000)">Success</b-button>
            <b-button variant="info" @click="makeToast(5, 'info','test', 'test text', 900000)">Info</b-button>
            <b-button variant="primary" @click="closeThis()">close</b-button>
            <b-button @click="makeProgress('primary', 'Грабление корованов', 3000)">test</b-button>
        </div> -->

        <transition name="list">
            <div class="progress_bar" v-if="showProgress" :class="variant">
                <div class="header">
                    <strong>{{name}}</strong>
                </div>
                <b-progress :max="max">
                    <b-progress-bar :variant="variant" :value="value"></b-progress-bar>
                    <span class="value">{{value.toFixed(2)}}%</span>
                </b-progress>
            </div>
        </transition>

        <!-- <b-progress-bar :variant="variant" :value="value" :label="`${value.toFixed(2)}%`"></b-progress-bar> -->

        
            <div class="offer_list">
                <transition-group name="list">
                    <div :class="`b-toast b-toast-prepend b-toast-`+ getColor(item.variant)" v-for="(item, index) in offerList" :key="'offer' + index">
                        <div class="toast">
                            <header class="toast-header">
                                <strong class="mr-2">Оповещение:</strong>
                                <button type="button" aria-label="Close" class="close ml-auto mb-1" @click="removeOffer(item.id)">×</button>
                            </header>
                            <b-progress :max="100">
                                <b-progress-bar :variant="getColor(item.variant)" :value="item.value"></b-progress-bar>
                            </b-progress>
                            <div class="toast-body">
                                {{item.text}}
                                <div class="buttons">
                                    <button :class="`btn btn-`+getColor(item.variant)" @click="acceptOffer(item.id)">Принять (+)</button>
                                    <button class="btn btn-danger" @click="declineOffer(item.id)">Отказаться (-)</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition-group>
            </div>
            
        
    </section>   
</template>

<script>
require('@/assets/css/hud.css')
import axios from "axios";
export default {
    name: 'HudNotifications',
    data() {
        return {
            // Variants: primary, secondary, danger, warning, success, info
            value: 0,
            max: 100,
            time: 0,
            name: undefined,
            showProgress: false,
            variant: 0,

            offerList: [
            ],
        }
    },
    methods: {
        makeToast(toaster, variant, title, text, time) {
            var position = this.getPosition(toaster);
            this.$bvToast.toast(text, {
                title: title,
                autoHideDelay: time,
                appendToast: false,
                variant: variant,
                toaster: position,
            })
        },
        closeThis() {
            this.$parent.showNot = false;
        },
        getPosition(toaster) {
            switch(toaster) {
                case 0:
                    return 'b-toaster-top-left';
                case 1:
                    return 'b-toaster-top-center';
                case 2:
                    return 'b-toaster-top-right';
                case 3:
                    return 'b-toaster-bottom-left';
                case 4:
                    return 'b-toaster-bottom-center';
                case 5:
                    return 'b-toaster-bottom-right';
            }
        },
        getColor(color) {
            switch(color) {
                case 0:
                    return 'primary';
                case 1:
                    return 'secondary';
                case 2:
                    return 'danger';
                case 3:
                    return 'warning';
                case 4:
                    return 'success';
                case 5:
                    return 'info';
            }
        },
        makeProgress(variant, name, time) {
            this.variant = variant;
            this.name = name;
            this.time = time;
            this.showProgress = true;
            var self = this;
            var count = Math.floor(time / 100);
            var i = 0;
            var Timer = setInterval(function(){
                i++;
                if (i == 100) {
                    clearTimeout(Timer);
                    setTimeout(() => {
                        self.showProgress = false;
                        self.value = 0;
                    }, 300);
                    // success !
                }
                self.value = i;
            }, count);
            
        },
        createOffer(variant, text, time, id) {
            var item = {variant: variant, text: text, time: time, id: id, value: 100};
            this.offerList.push(item);

            var self = this;
            var count = Math.floor(time / 100);
            var i = 0;
            var Timer = setInterval(function(){
                i++;
                item.value = (100 - i)
                if (i == 100) {
                    clearTimeout(Timer);
                    setTimeout(() => {
                        self.removeOffer(id);
                        self.value = 0;
                    }, 300);
                    // success !
                }
                self.value = i;
            }, count);

        },
        removeOffer(id) {
            this.declineOffer(id);
        },
        acceptOffer(id) {
            var index = this.offerList.findIndex(item => item.id == id);
            this.offerList.splice(index, 1);
            axios.post('https://eclipse/AcceptOffer', {
                Id: id
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        declineOffer(id) {
            var isExist = this.offerList.findIndex(item => item.id == id)
            if (isExist != -1) {
                 var index = this.offerList.findIndex(item => item.id == id);
                this.offerList.splice(index, 1);
                axios.post('https://eclipse/DeclineOffer', {
                    Id: id
                }).then(() => { return; }).catch(function () { console.log() });  
            }
        },
    },
    mounted() {
        window.addEventListener(
        "message",
        (event) => {
            if (event.data.type == "ShowNotifications") {
                var toaster = event.data.toaster;
                var variant = event.data.variant;
                var title = event.data.title;
                var text = event.data.text;
                var time = event.data.time;
                this.makeToast(toaster, variant, title, text, time);
            }
            else if(event.data.type == "ShowProgressBar") {
                var progressBarName = event.data.text;
                var progressBarTime = event.data.time;
                var color = this.getColor(event.data.interfaceColorType);
                this.makeProgress(color, progressBarName, progressBarTime);
            }
            else if(event.data.type == "SendOffer") {
                this.createOffer(0, event.data.offerText, event.data.time, event.data.id);
            }
            else if(event.data.type == "AcceptOffer") {
                this.acceptOffer(event.data.id);
            }
            else if(event.data.type == "DeclineOffer") {
                this.declineOffer(event.data.id);
            }
        },
        false
        );

        // this.createOffer(1, "DouglasProd предлагает сдаться", 50000, 124);
        // this.makeToast(0, "primary", "Уведомление", "Проверка", 90000)
        // this.makeProgress("warning", "Работаем", 90000);
    },
}
</script>

