<template>
    <section class="craft fullpage">
        <transition name="transition_03">
            <div class="fullpage page_1" v-if="page == 1">
                <h1>{{windowName}}</h1>

                <div class="content">
                    <div class="row">
                        <div class="col-8">
                            <h3 class="header">Доступные рецепты</h3>

                            <div class="recipes_list">
                                <div class="recipe" :class="{active: activeRecipeIndex == index}" v-for="(item, index) in recipeList" :key="'recipe_'+index" @click="chooseRecipe(index, item)">
                                    <div class="image" :style="'background-image: url(' + siteName + item.Image + ');'"></div>
                                    <h5>{{item.Name}}</h5>
                                </div>
                            </div>
                        </div>
                        <div class="col-4">
                            <h3 class="header">Информация</h3>

                            <transition name="transition_03">
                                <div class="crafted_item" v-if="activeRecipe">
                                    <div class="craft_block">
                                        <h5>Создание: {{activeRecipe.Name}}</h5>
                                        <div class="image" :style="'background-image: url(' + siteName + activeRecipe.Image + ');'"></div>
                                        <p class="heading">{{activeRecipe.Heading}}</p>
                                        <p class="description">{{activeRecipe.Description}}</p>
                                        <div class="d-flex justify-content-between">
                                            <span class="weight">Вес: {{activeRecipe.Weight}} грамм</span>
                                            <span class="weight"><b-icon icon="alarm" aria-hidden="true"></b-icon> {{activeRecipe.Crafttime}}</span>
                                        </div>
                                    </div>
                                    <div class="craft_block">
                                        <h5>Требуемые ресурсы:</h5>
                                        <div class="craft_item_recipe" v-if="activeRecipe.Recipe">
                                            <div class="craft_item_recipe_item" v-for="(item, index) in activeRecipe.Recipe" :key="'rec_' + index">
                                                <div class="left">
                                                    <div class="image" :style="'background-image: url(' + siteName + item.Image + ');'" v-if="item.Image"></div>
                                                    <h6>{{item.Name}}</h6>
                                                </div>
                                                
                                                <div>
                                                    <span>{{item.Count}} </span>
                                                    <span class="player_count">[{{findItemsCountInInventory(item)}}]</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button :disabled="!recipeAvailable" class="btn btn-block btn-craft craft_block" @click="craftItem(activeRecipe)">Создать</button>
                                </div>
                            </transition>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
require('@/assets/css/craft.css')
import axios from 'axios';
export default {
    name: 'HudCraft',
    props: {
        playerData: Object,
    },
    components: {
        
    },
    data() {
        return {
            page: 1,
            windowName: "Крафт",
            siteName: "https://storage.googleapis.com/eclipse-rp/archive/",

            fadeTime: 300,
            onAnimation: false,

            recipeList: [
                {
                    Type: 8,
                    ItemId: "drugsmeth",
                    Image: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fa/Methamphetamine-3d-CPK.png/290px-Methamphetamine-3d-CPK.png",
                    Count: 4,
                    Name: "Метамфетамин",
                    Heading: "Нет, он не синего цвета",
                    Description: "Метамфетамин является психостимулятором с высоким потенциалом к формированию зависимости, в связи с чем отнесён к наркотическим веществам.",
                    Weight: 10,
                    Crafttime: 120,
                    Recipe: [
                        {
                            Name: "Амфетамин",
                            ItemId: "drugsamph",
                            Image: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fa/Methamphetamine-3d-CPK.png/290px-Methamphetamine-3d-CPK.png",
                            Count: 1,
                        },
                        {
                            Name: "Метиламин",
                            ItemId: "metilamin",
                            Image: "https://upload.wikimedia.org/wikipedia/commons/thumb/6/6b/Methylamine-3D-vdW.png/132px-Methylamine-3D-vdW.png",
                            Count: 2,
                        },
                        {
                            Name: "Прекурсор",
                            ItemId: "precursor",
                            Image: "https://pngimg.com/uploads/pills/pills_PNG16521.png",
                            Count: 4,
                        }
                    ]
                },{
                    Name: "Амфетамин",
                    Image: "https://upload.wikimedia.org/wikipedia/commons/thumb/2/28/Amphetamine-2D-skeletal.svg/290px-Amphetamine-2D-skeletal.svg.png",
                },{
                    Name: "Косяк",
                    Image: "https://pngimg.com/uploads/cigarette/cigarette_PNG4767.png",
                }
            ],
            activeRecipeIndex: -1,
            activeRecipe: undefined,
            recipeAvailable: false,

            itemList: [
                {
                    Type: 8,
                    ItemId: "drugsamph",
                    Image: "weapons/pistols/Pistol.png",
                    Count: 12,
                    Name: "Амфетамин",
                    Heading: "",
                    Description: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fa/Methamphetamine-3d-CPK.png/290px-Methamphetamine-3d-CPK.png",
                    Weight: 5,
                },
                {
                    Type: 8,
                    ItemId: "metilamin",
                    Image: "weapons/pistols/Pistol.png",
                    Count: 15,
                    Name: "Металамин",
                    Heading: "",
                    Description: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fa/Methamphetamine-3d-CPK.png/290px-Methamphetamine-3d-CPK.png",
                    Weight: 5,
                },
                {
                    Type: 8,
                    ItemId: "precursor",
                    Image: "weapons/pistols/Pistol.png",
                    Count: 4,
                    Name: "Прекурсор",
                    Heading: "",
                    Description: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fa/Methamphetamine-3d-CPK.png/290px-Methamphetamine-3d-CPK.png",
                    Weight: 5,
                },
            ],
        }
    },
    methods: {
        chooseRecipe(index, item) {
            if (!this.onAnimation && this.activeRecipeIndex != index) {
                this.onAnimation = true;
                this.activeRecipeIndex = index;
                this.activeRecipe = undefined;
                setTimeout(() => {
                    this.activeRecipeIndex = index;
                    this.activeRecipe = item;
                    this.onAnimation = false;
                    this.checkRecipeAvailable();
                }, this.fadeTime);
            }
            
        },
        findItemsCountInInventory(item) {
            var invItem = this.itemList.find(elem => elem.ItemId == item.ItemId);
            var count = 0;
            if (invItem) {
                count = invItem.Count;
            }
            return count;
        },
        checkRecipeAvailable() {
            if (!this.activeRecipe.Recipe) {
                return false;
            }
            this.recipeAvailable = true;
            this.activeRecipe.Recipe.forEach(element => {
                var playerItemCount = this.findItemsCountInInventory(element);
                var recipeCount = element.Count;
                if (recipeCount > playerItemCount) {
                    this.recipeAvailable = false;
                }
            });
        },
        craftItem(item) {
            axios.post('https://eclipse/CraftItem', {
                Item: item,
                Time: item.Crafttime
            }).then(() => { return; }).catch(function () { console.log() });
        }
    },
    mounted() {
        window.addEventListener("message", (event) => {
            if (event.data.type == "LoadCraftTableData") {
                this.itemList = event.data.list;
                this.recipeList = event.data.recipes;
            } 
        },false);
    }
}

</script>