<template>
    <div class="app_fullscreen content_page app_auto">
        <transition name="transition_03">
            <div class="content_page_l2 page_1" v-show="page==1">
                <div class="contact_heading">
                    <button @click="closeApp" class="blue_button_back black">Выйти</button>
                    <div class="app_logo"></div>
                </div>
                <div class="content">
                    <div class="auto_item" v-for="(item, index) in carList" :key="'myauto_' + index" @click="showFullAuto(index)">
                        <span class="carname">{{item.Vehicle}}</span>
                        <span class="carfactory">{{item.Type}}</span>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_2" v-show="page == 2">
                 <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back white">Назад</button>
                    <h4 class="app_logo">Добавить</h4>
                </div>
                <div class="content">
                    
                </div>
                <div class="controls">
                    
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_2" v-if="page == 3">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back black">Назад</button>
                    <h4 class="app_name">{{activeVehicle.Vehicle}}</h4>
                </div>
                <div class="content car_info">
                    <div class="car_row" v-if="activeVehicle.Vehicle"><span>Марка:</span><span>{{activeVehicle.Vehicle}}</span></div>
                    <div class="car_row" v-if="activeVehicle.Type"><span>Класс:</span><span>{{activeVehicle.Type}}</span></div>
                    <div class="car_row" v-if="activeVehicle.Plate"><span>Номер:</span><span>{{activeVehicle.Plate}}</span></div>
                    <!-- <div class="car_row" v-if="activeVehicle.color"><span>Цвет:</span><span>{{activeVehicle.color}}</span></div> -->
                </div>
                <div class="controls mt-3">
                    <button class="btn btn-block btn-auto" @click="showCarOnMap()">Найти на карте</button>

                </div>
            </div>
        </transition>

    </div>
</template>

<script>
import axios from 'axios';
// import axios from 'axios';
import allCarList from '../../../json/vehicles.json'

export default {
    name: 'App_yellow',
    components: {
        
    },
    data() {
        return {
            page: 1,
            allCarList: allCarList,
            activeVehicle: {},
            activeVehFullInfo: {},
            carList: null
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.$parent.whiteColor = true;
        },
        changePage(page) {
            this.page = page;
        },
        showFullAuto(veh) {
            this.changePage(3);
            this.activeVehicle = this.carList[veh];
            this.activeVehFullInfo = this.allCarList.find(elem => elem.modelName ==  this.activeVehicle.name);
        },
        showCarOnMap() {
            axios.post('https://eclipse/ShowVehicleOnMap', {
                Id: this.activeVehicle.Id
            }).then(() => { return; }).catch(function () { console.log() });  
        }
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadMyVehicles") {
                this.carList = event.data.vehicles;
                console.log(this.carList)
            }
        },false);
    },
    
}
</script>