                            <div class="section_description">Оружие - твое конституционное право!<br>Не позволяй никому отобрать его под громкие лозунги.</div>
                        </div>
                        <swiper class="swiper swiper_weapon" :options="swiperOptions">
                            <swiper-slide class="product" v-for="(item, index) in popularWeapons" :key="'weapons'+index">
