                                    </div>
                                </div>
                            </swiper-slide>
                        </swiper>
                    </div>

                </transition>

                <transition-group tag="div" name="list">
                    <div class="content" v-show="page == count" v-for="(elem, count) in weaponFullList" :key="'weapon'+count">
                        <div class="top_block">
                            
                            <h2 class="section_title">{{showName(elem.category).name}}<span>{{showName(elem.category).name2}}</span></h2>
                            <div class="section_description" v-html="showName(elem.category).description"></div>
                        </div>
                        <swiper class="swiper swiper_weapon" :options="swiperOptions" v-if="weaponFullList">
