                                    <h4 class="name">{{item.Name}}</h4>
                                    <p class="description">{{item.Description}}</p>
                                    <h5 class="license">Лицензия: <span v-for="elem in item.License" :key="'lic_'+elem">{{elem}} </span></h5>
                                </div>
                                <h5 class="price">${{item.Price}}.00</h5>
                                <div class="hover_block">
                                    <div class="stats">
                                        <div class="stat_block">
                                            <h5 class="label_text">Урон</h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).damage + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text">Скорострельность</h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).speed + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text">Меткость</h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).accuracy + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text">Дальность</h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).range + '%'"></div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="controls">
                                        <button class="btn btn-block btn-fastbuy" @click="fastBuy(item)">Быстрая покупка</button>
                                        <button class="btn btn-block btn-buy" @click="addToCart(item)">В корзину</button>
                                    </div>
                                </div>
                            </swiper-slide>
                        </swiper>
                    </div>

                </transition-group>

                <transition name="list">
                    <div class="content cart_page" v-show="page == 100">
