                                        <span class="quantity">{{item.Count}} x <span class="amount">${{item.Price}}.00</span></span>
                                        <div class="count">
                                            <button class="btn-minus" @click="minusCount(index)"></button>
                                            {{item.Count}}
                                            <button class="btn-plus" @click="plusCount(index)"></button>
                                        </div>
                                    </li>
                                </ul>
                            </div>
