                {type: "Throwables", name: "Разное", name2: "", description: "Раньше эти штуки продавали из под полы,<br>но тяжелые времена вынуждают принимать сильные решения."},
                {type: "Ammo", name: "Патроны", name2: "", description: "„Добрым словом и пистолетом вы можете добиться<br>гораздо большего, чем одним только добрым словом.“"},
            ],
            siteName: "https://storage.googleapis.com/eclipse-rp/archive/"
        }
        
    },
    methods: {
        changePage(index) {
            if (index != this.page) {
                this.page = index;
            }
        },
        addToCart(item) {
            var result =  this.cartList.indexOf(item);
            if (result == -1) {
                item.Count = 1;
                this.cartList.push(item);
            } else {
                this.cartList[result].Count += 1;
            }
            this.calculate();
        },
        showName(text) {
            const newText = text.trim().toLowerCase()
            var elem = this.weaponTypeList.find(item => 
                item.type.trim().toLowerCase() === newText
            );
            if (elem) {
                return elem
            } else {
                return "не найдено"
            }
        },
        calculate() {
            this.fullPrice = 0;
            this.count = 0;
            this.cartList.forEach(item => {
                this.fullPrice += (item.Price * item.Count);
                this.count += item.Count;

            });
        },
        getItemByType(type) {
            var array = this.weaponFullList.filter((item) => item.type == type)
            return array;
        },
        getItemById(id) {
            var weapon = this.weaponData.find((item) => item.id == id)
            if (!weapon) {
                weapon = 0;
            }
            return weapon;
        },
        remove(index) {
            this.cartList.splice(index, 1);
            this.calculate();
        },
        plusCount(index) {
            this.cartList[index].Count += 1;
            this.calculate();
        },
        minusCount(index) {
            this.cartList[index].Count -= 1;
            if (this.cartList[index].Count == 0) {
                this.cartList.splice(index, 1);
            }
            this.calculate();
        },
