<template>
    <div class="content_outer loader">
        <div class="main_content">
            <h2 class="text-center">Профессия грузчика</h2>
            <p class="justify">Грузчик, грузчик - <b>парень работящий!</b> Посмотри на этих тупых качков, которые имеют горы мяса, а на деле не могут поднять монетку с асфальта.</p>
            <p class="justify">Или эти гламурные залы, где толпа людей двигает туда-сюда непонятные куски железа без толка.</p>
            <p class="justify">Только у нас, ты получишь настоящую силу, таская сутками ящики. А что самое главное - совершенно бесплатно. Наоборот, ты ещё и заработаешь. Какой ещё фитнес-клуб тебе такое предложит?</p>
            <p class="justify">И постарайся ничего не разбить и не сломать, всё таки, мы вычтем всё это из твоей зарплаты. И поспеши, следующая машина для разгрузки приедет с минуты на минуту!</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()">Стать грузчиком</button>
                 <button v-else class="btn btn-primary" @click="stopJob()">Закончить рабочий день</button>
                <button class="btn btn-secondary" @click="exit()">Выход</button>
            </div>
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobLoader',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/LoaderStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/LoaderStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>