<template>
    <div class="content_outer busdriver" >
        <div class="main_content" v-if="page == 0">
            <h2 class="text-center">Профессия водителя автобуса</h2>
            <p class="justify">Самая романтичная работа на свете. Что может быть лучше, чем возить бедняков из района в район и <b>выбивать всю дурь</b> из идиотов, решившихся прокатиться бесплатно?</p>
            <p class="justify">В городе десяток остановок, а водителей не хватает. Хватай баранку этого чудо-транспорта, непонятно как дожившего до наших дней, и отправляйся в город бесконечных пробок и парковок.</p>
            <p class="justify">Если ты решишь немного втопить и намотать в свою смену пару кругов побольше, то мы будем только рады. Премии лучшим! Ты же один из нас, я вижу.</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="changePage(1)">Стать водителем автобуса</button>
                <button v-else class="btn btn-primary" @click="stopJob()">Закончить рабочий день</button>
                <button class="btn btn-secondary" @click="exit()">Выход</button>
            </div>
        </div>

        <div class="main_content" v-if="page == 1">
            <h2 class="text-center">Выберите маршрут</h2>

            <div class="routes">
                <div class="route_item" v-for="item in routes" :key="'route_'+item.Id">
                    <div class="row">
                        <div class="col-8">
                            <h5>{{item.Name}}</h5>
                            <p>Маршрут: {{item.RouteName}}<br>{{item.Description}}</p>
                        </div>
                        <div class="col-4 d-flex align-items-center">
                            <button class="btn btn-primary btn-block" @click="startJob(item.Id)">Начать</button>
                        </div>
                    </div>
                </div>
            </div>

            <div class="menu">
                <button class="btn btn-secondary" @click="changePage(0)">Назад</button>
            </div>
        </div>

        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: 'JobBusdriver',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            page: 0,
            routes: [
                {
                    Name: "Маршрут №2",
                    RouteName: "Морнингвуд - Строуберри",
                    Description: "через Бульвар Эклипс",
                    Id: 0,
                },
                {
                    Name: "Маршрут №4",
                    RouteName: "Морнингвуд - Строуберри",
                    Description: "через Дель Пьерро и Хавик авеню",
                    Id: 1,
                },
                {
                    Name: "Маршрут №10",
                    RouteName: "Рокфорд Хиллз - Ла Пуэрта",
                    Description: "через Спэниш Авеню и Пауэр ст.",
                    Id: 2,
                },
                {
                    Name: "Маршрут №14",
                    RouteName: "Морнингвуд - Строуберри",
                    Description: "через Карцер Ву??",
                    Id: 3,
                },
                {
                    Name: "Маршрут №16",
                    RouteName: "Морнингвуд - Строуберри",
                    Description: "через Дорсет Др.",
                    Id: 4,
                },
                {
                    Name: "Маршрут №20",
                    RouteName: "Дель Пьерро - Восточный Лос-Сантос",
                    Description: "через Сан-Андреас авеню",
                    Id: 5,
                },
                {
                    Name: "Маршрут №21",
                    RouteName: "Морнингвуд - Даунтаун ЛС",
                    Description: "через Сан-Андреас авеню",
                    Id: 6,
                },
                {
                    Name: "Маршрут №30",
                    RouteName: "Западный Вайнвуд - Восточный Лос-Сантос",
                    Description: "через бульвар Сан Витус ",
                    Id: 7,
                },
                {
                    Name: "Маршрут №33",
                    RouteName: "Дель Пьерро - Восточный Лос-Сантос",
                    Description: "через пляж Веспуччи",
                    Id: 8,
                },
                {
                    Name: "Маршрут №45",
                    RouteName: "Даунтаун ЛС - Аэропорт Лос-Сантос",
                    Description: "через Строуберри авеню",
                    Id: 9,
                },
                {
                    Name: "Маршрут №60",
                    RouteName: "Вайнвуд - остров Терминал",
                    Description: "через Эглин и бульвар Ловенштейна",
                    Id: 10,
                }
            ]
        }
    },
    methods: {
        changePage(numb) {
            if (this.page !=numb) {
                this.page = numb;
            }
        },
        startJob(numb) {
            axios.post('https://eclipse/BusDriverStartWorkDay', {
                RouteId: numb
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/BusDriverStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>