        editLaw(index) {
            this.activeLawEdit = this.lawList[index];
            this.activeLawEditModel = JSON.parse(JSON.stringify(this.activeLawEdit))
            this.changePage(22);
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        changeStatus(law, index){
            law.Status = index;
            this.showEditLawDropdown = false;
        },
        saveEditLaw() {
            this.activeLawEditModel.Lastedit = this.playerData.id;
            var index = this.lawList.findIndex(item => item.Id == this.activeLawEditModel.Id);
            this.lawList[index] = JSON.parse(JSON.stringify(this.activeLawEditModel));
            this.sortByDate(this.lawList);
            this.changePage(2)
        },
        sortByDate(array) {
            array.sort(function (a, b) {
                var dateA = new Date(a.date),
                    dateB = new Date(b.date);
                return dateB - dateA;
            });
        },
        sortByDateBack(array) {
            array.sort(function (a, b) {
                var dateA = new Date(a.date),
                    dateB = new Date(b.date);
                return dateA - dateB;
            });
        },
        sendAppointment() {
            this.appointmentModel
        },
        showAllArray(array) {
            array.forEach(element => {
                element.show = true;
            });
        },
        searchPlayer() {
            var searchText = this.searchFieldModel;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.playerList.forEach((element) => {
                    var string = element.Id.toString()
                        + element.IdCard.Name
                        + element.IdCard.Lastname
                        + element.IdCard.Phone
                        + element.IdCard.About
                        + element.IdCard.Address
                        + this.getSex(element.IdCard.Sex)
                    var result = string.search(exp);
                    if (result == -1) {
                        element.show = false;
                    } else {
                        element.show = true;
                    }
                });
            } else {
                this.showAllArray(this.playerList);
            }
        },
        getSex(sex) {
            if (sex) {
                return "мужской"
            } else {
                return "женский"
            }
        },
        editPlayerLicense(index) {
            this.changePage(31);
            this.activePlayerLicense = this.playerList[index];
        },
        deletePlayerLicense(index) {
            this.activePlayerLicense.Licenses.splice(index, 1);
        },
        addPlayerLicense() {
            var license = this.newPlayerLicense.toLowerCase().trim();
            var isExist = this.activePlayerLicense.Licenses.findIndex(elem => elem == license);
            if (license != "" && isExist == -1) {
                this.activePlayerLicense.Licenses.push(license);
            }
            this.newPlayerLicense = "";
        },
        addNewNews() {
            this.page = 1001;
            this.newPublishModel = {};
        },
