            if (this.newPublishModel.Heading && this.newPublishModel.Description && this.newPublishModel.Text) {
                this.newPublishModel.Date = new Date();
                this.pressNews.push(this.newPublishModel);
                this.sortByDateBack(this.pressNews);    
                this.page = 100;
                
            }
        },
        readFullPublish(index) {
            this.page = 1002;
            this.activePublish = this.pressNews[index];
        },
        editPublish() {
            this.page = 1003;
            var index = this.pressNews.findIndex(item => item == this.activePublish);
            // this.newPublishModel = JSON.parse(JSON.stringify(this.pressNews[index]));
            this.newPublishModel = {...this.pressNews[index]};
        },
        saveEditPublish() {
            var index = this.pressNews.findIndex(item => item == this.activePublish);
            this.pressNews[index] = JSON.parse(JSON.stringify(this.newPublishModel));
            this.page = 100
        }
