<template>
    <transition name="list">
        <div v-if="showInteractionPoint" class="point"></div>
    </transition>
</template>

<script>
// import axios from "axios";
export default {
    name: 'InteractionPoint',
    data() {
        return {
            showInteractionPoint: false,
        }
    },
    methods: {
       
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "CrossHair") {
                this.showInteractionPoint = true;
            } else if (event.data.type == "CrossHairFalse") {
                this.showInteractionPoint = false;
            }
        });
    }
}
</script>

<style scoped>
.point {
    width: 10px;
    height: 10px;
    position: absolute;
    top: calc(50% - 5px);
    left: calc(50% - 5px);
    background-color: transparent;
    border: 1px solid var(--secondary);
    border-radius: 25px;
}
</style>