<template>
    <section class="dialogs">
        <DialogDrugs v-if="showDialog == `drugs`" />
        <DialogInformer v-if="showDialog == `informer`" />
    </section>
</template>

<script>
require('@/assets/css/npc/dialog.css')
import DialogDrugs from './dialogs/drugs.vue'
import DialogInformer from './dialogs/informer.vue'

export default {
    name: 'DialogSystem',
    props: {
        playerData: Object,
    },
    components: {
        DialogDrugs,
        DialogInformer,
    },
    data() {
        return {
            showDialog: ""
        }
    },
    methods: {

    },
    mounted() {
        window.addEventListener("message", (event) => {
            if (event.data.type == "ShowInformatorData") {
                this.showDialog = "informer";
            } 
        },false);
    }
}
</script>