    <section class="fullpage animation">
        <div class="block_animations" v-show="showAnim == true">
            <h5 v-show="activeCategory == -1">Анимации</h5>
            <div class="anim_section_list">
                <div class="parent_list" v-for="(item, index) in animationList" :key="'category'+index">
                    <button class="anim_section_element hoverA" v-show="activeCategory == -1" @click="chooseCategory(index)"><span>{{getNumbLessTen(index + 1)}}</span> {{item.Name}}</button>
                    <div class="child_list" v-show="activeCategory == index">
                        <h5>{{item.Name}}</h5>
                        <button class="anim_section_element hoverA" @click="chooseCategory(-1)">Назад</button>
                        <button class="anim_section_element hoverA" v-for="(newitem, newindex) in item.Animation" :key="'child'+index+newindex" @click="playAnim(index, newindex)"><span>{{getNumbLessTen(newindex + 1)}}</span> {{newitem.Name}}</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- <div class="" v-show="showEditor == true">
            <div class="section_outer">
                <div class="section_inner">
                    <h5>Все анимации</h5>
                    <p class="text-center">Выберите раздел</p>
                    <div class="animation_list anim_cell">
                        <button class="anim_section_element"
                            v-for="(item, index) in animationList"
                            :key="'l_menu' + index"
                            @click="activeColumn = index"
                            :class="{active: activeColumn == index}"
                        >{{item.Name}}</button>
                    </div>
                    
                </div>

                <div class="section_inner row2" v-for="(category, index) in animationList" :key="'row2_cat'+index" v-show="activeColumn == index">
                    <h5>{{category.Name}}</h5>
                    <p class="text-center">Выберите анимацию</p>
                    <draggable class="animation_list" :list="category.list" :group="{ name: 'people', pull: 'clone', put: false }" @change="log">
                        <button class="anim_section_element"
                            v-for="(item, newindex) in category.list"
                            :key="'l_menu_anim'+ index + newindex"
                        >{{item.Name}}</button>
                    </draggable>
                </div>

            </div>
            <div class="section_outer right">
                <div class="section_inner favorite">
                    <h5>Избранное</h5>
                    <p class="text-center">Перетащите анимации</p>
                    <draggable class="animation_list" :list="animationList[0].list" group="people" @change="log">
                        <div
                            class="anim_section_element"
                            v-for="(element, index) in animationList[0].list"
                            :key="element.name + index"
                        >
                        <span>{{getNumbLessTen(index + 1)}}</span>{{ element.name }} 
                        <button class="btn btn-delete" @click="deleteFromFavorites(index)"></button>
                        </div>
                        
