                </div>
            </div>
        </div> -->
    </section>
</template>

<script>
// import animationList from '../../json/animations.json'
import axios from "axios";

export default {
    name: 'AnimationHud',
    components: {
            
        },
    data() {
        return {
            activeCategory: -1,
            activeColumn: -1,
            animationList: [],
            favoritesList: [],
            showAnim: true,
            showEditor: false,
            
        }
    },
    methods: {
        chooseCategory(index) {
            this.activeCategory = index;
        },
        playAnim(group, elem) {
            // this.activeCategory = -1;
            var anim = this.animationList[group].Animation[elem];
            
            if (anim) {
                axios.post('https://eclipse/PlayAnimation', anim).then(() => { return; }).catch(function () { console.log() });
            }
            
        },
        getNumbLessTen(numb) {
            if (numb == 10) {
                numb = 0;
            }
            if (numb >= 0 && numb <=9) {
                return '[' + numb + ']';
            }
        },
        keyHandler(event) {
            var key = event.keyCode - 49;
            if (key == -1 ) {
                key = 9;
            }
            if (key > -2 && key < 10) {
                if (this.activeCategory == -1 ) {
                    this.activeCategory = key;
                } else {
                    this.playAnim(this.activeCategory, key)
                }
            }
            if (event.keyCode == 8 && this.activeCategory != -1) {
                this.activeCategory = -1;
            }
        }
    },
    mounted() {
        this.animationList = [{"Name":"Сидеть","Animation":[{"AnimDict":"missmic_4premiere","Base":"movie_prem_01_f_a","Name":"Сидеть на месте","Flag":8,"Time":0},{"AnimDict":"anim@heists@prison_heiststation@cop_reactions","Base":"cop_b_idle","Name":"Не Сидеть на месте","Flag":49,"Time":0}]}]
        
        window.addEventListener("message", (event) => {
            if (event.data.type == "LoadAnimationList") {
                this.animationList = JSON.parse(event.data.list);
            }
        });

        
        document.addEventListener('keydown', this.keyHandler);
    },
    beforeDestroy() {
        document.removeEventListener('keydown', this.keyHandler);
    },
}
</script>