<template>
    <section class="fullpage bg_test shop_car"
        @mousedown="toggleCameraClick(true)"
        @mouseup="toggleCameraClick(false)"
        @mousemove="toggleCameraMove"
        @mousewheel="wheelMove"
    >
        <!-- <div class="header">
            <h2>AUTO</h2>
            <div class="logo"></div>
            <h2>CENTRE</h2>
        </div> -->
z``
        <button class="btn btn-exit" @click="closePage()" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">Выход</button>

        <transition name="transition_03">
            <div class="" v-show="activePage == 1" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">

                <transition name="transition_03">
                    <div class="left_bar">
                        <div class="car_list_block">
                            <div class="car_item_outer" v-for="(item, index) in carList" :key="'swiperA'+index" :class="{active: activeIndex == index}">
                                <div class="car_item" @click="selectCar(index)" :style="'background-image: url(' + item.Image + ');'" >
                                    <h4>{{item.BrandName}} {{item.Name}}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="transition_03">
                    <div class="right_bar" v-if="activeCar">

                        <div class="logo_block">
                            <div :class="'logo ' + getBrand(activeCar.BrandName)"></div>
                            <div class="names">
                                <h4 class="carname">{{activeCar.Name}}</h4>
                                <hr>
                                <h4 class="manufacturer">{{getVehicleClass(activeCar.Class)}}</h4>
                            </div>
                        </div>

                        <div class="main_block">
                            <h3 class="price">${{getPrice(activeCar.Price)}}</h3>
                            <div class="handlings">
                                <div class="handling_block">
                                    <h5 class="handling_text">Скорость</h5>
                                    <div class="progress">
                                        <div class="progress-bar" :style="'width:' + activeCar.MaxSpeed + '%'"></div>
                                    </div>
                                </div>
                                <div class="handling_block">
                                    <h5 class="handling_text">Ускорение</h5>
                                    <div class="progress">
                                        <div class="progress-bar" :style="'width:' + activeCar.Acceleration * 100 + '%'"></div>
                                    </div>
                                </div>
                                <div class="handling_block">
                                    <h5 class="handling_text">Торможение</h5>
                                    <div class="progress">
                                        <div class="progress-bar" :style="'width:' + activeCar.Braking * 100 + '%'"></div>
                                    </div>
                                </div>
                                <div class="handling_block">
                                    <h5 class="handling_text">Маневренность</h5>
                                    <div class="progress">
                                        <div class="progress-bar" :style="'width:' + activeCar.Handling * 10 + '%'"></div>
                                    </div>
                                </div>
                                <div class="handling_block d-none">
                                    <div class="block_h6">
                                        <h6>Макс. скорость: {{activeCar.MaxSpeed}} км/ч</h6>
                                        <h6>Вес: {{activeCar.Mass}} кг</h6>
                                    </div>
                                    <div class="block_h6">
                                        <h6>Мест: {{activeCar.Seats}}</h6>
                                        <h6>Привод: {{activeCar.Train}}</h6>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="control_block">
                            <!-- <button class="btn btn-test" @click="testDriv()">Тест-драйв</button> -->
                            <button class="btn btn-buy" @click="changePage(2)">Подробнее</button>
                        </div>
                    </div>
                </transition>

                <!-- <div class="footer">
                    <swiper class="swiper" :options="swiperOptions">
                        <swiper-slide v-for="(item, index) in carList" :key="'swiperA'+index">
                            <div class="car_item" @click="selectCar(index)" :style="'background-image: url(' + item.Image + ');'">
                                <h4>{{item.Name}}</h4>
                            </div>
                        </swiper-slide>
                    </swiper>
                </div> -->

            </div>
        </transition>

        <transition name="transition_03">
            <div class="" v-show="activePage == 2" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                <transition name="transition_03">
                    <div class="right_bar" v-if="activeCar">
                        <div class="main_block">
                            <h4 class="carname">{{activeCar.BrandName}} <span>{{activeCar.ModelName}}</span></h4>
                            <h5 class="carclass">{{getVehicleClass(activeCar.Class)}}</h5>

                            <div class="price_block">
                                <div class="fulltax">
                                    <h5 class="handling_text">Месячный налог</h5>
                                    <h3 class="price">${{getPrice(activeCar.Price / tax)}}</h3>
                                </div>
                                <div class="fullprice">
                                    <h5 class="handling_text">Полная цена</h5>
                                    <h3 class="price">${{getPrice(activeCar.Price)}}</h3>
                                </div>
                            </div>
                        </div>
                        <div class="color_block">
                            <div class="color_list">
                                <div :class="{active: activeColor == index}" class="color_block_block" v-for="(item, index) in allColorsList[5].list" :key="'carcols'+index" :style="'background-color: ' + item.color + '; border: 2px solid' + item.color" @click="setActiveColor(index, item.id);">
                                </div>
                            </div>
                        </div>
                        <div class="control_block">
                            <button class="btn btn-test" @click="changePage(1)">Назад</button>
                            <button class="btn btn-buy" @click="buyCar()" :disabled="buyButtonDisabled">Купить</button>
                        </div>
                    </div>
                </transition>
            </div>
        </transition>
    </section>
</template>

<script>
import allColorsList from '../../json/carcolors.json'
import axios from "axios";
// import "swiper/swiper-bundle.min.css"

export default {
    name: "ShopAuto",
    props: {
        shopName: String,
    },
    data() {
        return {
            swiperOptions: {
                loop: false,
                autoplay: false,
                slidesPerView: 6,
                spaceBetween: 20,
                mousewheel: true,
                direction: 'horizontal',
            },

            activePage: 1,
            activeIndex: -1,
            carList: [
                {
                    BrandName: "Tesla",
                    Name: "ModelX"
                },
                {
                    BrandName: "Tesla2"
                }
            ],
            activeCar: undefined,
            activeColor: 0,
            inSelect: false,
            allColorsList: allColorsList,

            tax: 20,
            buyButtonDisabled: false,

            mouseState: false,
            blockScroll: false,
            isControlPressed: false,
            activePlayerAnim: 0,
           
        }
    },
    methods: {
        changePage(index){
            this.activePage = index; 
        },
        selectCar(index) {
            if (!this.inSelect & this.activeIndex!= index) {
                this.activeIndex = index;
                this.inSelect = true;
                this.setUnactive();
                this.carList[index].active = true;
                
                this.activeCar = undefined;
                axios.post('https://eclipse/SpawnVehicle', {
                    Model: this.carList[index].ModelName,
                }).then(() => { return; }).catch(function () { console.log() });

                setTimeout(() => {
                    this.activeCar = {};
                    this.activeCar = this.carList[index];
                }, 100);
                setTimeout(() => {
                    this.inSelect = false;
                }, 300);   
                
            }
        },
        doCommand(e) {
            if(e.keyCode == 27 ) {
                
                axios.post('https://eclipse/CloseShop', {
                }).then(() => { return; }).catch(function () { console.log() });  
            }
        },
        setActiveColor(index, id) {
            this.$set(this.activeCar, "color", id);
            this.activeColor = index;
            axios.post('https://eclipse/SetActiveVehicleColor', {
                Color: id
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        setUnactive() {
            this.carList.forEach(element => {
                element.active = false;
            });
        },
        getVehicleClass(name) {
            if (!name) {
                name = "Не указано в массиве"
            }
            switch (name) {
                case "VC_SUPER":
                    return "Суперкар"
                case "VC_MUSCLE":
                    return "Маслкар"
                case "VC_SPORT_CLASSIC":
                    return "Спортивная классика"
                case "VC_OFF_ROAD":
                    return "Внедорожник"
                case "VC_VAN":
                    return "Фургон"
                case "VC_SEDAN":
                    return "Седан"
                default:
                    return name
            }
        },
        getBrand(name) {
            if (name) {
                return name.toLowerCase()
            } else {
                return "не найдено"
            }
        },
        getPrice(numb) {
            var elem = String(numb);
            var str = elem.replace(/(?!^)(?=(?:\d{3})+(?:\.|$))/gm, '.');
            return str;
        },
        testDrive() {
            if(this.activeCar != undefined) {
            axios.post('https://eclipse/TestDrive', {
                    Model: this.activeCar.ModelName,

                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        rentCar() {
            console.log(this.activeCar.Name);
            console.log(this.activeColor);
        },
        buyCar(){
            this.buyButtonDisabled = true;
            axios.post('https://eclipse/BuyVehicle', {
                Model: this.activeCar.ModelName,
                Color: this.allColorsList[5].list[this.activeColor].id
            }).then(() => { return; }).catch(function () { console.log() });
            setTimeout(() => {
                this.buyButtonDisabled = false;
            }, 2000);
        },
        closePage() {
            axios.post('https://eclipse/CloseShop', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showPage = "";
        },
                toggleCameraClick(state) {
            if(state == true) {
            
                this.mouseState = true;
                this.mouseX = 0;
                this.mouseY = 0;
            }
            else if(state == false) {
                this.mouseState = false;
            }
        },
        scrollBlock(state) {
            if(state == true) {
                this.blockScroll = true;

            }
            else if(state == false) {
                this.blockScroll = false;
                this.mouseState = false;
            }
        },
        wheelMove($event) {
            if(!this.blockScroll) {
                axios.post('https://eclipse/Mouse_CarShop', {
                    DeltaY: $event.deltaY,
                    CtrlPressed: this.isControlPressed
                }).then(() => { return; }).catch(function (error) { console.log(error) });
                
            }
        },
        pressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = true;
            }
        },
        unpressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = false;
            }
        },
        toggleCameraMove($event) {
            if(this.mouseState == true && !this.blockScroll) {
                if(this.mouseX == 0 && this.mouseY == 0) {
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY;
                    return;
                }
                if($event.clientX || $event.clientY) {
                    var xPosition = $event.clientX - this.mouseX;
                    var yPositiuon = $event.clientY - this.mouseY;
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY; 
                    axios.post('https://eclipse/X_CarShop', {
                    Xposition: xPosition,
                    Yposition: yPositiuon
                }).then(() => { return; }).catch(function (error) { console.log(error) });
                }
            }
        },
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    },
    mounted() {
        this.setUnactive();
        window.addEventListener("message", (event) => {
            if (event.data.type == "LoadCarList") {
                this.carList = event.data.data;
            } 
        },false);
    }
}
</script>

