<template>
    <div class="content_outer electric">
        <div class="main_content">
            <h2 class="text-center">Профессия электрика</h2>
            <p class="justify">Все чаще слышатся разговоры: да какой урод поставил на этом повороте столб? <b>Электрическая Служба Лос-Сантоса!</b> Да, мы уроды, и что?</p>
            <p class="justify" v-if="playerData.sex">Они просто завидуют. Стань одним из настоящих <b>мужиков</b>, готовых на всё, ради обеспечения любимого города электричеством.</p>
            <p class="justify" v-else>Они просто завидуют. Стань одной из настоящих <b>женщин</b>, готовых на всё, ради обеспечения любимого города электричеством.</p>
            <p class="justify">В твои обязанности входит ремонт дорожных столбов, электросетей и всякой другой херни. За свой кропотливый труд ты получишь неиссякаемое количество уважения, почёта и немного бабла.</p>
            <p class="justify">Если ты не боишься удара жалких <b>1000</b> ампер, а «папа» и «мама» это не только устаревшее гендерное определение твоих родителей, но и обозначение разъемов, то эта профессия создана для <b>тебя</b>.</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()">Стать электриком</button>
                <button v-else class="btn btn-primary" @click="stopJob()">Закончить рабочий день</button>
                <button class="btn btn-secondary" @click="exit()">Выход</button>
            </div>
            
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobElectric',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/ElectricStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/ElectricStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>