<template>
    <div class="vehicle_hud">
        <div class="speedometer_outer type_2" v-if="speedometerType == 2">
            <Speedometer
                class="layer_speedometerA"
                :radius=150
                :progress="currentSpeed/4"
                :stroke=8
                :gradient="true"
            />
            <Speedometer
                class="layer_speedometerC"
                :radius=150
                :progress=75
                :stroke=8
                :gradient="false"
            />
            <div class="layer_speedometerD">
                <h2 class="speedometer">{{currentSpeed}}</h2>
                <h3 class="speedunits">kph</h3>
                <div class="gearbox">
                    <i class="gearbox_icon"></i>
                    <span>{{currentGear}}</span>
                </div>
            </div>
            <div class="layer_speedometerE">
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
            </div>
            <div class="layer_speedometerF">
                <b-icon v-bind:class="{ active: icon_lock == true}"  class="icon_lock icon" icon="lock"></b-icon>
                <b-icon v-if="!icon_beam" v-bind:class="{ active: icon_light == true}" class="icon_light icon" icon="lightning"></b-icon>
                <b-icon v-if="icon_beam" class="icon_light icon active" icon="lightning-fill"></b-icon>
                <b-icon v-bind:class="{ active: icon_fuel <= 25}"  class="icon_fuel icon" icon="droplet"></b-icon>
                <b-icon v-bind:class="{ active: icon_wrench == true}" class="icon_wrench icon" icon="wrench"></b-icon>
                <b-icon v-if="!icon_autopilot" class="icon_autopilot icon" icon="shift"></b-icon>
                <b-icon v-if="icon_autopilot" class="icon_autopilot icon active" icon="shift-fill"></b-icon>
                <b-icon v-bind:class="{ active: icon_engine == true}" class="icon_engine icon" icon="key"></b-icon>
            </div>
            <div class="layer_speedometerG">
                <h3 class="district">{{hud_district}}</h3><h3 class="road">{{hud_street}}<span v-if="hud_street2"> | {{hud_street2}}</span></h3>
            </div>
        </div>

        <div class="speedometer_outer" v-if="speedometerType == 1">
            <Speedometer
                class="layer_speedometerA"
                :radius=90
                :progress="currentSpeed/4"
                :stroke=8
                :gradient="true"
            />
            
            <Speedometer
                class="layer_speedometerC"
                :radius=90
                :progress=75
                :stroke=8
                :gradient="false"
            />
            <div class="layer_speedometerD">
                <h2 class="speedometer">{{currentSpeed}}</h2>
                <h3 class="speedunits">kph</h3>
                <div class="gearbox">
                    <i class="gearbox_icon"></i>
                    <span>{{currentGear}}</span>
                </div>
            </div>
            <div class="layer_speedometerE">
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>
            </div>
            <div class="layer_speedometerF">
                <b-icon v-bind:class="{ active: icon_lock == true}"  class="icon_lock icon" icon="lock"></b-icon>
                <b-icon v-if="!icon_beam" v-bind:class="{ active: icon_light == true}" class="icon_light icon" icon="lightning"></b-icon>
                <b-icon v-if="icon_beam" class="icon_light icon active" icon="lightning-fill"></b-icon>
                <b-icon v-bind:class="{ active: currenFuel <= 25}"  class="icon_fuel icon" icon="droplet"></b-icon>
                <b-icon v-bind:class="{ active: icon_wrench == true}" class="icon_wrench icon" icon="wrench"></b-icon>
                <b-icon v-if="!icon_autopilot" class="icon_autopilot icon" icon="capslock"></b-icon>
                <b-icon v-if="icon_autopilot" class="icon_autopilot icon active" icon="capslock-fill"></b-icon>
                <b-icon v-bind:class="{ active: icon_engine == true}" class="icon_engine icon" icon="key"></b-icon>

                <b-icon v-if="icon_left || icon_alarm" class="icon_left icon active" icon="arrow-left-circle-fill"></b-icon>
                <b-icon v-if="!icon_left" class="icon_left icon" icon="arrow-left-circle"></b-icon>

                <b-icon v-if="icon_right || icon_alarm" class="icon_right icon active" icon="arrow-right-circle-fill"></b-icon>
                <b-icon v-if="!icon_right" class="icon_right icon" icon="arrow-right-circle"></b-icon>

            </div>
            <div class="layer_speedometerG">
                <h3 class="district">{{hud_district}}</h3><h3 class="road">{{hud_street}}<span v-if="hud_street2"> | {{hud_street2}}</span></h3>
            </div>
            <div class="layer_speedometerH">
                <div class="scale"></div>
                <div class="scale"></div>
                <div class="scale"></div>

                <div class="fuel_arrow" :style="`transform: rotate(`+ fuelVolume +`deg)`"></div>
            </div>
        </div>
    </div>
</template>

<script>
require('@/assets/css/hud.css')
import Speedometer from './Speedometer.vue'
import sound from '../../assets/sounds/turn.mp3'

export default {
    name: 'VehicleCad',
    props: {
        vehicleData: Object,
    },
    components: {
        Speedometer
    },
    data() {
        return {
            icon_lock: false,
            icon_fuel: 100,
            icon_wrench: false,
            icon_light: false,
            icon_beam: false,
            icon_autopilot: false,
            icon_engine: false,
            icon_left: false,
            icon_right: false,
            icon_alarm: false,
            hud_district: "",
            hud_street: "",
            hud_street2: "",

            currentSpeed: this.vehicleData.Speed,
            currentGear: this.vehicleData.Gear,
            currenFuel: this.vehicleData.Fuel,
            
            speedometerType: 1,

            playTurnSound: false,
            isSoundStarted: false,
            timer: undefined,
        }
    },
    computed: {
        fuelVolume() {
            var fuel = (this.currenFuel - 50) * 1.3;
            if (fuel > 100) {
                fuel = 100;
            }
            return fuel;
        }
    },
    methods: {
        playSound() {
            var audio = new Audio(sound);
            audio.play();
        },
        checkTurnSoundStatus() {
            if (this.icon_left || this.icon_right || this.icon_alarm) {
                this.startTimer();
            } else {
                this.stopTimer();
            }
        },
        startTimer() {
            if (!this.isSoundStarted) {
                this.isSoundStarted = true;
                this.timer = setInterval(() => {
                if (this.isSoundStarted) {
                    this.playSound();
                } else {
                    this.stopTimer()
                }
            }, 730);
            }
            
        },
        stopTimer() {
            this.isSoundStarted = false;
            clearTimeout(this.timer);
        },
    },
    mounted() {
        window.addEventListener("message",(event) => { 
            if(event.data.type == "LoadVehicleData") {
                this.currentSpeed = JSON.parse(event.data.data).Speed;
                this.currentGear = JSON.parse(event.data.data).Gear;
                this.icon_light = JSON.parse(event.data.data).Light;
                this.icon_beam = JSON.parse(event.data.data).Beam;
                this.icon_engine = JSON.parse(event.data.data).Engine
                this.currenFuel = JSON.parse(event.data.data).Fuel;
                this.icon_lock = JSON.parse(event.data.data).Lock;
                this.hud_district = JSON.parse(event.data.data).Zone;
                this.icon_autopilot = JSON.parse(event.data.data).Cruise;
                this.hud_street = JSON.parse(event.data.data).StreetName;
                this.hud_street2 = JSON.parse(event.data.data).CrossRoad;
                this.icon_left = JSON.parse(event.data.data).Left;
                this.icon_right = JSON.parse(event.data.data).Right;
                this.icon_alarm = JSON.parse(event.data.data).Alarm;


                this.checkTurnSoundStatus();
            } else if (event.data.type == "OnPlayerExitVehicle") {
                console.log('OnPlayerExitVehicle')
                this.icon_left = false;
                this.icon_right = false;
                this.icon_alarm = false;
                this.stopTimer();
            }
        },false);
    }
}

</script>