<template>
    <section class="minigames">
        <ElectricGame v-if="activeGame == 'Electric'" />
        <HackerGame v-if="activeGame == 'Hacker'" />
        <LockpickingGame v-if="activeGame == 'Lockpicking'" />
        
    </section>
</template>

<script>
import ElectricGame from './games/ElectricGame.vue'
import HackerGame from './games/HackerGame.vue'
import LockpickingGame from './games/LockpickingGame.vue'

export default {
    name: "miniGames",
    props: {
        
    },
    components: {
        ElectricGame,
        HackerGame,
        LockpickingGame
    },
    data() {
        return {
            activeGame: "",
        }
    },
    methods: {
            
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "ActivateGame") {
                this.activeGame = event.data.game;
            }
        }, false);

        var random = Math.floor(Math.random() * 3);
        if (random == 1) {
            this.activeGame = "Hacker"
        } else if (random == 2) {
            this.activeGame = "Electric"
        } else {
            this.activeGame = "Lockpicking"
        }
    },
}
</script>