<template>
    <section>
        <transition name="list">
            <div class="showbusiness bg_primary" v-if="page == 0">
                <h3 class="text-center">{{businessData.Name}}</h3>
                <div class="business_info" v-if="businessData">
                    <!-- <h6 v-if="businessData.OwnerId != 0">Владелец: <span>{{businessData.OwnerId}}</span></h6> -->
                    <h6 v-if="businessData.OwnerId == 0">Владелец: <span>Отсутствует</span></h6>
                    <h6>Тип бизнеса: <span>{{getBusinessType(businessData.BusinessType)}}</span></h6>
                    <h6>Статус: <span>Открыт</span></h6>
                    <h6 v-if="businessData.OwnerId == 0">Цена: <span>{{businessData.Price}}$</span></h6>
                    <h6>Налог: <span>{{businessData.TaxPercent}}%</span></h6>
                </div>
                <span v-else>Ошибка загрузки данных</span>
                <div class="controls">
                    <button v-if="businessData.OwnerId == 0" @click="buyBusiness()" class="btn btn-secondary">Купить</button>
                    <button v-if="businessData.OwnerId == playerData.id" @click="changePage(1)" class="btn btn-secondary">Продать бизнес</button>
                    <button class="btn btn-primary" @click="closePage()">Закрыть</button>
                </div>
            </div>
        </transition>
         <transition name="list">
            <div class="showbusiness bg_primary" v-if="page == 1">
                <h3>Продать магазин:</h3>
                <label>Продать игроку по номеру телефона:</label>
                <input class="form-control" v-model="sellToPlayerById" placeholder="Введите номер телефона">
                <div class="controls mt-3">
                    <button @click="sellBusinessToPlayer()" class="btn btn-block btn-secondary">Продать игроку</button>
                    <button @click="sellBusinessToGover()" class="btn btn-secondary">Продать правительству</button>
                    <button @click="changePage(0)" class="btn btn-primary">Назад</button>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import axios from "axios";
export default {
    name: 'ShowBusiness',
    props: {
        playerData: Object,
    },
    data() {
        return {
            businessData: {
                Id: 0,
                Name: "Неизвестный магазин",
                BusinessType: 0,
                OwnerId: 1,
                Price: 90000,
                TaxPercent: 0,
                Status: "Открыт",
            },
            page: 0,
            sellToPlayerById: undefined,
        }
    },
    methods: {
        closePage() {
            this.$parent.showBusinessInfo = false;
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changePage(numb) {
            if (this.page != numb) {
                this.page = numb;
                this.sellToPlayerById = undefined;
            }
        },
        buyBusiness() {
            axios.post('https://eclipse/TryBuyBusiness', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        getBusinessType(type) {
            switch(type) {
                case 0:
                    return "Supermarket";
                case 1:
                    return "Ammunation";
                case 2:
                    return "Clothes";
                case 3:
                    return "Cafe";
                case 4:
                    return "Bar";
                case 5: 
                    return "Club";
                case 6:
                    return "AutoMarket";
                case 7:
                    return "Car Wash";
                case 8:
                    return "Gas Station";
            }
        },
        sellBusinessToPlayer() {
            var playerPhone = this.sellToPlayerById;
            if (!playerPhone) {
                // высираем ошибку: "Введите правильный номер телефона"
                return
            }
            if (playerPhone.length >= 5) {
                // продаем 
                // высираем "Бизнес успешно продан игроку Имя Фамилия"
                this.closePage()
            } else {
                // высираем ошибку "Введите правильный номер телефона"
            }
        },
        sellBusinessToGover() {
            // высираем "Бизнес успешно продан правительству"
            this.closePage()
        }
    },
    mounted() {
        
        window.addEventListener(
        "message",
        (event) => {
            if (event.data.type == "LoadBusinessInfo") {
                this.businessData = event.data.businessData;
            }
        },

        false
        );
    }
}

</script>