<template>
    <section class="elevator_outer">
         <transition name="list">
            <div class="elevator bg_white">
                <h3>Лифт</h3>
                <div class="elevator_buttons">
                    <div class="elevator_button" v-for="item in data.slice().reverse()" :key="'lvt_'+item.Id" @click="pressButton(item)">{{item.Id}}</div>
                </div>
                <div class="exit_btn">
                    <button class="btn btn-secondary" @click="closePage()">Выход</button>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import axios from "axios";
export default {
    name: 'EstateInfo',
    props: {
        playerData: Object,
    },
    data() {
        return {
            data: [
                {
                    Id: 1,
                    Name: "Kv1",
                    IsHouse: true,
                },
                {
                    Id: 2,
                    Name: "Kv1",
                    IsHouse: true,
                },
                {
                    Id: 3,
                    Name: "Kv1",
                    IsHouse: true,
                },
                {
                    Id: 4,
                    Name: "Kv1",
                    IsHouse: true,
                }
            ],
            address: "",
        }
    },
    methods: {
        closePage() {
            this.$parent.showElevator = false;
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        pressButton(item) {

            if(item.IsHouse) {
                axios.post('https://eclipse/ShowEstateInfo', {
                    HouseId: item.HouseId
                }).then(() => { return; }).catch(function () { console.log() });
            }
            else {
                ///
            }
            console.log(item);
            this.closePage();
        }
       
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadElevatorData") {
                this.data = event.data.elevatorData;
            
            }
        },false);
    }
}

</script>