<template>
    <section class="interaction_wheel">
        <transition name="list2">
            <div class="wheel_outer" v-if="show">
                <div class="wheel_inner level_0">
                    <div class="level_0_piece" v-for="(item, index) in 36" :key="index + 'l0'"></div>
                </div>
                <transition name="transition_03">
                    <div class="wheel_inner level_1" v-if="activeMenu != -2">
                        <div class="button_piece active" v-if="activeMenu != -1" @click="chooseMenu(-1)">
                            <span>Назад</span>
                            <div class="line"></div>
                        </div>
                        <div class="button_piece" :class="{active: item.SubMenu}" v-for="(item,index) in activeArray" :key="index+'menu'" @click="chooseMenu(index)">
                            <span>{{item.Name}}</span>
                            <div class="line"></div>
                            <div class="tip" v-if="item.Tip">{{item.Tip}}</div>
                        </div>

                    </div>
                </transition>
                <transition name="transition_03">
                    <div class="wheel_inner level_1" v-if="activeMenu == 122">
                        <div class="button_piece" @click="chooseMenu(0)">
                            <span>Назад</span>
                            <div class="line"></div>
                        </div>
                        <div class="button_piece" v-for="(item,index) in activeArray" :key="index+'menu'" @click="chooseSubMenu(index)">
                            <span>{{item.Name}}</span>
                            <div class="line"></div>
                            <div class="tip" v-if="item.Tip">{{item.Tip}}</div>
                        </div>

                    </div>
                </transition>
                <div class="wheel_inner level_2"></div>
                <button class="close_button" @click="closeWheel()"></button>
            </div>
        </transition>
    </section>
</template>

<script>
import axios from "axios";
export default {
    name: 'InteractionWheel',
    data() {
        return {
            show: false,
            menu: [],
            menuInteractionPed: [],
            activeArray: undefined,
            activeMenu: -1,
            activeSubMenu: 0,
            inAnimation: false,
        }
    },
    methods: {
        chooseMenu(index){
            this.activeMenu = -2;
            setTimeout(() => {
                if (index == -1 ) {
                    this.activeArray = this.menu;
                    this.activeMenu = -1;
                    return;
                }
                if (this.activeArray[index].Command){
                    this.sendCommand(this.activeArray[index].Name, this.activeArray[index].Command);
                    return
                }
                this.activeArray = this.activeArray[index].SubMenu;
                this.activeMenu = index;
            }, 250);

        },
        chooseSubMenu(index) {
            if (this.activeSubMenu != index) {
                this.activeSubMenu = index;
                this.activeArray = this.activeArray.SubMenu;
            }
        },
        sendCommand(name, Command) {
            this.activeArray = this.menu;
            this.activeMenu = -1;
            this.closeWheel();
            axios.post('https://eclipse/VehicleInteractionCommand', {
                Command: Command
            }).then(() => { return; }).catch(function () { console.log() });
        },
        closeWheel() {
            axios.post('https://eclipse/CloseVehicleInteractionWheel', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.show = false;
        },
        showHide() {
            this.show = !this.show;
        },
        doCommand(e) {
            if(e.keyCode == 27 && this.show) {     
                this.closeWheel();
            }
        },
    },
    mounted() {
        this.activeArray = this.menu;
        window.addEventListener("message",(event) => {
            if (event.data.type == "ActivateWheel") {
                this.show = true;
            }
            else if(event.data.type == "LoadMenu") {
                this.activeArray = event.data.Menu
                this.menu = event.data.Menu;
            }
            else if(event.data.type == "CloseWheel") {
                this.show = false;
            }
        },false);
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    },
}
</script>

