<template>
    <section class="fullpage">
        <div class="bank">
            <div class="bank_inner">

                <transition name="list">
                    <div class="page page_1" v-show="page == 1" @click="login()">
                        <h2>Добро пожаловать</h2>
                        <div class="insert_card"></div>
                        <h2>Вставьте вашу карту</h2>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_2" v-show="page == 2">
                        <div class="loader">Loading...</div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_99" v-show="page == 99">
                        <h2>Ошибка чтения карты!</h2>
                        <h2>Попробуйте ещё раз</h2>
                        <button class="exit" @click="changePage(1)">Выйти</button>
                    </div>
                </transition>

                <transition name="">
                    <div class="page page_98" v-show="page == 98">
                        <div class="bsod_text">
                            <p>A problem has been detected and Windows has been shut down to prevent damage to your computer.</p>
                            <p>The problem seems to be caused by the following file: atm_app_beta_000004_NEED_FIX.sys</p>
                            <p>CANT_HACK_CARD</p>
                            <p>If this is the first time you've seen this stop error screen, restart your computer. If this screen appears again, follow these steps:</p>
                            <p>Check to make sure any new hardware or software is properly installed. If this is a new installation ask your hardware or software manufacturer for any windows updates you might need.</p>
                            <p>If problem continue, disable or remove any newly installed hardware or software. Disable BIOS memory options such as caching or shadowing. If you need to use safe mode to remove or disable components restart your computer, press F8 to select advanced startup options, and then select safe mode.</p>
                            <p>technical information:</p>
                            <p>*** STOP: 0x0000000a (0xF85AE5b8, 0x00000000, 0XF8527AB9,0X00000000)</p>
                            <p>*** atm_app_beta_000004_NEED_FIX.sys - Address F8527AB9 base at 78525000, DateStamp 0x4802bb66</p>

                            <button class="bsod" @click="reload()">Перезагрузить</button>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_3" v-show="page == 3">
                        <h2>Добро пожаловать</h2>
                        <div class="main_menu">
                            <div class="element">
                                <div class="element_inner" @click="changePage(4)">
                                    <h3>Снять деньги</h3>
                                </div>
                            </div>
                            <div class="element">
                                <div class="element_inner" @click="changePage(6)">
                                    <h3>Пополнить счет</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="changePage(7)">
                                    <h3>Узнать баланс</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="changePage(8)">
                                    <h3>Перевести на счет</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner exit" @click="exit()">
                                    <h3>Выйти</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="changePage(9)">
                                    <h3>Оплатить счета</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 4">
                        <h2>Снять наличные</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner" @click="charge(500)">
                                    <h3>$500</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="charge(100)">
                                    <h3>$100</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="charge(500)">
                                    <h3>$500</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="charge(1000)">
                                    <h3>$1.000</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="changePage(5)">
                                    <h3>Другая сумма</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 5">
                        <h2>Введите сумму</h2>
                        <div class="input_block">
                            <input class="input-withdraw" v-model="withdraw">
                        </div>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(4)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner withdraw" @click="tryCharge()">
                                    <h3>Снять</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 20">
                        <h2>Деньги успешно выданы</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(1)">
                                    <h3>Выйти</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 21">
                        <h2>Недостаточно денег</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner withdraw" @click="changePage(4)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 6">
                        <h2>Пополнить счет</h2>
                        <div class="input_block">
                            <input class="input-withdraw" v-model="deposit">
                        </div>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="tryDeposit()">
                                    <h3>Пополнить</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 30">
                        <h2>Счет успешно пополнен</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 31">
                        <h2>Недостаточно денег</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(6)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 7">
                        <h2>Баланс счета</h2>
                        <div class="input_block">
                            <h2><span>На счету:</span> ${{getPrice(money)}}</h2>
                        </div>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 8">
                        <h2>Перевести на счет</h2>
                        <div class="input_block transfer">
                            <h3>Введите номер телефона:</h3>
                            <input class="input-withdraw" v-model="targetName">
                            <h3>Введите сумму:</h3>
                            <input class="input-withdraw" v-model="targetSum">
                        </div>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="tryTransfer()">
                                    <h3>Перевести</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 40">
                        <h2>Деньги успешно отправлены</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 41">
                        <h2>Недостаточно денег</h2>
                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(8)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 9">
                        <h2>Оплата счетов</h2>
                        <div class="penalty_elem_caption">
                            <div style="width: 5%" class="Id">ID</div>
                            <div style="width: 25%" class="Date">ДАТА</div>
                            <div style="width: 15%" class="Type">ТИП</div>
                            <div style="width: 28%" class="Text">ТЕКСТ</div>
                            <div style="width: 15%" class="Recipient">ПОЛУЧАТЕЛЬ</div>
                            <div style="width: 7%" class="Penalty">ЦЕНА</div>
                            <div style="width: 5%" class="Paid">СТАТУС</div>
                        </div>
                        <div class="penalty_list">
                            <div class="penalty_elem" :class="item.Type" v-for="(item, index) in AmountList" :key="'pen'+index" @click="pageAmount(index)">
                                <div style="width: 5%" class="id">{{item.Id}}</div>
                                <div style="width: 25%" class="date">{{getDate(item.Date)}}</div>
                                <div style="width: 15%" class="Type">{{getType(item.Type)}}</div>
                                <div style="width: 28%" class="text">{{item.Label}}</div>
                                <div style="width: 15%" class="Recipient">{{item.Recipient}}</div>
                                <div style="width: 7%" class="penalty">{{item.Amount}}$</div>
                                <div style="width: 5%" :class="'paid_' + item.IsPaid">{{item.IsPaid}}</div>
                            </div>
                        </div>

                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(3)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 91">
                        <h2>Оплата счёта</h2>

                        <div class="active_Amount">
                            <h3>Автор: {{activeAmount.Author}}</h3>
                            <h3>Дата: {{getDate(activeAmount.Date)}}</h3>
                            <h3>Причина: {{activeAmount.Label}}</h3>
                            <h3>Стоимость: {{activeAmount.Amount}}$</h3>
                            <h3 class="paid_true" v-if="activeAmount.IsPaid == true">Оплачен</h3>
                            <h3 class="paid_false" v-else>Не оплачен</h3>
                        </div>

                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(9)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                            <div class="element element_small">
                                <div class="element_inner" @click="payafine()">
                                    <h3>Оплатить счета</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 92">
                        <h2>Штраф успешно оплачен</h2>

                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(9)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page page_4" v-show="page == 93">
                        <h2>Недостаточно денег</h2>

                        <div class="main_menu">
                            <div class="element element_small">
                                <div class="element_inner exit" @click="changePage(9)">
                                    <h3>Назад</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>

            </div>

            <div class="controls">
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
            </div>

            <div class="controls right">
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
            </div>
            
        </div>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    name: "BankComponent",
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            chanse: 5,
            withdraw: undefined,
            deposit: undefined,
            money: this.playerData.money,
            targetName: undefined,
            targetSum: undefined,
            AmountList: [],
            activeAmount: {},
            activeAmountIndex: -1,

            bsodNumb: 0,
        }
    },
    methods: {
        login() {
            var numb = this.randomize();
            this.page = 0;
            setTimeout(() => {
                this.page = 2;
            }, 200);
            setTimeout(() => {
                if (numb <= this.chanse) {
                    this.page = 99;
                } else {
                    this.page = 3;
                }
            }, 2000);
            
        },
        changePage(index) {
            this.page = 0;
            this.bsodNumb = 0;
            setTimeout(() => {
                this.page = 2;
            }, 200);
            setTimeout(() => {
                this.page = index;
            }, 1000);
        },
        pageAmount(index) {
            this.changePage(91);
            this.activeAmountIndex = index;
            this.activeAmount = this.AmountList[index];
        },
        payafine() {
            // Проверяем штраф. Если успешно оплатил, продолжаем код
            //var success = true;
            //this.AmountList[this.activeAmountIndex].IsPaid = true;

            axios.post('https://eclipse/PayTicket', {
                Id: this.AmountList[this.activeAmountIndex].Id
            }).then(() => { return; }).catch(function () { console.log() });

        },
        randomize() {
            return Math.ceil(Math.random()*100)
        },
         exit() {
            axios.post('https://eclipse/CloseATM', {

            }).then(() => { return; }).catch(function () { console.log() });
        },
        tryCharge() {
            var sum = parseInt(this.withdraw)
            if (sum > 0) {
                this.charge(sum)
            }
        },
        tryDeposit() {
            var sum = parseInt(this.deposit)
            if (sum > 0) {
                this.cashIn()
            }
        },
        tryTransfer() {
            var sum = parseInt(this.targetSum)
            if (sum > 0) {
                this.transfer()
            }
        },
        charge(numb) {
            axios.post('https://eclipse/WithdrawMoney', {
                Amount: numb
            }).then(() => { return; }).catch(function () { console.log() });
            setTimeout(() => {
                this.withdraw = undefined;
            }, 200);
        },
        cashIn() {
            // Проверяем, есть ли наличные
            
            //
            // if (success) {
            //     this.changePage(30);
            // } else {
            //     this.changePage(31);
            // }
            axios.post('https://eclipse/DepositMoney', {
                Amount: this.deposit
            }).then(() => { return; }).catch(function () { console.log() });
            setTimeout(() => {
                this.deposit = undefined;
            }, 200);
        },
        transfer() {
            // Проверка номера телефона и наличие денег на счету
            // this.targetName
            // this.targetSum
            //
            axios.post('https://eclipse/TransferMoney', {
                    TargetName: this.targetName,
                    TargetSum: this.targetSum
                }).then(() => { return; }).catch(function () { console.log() });

            setTimeout(() => {
                this.targetName = undefined;
                this.targetSum = undefined;
            }, 200);

        },
        getPrice(numb) {
            var elem = String(numb);
            var result = elem;
            return result.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        },
        pashalka() {
            this.bsodNumb += 1;
            if (this.bsodNumb > 10 && this.page != 98) {
                this.bsodNumb = 0;
                this.page = 98;
            }
        },
        reload() {
            this.page = 2;
            setTimeout(() => {
                this.page = 1;
            }, 5000);
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                var time = ("0" + newdate.getHours()).slice(-2);
                var minutes = ("0" + newdate.getMinutes()).slice(-2);

                return time + ":" + minutes + ' ' + day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getType(type) {
            switch(type) {
                case 0:
                    return "Штраф";
                case 1: 
                    return "Аренда отеля";
                case 2:
                    return "Коммунальные услуги";
                case 3:
                    return "Счёт за лечение";

            }
        }
    },
    mounted() {
        // Test data
        // this.AmountList = [
        //     {"Id": 0, "Type": 0, "Author" : "Billy Donovan", "Date":"Thu Aug 19 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Headache pills X 1", "Amount": 300, "IsPaid": false},
        //     {"Id": 1, "Type": 0, "Author" : "Genry Morgan", "Date":"Thu Aug 16 2021 11:23:52 GMT+0300 (Москва, стандартное время)", "Label": "For Papers", "Amount": 1500, "IsPaid": true},
        //     {"Id": 2, "Type": 0, "Author" : "Eva Elfie", "Date":"Thu Aug 12 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Insurance for week", "Amount": 900, "IsPaid": false},
        //     {"Id": 3, "Type": 1, "Author" : "Officer Jessy Pinkman", "Date":"Thu Aug 10 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Exceeding the speed limit by 20 km per hour", "Amount": 500, "IsPaid": false},
        //     {"Id": 0, "Type": 2, "Author" : "Genry Morgan", "Date":"Thu Aug 19 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Headache pills X 1", "Amount": 300, "IsPaid": false},
        //     {"Id": 1, "Type": 2, "Author" : "Genry Morgan", "Date":"Thu Aug 16 2021 11:23:52 GMT+0300 (Москва, стандартное время)", "Label": "For Papers", "Amount": 1500, "IsPaid": true},
        //     {"Id": 2, "Type": 2, "Author" : "Johny Cage", "Date":"Thu Aug 12 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Insurance for week", "Amount": 900, "IsPaid": false},
        //     {"Id": 3, "Type": 2, "Author" : "Officer Jessy Pinkman", "Date":"Thu Aug 10 2021 15:23:52 GMT+0300 (Москва, стандартное время)", "Label": "Exceeding the speed limit by 20 km per hour", "Amount": 500, "IsPaid": false},
        
        // ]
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadTickets") {
                this.AmountList = event.data.data;
            }
            else if(event.data.type == "LoadBankData") {
                console.log(event.data.invoices)
                this.money = event.data.Money;
                this.AmountList = JSON.parse(event.data.invoices);
            }
            else if(event.data.type == "DepositError") {
                this.changePage(31);
            }
            else if(event.data.type == "WithdrawError") {
                this.changePage(21);
            }
            else if(event.data.type == "OnlineError") {
                //
            }
            else if(event.data.type == "TransferError") {
                this.changePage(41);
            }
            else if(event.data.type == "DepositSuccess") {
                this.changePage(30);
            }
            else if(event.data.type == "WithdrawSuccess") {
                this.changePage(20);
            }
            else if(event.data.type == "TransferSuccess") {
                this.changePage(40);
            }
            else if(event.data.type == "TicketSuccess") {
                this.changePage(92);
            }
            else if(event.data.type == "TicketError") {
                this.changePage(93);
            }
            else if(event.data.type == "LoadLanguage") {
                this.translate = JSON.parse(event.data.translate);
            }
        },

        false
        );
    }
}
</script>

<style scoped>
.loader {
    margin: 100px auto;
    font-size: 25px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    top: 32%;
    text-indent: -9999em;
    animation: load5 1.1s infinite ease;
    transform: translateZ(0);
}
@-webkit-keyframes load5 {
  0%,
  100% {
    box-shadow: 0em -2.6em 0em 0em #ffffff, 1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2), 2.5em 0em 0 0em rgba(255, 255, 255, 0.2), 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2), 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.5), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7);
  }
  12.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.7), 1.8em -1.8em 0 0em #ffffff, 2.5em 0em 0 0em rgba(255, 255, 255, 0.2), 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2), 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.2), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5);
