<template>
    <div id="app">
        <PlayerHud :playerData="playerData" v-show="!showPage"/>
        <VehicleHud :vehicleData="vehicleData" v-if="showVehicleHud"/>
        <Loginpage v-if="showPage == 'LoginPage'"/>
        <Charactereditorpage v-if="showPage == 'EditorPage'"/>
        <AnimationHud v-if="showPage == 'AnimationHud'" />
        <Notifications />
        <InteractionWheel />
        <InteractionPoint />
        <Phone :playerData="playerData" v-show="showPhone"/>
        <PoliceCad :playerData="playerData" v-if="showPage == 'PoliceCad'"/>
        <MedicalCad :playerData="playerData" v-if="showPage == 'MedicalCad'"/>
        <PlayerCad :playerData="playerData" v-if="showPage == 'PlayerCad'"/>
        <AdminPanel :playerData="playerData" v-if="showPage == 'AdminPanel'" />
        <ShopCloth :playerData="playerData" v-if="showPage == 'ShopCloth'" />
        <ShopClothNew :playerData="playerData" v-if="showPage == 'ShopClothNew'" />
        <ShopAuto shopName="VAPID" v-if="showPage == 'ShopAuto'" />
        <ShopFood v-if="showPage == 'ShopFood'" />
        <ShopWeapon v-if="showPage == 'ShopWeapon'" />
        <ShopBarber :playerData="playerData" v-if="showPage == 'ShopBarber'" />
        <ShopTattoo :playerData="playerData" v-if="showPage == 'ShopTattoo'" />
        <!-- <ShopTuning v-if="showPage == 'ShopTuning'"/> -->
        <Bank :playerData="playerData" v-if="showPage == 'Bank'" />
        <Garage :vehicleList="playerData.carList" v-if="showPage == 'Garage'" />
        <Pc v-if="showPage == 'Pc'" />
        <Minigames v-if="showMinigames" />
        <Jobs :playerData="playerData" />
        <Inventory :playerData="playerData" />
        <Vehicle :playerData="playerData" />
        <Dialog :playerData="playerData" />
        <Band :playerData="playerData" v-if="showPage == 'Gang'" />
        <Craft :playerData="playerData" v-if="showPage == 'Craft'" />
        <!-- <h2 class="under_constraction">WORK IN PROGRESS - DOES NOT REPRESENT THE FINAL LOOK OF THE GAME</h2> -->
        <div class="demo">
            <button class="btn btn-primary" @click="showPage=`LoginPage`">Логин</button>
            <button class="btn btn-primary" @click="showPage=`EditorPage`">Редактор</button>
            <button class="btn btn-primary" @click="showPage=`AnimationHud`">Анимации</button>
            <button class="btn btn-primary" @click="showPage=`PoliceCad`">PoliceCad</button>
            <button class="btn btn-primary" @click="showPage=`MedicalCad`">MedicalCad</button>
            <button class="btn btn-primary" @click="showPage=`PlayerCad`">PlayerCad</button>
            
            <button class="btn btn-primary" @click="showPage=`AdminPanel`">Админка</button>
            <button class="btn btn-primary" @click="showPage=`ShopAuto`">Автосалон</button>
            <button class="btn btn-primary" @click="showPage=`ShopBarber`">Барбершоп</button>
            <button class="btn btn-primary" @click="showPage=`ShopTattoo`">Тату</button>
            <button class="btn btn-primary" @click="showPage=`Bank`">Банк</button>
            <button class="btn btn-primary" @click="showPage=`Gang`">Банда</button>
            <button class="btn btn-primary" @click="showPage=`Craft`">Крафт</button>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import Loginpage from "./components/Loginpage.vue";
import Charactereditorpage from "./components/Charactereditor.vue";
import PlayerHud from "./components/hud/PlayerHud";
import VehicleHud from "./components/hud/VehicleHud";
import AnimationHud from "./components/hud/AnimationHud";
import InteractionWheel from "./components/hud/InteractionWheel";
import InteractionPoint from "./components/hud/InteractionPoint";
import Phone from "./components/phone/Phone";
import PoliceCad from "./components/cad/PoliceCad";
import MedicalCad from "./components/cad/MedicalCad";
import PlayerCad from "./components/cad/PlayerCad";
import ShopCloth from "./components/shop/ShopCloth";
import ShopClothNew from "./components/shop/ShopClothNew";
import ShopAuto from "./components/shop/ShopAuto";
import ShopFood from "./components/shop/ShopFood";
import ShopWeapon from "./components/shop/ShopWeapon";
import ShopBarber from "./components/shop/ShopBarber.vue";
import ShopTattoo from "./components/shop/ShopTattoo.vue";
import Bank from "./components/bank/bank";
import Garage from "./components/garage/garage";
import AdminPanel from "./components/admin/AdminPanel";
import Pc from "./components/pc/Pc.vue";
import Minigames from "./components/minigames/Games.vue";
// import ShopTuning from "./components/shop/Tuning.vue";
import Notifications from "./components/hud/Notifications.vue";
import Jobs from "./components/jobs/Jobs.vue";
import Inventory from "./components/inventory/Inventory.vue";
import Vehicle from "./components/vehicle/Vehicle.vue";
import Dialog from "./components/npc/dialog.vue";
import Craft from "./components/inventory/craft.vue";
import Band from "./components/band/band.vue";

export default {
        name: "App",
        components: {
            Loginpage,
            Charactereditorpage,
            PlayerHud,
            VehicleHud,
            Notifications,
            Phone,
            PoliceCad,
            MedicalCad,
            ShopCloth,
            ShopClothNew,
            ShopAuto,
            ShopFood,
            ShopWeapon,
            Bank,
            AnimationHud,
            InteractionWheel,
            InteractionPoint,
            Garage,
            AdminPanel,
            Pc,
            Minigames,
            // ShopTuning,
            Jobs,
            PlayerCad,
            Inventory,
            Vehicle,
            ShopBarber,
            ShopTattoo,
            Dialog,
            Craft,
            Band
        },
    data() {
        return {
            showPage: "",
            characterList: [],
            playerData: {
                id: 1,
                AccountId: 2,
                bgImage: "",
                phone: "8005555",
                phoneType: 0,
                name: "Jhon",
                surname: "Doe",
                age: "54",
                sex: true,
                roles: [],
                image: "https://storage.googleapis.com/eclipse-rp/archive/test/player.jpg",
                money: 0,
                reg: 1,
                policeData: {
                    rank: 0,
                    callsign: "L-1",
                    policeId: 0,
                    onDuty: 0,
                },
                medicData: {
                    rank: 0,
                    callsign: "",
                    medicId: 0,
                    onDuty: 0,
                },
                adminData: {
                    rank: 11,
                },
                carList: [
                    {
                        carId: 1,
                        name: "blade",
                        ownerId: 1,
                        type: "Muscle",
                        number: "V551GG",
                        status: 1,
                        color: "red",
                        sellerId: 1
                    }
                ],
                playerStatData: {
                    Health: 0,
                    Armor: 0,
                    Thirst: 0,
                    Hunger: 0,
                    Cigarette: 0,
                    Cash: 0,
                    Hours: 0,
                    Minutes: 0
                },
                playerPadData: {
                    Wallpaper: "https://storage.googleapis.com/eclipse-rp/archive/pad/wallpapers/wallpaper1.jpg",
                    ShowHud: false,
                    ShowMarker: false,
                    ShowTips: false,
                    Walkstyle: 2,
                },
                govermentData: {
                    Rank: 0,
                },
                siteName: "https://storage.googleapis.com/eclipse-rp/archive/"
            },
            vehicleData: {
                Speed: 0,
                Gear: 0,
                Fuel: 0,
                Lock: false,
                Light: false,
                Engine: false,
            },
            showVehicleHud: false,
            showPlayerHud: true,

            showChat: false,
            showMinigames: false,
            showPhone: false
        };
    },
    methods: {
        doCommand(e) {
            if (e.keyCode == 27 & this.showPage != "EditorPage" & this.showPage != "LoginPage" & this.showPage != "ShopBarber" & this.showPage != "ShopCloth" & this.showPage != "ShopTattoo" & this.showPage != "ShopAuto" & this.showPage != "ShopTuning" & !this.showPhone) {
                this.showPage = ""
                axios.post('https://eclipse/Tilda', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
            else if(e.keyCode == 73) {
                e.keyCode
            }
        },
    },
    computed: {},
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "OpenLoginMenu") {
                this.showPage = "LoginPage";
            }
            else if (event.data.type == "OpenPhone") {
                this.showPhone = true;
            }
            else if (event.data.type == "ClosePhone") {
                this.showPhone = false;
            }
            else if(event.data.type == "ShowCraft") {
                this.showPage = "Craft";
            }
            else if(event.data.type == "LoadPlayerPadData") {
                this.playerData.playerPadData = JSON.parse(event.data.playerPadData);
            }
            else if(event.data.type == "OpenBarbershop") {
                this.showPage = "ShopBarber";
            }
            else if(event.data.type == "OpenGangMenu") {
                this.showPage = "Gang";
            }
            else if(event.data.type == "UpdatePlayerData") {
                this.playerData.id = event.data.id;
                this.playerData.phone = event.data.phone;
                this.playerData.name = event.data.name;
                this.playerData.surname = event.data.surname;
                this.playerData.sex = event.data.sex;
                this.playerData.roles = event.data.roles;
                this.playerData.AccountId = event.data.AccountId;
                this.playerData.money = event.data.money;
                this.playerData.adminData.rank = event.data.adminLevel
            }
            else if(event.data.type == "UpdatePoliceData") {
                    this.playerData.policeData.policeId = event.data.policeId;
                    this.playerData.policeData.callsign = event.data.callsign;
                    this.playerData.policeData.rank = event.data.rank;
                    this.playerData.policeData.adam = event.data.adam;
                    this.playerData.policeData.onDuty = event.data.onDuty;
                    this.playerData.policeData.status = event.data.status;
            }
            else if(event.data.type == "UpdatePlayerPhoneData") {
                this.playerData.phone = event.data.PhoneNumber;
                this.playerData.name = event.data.Name;
                // this.playerData.roles = event.data.job;
                // this.playerData.policeData.policeId = event.data.policeId;
                // this.playerData.policeData.rank = event.data.rank;
            }
            else if(event.data.type == "ShowCharacterEditorMenu") {
                this.showPage = "EditorPage";
                
            }
            else if (event.data.type == "ShopAuto") {
                this.showPage = "ShopAuto";
            }
            else if(event.data.type == "CloseMenu") {
                this.showPage = "";
            }
            else if(event.data.type == "ShowPlayerCad") {
                this.showPage = "PlayerCad"
                axios.post('https://eclipse/GetBusinessCharacterData', {}).then(() => { return; }).catch(function () { console.log() });
            }
            else if(event.data.type == "OpenGarage") {
                this.showPage = "Garage";
            }
            else if(event.data.type == "ChangeBgImage") {
                this.playerData.bgImage = event.data.image;
            }
            else if(event.data.type == "ChangePhoneType") {
                this.playerData.phoneType = event.data.phoneType;
            }
            // else if(event.data.type == "VehicleData") {
            //     console.log(JSON.parse(event.data.data).Speed)
            //     this.vehicleData = JSON.parse(event.data.data)
            // }
            else if(event.data.type == "showAutoHud") {
                this.showVehicleHud = true
            }
            else if(event.data.type == "hideAutoHud") {
                this.showVehicleHud = false
            }
            else if(event.data.type == "OpenTuning") {
                this.showPage = "ShopTuning";
            }
            else if(event.data.type == "CloseTuning") {
                this.showPage = "";
            }
            else if(event.data.type == "OpenTattooShop") {
                this.showPage = "ShopTattoo";
            }
            else if(event.data.type == "ShopCloth") {
                this.showPage = "ShopCloth";
            }
            else if(event.data.type == "CloseClothShop") {
                this.showPage = "";
            }
            else if(event.data.type == "ShopFood") {
                this.showPage = "ShopFood";
            }
            else if(event.data.type == "ShopWeapon") {
                this.showPage = "ShopWeapon";
            }
            else if(event.data.type == "ClosePoliceCad") {
                this.showPage = ""
            }
            else if(event.data.type == "OpenPoliceCad") {
                this.showPage = "PoliceCad"
            }
            else if(event.data.type == "OpenBankMenu") {
                this.showPage = "Bank";
            }
             else if(event.data.type == "ShowAnimationMenu") {
                this.showPage = "AnimationHud";
            }
            else if(event.data.type == "CloseAnimationMenu") {
                this.showPage = "";
            }
            else if(event.data.type == "OpenAdminMenu") {
                this.showPage = "AdminPanel";
            }
            else if(event.data.type == "CloseATM") {
                this.showPage = "";
            }
        }, false);

    },
};
</script>

<style>
#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    }
@font-face {
    font-family: "weathericons";
    src: url("assets/font/weathericons-regular-webfont.eot");
    src: url("assets/font/weathericons-regular-webfont.eot?#iefix")
        format("embedded-opentype"),
        url("assets/font/weathericons-regular-webfont.woff2") format("woff2"),
        url("assets/font/weathericons-regular-webfont.woff") format("woff"),
        url("assets/font/weathericons-regular-webfont.ttf") format("truetype"),
        url("assets/font/weathericons-regular-webfont.svg#weather_iconsregular")
        format("svg");
    font-weight: normal;
    font-style: normal;
}
.demo {
    position: absolute;
    right: 0;
    display: flex;
    flex-direction: column;
    z-index: 9999;
    gap: 10px;
}
</style>
