<template>
    <div @wheel="wheel" :method='method' class="d-flex carousel_outer m-0 justify-content-between">
        <button class="btn btn-nostyle before_button" @click="minus()"><b-icon icon="chevron-left"></b-icon></button>
        <h6 class="text-center">{{result}} / {{list - 1}}</h6>
        <button class="btn btn-nostyle next_button" @click="plus()"><b-icon icon="chevron-right"></b-icon></button>
    </div>
</template>

<script>
export default {
    name: 'carouselStandart2',
    props: {
        list: Number,
        text: String,
        id: String,
        selected: String,
        method: String,
    },
    methods: {
        minus() {
            this.result -= 1;
            if (this.result < 0) {
                this.result = this.list - 1;
            }
            this.functionName = this.method;
         
            this.$emit(this.functionName, this.result);
        },
        plus() {
            this.result += 1;
            if (this.result > this.list - 1) {
                this.result = 0;
            }
            this.functionName = this.method;
            this.$emit(this.functionName, this.result);
        },
        wheel: function(ev) {
            if (ev.deltaY < 0) {
                this.plus();
            } else {
                this.minus();
            }
        }
    },
    data() {
        return {
            result: 0,
            functionName: ''
        }
    }
}
</script>