<template>
    <section class="fullpage main_bg d-flex justify-content-center align-items-center" >
        <transition name="transition_01">
            <div class="loginpage_outer" v-show="showThis == true">
                <div class="d-flex justify-content-left">
                    <i class="mainlogo"></i>
                    <h1>ECLIPSE</h1>
                </div>
                <div class="page_login bg_primary">
                <transition name="slide-to-left">
                    <div class="loginpage_inner" v-show="showPage == 1">
                    <div class="form-group">
                        <label for="editor_surname">Логин</label>
                        <div class="input_lane">
                        <input type=name class="form-control" placeholder="Введите логин"  v-model="UserLogin"> 
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="editor_surname">Пароль</label>
                        <div class="input_lane">
                        <input type=password class="form-control" placeholder="Введите пароль"  v-model="UserPassword" v-on:keyup.enter="login()">
                        </div>
                    </div>
                    <div class="d-flex justify-content-center pt-1 pb-2">
                        <button class="btn btn-secondary enterbutton button220" @click="registration()">Регистрация</button>
                    </div>
                    <div class="d-flex justify-content-center pt-2 pb-1">
                        <button class="btn btn-primary enterbutton button220" @click="login()" >Войти</button>
                    </div>
                    <div class="d-flex justify-content-center pt-2">
                        <button class="btn btn-nostyle" @click="forgotpassword()">Забыли пароль?</button>
                    </div>
                    </div>
                </transition>

                <transition name="slide-to-left">
                    <div class="loginpage_inner" v-show="showPage == 2">
                    <div class="form-group">
                        <label for="editor_surname">Логин</label>
                        <div class="input_lane">
                        <input type=name class="form-control" placeholder="Введите логин" v-model="UserLogin">
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="editor_surname">Пароль</label>
                        <div class="input_lane">
                        <input type=password class="form-control" placeholder="Введите пароль" v-model="UserPassword">
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="editor_surname">Ваша почта</label>
                        <div class="input_lane">
                        <input type=email class="form-control" placeholder="Введите почту" v-model="UserEmail">
                        </div>
                    </div>
                    <div class="d-flex justify-content-between pt-1 pb-2">
                        <button class="btn btn-secondary enterbutton flex-fill mr-2" @click="goback()">Назад</button>
                        <button class="btn btn-primary enterbutton flex-fill ml-2" @click="registerUser()">Создать</button>
                    </div>
                    </div>
                </transition>

                <transition name="slide-to-left">
                    <div class="loginpage_inner" v-show="showPage == 3">
                    <p>Найдите свою учетную запись.<br>Введите почту, на которую зарегистрирован аккаунт:</p>
                    <div class="form-group">
                        <div class="input_lane">
                        <input type=email class="form-control" placeholder="Введите почту">
                        </div>
                    </div>
                    <div class="d-flex justify-content-between pt-1 pb-2">
                        <button class="btn btn-secondary enterbutton flex-fill mr-2" @click="goback()">Назад</button>
                        <button class="btn btn-primary enterbutton flex-fill ml-2" @click="goback()">Восстановить</button>
                    </div>
                    </div>
                </transition>

                <div class="img_character" @click="clickImg()">
                    <transition name="slide-to-left">
                        <div class="pashalka" v-show="clicks > 5">
                            Да хватит по мне щелкать, шалун. Я же тебя отшлепаю!
                        </div>
                    </transition>
                </div>
            </div>
        </div>
    </transition>

    <transition name="transition_01">
        <div class="choose_player_outer" v-show="showPlayerChoose == true">
            <div class="page_choose bg_primary">
                <div class="choose_player_inner">
                    <h2>ECLIPSE</h2>
                        <div class="character_menu">
                        <button class="btn btn-primary" @click="backtologin()">Выход </button>
                    </div>
                    <div class="row">
                        <div class="col-3 pr-0">
                            <div class="bg_white choose_bar full_width">
                            <h6>Social club</h6>
                            <h5>{{playerData.SocialClub}}</h5>
                            <h6>Логин</h6>
                            <h5>{{playerData.Login}}</h5>
                            <h6>ID аккаунта</h6>
                            <h5>{{playerData.AccountId}}</h5>
                            <h6>Подписка</h6>
                            <h5>{{playerData.Subscribe}}</h5>
                            <h6>Наиграно</h6>
                            <h5>{{playerData.PlayedTime}}</h5>
                            <h6>Последний вход</h6>
                            <h5>{{playerData.LastSeen}}</h5>
                            </div>
                        </div>
                        <div class="col-9">
                            <div class="char_choose">
                                <div class="bg_primary choose_bar" v-for="(item, index) in characterList" :key="'char'+index">
                                    <div class="player">
                                        <div class="char_img" :class="{female: item.Sex == 0}"></div>
                                        <h4 class="character_name" v-if="item.Name">{{item.Name}}</h4>
                                        <h4 class="character_name" v-else>Ошибка загрузки имени</h4>
                                        <h5 class="character_main">Id: {{item.Id}}</h5>
                                        <button class="btn btn-secondary btn-block" @click="chooseCharacter(item.Id)">Выбрать</button>
                                    </div>
                                </div>
                                <div class="bg_primary choose_bar" v-if="showCreate">
                                    <div class="player">
                                        <div class="char_new_img"></div>
                                        <h4 class="character_name">Создать нового персонажа</h4>
                                        <button class="btn btn-secondary btn-block" @click="createCharacter()">Создать</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row mt-3">
                    <div class="col-6 pr-2">
                        <div class="banner_outer">
                        Реклама в разработке
                        </div>
                    </div>
                    <div class="col-6 pl-2">
                        <div class="banner_outer">
                        Реклама в разработке
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
  </section>
</template>

<script>

import axios from 'axios';

export default {
    name: 'LoginPage',
    data() {
        return {
            clicks: 0,
            showThis: true,
            showPlayerChoose: false,
            showPage: 1,
            animationTime: 600,
            UserLogin: null,
            UserPassword: null,
            UserEmail: null,
            showCreate: false,

            playerData: {
                SocialClub: "",
                Login: "",
                Subscribe: "false",
                PlayedTime: 0,
                LastSeen: ''
                
            },
            characterList: [],
        };
     },
    methods: {
        clickImg() {
            this.clicks += 1;
            if (this.clicks >= 5) {
                setTimeout(() => { this.clicks = 0; }, 5000);
            }
        },
        registration() {
            this.showPage = 0;
            setTimeout(() => {  this.showPage = 2; }, this.animationTime);
        },
        goback() {
            this.showPage = 0;
            setTimeout(() => {  this.showPage = 1; }, this.animationTime);
        },
        registerUser() {
            axios.post('https://eclipse/UserRegister', {
                Username: this.UserLogin,
                Password: this.UserPassword,
                Email: this.UserEmail
            }).then(() => { return; }).catch(function () { console.log() });
        },
        login() {
            axios.post('https://eclipse/TryUserLogin', {
                Username: this.UserLogin,
                Password: this.UserPassword,
            }).then(() => { return; }).catch(function () { console.log() });
        },
        forgotpassword() {
            this.showPage = 0;
            setTimeout(() => {  this.showPage = 3; }, this.animationTime);
        },
        backtologin() {
            this.showPlayerChoose = false;
            setTimeout(() => {
                this.showThis = true;
            }, 800);
        },
        chooseCharacter(char) {
            axios.post('https://eclipse/LoadCharacter', {
                CharacterId: char,
            }).then(() => { return; }).catch(function () { console.log() });
        },
        createCharacter() {
            this.showPlayerChoose = false;
            axios.post('https://eclipse/showCharacterCreateMenu', {
                Username: this.UserLogin,
                Password: this.UserPassword,
            }).then(() => { return; }).catch(function () { console.log() });
            // setTimeout(() => {
            //     this.$parent.showPage = "EditorPage";
            // }, 800);
        },
        checkCreateNew() {
            if (this.characterList.length < 3) {
                this.showCreate = true;
            } else {
                this.showCreate = false;
            }
        },
        sortById(array) {
            array.sort(function (a, b) {
                return a.Id - b.Id;
            });
        },

    }, 
    mounted() {
        window.addEventListener('message',(event) => {
            if (event.data.type == "ShowCharacterChooseMenu") {
                this.showThis = false;
                setTimeout(() => {
                    this.showPlayerChoose = true;
                }, 800);
            } else if(event.data.type == "CharacterChooseMenu") {
                this.characterList = JSON.parse(event.data.data);
                this.playerData = JSON.parse(event.data.playerData);
                this.sortById(this.characterList);
                this.checkCreateNew();
            }
            else if(event.data.type == "ShowCharacterCreateMenu") {
              this.$parent.showPage = "EditorPage";
            }
        }, false);
        this.checkCreateNew();
    },
}
</script>

<style scoped>
  .main_bg {
    background-image: url(../assets/img/art_01.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-color: var(--black);
    background-size: cover;
  }
  .pashalka {
    position: absolute;
    right: -200px;
    top: 14px;
    background: var(--gradient);
    width: 250px;
    height: 50px;
    text-align: center;
    border-radius: 10px;
  }
  .pashalka:after {
    content: ' '; 
    position: absolute; 
    width: 0; 
    height: 0; 
    left: 23px; 
    top: 50px; 
    border: 15px solid; 
    border-color: #68028a80 transparent transparent #68028a80; 
  }

</style>
