<template>
    <transition name="transition_02">
        <section class="fullpage" 
        @mousedown="toggleCameraClick(true)"
        @mouseup="toggleCameraClick(false)"
        @mousemove="toggleCameraMove"
        @mousewheel="wheelMove"
        v-show="showThis == true">
            <div class="fullpage">
                <div class="sectionA" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[0] == true">
                            <h4 class="primary_text">Создание персонажа</h4>
                            <div class="form-group">
                                <h5>Пол персонажа</h5>
                                <div class="btn-group btn-group-toggle">
                                <label class="btn btn-blue mr-2" v-bind:class="{ active: isMale == true || isMale == 'true'}">
                                    <input type="radio" @click="changeSex(true)">
                                    Мужской
                                    <i class="male"></i>
                                </label>
                                <label class="btn btn-red" v-bind:class="{ active: isMale == false || isMale == 'false'}">
                                    <input type="radio" @click="changeSex(false)">
                                    Женский
                                    <i class="female"></i>
                                </label>
                                </div>
                            </div>
                            <div class="form-group">
                                <h5>Паспортные данные</h5>
                            </div>
                            <div class="form-group">
                                <label class="st_label">Имя</label>
                                <input type="text" class="form-control" v-model="CharacterName" @change="checkName">
                            </div>
                            <div class="form-group">
                                <label class="st_label">Фамилия</label>
                                <input type="text" class="form-control" v-model="CharacterSurname" @change="checkSurname">
                            </div>
                            <div class="form-group">
                                <label class="st_label">Дата рождения</label>

                                <datepicker
                                    v-model="CharacterDateBirth"
                                    format="MM-dd-yyyy"
                                    input-class="form-control"
                                    :language="ru"
                                    :disabled-dates="disabledDates"
                                ></datepicker>
                            </div>
                        </div>
                    </transition>
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[1] == true">
                            <div class="form-group">
                                <h5>Выбор родителей</h5>
                                <button class="btn btn-block btn-card male" v-bind:class="{active: collapseButton[1] == true}" @click="chooseCollapse(1)">Отец</button>
                                <button class="btn btn-block btn-card female" v-bind:class="{active: collapseButton[2] == true}" @click="chooseCollapse(2)">Мать</button>
                            </div>
                            <div class="form-group">
                                <h5 class="mb-0">Генетика</h5>
                                <input type="range" value="0.5" min="0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="SimilarityPercent" class="form-control-range parents" >
                            </div>
                            <div class="form-group">
                                <h5 class="mb-0">Тон кожи</h5>
                                <input type="range" value="0.5" min="0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="SkinColor" class="form-control-range" >
                            </div>
                        </div>
                    </transition>
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[2] == true">
                            <div class="form-group">
                                <h5>Настройки лица</h5>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[3] == true}" @click="chooseCollapse(3)">Контуры лица</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[4] == true}" @click="chooseCollapse(4)">Глаза и брови</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[5] == true}" @click="chooseCollapse(5)">Нос</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[6] == true}" @click="chooseCollapse(6)">Губы</button>
                            </div>
                        </div>
                    </transition>
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[3] == true">
                            <div class="form-group">
                                <h5>Растительность</h5>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[7] == true}" @click="chooseCollapse(7)">Прическа</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[8] == true}" @click="chooseCollapse(8)">Брови</button>
                                <button class="btn btn-block btn-card" v-if="isMale" v-bind:class="{active: collapseButton[9] == true}" @click="chooseCollapse(9)">Борода и усы</button>
                                <button class="btn btn-block btn-card" v-if="isMale" v-bind:class="{active: collapseButton[10] == true}" @click="chooseCollapse(10)">Волосы на теле</button>
                            </div>
                        </div>
                    </transition>
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[4] == true">
                            <div class="form-group">
                                <h5>Детали</h5>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[11] == true}" @click="chooseCollapse(11)">Возраст</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[12] == true}" @click="chooseCollapse(12)">Пятна на лице</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[13] == true}" @click="chooseCollapse(13)">Ожоги на лице</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[14] == true}" @click="chooseCollapse(14)">Родинки и веснушки на лице</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[15] == true}" @click="chooseCollapse(15)">Дефекты кожи</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[19] == true}" @click="chooseCollapse(19)">Родинки на теле</button>
                            </div>
                        </div>
                    </transition>
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-show="activePage[5] == true">
                            <div class="form-group">
                                <h5>Косметика</h5>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[16] == true}" @click="chooseCollapse(16)">Цвет лица</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[17] == true}" @click="chooseCollapse(17)">Макияж</button>
                                <button class="btn btn-block btn-card" v-bind:class="{active: collapseButton[18] == true}" @click="chooseCollapse(18)">Помада</button>
                            </div>
                        </div>
                    </transition>
                </div>
                <div class="sectionB d-flex align-items-top justify-content-end" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[1] == true">
                            <div class="form-group">
                                <h5 class="text-right">Выбор отца</h5>

                                <div class="d-flex flex-wrap height_fix scrolled males">
                                    <div v-for="(item, index) in list_father" 
                                        :key="'father'+index"
                                        @click="changeFatherIndex(item)"
                                        v-bind:class="{active: FatherIndex == item}"
                                        class="card">
                                        <div class="card_img">
                                            <img :src="require(`../assets/img/characters/male_`+ item +`.png`)">
                                        </div>
                                        
                                        <h6 class="text-center">{{ fatherNames[index] }}</h6>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </transition>
                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[2] == true">
                            <div class="form-group">
                                <h5 class="text-right">Выбор матери</h5>
                                <div class="d-flex flex-wrap height_fix scrolled females">
                                    <div v-for="(item, index) in list_mother" 
                                        :key="'mother'+index"
                                        @click="changeMotherIndex(item)"
                                        v-bind:class="{active: MotherIndex == item}"
                                        class="card">
                                        <div class="card_img">
                                            <img :src="require(`../assets/img/characters/female_`+ item +`.png`)">
                                        </div>
                                        
                                        <h6 class="text-center">{{ motherNames[index] }}</h6>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[3] == true">
                            <h5 class="text-right">Контуры лица</h5>
                            <div class="height_fix scrolled pr-2">
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6 class="mt-0">Высота скул</h6><h6 class="mt-0">{{cheekBoneHeight}}</h6>
                                    </div>
                                    <input type="range" value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="cheekBoneHeight" class="form-control-range">
                                </div>

                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ширина скул</h6><h6>{{cheekBoneWidth}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="cheekBoneWidth" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ширина щёк</h6><h6>{{cheekWidth}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="cheekWidth" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ширина челюсти</h6><h6>{{jawWidth}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="jawWidth" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Форма челюсти</h6><h6>{{jawShape}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="jawShape" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Высота подбородка</h6><h6>{{chinHeight}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="chinHeight" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ямка на подбородке</h6><h6>{{chinDepth}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="chinDepth" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ширина подбородка</h6><h6>{{chinWidth}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="chinWidth" class="form-control-range">
                                </div>
                                <div class="form-group">
                                    <div class="d-flex justify-content-between">
                                        <h6>Выступ подбородка</h6><h6>{{chinIndent}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="chinIndent" class="form-control-range">
                                </div>
                                <div class="form-group mb-0">
                                    <div class="d-flex justify-content-between">
                                        <h6>Ширина шеи</h6><h6>{{neck}}</h6>
                                    </div>
                                    <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="neck" class="form-control-range">
                                </div>
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[4] == true">
                            <div class="form-group">
                                <h5 class="text-right">Глаза и брови</h5>
                            </div>
                            <div class="form-group">
                                <h6>Цвет глаз</h6>
                                <div class="d-flex flex-wrap block_colors">
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 0}" style="background-color:#4a694e" @click="changedLensesIndex(0)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 1}" style="background-color:#266306" @click="changedLensesIndex(1)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 2}" style="background-color:#626d88" @click="changedLensesIndex(2)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 3}" style="background-color:#3f5c76" @click="changedLensesIndex(3)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 4}" style="background-color:#653e1c" @click="changedLensesIndex(4)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 5}" style="background-color:#4f3722" @click="changedLensesIndex(5)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 6}" style="background-color:#543608" @click="changedLensesIndex(6)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 7}" style="background-color:#575757" @click="changedLensesIndex(7)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 8}" style="background-color:#717171" @click="changedLensesIndex(8)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 9}" style="background-color:#9c1286" @click="changedLensesIndex(9)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 10}" style="background-color:#bab801" @click="changedLensesIndex(10)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 11}" style="background-color:#3e119a" @click="changedLensesIndex(11)"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 12}" @click="changedLensesIndex(12)" style="background-color:#000"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 13}" @click="changedLensesIndex(13)" style="background-color:#444"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 14}" @click="changedLensesIndex(14)" style="background-color:#351c0a"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 15}" @click="changedLensesIndex(15)" style="background: radial-gradient(circle, rgba(5,3,12,1) 16%, rgba(106,112,9,1) 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 16}" @click="changedLensesIndex(16)" style="background: radial-gradient(circle, rgba(5,3,12,1) 16%, #5f635f 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 17}" @click="changedLensesIndex(17)" style="background: radial-gradient(circle, #440807 16%, #b4bfb3 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 18}" @click="changedLensesIndex(18)" style="background: radial-gradient(circle, #4f440b 16%, #691913 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 19}" @click="changedLensesIndex(19)" style="background: radial-gradient(circle, #0d0e0c 16%, #7a7f77 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 20}" @click="changedLensesIndex(20)" style="background: radial-gradient(circle, rgba(10,8,9,1) 16%, rgba(126,130,127,1) 44%, rgba(87,14,13,1) 72%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 21}" @click="changedLensesIndex(21)" style="background: radial-gradient(circle, #060508 16%, #4e762d 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 22}" @click="changedLensesIndex(22)" style="background: radial-gradient(circle, #060508 16%, #723514 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 23}" @click="changedLensesIndex(23)" style="background: radial-gradient(circle, #060508 16%, #262e4d 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 24}" @click="changedLensesIndex(24)" style="background: radial-gradient(circle, #645d4b 16%, #0c0a0b 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 25}" @click="changedLensesIndex(25)" style="background: radial-gradient(circle, #060508 16%, #a36e0e 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 26}" @click="changedLensesIndex(26)" style="background-color:#000"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 27}" @click="changedLensesIndex(27)" style="background-color:#780303"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 28}" @click="changedLensesIndex(28)" style="background: radial-gradient(circle, #2a3742 16%, #780303 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 29}" @click="changedLensesIndex(29)" style="background: radial-gradient(circle, #050403 16%, #233650 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 30}" @click="changedLensesIndex(30)" style="background: radial-gradient(circle, #504d4b 16%, #411f1f 59%);"></button>
                                    <button class="btn block_color" v-bind:class="{active: EyeColorIndex == 31}" @click="changedLensesIndex(31)" style="background: radial-gradient(circle, #8b8b86 16%, #605d5b 59%);"></button>
                                </div>
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Размер глаз</h6><h6>{{eyeScale}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="eyeScale" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Высота бровей</h6><h6>{{eyebrowHeight}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="eyebrowHeight" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Глубина бровей</h6><h6>{{eyebrowDepth}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="eyebrowDepth" class="form-control-range">
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[5] == true">
                            <div class="form-group">
                                <h5 class="text-right">Настройки носа</h5>
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Ширина носа</h6><h6>{{noseWidth}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseWidth" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Высота носа</h6><h6>{{noseHeight}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseHeight" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Длина носа</h6><h6>{{noseDepth}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseDepth" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Переносица</h6><h6>{{noseTipLength}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseTipLength" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Высота кончика носа</h6><h6>{{noseTipHeight}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseTipHeight" class="form-control-range">
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Кривизна носа</h6><h6>{{noseBroke}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="noseBroke" class="form-control-range">
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[6] == true">
                            <div class="form-group">
                                <h5 class="text-right">Настройки губ</h5>
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between">
                                    <h6>Толщина губ</h6><h6>{{lipThickness}}</h6>
                                </div>
                                <input type="range"  value="0" min="-1.0" max="1.0" step="0.05" @input="updateCharacterCustomization()" v-model="lipThickness" class="form-control-range">
                            </div> 
                        </div>
                    </transition>

                    <transition name="slide-to-right">
                        <div class="editor_inner" v-show="collapseObject[7] == true">
                            <h5 class="text-right">Прическа</h5>
                            <div class="height_fix scrolled pr-2">
                                <div class="form-group">
                                    <h6 class="mt-0">Основной цвет прически</h6>
                                    <div class="d-flex flex-wrap block_colors mb-1">
                                        <button
                                            v-for="(item, index) in colorSetA" 
                                            :key="'colA'+index"
                                            @click="chooseColor('hairMainColor', index)"
                                            v-bind:class="{active: HairColorIndex == index}"
                                            class="btn block_color"
                                            :style="{background: item}"
                                        >
                                        </button>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <h6>Дополнительный цвет прически</h6>
                                    <div class="d-flex flex-wrap block_colors mb-1">
                                        <button
                                            v-for="(item, index) in colorSetA" 
                                            :key="'ColAA'+index"
                                            @click="chooseColor('hairSecondColor', index)"
                                            v-bind:class="{active: HairSecondColorIndex == index}"
                                            class="btn block_color"
                                            :style="{background: item}"
