    methods: {
        chooseMenu(numb) {
            if ((this.currentPage != numb) & this.activeAnimation == false) {
                this.currentPage = numb;
                this.hideMenu();
                this.hideCollapse();
                this.currentCollapse = 0;
                this.$set(this.activeMenu, numb, !this.activePage[numb]);
                this.activeAnimation = true;
                setTimeout(() => {
                    this.$set(this.activePage, numb, !this.activePage[numb]);
                    this.activeAnimation = false;
                }, this.timeout);
            }
        },
        hideMenu() {
            this.activeMenu.forEach((value, index) => {
                this.$set(this.activeMenu, index, false);
            });
            this.activePage.forEach((value, index) => {
                this.$set(this.activePage, index, false);
            });
        },
        chooseCollapse(numb) {
            if ((this.currentCollapse != numb) & (this.activeAnimation == false)) {
                this.currentCollapse = numb;
                this.hideCollapse();
                this.$set(this.collapseButton, numb, !this.collapseButton[numb]);
                this.activeAnimation = true;
                setTimeout(() => {
                    this.$set(this.collapseObject, numb, !this.collapseObject[numb]);
                    this.activeAnimation = false;
                }, this.timeout);
            }
        },
        hideCollapse() {
            this.collapseButton.forEach((value, index) => {
                this.$set(this.collapseButton, index, false);
            });
            this.collapseObject.forEach((value, index) => {
                this.$set(this.collapseObject, index, false);
