<template>
    <section class="app app_browser">
        <div class="top_bar">
            <div class="container">
                <div class="row">
                    <div class="col-12 d-flex justify-content-between align-items-center">
                        <button class="btn-home" @click="goHome()"></button>
                        <input type="text" class="search" v-model="currentTopInput" @keydown.enter="enterSiteByInput()">
                        <button class="button-close" @click="closeApp()"></button>
                    </div>
                </div>
            </div>
        </div>

        <div class="site page404" v-if="currentSite == 0">
            <div class="section">
                <div class="container">
                    <h3>Ошибка 404</h3>
                    <p>Кажется что-то пошло не так!<br>Возможно, введён некорректный адрес или страница была удалена.<br>Попробуйте обратиться к губернатору штата, наверняка он что-то скрывает.</p>
                </div>
                
            </div>
            
        </div>

        <div class="site eyefinder" v-if="currentSite == 100">
            
            <div class="section header">
                <div class="container">
                    <div class="row justify-content-between">
                        <div class="eye_logo" @click="changePage(0)">

                        </div>
                        <div class="right_block">
                            <div class="date_block">
                                <h4 class="place">Сан—Андреас</h4>
                                <h4 class="week">{{currentWeek}}</h4>
                            </div>
                            <div class="weather_block">
                                <i class="wi wi-day-sunny"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="section topmenu">
                <div class="container">
                    <div class="row">
                        <div class="col-12">
                            <div class="buttons_list">
                                <button class="btn" @click="changePage(1)"><i class="icon eye-icon"></i>Медиа и<br>правительство</button>
                                <button class="btn" @click="changePage(2)"><i class="icon eye-food"></i> Еда и<br>напитки</button>
                                <button class="btn" @click="changePage(3)"><i class="icon eye-money"></i> Деньги<br>и услуги</button>
                                <button class="btn" @click="changePage(4)"><i class="icon eye-plane"></i> Путешествия<br>и транспорт</button>
                                <button class="btn" @click="changePage(5)"><i class="icon eye-bag"></i> Мода и<br>здоровье</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="section page page_0" v-show="currentPage == 0">
                 <div class="container">
                    <div class="row">
                        <div class="col-8">
                            <div class="linkblock">
                                <h3 class="block_title">Новость дня</h3>
                            </div>
                            <div class="linkblock flex-column">
                                <h3 class="block_title">Случайный сайт</h3>

                                <div class="linkblock p-0 mb-0">
                                    <div class="image" :style="'background-image: url('+ require('@/assets/img/pc/' + siteList[randomSite].Img) + ');'" @click="goToSite(siteList[randomSite].Id)">
                                        
                                    </div>
                                    <div class="block">
                                        <span class="link" @click="goToSite(siteList[randomSite].Id)"><b>{{siteList[randomSite].Name}}</b></span>
                                        <p>{{siteList[randomSite].Description}}</p>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                        <div class="col-4 pl-0">
                            <div class="linkblock">
                                <h3 class="block_title">Последние блиты</h3>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-12">
                            <div class="linkblock">
                                <h3 class="block_title">Рекомендуемые сайты</h3>
                            </div>
                        </div>
                    </div>
                 </div>
            </div>
            <div class="section page page_1" v-show="currentPage == 1">
                <div class="container">
                    <div class="row">
                        <div class="col-12">
                            <h4>Результат для: Медиа и правительство</h4>

                            <div class="linkblock" v-for="(item, index) in mediaSiteList" :key="'media_site_' + index">
                                <div class="image" :style="'background-image: url('+ require('@/assets/img/pc/' + item.Img) + ');'" @click="goToSite(item.Id)"></div>
                                <div class="block">
                                    <span class="link" @click="goToSite(item.Id)"><b>{{item.Name}}</b></span>
                                    <p>{{item.Description}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <sitelifeinvader v-if="currentSite == 201" />
        <joinlspd v-if="currentSite == 228" />

    </section>
</template>

<script>
import sitelifeinvader from "./sites/lifeinvader.vue"
import joinlspd from "./sites/joinlspd.vue"

export default {
    name: "AppBrowser",
    props: {
        pcData: Object,
    },
    components: {
        sitelifeinvader,
        joinlspd
    },
    data() {
        return {
            currentSite: 100,
            currentTopInput: "www.eyefind.info",

            siteList: [
                {Id: 100, Category: "No", Img: "sites/eyefind.png", Name: "www.eyefind.info", Description: "Лучшая и единственная поисковая система в Лос-Сантосе."},
                {Id: 101, Category: "No", Img: "sites/stopdragwar.png", Name: "www.stopdragwar.com", Description: "Повышение осведомленности о последствиях запрета."},
                {Id: 101, Category: "No", Img: "sites/watchtheroad.png", Name: "www.watchtheroad.org", Description: `Программа "Смотри за дорогой" Департамента транспорта Лос-Сантоса - это удостоенная премии "Эмми" программа просвещения населения и повышения безопасности.`},
                {Id: 201, Category: "Media", Img: "sites/lifeinvader.png", Name: "www.lifeinvader.com", Description: "Социальная сеть 21 века. Ну и немного новостей, конечно же."},
                {Id: 202, Category: "Media", Img: "sites/classicvinewood.jpg", Name: "www.classicvinewood.com", Description: "Сикевелы, ремейки, сиквелы романтических комедий, супергерои, CGI ерунда и одномерные андрогинные вампиры."},
                {Id: 228, Category: "Media", Img: "sites/joinlspd.png", Name: "www.joinlspd.com", Description: "Сотрудник Полицейского Департамента Лос-Сантоса - лучший гражданин своей страны! Вступай в ряды LSPD, тебя здесь ждёт: высокая зарплата, большая пенсия, пушка и крутая тачка."},

                
            ],
            mediaSiteList: [],

            weekList: [
                "Воскресенье",
                "Понедельник",
                "Вторник",
                "Среда",
                "Четверг",
                "Пятница",
                "Суббота",
            ],

            currentPage: 0,
            randomSite: 0,
        }
    },
    methods: {
        closeApp() {
            this.$parent.showApp = -1;
        },
        goHome() {
            this.currentSite = 100;
            this.currentTopInput = this.siteList.find(item => item.Id == this.currentSite).Name;
        },
        enterSiteByInput() {
            var site = this.currentTopInput;
            var siteIndex = this.siteList.findIndex(item => item.Name == site);
            if (siteIndex == -1) {
                siteIndex = this.siteList.findIndex(item => item.Name == 'www.' + site);
            }
            if (siteIndex != -1) {
                this.currentTopInput = this.siteList[siteIndex].Name;
                this.currentSite = this.siteList[siteIndex].Id;
            } else {
                this.currentSite = 0;
            }
        },
        goToSite(id) {
            var siteIndex = this.siteList.findIndex(item => item.Id == id);
            if (siteIndex != -1) {
                this.currentTopInput = this.siteList[siteIndex].Name;
                this.currentSite = this.siteList[siteIndex].Id;
            }
        },
        changePage(index) {
            if (this.currentPage != index) {
                this.currentPage = index;
            }
            if (index == 0) {
                this.getRandomSite();
            }
        },
        getRandomSite() {
            var min = 0;
            var max = this.siteList.length;
            this.randomSite = Math.floor(Math.random() * (max - min)) + min;
        },
        generateCategories() {
            this.mediaSiteList = [];
            this.siteList.forEach(element => {
                if (element.Category == "Media") {
                    this.mediaSiteList.push(element);
                }
            });
        }
    },
    computed: {
        currentWeek() {
            let date = new Date;
            return this.weekList[date.getDay()];
        }
    },
    mounted() {
        this.getRandomSite();
        this.generateCategories();
        


        console.log(this.randomSite);
        console.log(this.siteList[this.randomSite])

    }
}
</script>
