<template>
    <section class="fullpage"
        @mousemove="mousemove"
        @mousedown="mouseDown"
        @mouseup="mouseUp"
    >
        <div class="lockpicking_game" :class="{onwin: gameWin}">
            <div class="collar">
                <div class="cylinder"></div>
            </div>
            <div class="driver" :style="'transform: rotate(' + rotateDriver + 'deg);'"></div>
            <div class="pin" :style="'transform: rotate(' + rotatePin + 'deg);'" >
                <div class="pin_inner" :class="{onshake: isPinOnShake, ondamage: isPinOnDamage}">
                    <div class="top"></div>
                    <div class="bot"></div>
                </div>
            </div>
        </div>

        <div class="lockpicking_controls">
            <button class="btn btn-primary" v-if="!gameStart & !gameWin" :disabled="chances == 0" @click="startGame()">Начать игру</button>
            <button class="btn btn-primary" v-if="gameStart" @click="stopGame()">Остановить игру</button>
            <button class="btn btn-primary" @click="exitGame()">Выйти из игры</button>
        </div>

        <div class="lockpicking_timer bg_primary">
            <p>Осталось попыток:</p>
            <h3>{{chances}}</h3>
            <p>Осталось времени:</p>
            <h3>{{timerMinutes}}:{{timerSeconds}}</h3>
        </div>
    </section>
</template>


<script>
require('@/assets/css/games/lockpicking.css')
import axios from "axios";
export default {
    name: "LockpickingGames",
    props: {
        
    },
    components: {
        
    },
    data() {
        return {
            timeToWin: 120, // Время в секундах
            chances: 3, // количество попыток

            isDrag : false,
            isPinOnShake: false,
            isPinOnDamage: false,
            gameStart: false,
            timer: null,
            currentTime: 0,
            gameWin: false,

            rotateDriver: 0,
            realRotateDriver: 0,

            rotateDriverMax: 180,
            rotateDriverMin: 0,


            rotatePin: 0,
            realRotatePin: 0,
            rotatePinMin: -5,
            rotatePinMax: 5,
            
            winDegrees: 90,
            damageLevel: 0,  
        }
    },
    computed: {
        timerMinutes() {
            return ('0' + Math.trunc(this.currentTime / 60)).slice(-2);
        },
        timerSeconds() {
            return ('0' + (this.currentTime - (Math.trunc(this.currentTime / 60) * 60))).slice(-2);
        }
    },
    methods: {
        mousemove(event) {
            if (this.isDrag & this.gameStart) {
                var screenXCenter = window.innerWidth / 2;
                var screenYCenter = window.innerHeight  / 2;
                var clienX = event.clientX;
                var clienY = window.innerHeight - event.clientY;
                var x = clienX - screenXCenter;
                var y = clienY - screenYCenter;
                var angle = Math.atan(y/x) * 180 / Math.PI
                if (clienX < screenXCenter) { 
                    angle = (180 - angle) * -1;
                }
                this.rotateDriverToAngle(angle * -1 + 75);
                this.checkWinAngles()
            }
        },
        mouseDown() {
            this.isDrag = true;
        },
        mouseUp() {
            this.isDrag = false;
            
        },
        rotateDriverToAngle(angle) {
            this.rotateDriver = angle;
            angle += 15;
            if (angle <= 0) {
                this.realRotateDriver = Math.trunc(360 - angle);
            } else {
                this.realRotateDriver = Math.trunc(angle)
            }
        },
        rotatePinLeft() {
            if (this.rotatePin <= this.rotatePinMin) {
                this.shakePin();
                return
            }
            this.rotatePin -= 2;
            this.changeRealRotate()
        },
        rotatePinRight() {
            if (this.rotatePin >= this.rotatePinMax) {
                this.shakePin();
                return
            }
            this.rotatePin += 2;
            this.changeRealRotate()
        },
        shakePin() {
            var isWIn = this.checkGameForWin();
            if (this.isPinOnShake) {
                return
            }
            if (!this.isPinOnShake & isWIn) {
                this.winGame();
                return
            }
            this.isPinOnShake = true;
            var shaker = setInterval(() => {
                this.isPinOnShake = false;
                this.damageLevel += 1;
                this.checkDamage();
                clearTimeout(shaker);
            }, 1000);
        },
        changeRealRotate() {
            var circles = Math.trunc(this.rotatePin / 360);
            if (this.rotatePin <= 0) {
                this.realRotatePin = this.rotatePin - (360 * circles);
            } else {
                this.realRotatePin = this.rotatePin - (360 * circles);
            }
        },
        keyup(e) {
            if (!this.gameStart) {
                return
            }
            if (e.code == "KeyA") {
                this.rotatePinLeft();
            }
            if (e.code == "KeyD") {
                this.rotatePinRight();
            }
        },
        resetStats() {
            this.rotateDriver = 0;
            this.rotatePin = 0;
            this.rotatePinMin = -5;
            this.rotatePinMax = 5;
            this.damageLevel = 0;
        },
        startGame() {
            this.gameStart = true;
            this.resetStats();
            this.startTimer();
        },
        stopGame() {
            this.gameStart = false;
            this.stopTimer();
            this.lostChance();
        },
        exitGame() {
            this.stopGame();
             axios.post('https://eclipse/ExitGame', {
                }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.activeGame = "";
           
        },
        startTimer() {
            this.currentTime = this.timeToWin;
            this.timer = setInterval(() => {
                this.currentTime--
                if (this.currentTime < 0) {
                    this.stopGame();
                }
            }, 1000);
        },
        stopTimer() {
            this.currentTime = this.timeToWin;
            clearTimeout(this.timer);
        },
        checkWinAngles() {
            var sum = Math.abs(this.winDegrees - this.realRotateDriver);
            var percent = 0;
            if (sum < 30) {
                percent = sum * 3.3;
                this.rotatePinMin = (90 - percent) * -1;
                this.rotatePinMax = 90 - percent;
            } else {
                this.rotatePinMin = -5;
                this.rotatePinMax = 5;
            }
            
        },
        checkGameForWin() {
            var sum = Math.abs(this.winDegrees - this.realRotateDriver);
            if (sum <= 5) {
                return true
            } else {
                return false
            }
        },
        checkDamage() {
            if (this.damageLevel >= 4) {
                this.breakStick();
                console.log('вы сломали палку, ёпта')
                axios.post('https://eclipse/BrokeLockPick', {
                }).then(() => { return; }).catch(function () { console.log() });

                this.stopGame()
            }
        },
        breakStick() {
            this.isPinOnDamage = true;
            var damage = setInterval(() => {
                this.isPinOnDamage = false;
                clearTimeout(damage);
            }, 1000);

        },
        winGame() {
            this.gameStart = false;
            this.stopTimer();
            this.gameWin = true;
            var timer = setInterval(() => {
                this.exitGame();
                clearTimeout(timer);
            }, 1000);
            // Присылаем уведомление: вы успешно взломали замок
            console.log('win game')
            axios.post('https://eclipse/SuccesLockPick', {
                }).then(() => { return; }).catch(function () { console.log() });
        },
        lostChance() {
            this.chances -= 1;
            // Присылаем уведомление: Осталось this.chances попыток
            axios.post('https://eclipse/LeftChancesLockPick', {
                chances: this.chances  
                }).then(() => { return; }).catch(function () { console.log() });
            if (this.chances == 0) {
                this.lostGame();
            }

        },
        lostGame() {
            console.log('вы проиграли')
            axios.post('https://eclipse/LooseLockPick', {
                }).then(() => { return; }).catch(function () { console.log() });
            // игра окончена
            // Отправляем уведомление: Ваши попытки закончились
            // Отправляем вызов копов
        }
    },
    mounted() {
        this.currentTime = this.timeToWin;

        window.addEventListener('keypress', this.keyup);
    },
    destroyed() {
        this.stopTimer();
        window.removeEventListener('keypress', this.keyup);
    },
}

</script>