<template>
    <section>
        <transition name="list">
            <div class="showbusiness bg_primary" v-if="page == 0">
                <h3 class="text-center">{{estateData.Name}}</h3>
                <div class="business_info" v-if="estateData">
                    <!-- <h6 v-if="estateData.UserId != 0">Владелец: <span>{{estateData.UserId}}</span></h6> -->
                    <h6 v-if="estateData.UserId == 0">Владелец: <span>Отсутствует</span></h6>
                    <h6>Тип жилья: <span>{{getEstateType(estateData.Type)}}</span></h6>
                    <h6 v-if="estateData.UserId == 0">Цена: <span>{{estateData.Price}}$</span></h6>
                </div>
                <div class="controls">
                    <button v-if="estateData.UserId == 0" @click="buyEstate()" class="btn btn-secondary">Купить</button>
                    <button v-if="estateData.InterierPosition != null" @click="enterEstate()" class="btn btn-secondary">Войти</button>
                    <button v-if="estateData.IsLocked == true" class="btn btn-secondary" @click="changeLockStatus(false)">Открыть замок</button>
                    <button v-else class="btn btn-secondary" @click="changeLockStatus(true)">Закрыть на замок</button>
                    <button v-if="estateData.UserId == playerData.id" @click="changePage(1)" class="btn btn-secondary">Продать</button>
                    <button class="btn btn-primary" @click="closePage()">Закрыть</button>
                </div>
            </div>
        </transition>
         <transition name="list">
            <div class="showbusiness bg_primary" v-if="page == 1">
                <h3>Продать магазин:</h3>
                <label>Продать игроку по номеру телефона:</label>
                <input class="form-control" v-model="sellToPlayerById" placeholder="Введите номер телефона">
                <div class="controls mt-3">
                    <button @click="sellEstateToPlayer()" class="btn btn-block btn-secondary">Продать игроку</button>
                    <button @click="sellEstateToGover()" class="btn btn-secondary">Продать правительству</button>
                    <button @click="changePage(0)" class="btn btn-primary">Назад</button>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import axios from "axios";
export default {
    name: 'EstateInfo',
    props: {
        playerData: Object,
    },
    data() {
        return {
            estateData: {
                Id: 0,
                Name: "Неизвестное жильё",
                Price: 90000,
                UserId: 1,
                InterierPosition: null,
                Type: 1,
            },
            page: 0,
            sellToPlayerById: undefined,
        }
    },
    methods: {
        changeLockStatus(status) {
            axios.post('https://eclipse/ChangeHouseLockStatus', {
                Id: this.estateData.Id,
                Status: status,
                Key: this.estateData.Key
            }).then(() => { return; }).catch(function () { console.log() });
        },
        closePage() {
            this.$parent.showEstateInfo = false;
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changePage(numb) {
            if (this.page != numb) {
                this.page = numb;
                this.sellToPlayerById = undefined;
            }
        },
        getEstateType(key) {
            switch (key) {
                case 0:
                    return "Отель"
                case 1:
                    return "Дом"
                default:
                    return "Неизвестно"
            }
        },
        buyEstate() {
            if(this.estateData.Type == 0) {
                axios.post('https://eclipse/TryBuyHotelRoom', {
                    Id: this.estateData.Id
                }).then(() => { return; }).catch(function () { console.log() });
            }
            else if(this.estateData.Type == 1) {
                axios.post('https://eclipse/TryBuyHouse', {
                    Id: this.estateData.Id
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        sellEstateToPlayer() {
            var playerPhone = this.sellToPlayerById;
            if (!playerPhone) {
                // высираем ошибку: "Введите правильный номер телефона"
                return
            }
            if (playerPhone.length >= 5) {
                // продаем 
                // высираем "Недвижимость успешно продана игроку Имя Фамилия"
                this.closePage()
            } else {
                // высираем ошибку "Введите правильный номер телефона"
            }
        },
        sellEstateToGover() {
            // высираем "Недвижимость успешно продана правительству"
            this.closePage()
        },
        enterEstate() {
            if(this.estateData.Type == 1) {
                axios.post('https://eclipse/EnterHouse', {
                    Id: this.estateData.Id,
                    IsLocked: this.estateData.IsLocked
                }).then(() => { return; }).catch(function () { console.log() });
            }
            this.closePage()
        }
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadEstateInfo") {
                this.estateData.Id = event.data.Id;
                this.estateData.Name = event.data.Name;
                this.estateData.Price = event.data.Price;
                this.estateData.Type = event.data.Type;
                this.estateData.UserId = event.data.UserId;
                this.estateData.InterierPosition = event.data.InterierPosition;
                this.estateData.IsLocked = event.data.IsLocked;
                this.estateData.Key = event.data.Key;
            }
        },false);
    }
}

</script>