<template>
    <section class="admin main_panel" :class="{compact: compact}">
        <div class="top_bar">
            <div class="name">
                <h3 class="brand">Панель администрирования</h3>
                <!-- <button class="ml-2 btn reload_data btn-primary btn-esm" :class="{onload: iconLoading}" @click="refreshAdminData()" :disabled="iconLoading"></button> -->
            
            </div>
            <div class="admin_player">
                <h3 class="brand">{{playerData.name}} {{playerData.surname}} ({{playerData.id}}) id: {{playerData.AccountId}} — {{adminRanks[playerData.adminData.rank].name}}</h3>
            </div>
        </div>
        <div class="wrapper">
            <div class="sidebar">
                <div class="sidebar_wrapper">
                    <div class="logo">
                        <h1>ECLIPSE</h1>
                        <!-- <div class="mainlogo">
                        </div> -->
                    </div>
                    <div class="navigation">
                        <button class="btn btn-block btn-secondary" :class="{active: page == 1}" @click="changePage(1)">Основное</button>
                        <button class="btn btn-block btn-secondary" :class="{active: page == 3 || page == 31}" @click="changePage(3)">Игроки</button>
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 7" :class="{active: page == 2 || (page >= 20 && page <= 29)}" @click="changePage(2)">Авто и пропы</button>
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 7" :class="{active: page == 4}" @click="changePage(4)">Игровой мир</button>
                        <!-- <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 7" :class="{active: page == 5 || page == 51}" @click="changePage(5)">Вызовы игроков</button> -->
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 7" :class="{active: page == 6 || (page >= 600 && page <= 699)}" @click="changePage(6)">Данные</button>
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 10" :class="{active: page == 7}" @click="changePage(7)">Ивенты</button>
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 10" :class="{active: page == 8}" @click="changePage(8)">Бизнесы</button>
                        <button class="btn btn-block btn-secondary" v-if="playerData.adminData.rank >= 11" :class="{active: page == 9 || page == 91}" @click="changePage(9)">Разработка</button>
                    </div>
                    <div class="bottom_buttons">
                        <button class="btn-sm btn-primary btn-sm" @click="changeCompact()" v-if="!compact">Оконный режим</button>
                        <button class="btn btn-primary" style="width: initial.admin .content .page .card" @click="changeCompact()" v-if="compact">На весь экран</button>
                    </div>
                </div>
            </div>

            <div class="content">
                <div class="page page_1" v-show="page==1">
                    <div class="card">
                        <h3 class="mb-3">Администрирование</h3>
                        <div class="row mb-3">
                            <div class="col-4 pr-1">
                                <button class="btn btn-primary btn-block" @click="useCommand(0)">Ноуклип</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(6)" v-if="playerData.adminData.rank >= 7">Годмод</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(7)" v-if="playerData.adminData.rank >= 7">Режим спектатора</button>
                            </div>
                            <div class="col-4 pl-2 pr-2">
                                <button class="btn btn-primary btn-block" @click="useCommand(3)" v-if="playerData.adminData.rank >= 7">Блипы</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(4)" v-if="playerData.adminData.rank >= 7">Спрайты</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(5)" >Отображение хешэй</button>
                            </div>
                            <div class="col-4 pl-1">
                                
                            </div>
                        </div>
                        <h3 class="mb-3">Персонаж</h3>
                        <div class="row">
                            <div class="col-4 pr-1">
                                <button class="btn btn-primary btn-block" @click="useCommand(8)">Реснуться</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(9)">Вылечиться</button>
                            </div>
                            <div class="col-4 pl-2 pr-2">
                                <button class="btn btn-primary btn-block" @click="openModalWindowE()" v-if="playerData.adminData.rank >= 7">Выдать предмет</button>
                                <button class="btn btn-primary btn-block" @click="useCommand(2)">ТП к метке</button>
                            </div>
                            <div class="col-4 pl-1">
                                
                            </div>
                        </div>
                    </div>
                    <div class="card popup_window" v-show="showGiveItemWindowE">
                        <div class="popup_window_inner">
                            <div class="d-flex justify-content-between">
                                <h3 class="">Выдать себе предмет</h3>
                                <button class="btn btn-secondary pr-3 pl-3" @click="showGiveItemWindowE = false">Закрыть</button>
                            </div>
                            <div class="row">
                                <div class="col-6">
                                    <label label-for="gittp-name">Название предмета:</label>
                                </div>
                                <div class="col-2">
                                    <label label-for="gittp-count">Кол-во:</label>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-6">
                                    
                                    <input id="gittp-name" class="form-control red_input" v-model="giveItemModel.Name">
                                </div>
                                <div class="col-2">
                                    
                                    <input id="gittp-count" class="form-control red_input" v-model="giveItemModel.Count">
                                </div>
                                <div class="col-4">
                                    <button class="btn btn-primary btn-block" @click="giveItemToAdmin()">Выдать предмет</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_2" v-show="page==2">
                    <div class="card">
                        <h3 class="mb-3">Автомобили</h3>
                        <div class="row">
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="changePage(21)">Спавн авто</button>
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="changePage(22)">Удаление авто</button>
                            </div>
                            <div class="col-4">
                                <button class="btn btn-secondary btn-block" @click="useAdminCommand(6)">Ремонтировать авто</button>
                            </div>
                        </div>
                        <h3 class="my-3">Пропы</h3>
                        <div class="row">
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="changePage(23)">Спавн пропов</button>
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="changePage(24)">Удаление пропов</button>
                            </div>
                            <div class="col-4">
                                
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_2" v-show="page==21">
                    <div class="card">
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(2)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying">Спавн автомобиля</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-4">
                                <label v-b-tooltip.hover.right title="Name из списка https://wiki.rage.mp/index.php?title=Vehicles">"Name" автомобиля</label>
                                <input class="form-control red_input" v-model="createVehicleModel.Hash">
                            </div>
                            <div class="col-4">
                                <label v-b-tooltip.hover.right title="Номер из списка https://wiki.rage.mp/index.php?title=Vehicle_Colors">Цвет автомобиля</label>
                                <input class="form-control red_input" v-model="createVehicleModel.Color">
                            </div>
                            <div class="col-4">
                                <label v-b-tooltip.hover.top title="Не больше 8 знаков, латинские символы">Номер автомобиля</label>
                                <input class="form-control red_input" v-model="createVehicleModel.Plate">
                            </div>
                        </div>
                        <div class="row mt-3">
                            <div class="offset-8 col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(0)">Создать</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_2" v-show="page==22">
                    <div class="card">
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(2)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying">Удаление автомобилей</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-12">
                                <label>Удаление авто по ID</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                <input class="form-control red_input" v-model="deleteCarIndex">
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(1)">Удалить</button>
                            </div>
                        </div>
                        <div class="row mt-3">
                            <div class="col-12">
                                <label>Удаление ближайшего автомобиля</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(2)">Удалить</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_2" v-show="page==23">
                    <div class="card">
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(2)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying">Спавн пропов</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-4">
                                <label v-b-tooltip.hover title="Список всех пропов: https://gtahash.ru">"Name" пропа</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                <input class="form-control red_input" v-model="createPropModel.Hash">
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(3)">Создать</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_2" v-show="page==24">
                    <div class="card">
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(2)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying">Удаление пропов</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-12">
                                <label>Удаление пропа по хэшу</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                <input class="form-control red_input" v-model="deletePropIndex">
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(4)">Удалить</button>
                            </div>
                        </div>
                        <div class="row mt-3 d-none">
                            <div class="col-8">
                                <input class="form-control red_input" v-model="deletePropIndexById">
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="useAdminCommand(5)">Удалить</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_3" v-show="page==3">
                    <div class="card">
                        <div class="player_list_controls">
                            <h3>Игроки онлайн ({{playerList.length}})</h3>
                            <div class="filters">
                                <div class="search_bar">
                                    <input class="form-control search" placeholder="поиск" v-model="searchFieldModel" @input="searchPlayer()" />
                                </div>

                                <div class="btn-group btn-group-toggle">
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeSort==1}" @click="changePlayerSort(1)">
                                        <input type="radio" class="d-none d-sm-none">
                                        <span class="">по алфавиту</span>
                                    </label>
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeSort==2}" @click="changePlayerSort(2)">
                                        <input type="radio" class="" >
                                        <span class="">по ID</span>
                                    </label>
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeSort==3}" @click="changePlayerSort(3)">
                                        <input type="radio" class="">
                                        <span class="">по времени</span>
                                    </label>
                                </div>

                                <div class="btn-group btn-group-toggle">
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeFilter==1}" @click="changeFilter(1)">
                                        <input type="radio" class="d-none d-sm-none">
                                        <span class="">Все игроки</span>
                                    </label>
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeFilter==2}" @click="changeFilter(2)">
                                        <input type="radio" class="" >
                                        <span class="">Админы</span>
                                    </label>
                                    <label class="btn btn-sm btn-primary btn-simple" :class="{active: activeFilter==3}" @click="changeFilter(3)">
                                        <input type="radio" class="">
                                        <span class="">Полиция</span>
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div class="player_list scrolled">
                            <div class="player_list_item"
                                :class="{'online': item.isOnline, 'role_medic': item.Roles.includes('medic'), 'role_admin': playerData.adminData.rank > 0, 'role_police': item.Roles.includes('police')}"
                                v-for="(item, index) in playerList"
                                :key="'player_' + index"
                                v-show="item.show"
                                @click="showPlayerPage(index)">
                                <div class="player_item">
                                    <div class="id">
                                        {{item.AccountId}}
                                    </div>
                                    <div class="name">
                                        {{item.IdCard.Name}}
                                        {{item.IdCard.Lastname}} ({{item.Id}})
                                    </div>
                                    <div class="date">
                                        {{getDate(item.IdCard.Dob)}}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_4" v-show="page == 4">
                    <div class="card">
                        <h3 class="mb-3">Управление миром</h3>
                        <div class="row">
                            <div class="col-12">
                                <label>Погода:</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-4">
                                <div class="dropdown">
                                    <a class="btn btn-outline-secondary btn-block btn-dropdown dropdown-toggle" @click="showWeatherDropdown = !showWeatherDropdown">{{weather.text}}</a>
                                    <div class="weather dropdown-menu" v-show="showWeatherDropdown">
                                        <a :class="'dropdown-item status_' + index" v-for="(item, index) in weatherList" :key="'weather_' + index" @click="changeGameWeather(index)">{{item.text}}</a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-4 d-flex align-items-center">
                                <button class="btn btn-primary btn-block" @click="sendWeatherToBackend()">Сменить погоду</button>
                            </div>
                        </div>
                        <div class="row mt-3">
                            <div class="col-12">
                                <label>Время:</label>
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-2">
                                <input class="form-control red_input" v-model="timeHours">
                            </div>
                            <div class="col-2">
                                <input class="form-control red_input" v-model="timeMinutes">
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="changeServerTime()">Сменить время</button>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-12">
                                <label>Блэкаут:</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="TurnBlackout(true)">Включить блэкаут</button>
                            </div>
                            <div class="col-4">
                                <button class="btn btn-primary btn-block" @click="TurnBlackout(false)">Выключить блэкаут</button>
                            </div>
                        </div>
                        <div class="row mt-3">
                            <div class="col-12">
                                <label>Трафик:</label>
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-6">
                                <div class="form-group mb-0">
                                <input type="range" min="0" max="1" step="0.1" v-model="trafficA" class="form-control-range">
                                </div>
                            </div>
                            <div class="col-1 d-flex align-items-center">
                                {{trafficA}}
                            </div>
                            <div class="col-5">
                                <button class="btn btn-primary btn-block" @click="changeTrafficA()">Сменить кол-во транспорта</button>
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-6">
                                <div class="form-group mb-0">
                                <input type="range" min="0" max="1" step="0.1" v-model="trafficB" class="form-control-range">
                                </div>
                            </div>
                            <div class="col-1 d-flex align-items-center">
                                {{trafficB}}
                            </div>
                            <div class="col-5">
                                <button class="btn btn-primary btn-block" @click="changeTrafficB()">Сменить кол-во пешеходов</button>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-6">
                                <div class="form-group mb-0">
                                <input type="range" min="0" max="1" step="0.1" v-model="trafficC" class="form-control-range">
                                </div>
                            </div>
                            <div class="col-1 d-flex align-items-center">
                                {{trafficC}}
                            </div>
                            <div class="col-5">
                                <button class="btn btn-primary btn-block" @click="changeTrafficC()">Кол-во припаркованных тс</button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_5 data_pages data_adminpage" v-show="page == 5">
                    <div class="card scrolled admin_call_list">
                        <h3 class="mb-3">Вызовы игроков</h3>
                        <div class="data_element d-flex" :class="'elemestatus_'+item.Status" v-for="(item, index) in adminCalls" :key="'adm_call_'+ index" @click="showFullAdminCall(index)">
                            <div style="width: 10%" class="">
                                Id: {{item.Id}}
                            </div>
                            <div style="width: 70%" class="">
                                {{item.Message}}
                            </div>
                            <div style="width: 20%"  :class="'status_' + item.Status" class="text-right">
                                {{getStatus(item.Status)}}
                                <div class="admin_on_scene" v-if="item.OnScene">
                                    <span v-for="(admin, index2) in item.OnScene" :key="'call_'+item.Id + index2">{{admin}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="page page_5 data_adminpage" v-if="page == 51">
                    <div class="card scrolled">
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(5)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying"><b>Вызов:</b> {{activeAdminCall.Id}}</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                <p class="mb-0 copying"><b>Id:</b> {{activeAdminCall.Id}}</p>
                                <p class="mb-0 copying"><b>Игрок:</b> {{activeAdminCall.Name}} {{activeAdminCall.Lastname}}</p>
                                <p class="mb-0 copying"><b>На вызове:</b>
                                    <span class="" v-for="(crew, index) in activeAdminCall.OnScene" :key="'crew_'+ index">
                                        {{crew}}
                                    </span>
                                </p>
                                <div class="mb-0 copying"><b>Статус:</b> 
                                    <div class="dropdown">
                                        <a :class="'btn btn-dropdown dropdown-toggle status_' + activeAdminCall.Status" @click="showAdminDropdown = !showAdminDropdown">({{activeAdminCall.Status}}) {{getStatus(activeAdminCall.Status)}}</a>
                                        <div class="dropdown-menu" v-show="showAdminDropdown">
                                            <a :class="'dropdown-item status_' + index" v-for="(item, index) in statusList" :key="'units' + index" @click="changeStatus(index)">({{index}}) {{ item }}</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-4">
                                <button v-if="isAdminOnScene() == false" class="btn btn-primary btn-block" @click="adminCallCommand(0)">Взять вызов</button>
                                <button v-if="isAdminOnScene() == true" class="btn btn-primary btn-block" @click="adminCallCommand(1)">Отменить вызов</button>
                                <button class="btn btn-primary btn-block" @click="adminCallCommand(2)">Тп к игроку</button>
                                <button class="btn btn-primary btn-block" @click="adminCallCommand(3)">Тп игрока к себе</button>
                            </div>
                        </div>
                        
                    </div>
                </div>

                <div class="page page_3" v-if="page == 31">
                    <div class="card scrolled player_card">
                        
                        <div class="row mb-3">
                            <div class="col-3">
                                <button class="btn btn-primary btn-block" @click="changePage(3)">Назад</button>
                            </div>
                            <div class="col-9 d-flex align-items-center justify-content-end">
                                <h3 class="copying playername" :class="{'online': activePlayer.isOnline}"><b>Игрок:</b> {{activePlayer.IdCard.Name}} {{activePlayer.IdCard.Lastname}}</h3>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-8">
                                <p class="mb-0 copying"><b>Id:</b> {{activePlayer.Id}}</p>
                                <p class="mb-0 copying"><b>Ник:</b> {{activePlayer.NickName}}</p>
                                <p class="mb-0 copying"><b>Измерение:</b> {{activePlayer.Dimension }}
                                    <button class="ml-1 btn btn-outline-primary btn-esm" @click="editPlayerDimension()">Изменить</button>
                                </p>
                                <p class="mb-0 copying"><b>Пол:</b> {{getSex(activePlayer.IdCard.Sex)}}</p>
                                <p class="mb-0 copying"><b>Телефон:</b> {{activePlayer.IdCard.Phone}}</p>
                                <p class="mb-0 copying"><b>Возраст:</b> {{activePlayer.IdCard.Age}}</p>
                                <p class="mb-0 copying"><b>Баланс:</b> {{activePlayer.balance}}$</p>
                                <p class="mb-0 copying" v-if="activePlayer.Roles">
