</template>

<script>
// import playerList from "../../json/players.json";
// playerList.forEach(element => {element.show = true;});

import axios from 'axios';
import Business from './components/Business.vue';
import click from '../../assets/sounds/click.mp3';
import weaponNamesList from '../../json/weapon.json';

export default {
    name: "AdminPanel",
    props: {
        playerData: Object,
    },
    components: {
        Business,
    },
    data() {
        return {
            page: 1,
            activeFilter: 1,
            activeSort: 1,
            position: {
                posX: 0,
                posY: 0,
                posZ: 0,
                rot: 0
            },

            weaponNamesList: weaponNamesList,

            loader: false,
            iconLoading: false,

            playerList: [],
            compact: true,

            adminRanks: [
                {name: "Ивентер"},
                {name: "Куратор 1"},
                {name: "Куратор 2"},
                {name: "Куратор 3"},
                {name: "Модератор"},
                {name: "помощник фракции"},
                {name: "Руководитель фракции"},
                {name: "Администратор"},
                {name: "Главный администратор"},
                {name: "Руководитель проекта"},
                {name: "Разработчик"},
                {name: "Супер разработчик"}
            ],

            activePlayer: null,

            weather: { text: 'Солнечно', value: "EXTRASUNNY" },
            showWeatherDropdown: false,
            weatherList: [
                { text: 'Солнечно', value: "EXTRASUNNY" },
                { text: 'Чистое небо', value: "CLEAR" },
                { text: 'Нейтрально', value: "NEUTRAL" },
                { text: 'Смог', value: "SMOG" },
                { text: 'Туманно', value: "FOGGY" },
                { text: 'Пасмурно', value: "OVERCAST" },
                { text: 'Облачно', value: "CLOUDS" },
                { text: 'Мелкий дождь', value: "CLEARING" },
                { text: 'Дождь', value: "RAIN" },
                { text: 'Гром', value: "THUNDER" },
                { text: 'Снег', value: "SNOW" },
                { text: 'Буря', value: "BLIZZARD" },
                { text: 'Снежная', value: "SNOWLIGHT" },
                { text: 'Рождество', value: "XMAS" },
                { text: 'Хэллоуин', value: "HALLOWEEN" },
            ],

            statusList: [
                "Выполнен",
                "Активный",
                "В обработке",
                "Нужна помощь"
            ],

            timeHours: 23,
            timeMinutes: 59,
            showGiveItemWindowA: false,
            showGiveItemWindowB: false,
            showGiveItemWindowС: false,
            showGiveItemWindowD: false,
            showGiveItemWindowE: false,
            giveItemModel: {
                Name: "",
                Count: 0,
            },
            giveHash: null,
            kickReason: "",
            banReason: "",
            banTime: 0,

            showEditPlayerInfoWindow: false,
            showEditPlayerAutoWindow: false,
            showEditPlayerRolesWindow: false,
            showEditPlayerDimensionWindow: false,
            showAddNewAutoWindow: false,
            editPlayerInfoModel: {},
            editPlayerAutoModel: {},
            newPlayerAutoModel: {},
            activeAutoIndex: undefined,
            newPlayerRole: "",
            searchFieldModel: "",
            newPlayerDimension: "",

            adminCalls: [],
            activeAdminCall: undefined,
            showAdminDropdown: false,

            gameChatData: [{Author: "Jessie Pinkman", AuthorId: 21,"Group": "standart", "Type": "standart", "Chat": "All", "Message": "Проверка работоспособности чата..."}],
            policeCallData: [{id: 21, date: "23.12.44", status: "", message: "Требуются полицейские. Ограбление банка в Лос-Сантосе.", crew: [155, 233], from: "123", show: true,}],
            activePoliceCall: undefined,
            activePoliceCallModel: undefined,
            medicalCallData: [],
            taxiCallData: [],
            mechanicCallData: [],
            newsCallData: [],
            bleeterData: [],
            dynastyData: [],
            darkChatData: [],
            adminCallData: [],

            createVehicleModel: {},
            createPropModel: {},
            deleteCarIndex: undefined,
            deletePropIndex: undefined,
            deletePropIndexById: undefined,

            allFoodPriceData: [{Name: "Test 1",Price: 800,Id: "foodTest1"},],
            allVehiclesPriceData: [{Name: "Weapon 1",Price: 800,Id: "weaponTest1"},],
            allEstatePriceData: [{Name: "Mirror Park 3",Price: 80000,Id: "apartTest1"},],

            showAddNewWeaponWindow: false,
            showEditWeaponWindow: false,
            newPlayerWeaponModel: {},
            showWeaponError: false,

            trafficA: 0.3,
            trafficB: 0.3,
            trafficC: 0.3,

            pageNumber: 0,
            foodDataSize: 10,
            renderFoodData: [],
        }
    },
    computed: {
        pageCount(){
            let l = this.allFoodPriceData.length,
                s = this.foodDataSize;
            return Math.ceil(l/s);
        },
        paginatedData(){
            const start = this.pageNumber * this.foodDataSize,
            end = start + this.foodDataSize;
            return this.allFoodPriceData.slice(start, end);
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
                this.closeAllModals();
            }

            if(index == 91 || index == 92 || index == 93) {
                axios.post('https://eclipse/GetPricesData', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        nextPage() {
            this.pageNumber++;
        },
        prevPage(){
            this.pageNumber--;
        },
        closeAllModals() {
            this.showGiveItemWindowA = false;
            this.showGiveItemWindowB = false;
            this.showGiveItemWindowC = false;
            this.showGiveItemWindowD = false;
            this.showGiveItemWindowE = false;
            this.showEditPlayerInfoWindow = false;
            this.showEditPlayerAutoWindow = false;
            this.showEditPlayerRolesWindow = false;
            this.showEditPlayerDimensionWindow = false;
            this.showAddNewAutoWindow = false;
            this.showAdminDropdown = false;
        },
        showPlayerPage(index) {
            this.playSound();
            this.activePlayer = this.playerList[index];
            this.page = 31;
        },
        TurnBlackout(state) {
            axios.post('https://eclipse/Blackout', {
                State: state    
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changeFilter(index) {
            this.activeFilter = index;
            this.hideAllArray(this.playerList)
            switch (index) {
                case 1:
                    this.showAllArray(this.playerList)
                    break;
                case 2:
                    this.playerList.forEach(element => {
                        if (!element.Roles) {
                            element.adminData.rank > 0;
                        }
                        var searchRole = element.Roles.findIndex(item => item == "admin");
                        if (searchRole != -1) {
                            element.show = true
                        }
                    });
                    break;
                case 3:
                    this.playerList.forEach(element => {
                        if (!element.Roles) {
                            element.Roles = [];
                        }
                        var searchRole = element.Roles.findIndex(item => item == "police");
                        if (searchRole != -1) {
                            element.show = true
                        }
                    });
                    break;
                default:
                    break;
            }
        },
        showAllArray(array) {
            array.forEach(element => {
                element.show = true;
            });
        },
        hideAllArray(array) {
            array.forEach(element => {
                element.show = false;
            });
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getSex(sex) {
            if (sex) {
                return "мужской"
            } else {
                return "женский"
            }
        },
        byField(field) {
            return (a, b) => a[field] > b[field] ? 1 : -1;
        },
        changePlayerSort(numb) {
            this.activeSort = numb;
            if (numb == 1) {
                this.playerList.sort(this.byField('Name'));
            }
            if (numb == 2) {
                this.playerList.sort(this.byField('Id'));
            }
        },
        changeCompact() {
            this.compact = !this.compact;
        },
        useCommand(index) {
            axios.post('https://eclipse/AmdCommand', {index}).then(() => { return; }).catch(function () { console.log() });
        },
        usePlayerCommand(id, command) {
            axios.post('https://eclipse/AmdCommand', {
                    UserId: id,
                    index: command
            }).then(() => { return; }).catch(function () { console.log() });
        },
        useAdminCommand(index) {
            switch (index) {
                case 0:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "CreateVehicle",
                        VehicleData: this.createVehicleModel
                    }).then(() => { return; }).catch(function () { console.log() });
                    // Создать авто
                    this.createVehicleModel.Hash;
                    this.createVehicleModel.Color;
                    this.createVehicleModel.Plate;
                    break;
                case 1:
                    // Удалить авто по хэшу
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteVehicle",
                        CarIndex: this.deleteCarIndex
                    }).then(() => { return; }).catch(function () { console.log() });
                    this.deleteCarIndex
                    break;
                case 2:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteNearestVehicle",
                    }).then(() => { return; }).catch(function () { console.log() });
                    // Удалить близжайший автомобиль
                    break;
                case 3:
                     axios.post('https://eclipse/AmdCommand', {
                        index: "CreateProp",
                        PropHash: this.createPropModel.Hash
                    }).then(() => { return; }).catch(function () { console.log() });
                
                    break;
                case 4:
                    // Удалить проп по хэшу
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteProp",
                        PropHash: this.deletePropIndex
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;
                case 5:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeletePropById",
                        PropHash: this.deletePropIndexById
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;
                case 6:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "HealVehicle",
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;  
                default:
                    break;
            }
        },
        changeGameWeather(index) {
            this.showWeatherDropdown = false;
            this.weather = this.weatherList[index];
        },
        sendWeatherToBackend() {          
            if (this.weather) {
                axios.post('https://eclipse/ChangeGameWeather', {
                    Weather: this.weather.value,
                }).then(() => { return; }).catch(function () { console.log() });
            }
            
        },
        syncServerTime() {
            axios.post('https://eclipse/SyncServerTime', {
                    Weather: this.weather.value,
                }).then(() => { return; }).catch(function () { console.log() });
        },
        changeServerTime() {
            this.timeHours = parseInt(this.timeHours);
            this.timeMinutes = parseInt(this.timeMinutes);
            var testHours = Number.isInteger(this.timeHours);
            var testMinutes = Number.isInteger(this.timeMinutes)

            if (!testHours || !testMinutes) {
                this.timeHours = "";
                this.timeMinutes = "";
                return
            }
            if (this.timeHours > 24 || this.timeHours < 0) {
                return
            }
            if (this.timeMinutes > 60 || this.timeMinutes < 0) {
                return
            }
            
            axios.post('https://eclipse/ChangeGameTime', {
                Hours: this.timeHours,
                Minutes: this.timeMinutes
            }).then(() => { return; }).catch(function () { console.log() });
        },
        isAN(value) {
            return (value instanceof Number||typeof value === 'number') && !isNaN(value);
        },
        openModalWindowA() {
            this.showGiveItemWindowA = true;
        },
        openModalWindowE() {
            this.showGiveItemWindowE = true;
        },
        giveItemToPlayer() {
            var data = {
                ItemId: this.giveItemModel.Name.trim().toLowerCase(),
                Count: this.giveItemModel.Count,
            }
            axios.post('https://eclipse/AmdCommand', {
                index: "addItem",
                UserId: this.activePlayer.Id,
                Item: data
            }).then(() => { return; }).catch(function () { console.log() });
            this.giveItemModel.Name = "";
            this.giveItemModel.Count = 0;
        },
        giveItemToAdmin() {
            var data = {
                ItemId: this.giveItemModel.Name.trim().toLowerCase(),
                Count: this.giveItemModel.Count,
            }
            axios.post('https://eclipse/AmdCommand', {
                index: "addItem",
                UserId: this.playerData.id,
                Item: data
            }).then(() => { return; }).catch(function () { console.log() });
            this.giveItemModel.Name = "";
            this.giveItemModel.Count = 0;
        },
        giveHashToPlayer() {
            if (this.giveHash) {
                axios.post('https://eclipse/AmdCommand', {
                    index: "addHash",
                    UserId: this.activePlayer.Id,
                    Hash: this.giveHash
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        kickPlayerFromServer() {
            axios.post('https://eclipse/AmdCommand', {
                index: "kickPlayer",
                UserId: this.activePlayer.Id,
                Reason: this.kickReason
            }).then(() => { return; }).catch(function () { console.log() });
            this.kickReason = "";
            this.showGiveItemWindowС = false;
            
        },
        banPlayerOnServer() {
            axios.post('https://eclipse/AmdCommand', {
                index: "ban",
                UserId: this.activePlayer.Id,
                Reason: this.banReason
            }).then(() => { return; }).catch(function () { console.log() });
            this.banReason = "";
            this.banTime = 0;
            this.showGiveItemWindowD = false;
            
        },
        editPlayerInfo() {
            this.showEditPlayerInfoWindow = true;
            this.editPlayerInfoModel = JSON.parse(JSON.stringify(this.activePlayer))
        },
        editPlayerAuto(index) {
            this.showEditPlayerAutoWindow = true;
            this.activeAutoIndex = index;
