            this.activePlayer.IdCard.Lastname = this.editPlayerInfoModel.IdCard.Lastname;
            this.activePlayer.IdCard.Adress = this.editPlayerInfoModel.IdCard.Adress;
            this.activePlayer.IdCard.About = this.editPlayerInfoModel.IdCard.About;
