        getStatus(index){
            if (!index) {
                index = 0;
            }
            return this.statusList[index];
        },
        showFullAdminCall(index) {
            this.changePage(51);
            this.activeAdminCall = this.adminCalls[index];
            if (!this.activeAdminCall.OnScene) {
                this.activeAdminCall.OnScene = [];
            }
        },
        changeStatus(index) {
            this.activeAdminCall.Status = index;
            this.showAdminDropdown = false;
        },
        adminCallCommand(command) {
            switch (command) {
                case 0:
                    var myId = this.playerData.id;
                    var index = this.activeAdminCall.OnScene.findIndex(item => item == this.playerData.id);
                    if (index == -1) {
                        this.activeAdminCall.OnScene.push(myId);
                    }
                    break;
                case 1:
                    var index2 = this.activeAdminCall.OnScene.findIndex(item => item == this.playerData.id);
                    this.activeAdminCall.OnScene.splice(index2, 1);
                    break;
                case 2:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "TpToPlayer",
                        UserId: this.activeAdminCall.Id
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;
                case 3:
                    console.log('Тп игрока к себе ' + this.activeAdminCall.Id)
                    break;
                default:
                    break;
            }
        },
        isAdminOnScene(){
            var adminId = this.playerData.id;
            var adminIndex = this.activeAdminCall.OnScene.findIndex(item => item == adminId);
            if (adminIndex == -1) {
                return false
            } else {
                return true
            }
        },
