<template>
    <div class="content_outer lawnmover">
        <div class="main_content">
            <h2 class="text-center">Профессия газонокосильщика</h2>
            <p class="justify" v-if="playerData.sex">Ты <b>мальчик на побегушках</b>, который не может найти себя в жизни? Тебя манит жизнь алчных толстосумов и ты всегда мечтал проводить время в элитном обществе?</p>
            <p class="justify" v-else>Ты <b>девочка на побегушках</b>, которая не может найти себя в жизни? Тебя манит жизнь алчных толстосумов и ты всегда мечтала проводить время в элитном обществе?</p>
            <p class="justify">Тогда ты - то что нам нужно! Не важно, что у тебя нет жирного счета в <b>швейцарском банке</b>. Будучи газонокосильщиком, ты всегда будешь находиться в самом высшем обществе Лос-Сантоса, по крайней мере стричь их траву. Если повезет, то и их деньги.</p>
            <p class="justify">Утренняя роса, ливневый дождь, пыльная буря, запах свежего напалма: ничто не остановит тебя и твоего четырехколесного друга. Каждый сорняк будет идентефицирован и <b>уничтожен</b>. И никаких пестицидов!</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()">Стать газонокосильщиком</button>
                 <button v-else class="btn btn-primary" @click="stopJob()">Закончить рабочий день</button>
                <button class="btn btn-secondary" @click="exit()">Выход</button>
            </div>
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobLawnmover',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/LawnmowerStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/LawnmowerStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>