<template>
    <div class="page page_1 app_business">
        <div class="content">

            <transition name="transition_02">
                <div class="page_2" v-if="page == 0">
                    <div class="header">
                        <div class="d-flex align-items-center">
                            <div class="app_image">
                                <div class="circle"></div>
                            </div>
                            <div class="headings">
                                <h2>Управление бизнесом</h2>
                                <h3>Преврати свой бизнес в мечту...</h3>
                            </div>
                        </div>
                        <button class="btn btn-business" @click="exitApp()">Выход</button>
                        
                    </div>
                    <h4>Выберите бизнес для управления:</h4>
                    <div class="business_list scrolled">
                        <div class="business_item bus_style" v-for="(item, index) in businessList" :key="'business'+ index" @click="openBusiness(index)">
                            <h3>{{item.Name}}</h3>
                            <span class="address">{{item.Adress}}</span>
                            <div class="text-right">
                                <span v-if="item.Missions.length || item.Missions.length > 0"><b>Доступно заданий:</b> {{item.Missions.length}}</span>
                                <span v-else><b>Нет доступных заданий</b></span>
                            </div>
                        </div>
                    </div>

                </div>
            </transition>

            <transition name="transition_02">
                <div class="page_1 scrolled" v-if="page == 2">
                    <div class="d-flex justify-content-between align-items-center">
                        <div class="headings">
                            <h2>{{activeBusiness.Name}}</h2>
                            <h3 class="mt-2">{{activeBusiness.Adress}}</h3>
                        </div>
                        <button class="btn btn-business" @click="changePage(0)">Назад</button>
                    </div>
                    <div class="des_block">
                        <div class="d-flex">
                            <div class="m_block">
                                <span class="heading">Заработано</span>
                                <span class="">$ <span class="money">{{activeBusiness.Bank}}</span></span>
                            </div>
                            <div class="m_block">
                                <span class="heading">Потрачено</span>
                                <span class="">$ <span class="money2">{{activeBusiness.Spent}}</span></span>
                            </div>
                            <div class="m_block" v-if="activeBusiness.Employees">
                                <span class="heading">Сотрудники</span>
                                <span class=""><span class="money"><b-icon icon="person-fill" class="m_icon"></b-icon> {{activeBusiness.Employees.length}}</span></span>
                            </div>
                        </div>
                        <div class="d-flex align-items-center">
                            <div v-if="activeBusiness.BusinessType!=11" class="btn btn-outline-business" :class="{active: subpage == 1}" @click="changeSubpage(1)">Задания</div>
                            <div v-if="activeBusiness.BusinessType!=11" class="btn btn-outline-business" :class="{active: subpage == 4}" @click="changeSubpage(4)">Заказы</div>
                            <div v-if="activeBusiness.BusinessType==11" class="btn btn-outline-business" :class="{active: subpage == 5}" @click="changeSubpage(5)">Комнаты</div>
                            <div class="btn btn-outline-business" :class="{active: subpage == 2}" @click="changeSubpage(2)">Персонал</div>
                            <div class="btn btn-outline-business" :class="{active: subpage == 3}" @click="changeSubpage(3)">Экономика</div>
                        </div>
                    </div>
                    <transition name="list" v-if="subpage == 1">
                        <div class="business_block">
                            <div class="left_block">
                                <div class="title">
                                    <h4>Доступные задания</h4>
                                    <hr class="hr">
                                </div>
                                <div class="task_list">
                                    <div class="task_element bus_style" v-for="(item, index) in activeBusiness.Missions" :key="'mis_' + index + item.id" @click="openTask(index)" :class="{active: item.Id == activeTask.Id}">
                                        <h3>#{{item.Id}} {{item.Name}}</h3>
                                        
                                        <div class="d-flex justify-content-between">
                                            <span class="type">Тип задания: {{item.Translate}}</span>
                                            <div>Доступно до: <span>{{getTime(item.Exptime)}}</span></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="right_block">
                                <div class="task_active" v-if="activeTask.Id">
                                    <h3>#{{activeTask.Id}} {{activeTask.Name}}</h3>
                                    <div v-if="activeTask.PerformingId != 0" class="owner">Выполняется игроком ID {{activeTask.PerformingId}}</div>
                                    <div>Тип задания: {{activeTask.Translate}}</div>
                                    <div>Описание: {{activeTask.Description}}</div>
                                    <div>Доступно до: {{activeTask.Exptime}}</div>
                                    <div>Длина маршрута: {{activeTask.Route}} км.</div>
                                    <div class="controls">
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="takeTaskByPlayer()">Взять задание</button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == playerData.id" @click="cancelTaskByPlayer()">Отменить задание</button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="offerTaskToNearestPlayer()">Предложить игроку рядом</button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="showModalC()">Предложить игроку по id</button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId !=0 && activeTask.PerformingId != playerData.id"  @click="cancelTaskToPlayer()">Отменить задание игроку id {{activeTask.PerformingId}}</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </transition>
                    <transition name="list" v-if="subpage == 2">
                        <div class="business_block">
                            <div class="left_block">
                                <div class="title">
                                    <h4>Управление персоналом</h4>
                                    <hr class="hr">
                                </div>
                                <div class="employees_list">
                                    <div class="employee_item bus_style" v-for="(item, index) in activeBusiness.Employees" :key="'employee_' + index" @click="chooseEmployee(index)" :class="{active: activeEmployee.UserId == item.UserId }">
                                        <div class="index">
                                            <b>{{index}}</b>
                                        </div>
                                        <div class="data">
                                            {{item.Name}} {{item.Phone}} 
                                        </div>
                                        <div class="level">
                                            {{item.Level}}
                                        </div>
                                    </div>
                                    <div class="employee_item bus_style2" @click="addNewEmployee()">Добавить нового сотрудника</div>
                                </div>
                            </div>
                            <div class="right_block">
                                <div class="task_active" v-if="activeEmployee.UserId">
                                    <h3>#{{activeEmployee.UserId}} {{activeEmployee.Name}}</h3>
                                    <div>Телефон: {{activeEmployee.Phone}}</div>
                                    <div>Уровень: {{activeEmployee.Level}}</div>
                                    <div>Зарплата: {{activeEmployee.Salary}}$</div>
                                    <div class="controls">
                                        <button class="btn btn-business" @click="increasePlayerLevel()">Повысить сотрудника</button>
                                        <button class="btn btn-business" @click="decreasePlayerLevel()">Понизить сотрудника</button>
                                        <button class="btn btn-business" @click="changeSalary()">Изменить зарплату</button>
                                        <button class="btn btn-business" @click="fireEmployee()">Уволить сотрудника</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </transition>
                    <transition name="list" v-if="subpage == 3">
                        <div class="business_block">
                            <div class="left_block">
                                <div class="title">
                                    <h4>Управление экономикой</h4>
                                    <hr class="hr">
                                </div>
                                <button class="btn btn-business btn-sm btn-block" :class="{active: statpage == 1}" @click="changeStatPage(1)">Статистика</button>
                                <button class="btn btn-business btn-sm btn-block" :class="{active: statpage == 2}" @click="changeStatPage(2)">Зарплата</button>
                                <button class="btn btn-business btn-sm btn-block" :class="{active: statpage == 3}" @click="changeStatPage(3)">Цены</button>
                            </div>
                            <div class="right_block">
                                <div class="task_active" v-if="statpage==1">
                                    <h3>Статистика</h3>
                                    <div>Бюджет: ${{activeBusiness.Bank}}</div>
                                    <div>Заработано: ${{activeBusiness.Earned}}</div>
                                    <div v-if="activeBusiness.BusinessType!=11">Потрачено: ${{activeBusiness.Spent}}</div>
                                    <div v-if="activeBusiness.BusinessType!=11">Заданий выполнено: {{activeBusiness.Taskcompleted}}</div>
                                    <div>Сотрудников: <span v-if="activeBusiness.Employees">{{activeBusiness.Employees.length}}</span><span v-else>0</span></div>
                                </div>
                                <div class="task_active" v-if="statpage==2">
                                    <h3>Зарплата</h3>
                                    <div class="empl_list mb-2">
                                        <div class="empl_item mb-2 d-flex">
                                            <span class="text-center" style="width: 70%"><b>Имя</b></span>
                                            <span class="text-center" style="width: 30%"><b>Зарплата</b></span>
                                        </div>
                                        <div class="empl_item mb-1 d-flex align-items-center justify-content-between" v-for="(item, index) in activeBusiness.Employees" :key="'emp_salary_'+index">
                                            <h6 style="width: 70%" class="m-0">{{item.Name}}</h6>
                                            <input style="width: 30%" class="form-control" v-model="item.Salary">
                                        </div>
                                    </div>
                                    <button class="btn btn-business btn-sm btn-block" @click="changeEmployersSalary()">Сохранить изменения</button>
                                </div>
                                <div class="task_active" v-if="statpage==3">
                                    <h3>Цены</h3>
                                    

