                                            <div class="price_item mb-2 d-flex">
                                                <span class="text-center" style="width: 50%"><b>Название</b></span>
                                                <span class="text-center" style="width: 20%" v-if="activeBusiness.BusinessType!=11"><b>Кол-во</b></span>
                                                <span class="text-center" style="width: 30%"><b>Цена</b></span>
                                            </div>
