                                                <h6 style="width: 20%" class="m-0 text-center" v-if="activeBusiness.BusinessType!=11">{{elem.Count}}</h6>
                                                <input style="width: 30%" class="form-control" v-model="elem.Price">
                                            </div>
                                        </div>
                                    </div>

                                    
                                    
