                                        <div class="col-7">
                                            <h5 class="heading">{{item.Heading}}</h5>
                                            <h4 class="price">{{item.Price}}$/сутки</h4>
                                        </div>
                                        <div class="col-5">
                                            <button class="btn btn-success btn-block btn-sm" v-if="item.IsFree" @click="changeRoomStatis(item.ItemId)">Свободно</button>
                                            <button class="btn btn-danger btn-block btn-sm" v-if="!item.IsFree" @click="changeRoomStatis(item.ItemId)">Занято</button>
                                            <button class="btn btn-business btn-block btn-sm" @click="giveKeyToPlayer(item.ItemId)">Выдать ключ</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                                
                        </div>
                    </transition>

                </div>
            </transition>
        </div>

        <transition name="list">
            <div class="business-modal" v-if="showModalWindowA">
