                    "Type": "Transporter",
                    "Translate": "Перевозчик",
                    "Description": "Ваша задача доставить небольшой груз из магазина в указанную точку. Чем быстрее будет выполнена задача, тем больше награду вы получите.",

                }
            ],

            showModalWindowA: false,
            showModalWindowB: false,
            showModalWindowC: false,
            showModalWindowD: false,

            newEmployeeSalary: 0,
            idShareTask: "",

            statpage: 0,
            idInviteEmployee: "",

            orderpage: 0,
            newOrder: {
                Name: "",
                Description: "",
            }
        }
    },
    methods: {
        changePage(index) {
            if (index != this.page) {
                this.page = index;
                this.closeAllModals();
                this.subpage=0;
            }
        },
        openBusiness(index) {
            this.activeBusiness = this.businessList[index];
            this.activeTask = {};
            this.changePage(2);
        },
        closeAllModals() {
            this.showModalWindowA = false;
            this.showModalWindowB = false;
            this.showModalWindowC = false;
            this.showModalWindowD = false;
        },
        exitApp() {
            this.closeAllModals();
            this.$parent.closeAllApps();
        },
        openTask(index) {
            this.activeTask = this.activeBusiness.Missions[index];
        },
        findTranslate(type) {
            var elem = this.taskTypes.find(item => item.Type == type)
            if (elem) {
                return elem.Translate
            } else {
                return "Не найдено"
            }
            
        },
        findDescription(type) {
            var elem = this.taskTypes.find(item => item.Type == type)
            if (elem) {
                return elem.Description
            } else {
                return "Не найдено"
            }
        },
        showModalC() {
            this.showModalWindowC = true;
            this.idShareTask = "";
        },
        hideModalC() {
            this.showModalWindowC = false;
        },
        changeSubpage(index) {
            if (index != this.subpage) {
                this.subpage = -1;
                setTimeout(() => {
                    this.subpage = index;
                    this.activeTask = {};
                    this.activeEmployee = {};
                    this.statpage = 0;
                }, 500);
            }
        },
        chooseEmployee(index) {
            this.activeEmployee = this.activeBusiness.Employees[index];
        },
        increasePlayerLevel() {
            this.activeEmployee.Level +=1;
            axios.post('https://eclipse/increasePlayerLevel', {
                    Level: this.activeEmployee.Level,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        decreasePlayerLevel() {
            this.activeEmployee.Level -=1;
            if (this.activeEmployee.Level < 0) {
                this.activeEmployee.Level = 0;
            }
            axios.post('https://eclipse/decreasePlayerLevel', {
                    Level: this.activeEmployee.Level,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        fireEmployee() {
            this.showModalWindowB = true;
        },
        cancelFireEmployee() {
            this.showModalWindowB = false;
        },
        changeSalary() {
            this.showModalWindowA = true;
            this.newEmployeeSalary = JSON.parse(JSON.stringify(this.activeEmployee.Salary));
            axios.post('https://eclipse/changeBusinessSalary', {
                    Salary: this.activeEmployee.Salary,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        saveNewSalary() {
            this.showModalWindowA = false;
            this.activeEmployee.Salary = JSON.parse(JSON.stringify(this.newEmployeeSalary));
            axios.post('https://eclipse/changeBusinessSalary', {
                    Salary: this.activeEmployee.Salary,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        cancelNewSalary() {
            this.showModalWindowA = false;
        },
        changeStatPage(index) {
            if (index != this.statpage) {
                this.statpage = index;
            }
        },
        addNewEmployee() {
            this.showModalWindowD = true;
            this.idInviteEmployee = "";
        },
        hideModalD() {
            this.showModalWindowD = false;
        },
        takeTaskByPlayer() {
            this.activeTask.PerformingId = this.$parent.$parent.playerData.id;
            axios.post('https://eclipse/takeTaskByPlayer', {
                    Task: this.activeTask.Id,      
            }).then(() => { return; }).catch(function () { console.log() });
            // Игрок берет задание
        },
        cancelTaskByPlayer() {
            axios.post('https://eclipse/cancelTaskByPlayer', {
                    Task: this.activeTask.Id,
                    
            }).then(() => { return; }).catch(function () { console.log() });
            // Игрок отменяет задание
             this.activeTask.PerformingId = 0;
        },
        offerTaskToNearestPlayer() {
            // Предлагаем задание близжайшему игроку
            axios.post('https://eclipse/offerTaskToNearestPlayer', {
                    Task: this.activeTask.Id,
                    MissionName: this.activeTask.Name
            }).then(() => { return; }).catch(function () { console.log() });
        },
        offerTaskToPlayerById() {
            this.showModalWindowC = false;
            axios.post('https://eclipse/offerTaskToPlayerById', {
                    Task: this.activeTask.Id,
                    Id: this.idShareTask,
                    MissionName: this.activeTask.Name
            }).then(() => { return; }).catch(function () { console.log() });
            // Предлагаем задание игроку по id this.idShareTask
        },
        cancelTaskToPlayer() {
            // Отменяем задание (удаляем ownerId)
            axios.post('https://eclipse/cancelTaskPlayer', {
                    Task: this.activeTask.Id,
                    
            }).then(() => { return; }).catch(function () { console.log() });
            this.activeTask.PerformingId = 0;
        },
        fireEmployeeNow() {
            // Увольняем игрока this.activeEmployee
            this.showModalWindowB = false;
             axios.post('https://eclipse/fireEmployee', {
                     Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        inviteNewEmployee() {
            // Приглашаем игрока this.idInviteEmployee
            axios.post('https://eclipse/inviteNewEmployee', {
                     Id: this.idInviteEmployee,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            this.showModalWindowD = false;
        },
        changeEmployersSalary() {
            // Меняем зарплаты игроков
            axios.post('https://eclipse/changeBusinessSalarys', {
                    Employees: this.activeBusiness.Employees,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changePrices() {
            axios.post('https://eclipse/changeBusinessPrices', {
                    ProductsData: this.activeBusiness.ProductsData,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            // Меняем цены на продукты
        },
        getTime(timer) {
            var today = new Date(timer);
            var time = ("0" + today.getHours()).slice(-2);
            var minutes = ("0" + today.getMinutes()).slice(-2);
            var year = today.getFullYear();
            var month = ("0" + (today.getMonth() + 1)).slice(-2);
            var day = ("0" + today.getDate()).slice(-2);
            var date =
                time + ":" + minutes + " " + day + "." + month + "." + year;
            return date;
        },
        changeOrderPage(index) {
            if (index != this.orderpage) {
                this.orderpage = index;
            }
        },
        registerNewOrder() {
            axios.post('https://eclipse/registerNewOrder', {
                    Name: this.newOrder.Name,
                    Description: this.newOrder.Description,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            this.newOrder = {
                Name: "",
                Description: "",
            }
            this.orderpage = 0;
        },
        changeRoomStatis(ItemId) {
            var item = this.activeBusiness.ProductsData.find(item => item.ItemId == ItemId)
            item.IsFree = !item.IsFree;

        },
        giveKeyToPlayer(ItemId) {
            ItemId // Id комнаты
            this.activeBusiness.Id // Id бизнеса
        }

    },

    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadBusinessList") {
                this.businessList = JSON.parse(event.data.businessList);
                this.businessList.forEach((element) => (element.ProductsData = JSON.parse(element.ProductsData)));
            }
            else if(event.data.type == "LoadBusinessMissions") {
                this.businessList.forEach((element) => {
                    if(element.Id == event.data.businessId) {
                        element.Missions = JSON.parse(event.data.mission);
                    }
                });
            }
        });
    }
}
</script>