<template>
    <transition name="transition_02">
        <section class="fullpage tattooshop"
            @mousedown="toggleCameraClick(true)"
            @mouseup="toggleCameraClick(false)"
            @mousemove="toggleCameraMove"
            @mousewheel="wheelMove">
            <div class="fullpage">
                <div class="sectionA" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 1">
                            <h5>Голова</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_HEAD.length
                                    v-if = renderList.ZONE_HEAD
                                    text = "ZONE_HEAD"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 2">
                            <h5>Грудь</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_TORSO.length
                                    v-if = renderList.ZONE_TORSO
                                    text = "ZONE_TORSO"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 3">
                            <h5>Левая рука</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_LEFT_ARM.length
                                    v-if = renderList.ZONE_LEFT_ARM
                                    text = "ZONE_LEFT_ARM"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 4">
                            <h5>Правая рука</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_RIGHT_ARM.length
                                    v-if = renderList.ZONE_RIGHT_ARM
                                    text = "ZONE_RIGHT_ARM"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 5">
                            <h5>Левая нога</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_LEFT_LEG.length
                                    v-if = renderList.ZONE_LEFT_LEG
                                    text = "ZONE_LEFT_LEG"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>

                    <transition name="slide-to-left">
                        <div class="editor_inner" v-if="activePage == 6">
                            <h5>Правая нога</h5>
                             <div class="form-group">
                                <h6>Тип</h6>
                                <CarouselStandart
                                    :list = renderList.ZONE_RIGHT_LEG.length
                                    v-if = renderList.ZONE_RIGHT_LEG
                                    text = "ZONE_RIGHT_LEG"
                                    selected = 1
                                    @showTattoo="showTattoo"
                                    method = 'showTattoo'
                                />
                            </div>
                        </div>
                    </transition>
                </div>

                <div class="sectionB d-flex align-items-top justify-content-end" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                    <transition name="slide-to-right">
                        <div class="editor_inner" v-if="activeTattoo">
                            <div class="form-group">
                                <h5 class="text-right">Цена</h5>
                                <div class="d-flex justify-content-between align-items-end">
                                    <span>{{activeTattoo.LocalizedName}}</span>
                                    <span class="price">${{activeTattoo.Price}}</span>
                                </div>
                            </div>
                            <div class="d-flex justify-content-end">
                                <button class="btn btn-buy" @click="buyTattoo()">Купить</button>
                            </div>
                        </div>
                    </transition>


                </div>
            </div>


            <div class="top_menu d-flex justify-content-center align-items-center" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                <ul class="nav nav-tabs d-flex align-items-center" id="main_nav">
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 1}" @click="chooseMenu(1)">Голова</a></li>
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 2}" @click="chooseMenu(2)">Грудь</a></li>
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 3}" @click="chooseMenu(3)">Левая рука</a></li>
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 4}" @click="chooseMenu(4)">Правая рука</a></li>
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 5}" @click="chooseMenu(5)">Левая нога</a></li>
                    <li class="page-item"><a class="page-link" :class="{active: activePage == 6}" @click="chooseMenu(6)">Правая нога</a></li>
                    <li class="page-item"><a class="page-link delete-tattoo" @click="showPopup()">Удалить</a></li>
                </ul>
            </div>

            <div class="bottom_menu_left" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
               <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 0}" @click="changePlayerAnimation(0)">Стандарт</button>
               <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 1}" @click="changePlayerAnimation(1)">Руки</button>
               <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 2}" @click="changePlayerAnimation(2)">Поза</button>
            </div>

            <transition name="transition_03">
                <div class="popup_window" v-if="showPopupWindow" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
                    <h5>Вы уверены что хотите очистить все татуировки?</h5>
                    <div class="d-flex justify-content-between">
                        <button class="btn btn-delete" @click="DeleteTattoo()">Удалить</button>
                        <button class="btn btn-buy" @click="closeModal()">Отмена</button>
                    </div>
                </div>
            </transition>

        </section>
    </transition>

</template>

<script>
import mpairraces_overlays from '../../json/tattooshop/mpairraces_overlays.json';
import mpbeach_overlays from '../../json/tattooshop/mpbeach_overlays.json';
import mpbusiness_overlays from '../../json/tattooshop/mpbusiness_overlays.json';
import mpbiker_overlays from '../../json/tattooshop/mpbiker_overlays.json';
import mpchristmas2_overlays from '../../json/tattooshop/mpchristmas2_overlays.json';
import mpchristmas2017_overlays from '../../json/tattooshop/mpchristmas2017_overlays.json';
import mpchristmas2018_overlays from '../../json/tattooshop/mpchristmas2018_overlays.json';
import mpgunrunning_overlays from '../../json/tattooshop/mpgunrunning_overlays.json';
import mpheist3_overlays from '../../json/tattooshop/mpheist3_overlays.json';
import mpheist4_overlays from '../../json/tattooshop/mpheist4_overlays.json';
import mphipster_overlays from '../../json/tattooshop/mphipster_overlays.json';
import mpimportexport_overlays from '../../json/tattooshop/mpimportexport_overlays.json';
import mplowrider_overlays from '../../json/tattooshop/mplowrider_overlays.json';
import mplowrider2_overlays from '../../json/tattooshop/mplowrider2_overlays.json';
import mpluxe_overlays from '../../json/tattooshop/mpluxe_overlays.json';
import mpluxe2_overlays from '../../json/tattooshop/mpluxe2_overlays.json';
import mpsecurity_overlays from '../../json/tattooshop/mpsecurity_overlays.json';
import mpsmuggler_overlays from '../../json/tattooshop/mpsmuggler_overlays.json';
import multiplayer_overlays from '../../json/tattooshop/multiplayer_overlays.json';
import mpstunt_overlays from '../../json/tattooshop/mpstunt_overlays.json';
import mpvinewood_overlays from '../../json/tattooshop/mpvinewood_overlays.json';

import CarouselStandart from '../small/CarouselTattoo.vue'
import axios from 'axios';
export default {
    name: 'tattooShop',
    components: {
        CarouselStandart,
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            collection_01: mpairraces_overlays,
            collection_02: mpbeach_overlays,
            collection_03: mpbiker_overlays,
            collection_04: mpbusiness_overlays,
            collection_05: mpchristmas2_overlays,
            collection_06: mpchristmas2017_overlays,
            collection_07: mpchristmas2018_overlays,
            collection_08: mpgunrunning_overlays,
            collection_09: mpheist3_overlays,
            collection_10: mpheist4_overlays,
            collection_11: mphipster_overlays,
            collection_12: mpimportexport_overlays,
            collection_13: mplowrider_overlays,
            collection_14: mplowrider2_overlays,
            collection_15: mpluxe_overlays,
            collection_16: mpluxe2_overlays,
            collection_17: mpsecurity_overlays,
            collection_18: mpsmuggler_overlays,
            collection_19: mpstunt_overlays,
            collection_20: mpvinewood_overlays,
            collection_21: multiplayer_overlays,
            tattoo_list: [
                {
                    Collection: "mpairraces_overlays",
                    List: this.collection_01
                },{
                    Collection: "mpbeach_overlays",
                    List: this.collection_02
                },{
                    Collection: "mpbiker_overlays",
                    List: this.collection_03
                },{
                    Collection: "mpbusiness_overlays",
                    List: this.collection_04
                },{
                    Collection: "mpchristmas2017_overlays",
                    List: this.collection_05
                },{
                    Collection: "mpchristmas2018_overlays",
                    List: this.collection_06
                },{
                    Collection: "mpchristmas2_overlays",
                    List: this.collection_07
                },{
                    Collection: "mpgunrunning_overlays",
                    List: this.collection_08
                },{
                    Collection: "mpheist3_overlays",
                    List: this.collection_09
                },{
                    Collection: "mpheist4_overlays",
                    List: this.collection_10
                },{
                    Collection: "mphipster_overlays",
                    List: this.collection_11
                },{
                    Collection: "mpimportexport_overlays",
                    List: this.collection_12
                },{
                    Collection: "mplowrider2_overlays",
                    List: this.collection_13
                },{
                    Collection: "mplowrider_overlays",
                    List: this.collection_14
                },{
                    Collection: "mpluxe2_overlays",
                    List: this.collection_15
                },{
                    Collection: "mpluxe_overlays",
                    List: this.collection_16
                },{
                    Collection: "mpsecurity_overlays",
                    List: this.collection_17
                },{
                    Collection: "mpsmuggler_overlays",
                    List: this.collection_18
                },{
                    Collection: "mpstunt_overlays",
                    List: this.collection_19
                },{
                    Collection: "mpvinewood_overlays",
                    List: this.collection_20
                },{
                    Collection: "multiplayer_overlays",
                    List: this.collection_21
                }
            ],
            renderList: [],
            zones: [
                "ZONE_HEAD",
                "ZONE_TORSO",
                "ZONE_LEFT_ARM",
                "ZONE_RIGHT_ARM",
                "ZONE_LEFT_LEG",
                "ZONE_RIGHT_LEG",
            ],

            activePage: 0,
            subpage: 0,
            tattootypes: [],

            activeTattoo: undefined,
            showPopupWindow: false,

            mouseState: false,
            blockScroll: false,
            isControlPressed: false,
            activePlayerAnim: 0,
        }
    },
    methods: {
        chooseMenu(numb) {
            if ((this.activePage != numb)) {
                this.closeModal();
                this.activePage = 0;
                this.activeTattoo = undefined;
                setTimeout(() => {
                    this.activePage = numb;
                }, 500);
                if(numb == 5 || numb == 6) {
                    axios.post('https://eclipse/chooseCamera', {
                        CameraType: "leg",
                    });
                }
                else {
                    axios.post('https://eclipse/chooseCamera', {
                        CameraType: "body",
                    });
                }
            }
        },
        prepareTattoos() {
            if (this.tattootypes.length) {
                this.tattootypes.forEach(element => {
                    var tattooCollection = this.tattoo_list.find(item => item.Collection == element);
                    tattooCollection.List.forEach(elem => {
                        elem.Collection = tattooCollection.Collection;
                        this.renderList.push(elem);
                    });
                });
            } else {
                this.tattoo_list.forEach(list => {
                    list.List.forEach(elem=> {
                        elem.Collection = list.Collection
                        this.renderList.push(elem);
                    })
                })
            }
            this.renderList = this.groupBy(this.renderList, "Zone");
        },
        groupBy(array, key) {
            return array.reduce(function(rv, x) {
                (rv[x[key]] = rv[x[key]] || []).push(x);
                return rv;
            }, {});
        },
        showTattoo(numb, text) {
            console.log(numb, text);
            if (numb == 0) {
                this.activeTattoo = undefined;
                return
            }
            switch (text) {
                case "ZONE_TORSO":
                    this.activeTattoo = this.renderList.ZONE_TORSO[numb - 1]
                    break;
                case "ZONE_HEAD":
                    this.activeTattoo = this.renderList.ZONE_HEAD[numb - 1]
                    break;
                case "ZONE_LEFT_ARM":
                    this.activeTattoo = this.renderList.ZONE_LEFT_ARM[numb - 1]
                    break;
                case "ZONE_RIGHT_ARM":
                    this.activeTattoo = this.renderList.ZONE_RIGHT_ARM[numb - 1]
                    break;
                case "ZONE_LEFT_LEG":
                    this.activeTattoo = this.renderList.ZONE_LEFT_LEG[numb - 1]
                    break;
                case "ZONE_RIGHT_LEG":
                    this.activeTattoo = this.renderList.ZONE_RIGHT_LEG[numb - 1]
                    break;
                default:
                    console.log('error! not found');
                    break;
            }
            this.showTattooOnPlayer()
        },
        showTattooOnPlayer() {
            axios.post('https://eclipse/showTattooOnPlayer', {
                Collection: this.activeTattoo.Collection,
                HashNameMale: this.activeTattoo.HashNameMale,
                HashNameFemale: this.activeTattoo.HashNameFemale,
                Price: this.activeTattoo.Price
            });
        },
        buyTattoo() {
            axios.post('https://eclipse/buyTattoo', {
                Collection: this.activeTattoo.Collection,
                HashNameMale: this.activeTattoo.HashNameMale,
                HashNameFemale: this.activeTattoo.HashNameFemale,
                Price: this.activeTattoo.Price
            });
        },
        showPopup() {
            this.showPopupWindow = true;
        },
        closeModal() {
            this.showPopupWindow = false;
        },
        DeleteTattoo() {
            this.closeModal();
             axios.post('https://eclipse/deleteTattoo', {
            });
        },
        doCommand(e) {
            if (e.keyCode == 27) {
                this.showPage = ""
                axios.post('https://eclipse/CloseTattooShop', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        toggleCameraClick(state) {
            if(state == true) {
            
                this.mouseState = true;
                this.mouseX = 0;
                this.mouseY = 0;
            }
            else if(state == false) {
                this.mouseState = false;
            }
        },
        scrollBlock(state) {
            if(state == true) {
                this.blockScroll = true;

            }
            else if(state == false) {
                this.blockScroll = false;
                this.mouseState = false;
            }
        },
        wheelMove($event) {
            if(!this.blockScroll) {
                axios.post('https://eclipse/Mouse', {
                    DeltaY: $event.deltaY,
                    CtrlPressed: this.isControlPressed
                }).then(() => { return; }).catch(function (error) { console.log(error) });
                
            }
        },
        pressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = true;
            }
        },
        unpressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = false;
            }
        },
        toggleCameraMove($event) {
            if(this.mouseState == true && !this.blockScroll) {
                if(this.mouseX == 0 && this.mouseY == 0) {
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY;
                    return;
                }
                if($event.clientX || $event.clientY) {
                    var xPosition = $event.clientX - this.mouseX;
                    var yPositiuon = $event.clientY - this.mouseY;
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY; 
                    axios.post('https://eclipse/X', {
                        Xposition: xPosition,
                        Yposition: yPositiuon
                    }).then(() => { return; }).catch(function (error) { console.log(error) });
                }
            }
        },
        changePlayerAnimation(numb) {
            this.activePlayerAnim = numb;
            axios.post(`https://eclipse/changePlayerAnimation`, {
                Anim: this.activePlayerAnim,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
        }
    },
    mounted() {
        this.tattoo_list = [
                {
                    Collection: "mpairraces_overlays",
                    List: this.collection_01
                },{
                    Collection: "mpbeach_overlays",
                    List: this.collection_02
                },{
                    Collection: "mpbiker_overlays",
                    List: this.collection_03
                },{
                    Collection: "mpbusiness_overlays",
                    List: this.collection_04
                },{
                    Collection: "mpchristmas2017_overlays",
                    List: this.collection_05
                },{
                    Collection: "mpchristmas2018_overlays",
                    List: this.collection_06
                },{
                    Collection: "mpchristmas2_overlays",
                    List: this.collection_07
                },{
                    Collection: "mpgunrunning_overlays",
                    List: this.collection_08
                },{
                    Collection: "mpheist3_overlays",
                    List: this.collection_09
                },{
                    Collection: "mpheist4_overlays",
                    List: this.collection_10
                },{
                    Collection: "mphipster_overlays",
                    List: this.collection_11
                },{
                    Collection: "mpimportexport_overlays",
                    List: this.collection_12
                },{
                    Collection: "mplowrider2_overlays",
                    List: this.collection_13
                },{
                    Collection: "mplowrider_overlays",
                    List: this.collection_14
                },{
                    Collection: "mpluxe2_overlays",
                    List: this.collection_15
                },{
                    Collection: "mpluxe_overlays",
                    List: this.collection_16
                },{
                    Collection: "mpsecurity_overlays",
                    List: this.collection_17
                },{
                    Collection: "mpsmuggler_overlays",
                    List: this.collection_18
                },{
                    Collection: "mpstunt_overlays",
                    List: this.collection_19
                },{
                    Collection: "mpvinewood_overlays",
                    List: this.collection_20
                },{
                    Collection: "multiplayer_overlays",
                    List: this.collection_21
                }
                ]
        this.prepareTattoos();

    },
    created() {
        window.addEventListener('keydown', this.doCommand);
        window.addEventListener('keydown', this.pressCtrl);
        window.addEventListener('keyup', this.unpressCtrl);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
        window.removeEventListener('keydown', this.pressCtrl);
        window.removeEventListener('keyup', this.unpressCtrl);
    },
}
</script>
