<template>
    <section>
        <transition name="list">
            <div class="vehicle_gas" v-if="!showModal">
                <h3 class="text-center">Gas Station</h3>

                <div class="panel">
                    <div class="screen">
                        <h4 class="price">{{totalPrice}}</h4>
                        <h4 class="price_symv">$</h4>

                        <h4 class="type">{{getGasName(activeGas.Id)}}</h4>
                        <h4 class="type_symv">Type</h4>

                        <h4 class="size">{{activeSize}}</h4>
                        <h4 class="size_symv">L</h4>

                        <h4 class="cost">{{activeGas.Price}}</h4>
                        <h4 class="cost_symv">$/L</h4>
                    </div>
                </div>

                <div class="control_buttons">
                    <button class="btn btn-gas" @click="changeGasType('regulargas')">Regular</button>
                    <button class="btn btn-gas premium" @click="changeGasType('premiumgas')">Premium</button>
                    <button class="btn btn-gas plus" @click="changeGasType('diesel')">Diesel</button>
                </div>

                <div class="game_controls">
                    <div class="d-flex justify-content-between">
                        <h6>0L</h6><h6>{{tankMax - tankSize}}L</h6>
                    </div>
                    <div class="form-group mb-0">
                        <input class="form-control-range" value="0" min="0" :max="tankMax - tankSize" step="1" type="range" v-model="activeSize" @input="calculateTotal()">
                    </div>
                    <div class="d-flex justify-content-between mt-3">
                        <button class="btn btn-primary" @click="closePage()">Выход</button>
                        <button class="btn btn-secondary" @click="showModalWindow();">Заправить</button>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="list">
            <div class="vehicle_gas" v-if="showModal">
                <h3 class="text-center">Оплата топлива</h3>
                <div class="d-flex justify-content-between">
                    <h5>Итоговая сумма:</h5>
                    <h5>{{totalPrice}}$</h5>
                </div>
                    <button class="btn btn-primary btn-block" @click="hideModalWindow()">Назад</button>
                    <button class="btn btn-info btn-block" @click="buyByCash()">Оплатить наличкой</button>
                    <button class="btn btn-success btn-block" @click="buyByCard()">Оплатить картой</button>
                
            </div>
        </transition>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    name: 'VehicleFuel',
    props: {
        playerData: Object,
    },
    data() {
        return {
            gasPrice: undefined,
            activeGas: 0,
            activeSize: 0,
            tankSize: 15,
            tankMax: 50,
            totalPrice: 0,

            showModal: false,
        }
    },
    methods: {
        closePage() {
            this.$parent.showFuel = false;
            axios.post('https://eclipse/Tilda', {}).then(() => { return; }).catch(function () { console.log() });
        },
        changeGasType(numb) {
            this.gasPrice.forEach((element) => {
                if(element.Id == numb) {
                    this.activeGas = element;
                }
            });
            this.calculateTotal();
        },
        showModalWindow() {
            if (this.activeSize >= 1) {
                this.showModal = true;
            }
        },
        hideModalWindow() {
            this.showModal = false;
        },
        calculateTotal() {
            this.totalPrice = this.activeSize * this.activeGas.Price;
            if (!this.totalPrice) {
                this.totalPrice = 0;
            }
        },
        doCommand(e) {
            if (e.keyCode == 27 & this.$parent.showFuel == true) {
                this.closePage()
                axios.post('https://eclipse/Tilda', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        getGasName(id) {
            switch(id) {
                case "premiumgas":
                    return "Premium";
                case "regulargas":
                    return "Regular";
                case "diesel":
                    return "Diesel";
            }
        },
        buyByCash() {
            axios.post('https://eclipse/BuyGasCash', {
                Count: this.activeSize,
                GasType: this.activeGas.Id
            }).then(() => { return; }).catch(function () { console.log() });
            this.closePage();
        },
        buyByCard() {
            this.totalPrice
        },
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    },
    mounted() {
        window.addEventListener('message',(event) => {
            if (event.data.type == "LoadGasolineStationData") {
                this.gasPrice = event.data.gasData
                this.activeGas = this.gasPrice[0];
                this.tankMax = event.data.tankMax;
                this.tankSize = event.data.tankSize.toFixed(0);
            }
        }, false);
    }
}
</script>

