<template>
    <section class="police_cad fullpage">
        <transition name="pad">
            <div class="pad_outer" v-show="show">
                <div class="pad_inner">
                    <div class="pad">
                        <transition name="transition_03">
                            <div class="page page_0" v-show="logged == 0">
                                <div class="logo"></div>
                                <h2>SAN-ANDREAS CAD</h2>
                                <button class="btn btn-login" @click="login">Вход</button>
                                <h5>TO PROTECT AND TO SERVE</h5>
                            </div>
                        </transition>

                        <div class="page page_loader">
                            <div class="preloader-1">
                                <h3 class="text-center">Загрузка</h3>
                                <span class="line line-1"></span>
                                <span class="line line-2"></span>
                                <span class="line line-3"></span>
                                <span class="line line-4"></span>
                                <span class="line line-5"></span>
                                <span class="line line-6"></span>
                                <span class="line line-7"></span>
                                <span class="line line-8"></span>
                                <span class="line line-9"></span>
                            </div>
                        </div>

                        <transition name="transition_03">
                            <div class="page page_1" v-show="logged == 2">
                                <div class="top_bar">
                                    <div class="row">
                                        <div class="col-3 d-flex justify-content-start align-items-center">
                                            <div class="logo"></div>
                                        </div>
                                        <div class="col-6 d-flex justify-content-center align-items-center">
                                            <h2>Computer-Aided Dispatching</h2>
                                        </div>
                                        <div class="col-3 d-flex justify-content-end align-items-center">
                                            <button class="btn btn-gold" @click="closeCad">Выход</button>
                                        </div>
                                    </div>
                                </div>
                                <div class="content">
                                    <div class="left_block">
                                        <div class="player_info">
                                            <button :class="['onDuty_' + this.policeData.onDuty + ' btn-shift hover-shift']"></button>
                                            <div class="d-flex justify-content-center">
                                                <h3>{{ this.playerData.name }} {{ this.playerData.surname }}</h3>
                                            </div>
                                            <h4>{{this.rankList[this.policeData.rank].name}} ({{ this.policeData.callsign}})</h4>
                                        </div>
                                        <ul class="list-group">
                                            <li class="list-group-item" :class="{active: activeMenu == 0}" @click="selectMenu(0)">Основная панель</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 1}" @click="selectMenu(1)">Вызовы</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 2}" @click="selectMenu(2)">База жителей</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 3}" @click="selectMenu(3)">База автомобилей</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 7}" @click="selectMenu(7)">База оружия</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 4}" @click="selectMenu(4)">Законы штата</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 5}" @click="selectMenu(5)">10-коды</li>
                                            <li class="list-group-item" :class="{active: activeMenu == 6}" @click="selectMenu(6)" v-if="this.policeData.rank >= rankAdmin">Управление</li>
                                        </ul>

                                        <div class="bottom_block">
                                            <div class="onDuty_buttons">
                                                <b-button v-b-tooltip.hover.top title="На смене" class="btn onDuty_1" :class="{active: this.policeData.onDuty == 1}" @click="changeShift(1)"></b-button>
                                                <b-button v-b-tooltip.hover.top title="Но вызове" class="btn onDuty_2" :class="{active: this.policeData.onDuty == 2}" @click="changeShift(2)"></b-button>
                                                <b-button v-b-tooltip.hover.top title="Занят" class="btn onDuty_3" :class="{active: this.policeData.onDuty == 3}" @click="changeShift(3)"></b-button>
                                                <b-button v-b-tooltip.hover.top title="Вне смены" class="btn onDuty_0" :class="{active: this.policeData.onDuty == 0}" @click="changeShift(0)"></b-button>
                                            </div>

                                            <button class="btn btn-block btn-panic" :class="{ active: panicOn }" @click="panicClick()">Кнопка паники</button>
                                        </div>
                                    </div>

                                    <div class="center_block">
                                        <transition name="list">
                                            <div class="c_page c_page_1 scrolled" v-show="page == 0">
                                                <div class="d-flex justify-content-between" >
                                                    <h2>Основная панель</h2>
                                                </div>

                                                <div class="onduty_block_outer" v-show="onPanic()">
                                                    <h5 class="redButton">Активный красный код</h5>
                                                    <div class="onduty_block">
                                                        <div class="onduty_list">
                                                            <div class="onduty_element" v-for="(item, index) in policeDutyList" :key="'panic'+index" v-show="item.onPanic">
                                                                <span class="callsign">{{item.callsign}}</span>
                                                                {{ item.name }}
                                                                <i class="callsign" v-if="item.rank" :class="rankList[item.rank].shortname"></i>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="onduty_block_outer">
                                                    <h5>Сотрудники на смене</h5>
                                                    <div class="onduty_block">
                                                        <div class="onduty_list">
                                                            <div class="onduty_element" v-for="(item, index) in showOnDuty(true)" :key="'activeDuty_' + index">
                                                                <span class="callsign">{{item.callsign}}</span>
                                                                {{ item.name }}
                                                                <i class="callsign" v-if="item.rank" :class="rankList[item.rank].shortname"></i>
                                                                <i :class="'status_' + item.onDuty"></i>
                                                            </div>
                                                        </div>
                                                        <div class="onduty_list">
                                                            <div class="onduty_element inactive" v-for="(item, index) in showOnDuty(false)" :key="'inactiveDuty_' + index">
                                                                <span class="callsign">{{item.callsign}}</span>{{ item.name }}
                                                                <i class="callsign" v-if="item.rank" :class="rankList[item.rank].shortname"></i>
                                                                <i :class="'status_' + item.onDuty"></i>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="onduty_block_outer">
                                                    <div class="d-flex justify-content-between">
                                                        <h5> Сотрудники в патруле</h5>
                                                        <div class="dropdown">
                                                            <a class="btn btn-dropdown dropdown-toggle"
                                                                @click="showDropDown(3)">{{playerOnPatrol}}</a>
                                                            <div class="dropdown-menu" v-show="showDropdownC">
                                                                <a class="dropdown-item" v-for="index in 11" :key="'drop_patrol'+index" @click="changePatrolStatus(index - 1)">
                                                                    <span v-if="index == 1">Покинуть</span>
                                                                    <span v-if="index != 1">{{index - 1}} <span>патруль</span></span>
                                                                </a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="onduty_block">
                                                       <div class="onduty_list full">
                                                            <div class="onduty_element" v-for="(item, index) in policeAdamList" :key="'adam' + index" v-show="index !=0">
                                                                <span class="callsign-adam">({{index}}) {{generateAdamName(index)}}:</span>
                                                                <div class="adam_player" :class="{active: cop.policeId == policeData.policeId}" v-for="(cop, newindex) in item" :key="'adam'+index + newindex">
                                                                    <span class="callsign">{{cop.callsign}}</span>
                                                                    <span class="mr-0">{{cop.name}}</span>
                                                                    <i class="callsign" v-if="cop.rank" :class="rankList[cop.rank].shortname"></i>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="wanted_block_outer">
                                                    <div class="wanted_block">
                                                        <h5>Люди в розыске</h5>
                                                       <div class="wanted_list person">
                                                            <div class="wanted_element" v-for="(item, index) in showWantedPerson(2)" :key="'wanted_person' + index" @click="showFullContact(findPlayerIndexById(item.id))">
                                                                <span>{{item.name}}</span>
                                                                <span>{{item.phone}}</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="wanted_block">
                                                        <h5>Машины в розыске</h5>
                                                       <div class="wanted_list vehicle">
                                                            <div class="wanted_element" v-for="(item, index) in showWantedVehicles(2)" :key="'wanted_vehicle' + index" @click="showFullVehiclePage(findVehicleIndexById(item.carId))">
                                                                <span>{{item.name}}</span>
                                                                <span>{{item.number}}</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_1 scrolled" v-show="page == 1">
                                                <div class="d-flex justify-content-between">
                                                    <h2>Последние вызовы</h2>
                                                    <input v-model="searchIdInput" @input="searchId" class="form-control search" placeholder="Поиск" />
                                                </div>
                                                <div class="police_call_list">
                                                    <div :class="[{active: onScene(item.crew) == true}, 'police_call_item status_' + item.status]" v-for="(item, index) in policeCallsList" :key="'calls_' + item.id" v-show="item.show" @click="showFullPoliceCall(index)">
                                                        <div class="row">
                                                            <div class="col-3">
                                                                <h6><b>{{getTime(item.date)}}</b> {{getDate(item.date)}}</h6>
                                                                <h6 :class="'status status_' + item.status">({{item.status}}) {{getStatus(item.status)}}</h6>
                                                            </div>
                                                            <div class="col-6">
                                                                <h6><span>Вызов</span> №{{item.id}}</h6>
                                                                <p>{{item.message}}</p>
                                                            </div>
                                                            <div class="col-3">
                                                                <h6 class="text-right">На сцене</h6>
                                                                <div class="d-flex justify-content-end" v-if="item.crew">
                                                                    <span class="unit" :class="{active:unit == policeData.policeId}" v-for="(unit, index) in item.crew" :key="'crew' + index">
                                                                        {{getCallsignById(unit)}}
                                                                    </span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div
                                                class="c_page c_page_11"
                                                v-if="page == 11">
                                                <div class="d-flex justify-content-between">
                                                    <button class="btn btn-gold" @click="selectPage(1)">Назад</button>
                                                    <h2 class="mb-0"><span>Вызов</span> №{{activeCall.id}}</h2>
                                                </div>
                                                <div :class="'police_call_item_description status_' + activeCall.status">
                                                    <p>{{activeCall.message}}</p>
                                                    <div class="d-flex justify-content-between">
                                                    <h6><span>Вызывавший</span>: <span v-if="activeCall.from == 0" class="golden">Местный житель</span><span @click="showFullContact(findPlayerIndexById(getOwnerNameByPhone(activeCall.from).id))" v-if="activeCall.from != 0" class="golden">{{activeCall.from}} ({{getOwnerNameByPhone(activeCall.from).name}})</span></h6>
                                                    <h6>{{getTime(activeCall.date)}} {{getDate(activeCall.date)}}</h6>
                                                    </div>
                                                    <div class="d-flex justify-content-between">
                                                        <h6><span>Юниты на сцене</span>: <span class="unit" :class="{active: unit == policeData.policeId}" v-for="(unit, index) in activeCall.crew" :key="'scene' + index">{{getCallsignById(unit)}}</span>
                                                        </h6>
                                                        <div class="dropdown">
                                                            <a :class="'btn btn-dropdown dropdown-toggle status_' + activeCall.status" @click="showDropDown(1)">({{activeCall.status}}) {{statusNames[activeCall.status]}}</a>
                                                            <div class="dropdown-menu" v-show="showDropdownA">
                                                                <a :class="'dropdown-item status_' + index" v-for="(item, index) in statusNames" :key="'units' + index" @click="changeStatus(index)">({{index}}) {{ item }}</a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="police_call_item_buttons">
                                                    <button class="btn btn-gold" :class="{active: onScene( activeCall.crew)}" @click="leaveScene(activeCall.crew)">{{onScene(activeCall.crew)? "Покинуть вызов": "Принять вызов"}}</button>
                                                    <button class="btn btn-gold" @click="setMark(activeCall.id)" >Показать на карте</button>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 scrolled" v-show="page == 2">
                                                <div class="d-flex justify-content-between">
                                                    <h2>База жителей штата</h2>
                                                    <input v-model="searchInput" @input="searchName" class="form-control search" placeholder="Поиск" />
                                                </div>
                                                <div class="player_list">
                                                    <div :class="'police_call_item status_' + item.status" v-for="(item, index) in playersList" :key="'searchA' + item.id" v-show="item.show" @click="showFullContact(index)">
                                                        <div class="d-flex">
                                                            <div class="item_index">
                                                                {{ item.id }}
                                                            </div>
                                                            <div class="item_name">
                                                                {{ item.name }}
                                                            </div>
                                                            <div class="item_phone">
                                                                {{ item.phone }}
                                                            </div>
                                                            <div class="item_date">
                                                                {{getDate(item.registered)}}
                                                            </div>
                                                            <div :class="'item_status status_' + item.status">
                                                                {{getPlayerStatus2(item.status)}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 profile scrolled" v-if="page == 21">
                                                <div class="d-flex justify-content-between">
                                                    <button class="btn btn-gold" @click="selectPage(2)">Назад</button>
                                                    <h2 class="mb-0">{{activePlayer.name}}</h2>
                                                </div>
                                                <div class="row">
                                                    <div class="col-4">
                                                       <div class="profile_image">
                                                           <button class="btn btn-gold upload" @click="showModalC = true">Upload photo</button>
                                                            <img class="img-fluid" v-if="activePlayer.picture" :src="activePlayer.picture" />
                                                        </div>
                                                    </div>
                                                    <div class="col-8 pl-0">
                                                        <div :class="'profile_description status_' + activePlayer.status">
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Дата рождения:</h5>
                                                                <h5>{{getDate(activePlayer.registered)}}
                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Пол:</h5>
                                                                <h5>{{activePlayer.gender}}
                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Вод. лицензия:</h5>
                                                                <h5 :class="{license_false: !activePlayer.driveLicense}">{{checkLicense(activePlayer.driveLicense)}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Телефон:</h5>
                                                                <h5>{{activePlayer.phone}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5 class="mb-0">Запись в БД:</h5>
                                                                <h5>{{activePlayer.id}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5 class="mb-0">Статус:</h5>
                                                                <div class="dropdown">
                                                                    <a :class="'btn btn-dropdown dropdown-toggle status_' + activePlayer.status" @click="showDropDown(2)">({{activePlayer.status}}) {{playerStatusNames[activePlayer.status]}}</a>
                                                                    <div class="dropdown-menu" v-show="showDropdownB">
                                                                        <a :class="'dropdown-item status_' + index" v-for="(item, index) in playerStatusNames" :key="'searchB' + index" @click="changePlayerStatus(index)">({{index}}) {{item}}</a>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="d-none profile_content">
                                                    <p><b>Место жительства:</b>{{activePlayer.address}}</p>
                                                    <p><b>Комментарий:</b>{{activePlayer.about}}</p>
                                                </div>

                                                <div class="profile_vehicles vehicles_list" v-if="activePlayer.cars.length != 0">
                                                    <h4>Автомобили гражданина</h4>
                                                    <div :class="'police_call_item status_' + car.status" v-for="(car, index) in activePlayer.cars" :key="'player_vehs' + index + car.id" @click="showFullVehiclePage(findVehicleIndexById(car.carId))">
                                                        <div class="d-flex">
                                                            <div class="item_veh_index">
                                                                {{ index + 1 }}
                                                            </div>
                                                            <div class="item_veh_number">
                                                                {{ car.carId }}
                                                            </div>
                                                            <div class="item_veh_name">
                                                                {{ car.number }}
                                                            </div>
                                                            <div class="item_veh_owner">
                                                                {{ car.name }}
                                                                ({{ car.type }})
                                                            </div>
                                                            <div :class="'item_veh_status status_' + car.status">{{getVehicleStatus(car.status)}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="profile_vehicles vehicles_list" v-if="activePlayer.weapons.length != 0">
                                                    <h4>Оружие гражданина</h4>
                                                    <div :class="'police_call_item status_' + weapon.Status" v-for="(weapon, index) in activePlayer.weapons" :key="'player_weps' + index" @click="showFullWeaponPage(findWeaponIndexById(weapon.Id))">
                                                        <div class="d-flex">
                                                            <div class="item_veh_index">
                                                                {{ weapon.Id }}
                                                            </div>
                                                            <div class="item_veh_number">
                                                                {{ weapon.SerialNumber }}
                                                            </div>
                                                            <div class="item_veh_name">
                                                                {{ weapon.Name }}
                                                            </div>
                                                            <div :class="'item_weapon_status status_' + weapon.Status">{{getWeaponStatus(weapon.Status)}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="ticket_list" v-if="activeTickets.length != 0">
                                                    <h4>Штрафы гражданина</h4>
                                                    <div class="ticket_list_inner">
                                                       <div class="ticket header">
                                                            
                                                            <div class="item_ticket_id">ID</div>
                                                            <div class="item_ticket_owner">Выписал</div>
                                                            <div class="item_ticket_date">Дата</div>
                                                            <div class="item_ticket_text">Причина штрафа</div>
                                                            <div class="item_ticket_price">Сумма</div>
                                                            <div class="item_ticket_index">Срок</div>
                                                            <div class="item_ticket_paid">Плата</div>
                                                            <div class="item_ticket_delete"></div>
                                                        </div>
                                                       <div class="ticket" v-for="(ticket, newindex) in activeTickets" :key="'ticket' + newindex + ticket.id">
                                                            
                                                            <div class="item_ticket_id">
                                                                {{ ticket.id }}
                                                            </div>
                                                            <div class="item_ticket_owner">
                                                                {{getOwnerId(ticket.Recipient)}}
                                                            </div>
                                                            <div class="item_ticket_date">
                                                                {{getDate(ticket.Date)}}
                                                            </div>
                                                            <div class="item_ticket_text">
                                                                {{ticket.Label}}
                                                            </div>
                                                            <div class="item_ticket_price">
                                                                {{ticket.Amount}}
                                                            </div>
                                                            <div class="item_ticket_index">
                                                                {{ticket.penalty}}
                                                            </div>
                                                            <div :class="{false: !ticket.IsPaid}" class="item_ticket_paid">
                                                                {{checkPaid(ticket.IsPaid)}}
                                                            </div>
                                                            <div class="item_ticket_delete">
                                                                <button class="del" @click="deleteTicket(newindex)"></button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="police_tickets">
                                                    <div class="d-flex justify-content-end">
                                                        <button class="btn btn-gold" @click="addTicketToActivePlayer()">Выписать штраф</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 scrolled" v-show="page == 3">
                                                <div class="d-flex justify-content-between">
                                                    <h2>База автомобилей</h2>
                                                    <input v-model="searchVehicleInput" @input="searchVehicle" class="form-control search" placeholder="Поиск" />
                                                </div>
                                                <div class="vehicles_list">
                                                    <div :class="'police_call_item status_' + item.status" v-for="(item, index) in vehiclesList" :key="'vehlist' + index + item.id" v-show="item.show" @click="showFullVehiclePage(index)">
                                                        <div class="d-flex">
                                                            <div class="item_veh_index">
                                                                {{ item.carId }}
                                                            </div>
                                                            <div class="item_veh_number">
                                                                {{item.number}}
                                                            </div>
                                                            <div class="item_veh_name">
                                                                {{ item.name }} ({{item.type}})
                                                            </div>
                                                            <div class="item_veh_owner">
                                                                {{getOwnerName(item.ownerId)}}
                                                            </div>
                                                            <div :class="'item_veh_status status_' + item.status">
                                                                {{getVehicleStatus(item.status)}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div
                                                class="c_page c_page_2 profile car_profile" v-if="page == 31">
                                                <div class="d-flex justify-content-between">
                                                    <button class="btn btn-gold" @click="selectPage(3)">Назад</button>
                                                    <h2 class="mb-0">{{activeVehicle.name}} {{activeVehicle.number}}</h2>
                                                </div>

                                                <div class="row">
                                                    <div class="col-4">
                                                       <div class="profile_image">
                                                            <img class="img-fluid" :src="findImageByName(activeVehicle.name)" />
                                                        </div>
                                                    </div>
                                                    <div class="col-8 pl-0">
                                                        <div :class="'profile_description status_' + activeVehicle.status">
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Владелец:</h5>
                                                                <a @click="showFullContact(findPlayerIndexById(activeVehicle.ownerId))">{{getOwnerName(activeVehicle.ownerId)}}</a>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Номер владельца:</h5>
                                                                <h5>{{getOwnerPhoneByName(activeVehicle.ownerId)}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Запись в БД:</h5>
                                                                <h5>{{activeVehicle.carId}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Цвет:</h5>
                                                                <h5>{{activeVehicle.color}} <span class="vehicle-color" v-show="activeVehicle.color" :style="'background-color:' + activeVehicle.color"></span>
                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Продавец:</h5>
                                                                <h5>{{getOwnerName(activeVehicle.sellerId)}}
                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
