                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5 class="mb-0">Статус:</h5>
                                                                <div class="dropdown">
                                                                    <a :class="'btn btn-dropdown dropdown-toggle status_' + activeVehicle.status" @click="showDropDown(2)">({{activeVehicle.status}}) {{vehicleStatusNames[activeVehicle.status]}}</a>
                                                                    <div class="dropdown-menu" v-show="showDropdownB">
                                                                        <a :class="'dropdown-item status_' + index" v-for="(item, index) in vehicleStatusNames" :key="'dropdown2' + index" @click="changeVehicleStatus(index)">({{index}}) {{item}}</a>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2" v-show="page == 4">
                                                <div class="d-flex justify-content-between">
                                                    <h2>Законы штата</h2>
                                                    <input v-model="searchLawsInput" @input="searchLaws" class="form-control search" placeholder="Поиск" />
                                                </div>
                                                <div class="d-flex law_header">
                                                    <div style="width: 10%">№</div>
                                                    <div style="width: 70%">Текст</div>
                                                    <div style="width: 10%">Срок</div>
                                                    <div style="width: 10%">Штраф</div>
                                                </div>

                                                <div class="law_list" v-for="(item, index) in allLaws" :key="'laws' + index" v-show="item.show">
                                                    <div style="width: 10%">
                                                        {{item.code}}
                                                    </div>
                                                    <div style="width: 70%">
                                                        <p>{{item.name}}</p>
                                                    </div>
                                                    <div style="width: 10%">
                                                        <p>{{item.penalty}}</p>
                                                    </div>
                                                    <div style="width: 10%">
                                                        {{item.price}}
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 scrolled" v-show="page == 5">
                                                <div class="d-flex justify-content-between">
                                                    <h2>10-коды</h2>
                                                    <input v-model="search10Input" @input="searchPoliceCodes" class="form-control search" placeholder="Поиск" />
                                                </div>
                                                <p v-for="(item, index) in policeCodes" :key="'codes' + index" v-show="item.show">{{ item.text }}</p>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 admin_police scrolled" v-show="page == 6">
                                                <div class="d-flex justify-content-between">
                                                    <h2>Управление</h2>
                                                </div>
                                                <div class="player_list">
                                                    <div class="police_call_item" v-for="(item, index) in policeDutyList" :key="'edit_cop_' + index" @click="showPoliceFullPage(index)">
                                                       <div class="d-flex admin_police_item">
                                                            <div class="numb">
                                                                {{index}}
                                                            </div>
                                                            <div class="name">
                                                                {{item.name}}
                                                            </div>
                                                            <div class="callsign">
                                                                {{item.callsign}}
                                                            </div>
                                                            <div class="rank">
                                                                {{rankList[item.rank].name}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 scrolled" v-show="page == 7">
                                                <div class="d-flex justify-content-between">
                                                    <h2>База оружия</h2>
                                                    <input v-model="searchWeaponInput" @input="searchWeapons" class="form-control search" placeholder="Поиск" />
                                                </div>

                                                <div class="weapon_list">
                                                    <div :class="'police_weapon_item status_' + item.Status" v-for="(item, index) in weaponList" :key="'vehlist' + index + item.id" v-show="item.show" @click="showFullWeaponPage(index)">
                                                        <div class="d-flex">
                                                            <div class="item_weapon_id">
                                                                {{ item.Id }}
                                                            </div>
                                                            <div class="item_weapon_owner">
                                                                {{getOwnerName(item.Owner)}}
                                                            </div>
                                                            <div class="item_weapon_name">
                                                                {{getWeaponDataByHash(item.HashWeapon).name}}
                                                            </div>
                                                            <div class="item_weapon_serial">
                                                                {{item.SerialNumber}}
                                                            </div>
                                                            <div class="item_weapon_creator">
                                                                {{getOwnerName(item.Creator)}}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="c_page c_page_2 weapon scrolled" v-if="page == 71">
                                                <div class="d-flex justify-content-between">
                                                    <button class="btn btn-gold" @click="selectPage(7)">Назад</button>
                                                    <h2 class="mb-0">{{getWeaponDataByHash(activeWeapon.WeaponName).name}}</h2>
                                                </div>
                                                <div class="row">
                                                    <div class="col-4">
                                                       <div class="profile_image">
                                                            <img class="img-fluid" :src="getWeaponDataByHash(activeWeapon.HashWeapon).image" />
                                                        </div>
                                                    </div>
                                                    <div class="col-8 pl-0">
                                                        <div class="profile_description">
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Название:</h5>
                                                                <h5>{{getWeaponDataByHash(activeWeapon.HashWeapon).name}}</h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Серийный номер:</h5>
                                                                <h5>{{activeWeapon.SerialNumber}}
                                                                </h5>
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Владелец:</h5>
                                                                <a @click="showFullContact(findPlayerIndexById(activeWeapon.Owner))">{{getOwnerName(activeWeapon.Owner)}}</a>
                                                                
                                                            </div>
                                                            <div class="d-flex justify-content-between">
                                                                <h5>Продавец:</h5>
                                                                <a v-if="activeWeapon.Creator" @click="showFullContact(findPlayerIndexByName(activeWeapon.Creator))">{{getOwnerName(activeWeapon.Creator)}}</a>
                                                                <a v-if="!activeWeapon.Creator">Не найден</a>
                                                            </div>

                                                            

                                                            <div class="d-flex justify-content-between">
                                                                <h5 class="mb-0">Статус:</h5>
                                                                <div class="dropdown">
                                                                    <a :class="'btn btn-dropdown dropdown-toggle status_' + activeWeapon.Status" @click="showDropDown(5)">({{activeWeapon.Status}}) {{weaponStatusNames[activeWeapon.Status]}}</a>
                                                                    <div class="dropdown-menu" v-show="showDropdownE">
                                                                        <a :class="'dropdown-item status_' + index" v-for="(item, index) in weaponStatusNames" :key="'dropdown2' + index" @click="changeWeaponStatus(index)">({{index}}) {{item}}</a>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="police-modal" v-if="showModalA">
                                                <div class="modal_inner">
                                                    <div class="d-flex justify-content-between">
                                                        <h2>Выписка штрафа</h2>
                                                        <button class="btn btn-gold" @click="(showModalA = false), clearViolationList()">Закрыть</button>
                                                    </div>
                                                    <div class="modal_content">
                                                        <h5><span>Имя офицера: </span>{{playerData.name}}</h5>
                                                        <h5><span>Нарушитель: </span>{{activePlayer.name}}</h5>
                                                        <h5><span>Дата: </span>{{ todayDate() }}</h5>
                                                        <h4>Список нарушений:</h4>

                                                        <div class="violation_list added scrolled">
                                                            <div class="violation_item" v-for="(violation,index) in activeViolations" :key="'addedviolation' + index">
                                                                <div class="violation_item_code">
                                                                    {{violation.Code}}
                                                                </div>
                                                                <div class="violation_item_name">
                                                                    {{violation.Label}}
                                                                </div>
                                                                <div class="violation_item_price">
                                                                    {{violation.Amount}}
                                                                </div>
                                                                <div class="violation_item_button">
                                                                    <a @click="deleteViolationFromList(index)">Удалить</a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="violation_list scrolled">
                                                            <div class="violation_item"
                                                                v-for="(violation, index) in allLaws" :key="'violations' + index">
                                                                <div class="violation_item_code">
                                                                    {{violation.Code}}
                                                                </div>
                                                                <div class="violation_item_name">
                                                                    {{violation.Label}}
                                                                </div>
                                                                <div class="violation_item_price">
                                                                    ${{violation.Amount}}
                                                                </div>
                                                                <div class="violation_item_button">
                                                                    <a @click="addViolationToList(index)">Добавить</a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="d-flex justify-content-between align-items-center">
                                                        <h5><span>Сумма</span>: ${{violationsSum}}<span v-show="violationsTime" class="nob">, <span>срок</span>: {{violationsTime}} <span>мин.</span> ({{violationsTimeConverted}} h.)</span></h5>
                                                        <button class="btn btn-gold" @click="issueFine()">Выписать штраф</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="police-modal" v-if="showModalB">
                                                <div class="modal_inner">
                                                    <div class="d-flex justify-content-between">
                                                        <h2>Данные сотрудника</h2>
                                                        <button class="btn btn-gold" @click="(showModalB = false), (showDropdownD = false)">Закрыть</button>
                                                    </div>
                                                    <div class="modal_content">
                                                        <h5><span>Имя офицера:</span>{{activeCop.name}}</h5>
                                                        <div class="d-flex">
                                                            <h5><span>Звание:</span></h5>
                                                            <div class="dropdown">
                                                                <a class="btn btn-dropdown dropdown-toggle police-toggle" @click="showDropDown(4)">{{rankList[activeCop.rank].name}}
                                                                    <i class="callsign" v-if="activeCop.rank" :class="rankList[activeCop.rank].shortname"></i>
                                                                </a>
                                                                <div class="dropdown-menu" v-show="showDropdownD">
                                                                    <a class="dropdown-item" :class="{active: activeCop.rank == index}" v-for="(item, index) in rankList" :key="'rank_' + index" @click="changePlayerRank(index)">{{index}} {{item.name}}</a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="d-flex">
                                                            <h5><span>Позывной: </span></h5>
                                                            <input class="callsign" v-model="callsignTemp">
                                                            <button class="btn btn-callsign" @click="saveNewCallsign()">Изменить</button>
                                                        </div>
                                                    </div>
                                                    <div class="d-flex justify-content-start align-items-center">
                                                        <button v-show="!sureFire" class="btn btn-danger" @click="(sureFire = true), (showDropdownD = false)">Уволить сотрудника</button>
                                                        <h5 class="mb-0" v-show="sureFire">Вы уверены?</h5>
                                                        <button v-show="sureFire" class="ml-3 mr-3 btn btn-danger" @click="fireCop(activeCop)">Уволить</button>
                                                        <button v-show="sureFire" class="btn btn-gold" @click="sureFire = false">Отмена</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>

                                        <transition name="list">
                                            <div class="police-modal" v-if="showModalC">
                                                <div class="modal_inner">
                                                    <div class="d-flex justify-content-between">
                                                        <h2>Загрузить фотографию</h2>
                                                        <button class="btn btn-gold" @click="(showModalC = false)">Закрыть</button>
                                                    </div>
                                                    <div class="modal_content d-flex flex-column align-items-start">
                                                        <label>Ссылка на фотографию</label>
                                                        <input class="photo_upload" v-model="inputImage">
                                                        <button class="btn btn-green" @click="addImageToPlayer()">Сохранить</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>
                                    </div>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
require('@/assets/css/police_cad.css')

import axios from "axios";
import police10codes from "../../json/policecodes.json";
import vehicles from "../../json/vehicles.json";
import laws from "../../json/laws.json";
import weapons from "../../json/weapon.json"

export default {
    name: "PoliceCad",
    components: {},
    props: {
        playerData: Object,
    },
    data() {
        return {
            show: false,
            logged: 0,
            page: 0,
            activeMenu: 0,
            panicOn: false,
            playerOnPatrol: 'Not selected',

            policeCodes: {},
            allVehicles: vehicles,
            allLaws: laws,
            allWeapons: weapons,

            policeData: this.playerData.policeData,
            playersList: [],
            vehiclesList: [],
            ticketsList: [],
            policeCallsList: [],
            policeDutyList: [],
            policeAdamList: [],

            activePlayer: {},
            activeTickets: [],
            activeCall: {},
            activeVehicle: {},
            activeViolations: [],
            activeCop: {},
            activePanic: [],

            searchInput: "",
            searchIdInput: "",
            search10Input: "",
            searchLawsInput: "",
            searchVehicleInput: "",

            statusNames: [
                "Под контролем",
                "Неизвестно",
                "Нормально",
                "Угроза",
                "Опасно",
            ],
            playerStatusNames: [
                "Мертв",
                "Стандарт",
                "В розыске",
                "Арестован",
                "В тюрьме",
                "Специальный",
            ],
            vehicleStatusNames: [
                "Уничтожен",
                "Стандарт",
                "В розыске",
                "Арестован",
                "Угнан",
                "Специальный",
            ],
            weaponStatusNames: [
                "Уничтожено",
                "Стандарт",
                "Потерян",
                "Арестован",
                "Украдено",
                "Специальный",
            ],
            rankList: [
                { name: "Recruet", shortname: "rec"},
                { name: "Officer", shortname: "po1"},
                { name: "Sergeant", shortname: "ser1"},
                { name: "Lieutenant", shortname: "lie" },
                { name: "Commandant", shortname: "chief" },
            ],

            rankAdmin: 4,

            showDropdownA: false,
            showDropdownB: false,
            showDropdownC: false,
            showDropdownD: false,
            showDropdownE: false,
            showModalA: false,
            showModalB: false,
            showModalC: false,

            sureFire: false,
            violationsSum: 0,
            violationsTime: 0,
            violationsTimeConverted: 0,
            callsignTemp: '',

            inputImage: undefined,

            weaponList: [
                {
                    Id: 182,
                    WeaponName: "weapon_combatpdw",
                    SerialNumber: "ecSlr9DQ",
                    Creator: 0,
                    IsSold: true,
                    Owner: null,
                }
            ],
            searchWeaponInput: "",
            activeWeapon: {},

        };
    },
    methods: {
        closeCad() {
            axios.post('https://eclipse/CloseCad', {
            }).then(() => { return; }).catch(function () { console.log() });  
            //this.show = false;
        },
        login() {
            this.logged = 1;

            this.showAdamList();
            this.sortyBycallsign(this.policeDutyList);
            this.addTicketsToPlayers();
            this.addVehiclesToPlayers();

            this.showAll(this.playersList);
            this.showAll(this.policeCallsList);
            this.showAll(this.vehiclesList);
            this.showAll(this.allLaws);

            this.sortByDate(this.policeCallsList);
            this.sortByNames(this.playersList);
            this.sortByNumbers(this.vehiclesList);

            this.convertPoliceCodes();

            setTimeout(() => {
                this.logged = 2;
            }, 2000);
        },
        selectMenu(page) {
            this.activeMenu = page;
            this.selectPage(page);
            this.showModalA = false;
            this.showModalB = false;
            this.showModalC = false;
            this.clearViolationList();
            this.closeDropDowns();
        },
        selectPage(page) {
            this.page = page;
            this.closeDropDowns();
        },
        closeDropDowns() {
            this.showDropdownA = false;
            this.showDropdownB = false;
            this.showDropdownC = false;
            this.showDropdownD = false;
            this.showDropdownE = false;
        },
        getName(str) {
            var name = str.split(" ");
            return name[0];
        },
        getSurname(str) {
            var name = str.split(" ");
            return name[1];
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getTime(date) {
            var newdate = new Date(date);
            if (newdate) {
                var hours = ("0" + newdate.getHours()).slice(-2);
                var minutes = ("0" + newdate.getMinutes()).slice(-2);
                return hours + ":" + minutes;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        todayDate() {
            var today = new Date();
            var time = ("0" + today.getHours()).slice(-2);
            var minutes = ("0" + today.getMinutes()).slice(-2);
            var year = today.getFullYear();
            var month = ("0" + (today.getMonth() + 1)).slice(-2);
            var day = ("0" + today.getDate()).slice(-2);
            var date =
                time + ":" + minutes + " " + day + "." + month + "." + year;
            return date;
        },
        getStatus(numb) {
            return this.statusNames[numb];
        },
        getPlayerStatus(numb) {
            return this.playerStatusNames[numb];
        },
        getPlayerStatus2(numb) {
            if (numb <= 1) {
                return;
            } else {
                return this.playerStatusNames[numb];
            }
        },
        getVehicleStatus(numb) {
            if (numb <= 1) {
                return;
            } else {
                return this.vehicleStatusNames[numb];
            }
        },
        getWeaponStatus(numb) {
            if (numb <= 1) {
                return;
            } else {
                return this.weaponStatusNames[numb];
            }
        },
        getOwnerName(id) {
            var player = this.playersList.find((item) => item.id == id);
            if (player) {
                return player.name;
            } else {
                var errortext = "Не найден";
                return errortext;
            }
        },
        getOwnerId(id) {
            var player = this.playersList.find((item) => item.id == id);
            if (player) {
                return player.name;
            } else {
                var errortext = "Не найден";
                return errortext;
            }
        },
        getOwnerPhone(id) {
            var player = this.playersList.find((item) => item.id == id);
            if (player) {
                return player.phone;
            } else {
                return;
            }
        },
        getOwnerPhoneByName(id) {
            var player = this.playersList.find((item) => item.id == id);
            if (player) {
                return player.phone;
            } else {
                return;
            }
        },
        getOwnerNameByPhone(numb) {
            var player = this.playersList.find((item) => item.phone == numb);
            if (player) {
                return player;
            } else {
                return;
            }
        },
        getCallsignById(id) {
            var player = this.policeDutyList.find(
                (item) => item.policeId == id
            );
            if (player) {
                return player.callsign;
            } else {
                return;
            }
        },
        getWeaponDataByHash(hash) {
            var weapon = this.allWeapons.find(item => item.id == hash);
            if (weapon) {
                return weapon
            } else {
                return "hash оружия не найдет в json массиве"
            }
        },
        findCarByName(name) {
            if (name) {
                var car = this.allVehicles.find(
                    (item) => item.gameName == name
                );
                return car;
            }
        },
        findImageByName(name) {
            if (name) {
                var car = this.allVehicles.find(
                    (item) => item.gameName == name
                );
                if (car) {
                    return car.image;
                } else {
                    return " "
                }
                
            }
        },
        findPlayerIndexById(id) {
            if (id != undefined) {
                return this.playersList.findIndex((item) => item.id == id);
            }
        },
        findPlayerIndexByName(name) {
            if (name != undefined) {
                return this.playersList.findIndex((item) => item.name == name);
            }
        },
        findWeaponIndexById(id) {
            if (id != undefined) {
                return this.weaponList.findIndex((item) => item.Id == id);
            }
        },
        findVehicleIndexById(id) {
            if (id != undefined) {
                return this.vehiclesList.findIndex((item) => item.carId == id);
            }
        },
        addTicketsToPlayers() {
            this.playersList.forEach((player) => {
                player.tickets = this.ticketsList.filter(
                    (ticket) => ticket.UserId == player.id
                );
            });
        },
        addVehiclesToPlayers() {
            this.playersList.forEach((player) => {
                player.cars = [];
                player.cars = this.vehiclesList.filter(
                    (vehicle) => vehicle.ownerId == player.id
                );
            });
        },
        addWeaponsToPlayers() {
            this.playersList.forEach((player) => {
                player.weapons = [];
                player.weapons = this.weaponList.filter(
                    (weapon) => weapon.Owner == player.id
                );
            });
        },
        showAll(array) {
            array.forEach((element) => (element.show = true));
        },
        searchName() {
            var searchText = this.searchInput;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.playersList.forEach((element) => {
                    var string =
                        element.name +
                        " " +
                        element.id +
                        " " +
                        element.phone +
                        " " +
                        element.registered + 
                        " " +
                        this.getPlayerStatus2(element.status);
                    var result = string.search(exp);
                    if (result != -1) {
                        element.show = true;
                    } else {
                        element.show = false;
                    }
                });
            } else {
                this.showAll(this.playersList);
            }
        },
        searchId() {
            var searchText = this.searchIdInput;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.policeCallsList.forEach((element) => {
                    var string = element.id.toString() + this.getDate(element.date) + element.from + this.getStatus(element.status);
                    var result = string.search(exp);
                    if (result == -1) {
                        element.show = false;
                    } else {
                        element.show = true;
                    }
                });
            } else {
                this.showAll(this.policeCallsList);
            }
        },
        searchPoliceCodes() {
            var searchText = this.search10Input;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                for (var key in this.policeCodes) {
                    var element = this.policeCodes[key];
                    var string = element.text.toString();
                    var result = string.search(exp);
                    if (result == -1) {
                        element.show = false;
                    } else {
                        element.show = true;
                    }
                }
            } else {
                for (var i in this.policeCodes) {
                    this.policeCodes[i].show = true;
                }
            }
        },
        searchVehicle() {
            var searchText = this.searchVehicleInput;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.vehiclesList.forEach((element) => {
                    var string =
                        element.name +
                        " " +
                        element.type +
                        " " +
                        this.getOwnerName(element.ownerId) +
                        "" +
                        element.number +
                        "" +
                        element.carId +
                        "" +
                        this.getVehicleStatus(element.status);
                    var result = string.search(exp);
                    if (result != -1) {
                        element.show = true;
                    } else {
                        element.show = false;
                    }
                });
            } else {
                this.showAll(this.vehiclesList);
            }
        },
        searchLaws() {
            var searchText = this.searchLawsInput;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.allLaws.forEach((element) => {
                    var string =
                        element.code + " " + element.name + " " + element.price;
                    var result = string.search(exp);
                    if (result != -1) {
                        element.show = true;
                    } else {
                        element.show = false;
                    }
                });
            } else {
                this.showAll(this.allLaws);
            }
        },
        searchWeapons() {
            var searchText = this.searchWeaponInput;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.weaponList.forEach((element) => {
                    var string =
                        this.getOwnerName(element.Owner) + " " + element.Id + " " + element.SerialNumber + " " + this.getOwnerName(element.Creator);
                    var result = string.search(exp);
                    if (result != -1) {
                        element.show = true;
                    } else {
                        element.show = false;
                    }
                });
            } else {
                this.showAll(this.weaponList);
            }
        },
