<template>
    <div class="dialog dialog_drugs">
        <div class="dialog_inner scrolled">
            <div class="dialog_message_outer" :class="{left: !message.Player}" v-for="(message, index) in messages.slice().reverse()" :key="'message' + index">
                <div class="dialog_message">
                    <h5>{{message.Author}}:</h5>
                    <p>{{message.Text}}</p>
                </div>
            </div>
        </div>
        <div class="dialog_buttons">
            <button class="btn btn-primary mx-1" v-for="(button, index) in activeMessages.Answers" :key="'btn_' + index" @click="runCommand(button.Command, button.Text)">{{button.Text}}</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'dialog_drugs',
    data() {
        return {
            messages: [
                {
                    "Author": "Незнакомец",
                    "Text": "Йоу, у тебя что-то есть для меня?",
                    "Player": false,
                    Answers: [
                        {
                            Text: "Да, я принес товар. Берешь?",
                            Command: "SellDrugs"
                        },
                        {
                            Text: "Простите, я ошибся.",
                            Commadn: "Exit"
                        }
                    ]
                }, {
                    "Author": "Незнакомец",
                    "Text": "Хоуми, ",
                    "Player": false,
                    Answers: [
                        {
                            Text: "Да, я принес товар. Берешь?",
                            Command: "SellDrugs"
                        },
                        {
                            Text: "Простите, я ошибся.",
                            Commadn: "Exit"
                        }
                    ]
                }

            ],
            activeMessages: {},
            activeIndex: 0,
            
        }
    },
    methods: {
        answer(numb) {
            var message = {};
            if (numb == 1) {
                message.Author = "Вы";
                message.Text = "Давай, удачи.";
                message.Player = true;
            }
            if (numb == 2) {
                message.Author = "Вы";
                message.Text = "Пошел нахер козёл!";
                message.Player = true;
            }

            this.messages.push(message);
        },
        runCommand(command, text) {
            var message = {};
            message.Author = "Вы";
            message.Text = text;
            message.Player = true;
            this.messages.push(message);

            if (command != "Exit") {
                this.nextMove()
                
            }
        },
        nextMove() {
            this.activeIndex += 1;
            this.activeMessages = this.messages[this.activeIndex];
            
        }
    },
    mounted() {
        this.activeMessages = this.messages[this.activeIndex];
    }
}
</script>

