<template>
    <section
        class="fullpage"
        @mousedown="toggleCameraClick(true)"
        @mouseup="toggleCameraClick(false)"
        @mousemove="toggleCameraMove"
        @mousewheel="wheelMove"
    >
        >
        <div class="shop shop_cloth" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
        <div class="header">
            <h2 @click="changePage(0)">Ponsonbys</h2>
            <div class="shop_menu">
            <button
                class="btn"
                :class="{ active: activePage == 5 }"
                @click="changePage(5)"
            >
                Временный магазин
            </button>

            <!-- <button class="btn" :class="{active: activePage == 1}" @click="changePage(1)">Комплекты</button>
                        <button class="btn" :class="{active: activePage == 2}" @click="changePage(2)">Одежда</button>
                        <button class="btn" :class="{active: activePage == 3}" @click="changePage(3)">Обувь</button>
                        <button class="btn" :class="{active: activePage == 4}" @click="changePage(4)">Аксессуары</button> -->
            </div>
        </div>

        <div class="body">
            <transition name="list">
            <div class="page page_0" v-show="activePage == 0">
                <div class="about_block">
                <h4>ДОМ PONSONBYS</h4>
                <p>
                    Имя <b>Ponsonbys</b> всегда служило символом авангардной
                    элегантности. От коллекций Hate Couture до смелых косметических
                    запусков - узнайте, как <b>Дом Ponsonbys</b> на протяжении всей
                    своей истории бросает вызов условностям.
                </p>
                </div>
                <h5 class="text-center">Новые коллекции</h5>
                <div class="new_collections row">
                <div class="col-sm">
                    <div class="element elem_1">
                    <h5>ENGLAND SUIT</h5>
                    </div>
                </div>
                <div class="col-sm">
                    <div class="element elem_2">
                    <h5>ELITE SHORTS</h5>
                    </div>
                </div>
                </div>
            </div>
            </transition>

            <transition name="list">
            <div class="page page_1" v-show="activePage == 1">
                <div class="page_inner">
                <div
                    class="carousel_block"
                    v-for="(element, index) in clothList"
                    :key="'swiper' + index"
                >
                    <h5 class="type">{{ element.name }}</h5>
                    <swiper :options="swiperOptions">
                    <swiper-slide
                        v-for="(item, index2) in clothList[index].list"
                        :key="element.name + index2"
                        v-show="item.show == true"
                    >
                        <div class="slider-item">
                        <div
                            class="slider-image"
                            :style="'background-image: url(' + item.image + ')'"
                        ></div>
                        <h5 class="price">${{ item.price }}</h5>
                        <div class="slider-item-bottom">
                            <h5 class="name">
                            {{ item.manufacturer }} {{ item.name }}
                            </h5>
                            <button class="btn btn-block btn-try">Примерить</button>
                            <button
                            class="btn btn-block btn-buy"
                            @click="addToCart(clothList, index, index2)"
                            >
                            В корзину
                            </button>
                        </div>
                        </div>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                </div>
                </div>
            </div>
            </transition>

            <transition name="list">
            <div class="page page_2" v-show="activePage == 2">
                <div class="page_inner">
                <div
                    class="carousel_block"
                    v-for="(element, index) in allClothList"
                    :key="'swiper' + index"
                >
                    <h5 class="type">{{ element.name }}</h5>
                    <swiper :options="swiperOptions">
                    <swiper-slide
                        v-for="(item, index2) in allClothList[index].list"
                        :key="element.name + index2"
                        v-show="item.show == true"
                    >
                        <div class="slider-item">
                        <div
                            class="slider-image"
                            :style="'background-image: url(' + item.image + ')'"
                        ></div>
                        <h5 class="price">${{ item.price }}</h5>
                        <div class="slider-item-bottom">
                            <h5 class="name">{{ item.name }}</h5>
                            <button class="btn btn-block btn-try">Примерить</button>
                            <button
                            class="btn btn-block btn-buy"
                            @click="addToCart(allClothList, index, index2)"
                            >
                            В корзину
                            </button>
                        </div>
                        </div>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                </div>
                </div>
            </div>
            </transition>

            <transition name="list">
            <div class="page page_3" v-show="activePage == 3">
                <div class="page_inner">
                <div
                    class="carousel_block"
                    v-for="(element, index) in allShoesList"
                    :key="'swiper' + index"
                >
                    <h5 class="type">{{ element.name }}</h5>
                    <swiper :options="swiperOptions">
                    <swiper-slide
                        v-for="(item, index2) in allShoesList[index].list"
                        :key="element.name + index2"
                        v-show="item.show == true"
                    >
                        <div class="slider-item">
                        <div
                            class="slider-image"
                            :style="'background-image: url(' + item.image + ')'"
                        ></div>
                        <h5 class="price">${{ item.price }}</h5>
                        <div class="slider-item-bottom">
                            <h5 class="name">{{ item.name }}</h5>
                            <button class="btn btn-block btn-try">Примерить</button>
                            <button
                            class="btn btn-block btn-buy"
                            @click="addToCart(allShoesList, index, index2)"
                            >
                            В корзину
                            </button>
                        </div>
                        </div>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                </div>
                </div>
            </div>
            </transition>

            <transition name="list">
            <div class="page page_4" v-if="activePage == 4">
                <div class="page_inner">
                <div
                    class="carousel_block"
                    v-for="(element, index) in allAccessoriesList"
                    :key="'swiper' + index"
                >
                    <h5 class="type">{{ element.name }}</h5>
                    <swiper :options="swiperOptions">
                    <swiper-slide
                        v-for="(item, index2) in allAccessoriesList[index].list"
                        :key="element.name + index2"
                        v-show="item.show == true"
                    >
                        <div class="slider-item">
                        <div
                            class="slider-image"
                            :style="'background-image: url(' + item.image + ')'"
                        ></div>
                        <h5 class="price">${{ item.price }}</h5>
                        <div class="slider-item-bottom">
                            <h5 class="name">{{ item.name }}</h5>
                            <button class="btn btn-block btn-try">Примерить</button>
                            <button
                            class="btn btn-block btn-buy"
                            @click="addToCart(allAccessoriesList, index, index2)"
                            >
                            В корзину
                            </button>
                        </div>
                        </div>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                </div>
                </div>
            </div>
            </transition>

            <transition name="list">
                <div class="page page_5" v-if="activePage == 5">
                <div class="page_inner">
                    <div class="carousel_block">
                    <h5 class="type">Временный магазин одежды</h5>
                    <b v-if="sex == true">Мужской каталог</b><b v-else>Женский каталог</b>
                    <div class="test_catalog">
                        <div v-for="(item, index) in result" :key="'a'+index" class="" v-show="item[0].Category != 2">
                        <CarouselCloth
                            :list="item"
                            :categoryName="findTypeByCategory(item[0].Category)"
                            :category="item[0].Category"
                            @sendData="sendData"
                            :drawable="findSelectedIndexByCategory(item[0].Category).Drawable"
                            :texture="findSelectedIndexByCategory(item[0].Category).Texture"
                        />
                        </div>
                    </div>
                    <b>Аксессуары</b>
                    <div class="test_catalog">
                        <div v-for="(item, index) in accessories" :key="'b'+index" class="">
                        <CarouselCloth
                            :list="item"
                            :categoryName="findTypeByCategory(item[0].Category)"
                            :category="item[0].Category"
                            @sendData="sendData"
                            :drawable="findSelectedIndexByCategory(item[0].Category).Drawable"
                            :texture="findSelectedIndexByCategory(item[0].Category).Texture"
                        />
                        </div>
                    </div>
                    </div>
                </div>
                </div>
            </transition>
            </div>

        <div class="footer">
            <span :class="{ active: activeShop == 1 }">Portola Drive</span>
            <span :class="{ active: activeShop == 2 }">Rockford Plaza</span>
            <span :class="{ active: activeShop == 3 }">Cougar Avenue</span>
        </div>
        </div>

        <div class="bottom_menu_center" @mouseover="scrollBlock(true)" @mouseout="scrollBlock(false)">
            <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 0}" @click="changePlayerAnimation(0)">Стандарт</button>
            <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 1}" @click="changePlayerAnimation(1)">Руки</button>
            <button class="btn btn-secondary enterbutton" :class="{active: activePlayerAnim == 2}" @click="changePlayerAnimation(2)">Поза</button>
        </div>

        <div class="shop_cart">
        <div class="cart_buy">
            <button class="btn btn-block btn-buy" @click="buyCloth()">
            Купить
            </button>
        </div>
        <h3 v-if="clothCartList.length == 0" class="d-none">Корзина пуста</h3>
        <div v-if="clothCartList.length != 0" class="cart_outer">
            <div
            class="cart_item"
            v-for="(item, index) in clothCartList"
            :key="'cart_' + index"
            >
            <div
                class="left_block"
                :style="'background-image: url(' + item.image + ')'"
            ></div>
            <div class="right_block">
                <h5 class="name">{{ item.manufacturer }} {{ item.name }}</h5>
                <div class="d-flex justify-content-between">
                <h5 class="price">${{ item.price }}</h5>
                <button class="btn btn-remove" @click="removeItemFromCart(index)">
                    Убрать
                </button>
                </div>
            </div>
            </div>
        </div>
        <div class="cart_control" v-if="clothCartList.length != 0">
            <div class="cart_amount">
            <h5>Всего: {{ cartLength }}</h5>
            <h5>${{ cartAmount }}</h5>
            </div>
            <div class="cart_buy">
            <button class="btn btn-block btn-buy">Завершить покупку</button>
            </div>
        </div>
        </div>
    </section>
</template>

<script>
require('@/assets/css/clothshop.css');
// import { Swiper, SwiperSlide } from "vue-awesome-swiper";
// import "../../assets/css/swiper.min.css";
import allClothList from "../../json/cloth.json";
import allShoesList from "../../json/shoes.json";
import allAccessoriesList from "../../json/accessories.json";
import femAccessoriesList from "../../json/femaccessories.json";
import axios from "axios";
import testMaleClothList from "../../json/male.json";
import testFemaleClothList from "../../json/female.json";
import CarouselCloth from "../small/CarouselCloth.vue";

export default {
    name: "ShopCloth",
    components: {
        // Swiper,
        // SwiperSlide,
        CarouselCloth,
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            activePage: 0,
            activeShop: 1,
            swiperOptions: {
                pagination: {
                    el: ".swiper-pagination",
                },
                loop: false,
                autoplay: false,
                slidesPerView: 3,
                spaceBetween: 10,
            },
            cartAmount: 0,
            cartLength: 0,
            sex: this.playerData.sex,
            mouseState: false,
            blockScroll: false,
            clothList: [
                {
                name: "sport",
                list: [
                    {
                    type: 1,
                    show: true,
                    manufacturer: "ADIDAS",
                    name: "Sport skirt",
                    price: 5000,
                    image:
                        "https://img.gta5-mods.com/q95/images/modified-mp-female-clothes/6e7cc8-20170828214601_1.jpg",
                    },
                    {
                    type: 2,
                    show: true,
                    manufacturer: "SUPREME",
                    name: "PULLOVER HOODIE",
                    price: 6800,
                    image:
                        "https://img.gta5-mods.com/q95/images/supreme-x-ny-yankee-s-pullover-hoodie/b5ac7a-3.png",
                    },
                    {
                    type: 3,
                    show: true,
                    manufacturer: "ADIDAS",
                    name: "SPORTWEAR",
                    price: 9900,
                    image:
                        "https://img.gta5-mods.com/q95/images/yeezy-season-4-calabasas-sweats-sweaters/459ee7-8.png",
                    },
                    {
                    type: 4,
                    show: true,
                    manufacturer: "ADIDAS",
                    name: "CLASSIC PRIMEBLUE",
                    price: 5000,
                    image:
                        "https://assets.adidas.com/images/h_2000,f_auto,q_auto:sensitive,fl_lossy/310f3418e0074d7c956bab8b00aa9289_9366/Olimpijka_Adicolor_Classics_Primeblue_SST_krasnyj_GF0196_21_model.jpg",
                    },
                ],
                },
                {
                name: "business",
                list: [
                    {
                    type: 1,
                    show: true,
                    manufacturer: "HENDERSON",
                    name: "ПРЯМОЙ СИЛУЭТ",
                    price: 19990,
                    image:
                        "https://henderson.ru.airee.cloud/uimages/catalog/product/JT-0260-N/JT-0260-N-LBLUE-6-1.jpg",
                    },
                    {
                    type: 2,
                    show: true,
                    manufacturer: "HENDERSON",
                    name: "КОСТЮМ СИНИЙ",
                    price: 15990,
                    image:
                        "https://henderson.ru.airee.cloud/uimages/catalog/product/JT1-0198-N/JT1-0198-N-NAVY-6-2.jpg",
                    },
                    {
                    type: 3,
                    show: true,
                    manufacturer: "SUIT DIRECT",
                    name: "NAVY PANAMA SLIM",
                    price: 18400,
                    image:
                        "https://cdn.suitdirect.co.uk/upload/siteimages/large//tb021js_170_b.jpg",
                    },
                    {
                    type: 4,
                    show: true,
                    manufacturer: "SUIT DIRECT",
                    name: "Blue Summer",
                    price: 59000,
                    image:
                        "https://cdn.suitdirect.co.uk/upload/siteimages/large//0062517_150_a.jpg",
                    },
                ],
                },
                {
                name: "police",
                list: [
                    {
                    type: 1,
                    show: true,
                    manufacturer: "POLICE",
                    name: "OFFICER SET",
                    price: 90,
                    image:
                        "https://img.gta5-mods.com/q95/images/ls-la-police-department/041bc5-01.jpg",
                    },
                    {
                    type: 2,
                    show: true,
                    manufacturer: "POLICE",
                    name: "SEARGANT SET",
                    price: 99,
                    image:
                        "https://img.gta5-mods.com/q95/images/ls-la-police-department/041bc5-01.jpg",
                    },
                    {
                    type: 2,
                    show: true,
                    manufacturer: "POLICE",
                    name: "DETECTIVE SET",
                    price: 100,
                    image:
                        "https://img.gta5-mods.com/q95/images/ls-la-police-department/041bc5-01.jpg",
                    },
                ],
                },
            ],

            allClothList: allClothList,
            allShoesList: allShoesList,
            allAccessoriesList: allAccessoriesList,
            femAccessoriesList: femAccessoriesList,
            clothCartList: [],

            testMaleClothList: testMaleClothList,
            testFemaleClothList: testFemaleClothList,
            result: undefined,
            accessories: undefined,
            clothCategory: [
                {ComponentId: 1, Order: 0, Type: "Маски"},
                {ComponentId: 2, Order: 10, Type: "Причёски??" },
                {ComponentId: 3, Order: 4, Type: "Руки" },
                {ComponentId: 4, Order: 5, Type: "Ноги" },
                {ComponentId: 5, Order: 9, Type: "Сумки" },
                {ComponentId: 6, Order: 6, Type: "Ботинки" },
                {ComponentId: 7, Order: 8, Type: "Аксессуары" },
                {ComponentId: 8, Order: 2, Type: "Футболки" },
                {ComponentId: 9, Order: 3, Type: "Броня" },
                {ComponentId: 10, Order: 7, Type: "Декали" },
                {ComponentId: 11, Order: 1, Type: "Верхняя одежда" },
                {ComponentId: 100, Order: 0, Type: "Шляпы"},
                {ComponentId: 101, Order: 0, Type: "Очки"},
                {ComponentId: 102, Order: 0, Type: "Уши"},
                {ComponentId: 106, Order: 0, Type: "Часы"},
                {ComponentId: 107, Order: 0, Type: "Браслеты"},
            ],
            array: undefined,
            isControlPressed: false,
            activePlayerAnim: 0,

            selectedPlayerItems: [],
        };
    },
    methods: {
        changePage(numb) {
            this.activePage = numb;
        },
        addToCart(array, group, index) {
            this.clothCartList.push(array[group].list[index]);
            this.calculatePrice();
        },
        removeItemFromCart(index) {
            this.clothCartList.splice(index, 1);
            this.calculatePrice();
        },
        calculatePrice() {
            this.cartAmount = 0;
            this.cartLength = this.clothCartList.length;
            this.clothCartList.forEach((item) => {
                this.cartAmount = this.cartAmount + item.price;
            });
        },
        toggleCameraClick(state) {
            if (state == true) {
                this.mouseState = true;
                this.mouseX = 0;
                this.mouseY = 0;
            } else if (state == false) {
                this.mouseState = false;
            }
        },
        scrollBlock(state) {
            if (state == true) {
                this.blockScroll = true;
            } else if (state == false) {
                this.blockScroll = false;
            }
        },
        wheelMove($event) {
            if (!this.blockScroll) {
                axios.post("https://eclipse/Mouse", {
                    DeltaY: $event.deltaY,
                    CtrlPressed: this.isControlPressed
                }).then(() => {return;}).catch(function(error) {console.log(error);});
            }
        },
        pressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = true;
            }
        },
        unpressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = false;
            }
        },
        toggleCameraMove($event) {
            if (this.mouseState == true && !this.blockScroll) {
                if (this.mouseX == 0 && this.mouseY == 0) {
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY;
                    return;
                }
                if ($event.clientX || $event.clientY) {
                //console.log('X: ' + ($event.clientX - this.mouseX))
                //console.log('Y: ' + ($event.clientY - this.mouseY))
                var xPosition = $event.clientX - this.mouseX;
                var yPositiuon = $event.clientY - this.mouseY;
                this.mouseX = $event.clientX;
                this.mouseY = $event.clientY;
                axios
                    .post("https://eclipse/X", {
                    Xposition: xPosition,
                    Yposition: yPositiuon,
                    })
                    .then(() => {
                    return;
                    })
                    .catch(function(error) {
                    console.log(error);
                    });
                }
            }
        },
        deleteCloth() {
            allClothList;
        },
        minus(index) {
            if (this.sex) {
                this.array = this.testMaleClothList;
            } else {
                this.array = this.testFemaleClothList;
            }

            this.array[index].Value = parseInt(this.array[index].Value) - 1;
            if (this.array[index].Value < 0) {
                this.array[index].Value = this.array[index].Size + 1;
            }
            this.sendCloth(index);
        },
        plus(index) {
            if (this.sex) {
                this.array = this.testMaleClothList;
            } else {
                this.array = this.testFemaleClothList;
            }

            this.array[index].Value = parseInt(this.array[index].Value) + 1;
            if (this.array[index].Value > this.array[index].Size) {
                this.array[index].Value = 0;
            }
            this.sendCloth(index);
        },
        changeTo(index) {
            if (this.sex) {
                this.array = this.testMaleClothList;
            } else {
                this.array = this.testFemaleClothList;
            }

            if (this.array[index].Value > this.array[index].Size) {
                this.array[index].Value = this.array[index].Size + 1;
            }
            if (this.array[index].Value < 0) {
                this.array[index].Value = 0;
            }
            var bool = Number.isInteger(this.array[index].Value);
            if (!bool) {
                this.array[index].Value = 0;
            }
            this.sendCloth(index);
        },
        groupByKey(array, key) {
            return array.reduce((hash, obj) => {
                if (obj[key] === undefined) return hash;
                    return Object.assign(hash, {
                [obj[key]]: (hash[obj[key]] || []).concat(obj),
                });
            }, {});
        },
        findTypeByCategory(name) {
            return this.clothCategory.find((item) => item.ComponentId == name).Type;
        },
        sendData(category, drawable, texture) {
            console.log(category, drawable, texture)
            axios.post("https://eclipse/updateCloth", {
                Category: category,
                Drawable: drawable,
                Texture: texture,
            }).then(() => {
                return;
            }).catch(function(error) {
                console.log(error);
            });
        },
        buyCloth() {
            axios.post("https://eclipse/buyCloth", {}).then(() => {
                return;
            }).catch(function(error) {
                console.log(error);
            });
        },
        doCommand(e) {
            if(e.keyCode == 27) {
                    axios.post('https://eclipse/CloseClothShop', {
                    }).then(() => { return; }).catch(function () { console.log() });  
            }
            if (e.keyCode == 37) {
                //left
                console.log('left')
            }
            if (e.keyCode == 39) {
                //right
                console.log('right')
            }
            if (e.keyCode == 38) {
                //top
                console.log('top')
            }
            if (e.keyCode == 40) {
                //bottom
                console.log('bottom')
            }
        },
        changePlayerAnimation(numb) {
            this.activePlayerAnim = numb;
            axios.post(`https://eclipse/changePlayerAnimation`, {
                Anim: this.activePlayerAnim,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
        },
        sortByNames(array) {
            array.sort((a, b) => a.name.localeCompare(b.name));
        },
        findSelectedIndexByCategory(id) {
            let item = this.selectedPlayerItems.find(elem => elem.Category == id);
            if (!item) {
                item = {
                    Category: id,
                    Drawable: 0,
                    Texture: 0,
                }
            }
            return item;
        }
    },
    mounted() {
        if (this.sex) {
            this.result = this.groupByKey(this.testMaleClothList, "Category");
            this.accessories = this.groupByKey(this.allAccessoriesList, "Category")
        } else {
            this.result = this.groupByKey(this.testFemaleClothList, "Category");
            this.accessories = this.groupByKey(this.femAccessoriesList, "Category")
        }
        window.addEventListener("message", (event) => {
            if (event.data.type == "LoadCharacterCloth") {
                this.selectedPlayerItems = event.data.cloth;
                console.log(event.data.cloth)
            } 
        },false);
    },
    created() {
        window.addEventListener("keydown", this.doCommand);
        window.addEventListener('keydown', this.pressCtrl);
        window.addEventListener('keyup', this.unpressCtrl);
    },
    destroyed() {
        window.removeEventListener("keydown", this.doCommand);
        window.removeEventListener('keydown', this.pressCtrl);
        window.removeEventListener('keyup', this.unpressCtrl);
    },
};
</script>

