<template>
    <div class="app_fullscreen content_page app_taxi">
        <button class="app_close black" @click="closeApp"></button>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==1">
                
                <div class="app_logo"></div>
                <h4 class="app_name">Вызов такси</h4>
                <div class="text-center mt-3">
                    <button class="btn btn-block btn-taxi" @click="searchCar()">Обычный вызов</button>
                    <button class="btn btn-block btn-taxi" @click="changepage(4)">Подробный вызов</button>
                    <button v-if="isTaxi" class="btn btn-block btn-taxi" @click="changepage(5)">Список вызовов</button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==2">
                <div class="contact_heading">
                    <button @click="changepage(1)" class="blue_button_back black">Назад</button>
                </div>
                <div class="app_logo"></div>
                <div class="searching" v-show="searching">
                    <p class="text-center"><span></span>{{dots}}</p>
                </div>
                <div class="searching" v-show="!searching">
                    <p class="text-center">К сожалению, все<br>автомобили заняты.<br>Попробуйте позднее.</p>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==3">
                <div class="app_logo"></div>
                <p class="text-center">Машина найдена!</p>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page == 4">
                <div class="contact_heading">
                    <button @click="changepage(1)" class="blue_button_back black">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Подробный вызов</h4>
                    <p>Поставьте маркер на карте и вызовите такси. Таксист получит всю информацию о вызове заранее.</p>
                    <div class="text-center mt-3">
                        <button class="btn btn-taxi" @click="searchCar()">Вызвать</button>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2" v-if="page == 5">
                <div class="contact_heading">
                    <button @click="changepage(1)" class="blue_button_back black">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name mt-0">Вызовы</h4>
                    <div class="taxi_call_list scrolled">
                        <div class="taxi_call_item" v-for="(item, index) in callList" :key="'taxicall_' + index" :class="{free: !item.IsFree}">
                            <div class="d-flex justify-content-between">
                                <span class="time">Id: {{item.Id}}</span>
                                <span class="time">{{item.Time}}</span>
                            </div>
                            <h5>{{item.PlayerName}} {{item.PlayerPhone}}</h5>
                            <h6><span>До клиента</span>: {{item.MetresToClient}}m.</h6>
                            <h6><span>Длина маршрута</span>: {{item.MetresToDestination}}m.</h6>
                            <button class="btn btn-block" @click="readmore(index)">Подробнее</button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 active_call" v-if="page == 6">
                <div class="contact_heading">
                    <button @click="changepage(5)" class="blue_button_back black">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name mt-0" ><span>Активный вызов</span>: {{activeCall.Id}}</h4>
                    <h5><span>Дата: </span>{{activeCall.Time}}</h5>
                    <h5><span>Имя: </span>{{activeCall.PlayerName}}</h5>
                    <h5><span>Телефон: </span>{{activeCall.PlayerPhone}}</h5>
                    <h5><span>До клиента: </span>{{activeCall.MetresToDestination}} км.</h5>
                    <p><b>Начало маршрута:</b></p>
                    <h6>{{activeCall.ClientZoneText}}</h6>
                    <h6>{{activeCall.ClientStreetText}}</h6>
                    <p><b>Конец маршрута:</b></p>
                    <h6>{{activeCall.DestinationZoneText}}</h6>
                    <h6>{{activeCall.DestinationStreetText}}</h6>
                    
                </div>

                <div class="control">
                    <button class="btn btn_taxi" @click="takeCall()" v-if="activeCall.IsFree">Принять вызов</button>
                    <button class="btn btn_taxi" @click="showRoute()" v-if="activeCall.IsFree">Показать маршрут</button>
                    <button class="btn btn_taxi" @click="hideRoute()" v-if="activeCall.IsFree">Скрыть маршрут</button>
                    <button class="btn btn_taxi" @click="cancelRoute()" v-if="!activeCall.IsFree && activeCall.DriverPhone == playerData.phone">Отменить вызов</button>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'App_taxi',
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            searching: false,
            dots: '',
            dotsCount: 1,
            dotsActive: 10,
            count: 0,

            isTaxi: false,
            activeCall: {},
            callList: [
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    DestinationZoneText: "Центральный район",
                    DestinationStreetText: "Маленький сеул",
                    MetresToClient: '1200',
                    MetresToDestination: '500',
                    IsFree: false,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 0,
                },
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    DestinationZoneText: "Центральный район",
                    DestinationStreetText: "Маленький сеул",
                    MetresToClient: '1200',
                    MetresToDestination: '500',
                    IsFree: false,
                    Time: '2021-04-26 16:18:30',
                    Id: 1,
                },
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    DestinationZoneText: "Центральный район",
                    DestinationStreetText: "Маленький сеул",
                    MetresToClient: '1200',
                    MetresToDestination: '500',
                    IsFree: true,
                    Time: '2021-04-26 16:18:30',
                    Id: 2,
                },
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    DestinationZoneText: "Центральный район",
                    DestinationStreetText: "Маленький сеул",
                    MetresToClient: '1200',
                    MetresToDestination: '500',
                    IsFree: true,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 3,
                },
            ]
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.page = 1;
            this.$parent.whiteColor = true;
           
        },
        changepage(page) {
            this.page = page;
        },
        searchCar() {
            
            this.count = 0;
            this.searching = true;
            this.changepage(2);
            this.dotsChange();
            axios.post('https://eclipse/TaxiRequest', {
                PlayerName: this.$parent.playerData.name,
                PlayerPhone: this.$parent.$parent.playerData.phone
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        dotsChange() {
            this.count++;
            if (this.count < this.dotsActive) {
                if (this.dotsCount == 4) {
                    this.dotsCount = 0;
                    this.dots = '';
                } else {
                    this.dots = this.dots + '.';
                }
                this.dotsCount +=1;
                setTimeout(this.dotsChange, 1000);
            } else {
                this.carUnfinded();
            }
        },
        carFinded() {
            this.searching = false;
        },
        carUnfinded() {
            this.searching = false;
        },
        readmore(index) {
            this.changepage(6);
            this.activeCall = this.callList[index];
        },
        takeCall() {
            this.changepage(1);
            axios.post('https://eclipse_phone/TakeTaxiCall', {
                DriverPhone: this.playerData.phone,
                PlayerPhone: this.activeCall.PlayerPhone,
                Position: this.activeCall.ClientPosition,
                Id: this.activeCall.Id 
            }).then(() => { return; }).catch(function () { console.log() });  
            // Принимаем вызов this.activeCall - тут все данные вызова
        },
        showRoute() {
           axios.post('https://eclipse_phone/ShowTaxiRoute', {
                Position: this.activeCall.ClientPosition,
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        hideRoute() {
            // скрываем маршрут   
            axios.post('https://eclipse_phone/HideRoute', {
                
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        cancelRoute() {
            // отменяем маршрут
            this.changepage(1);
             axios.post('https://eclipse_phone/DeclineTaxiCall', {
                DriverPhone: this.playerData.phone,
                PlayerPhone: this.activeCall.PlayerPhone,
                Id: this.activeCall.Id 
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        getWork(job) {
            if(job == "taxi") {
                this.isTaxi = true;
            }
        }
    },
    mounted() {
        window.addEventListener(
            "message",
            (event) => {
                if (event.data.type == "TaxiFound") {
                    this.carFinded();
                    this.changepage(3);
                }
                else if(event.data.type == "TaxiNotFound") {
                    this.carUnfinded();
                }
                else if(event.data.type == "SetPlayerJob") {
                    this.getWork(event.data.job)
                }
                else if(event.data.type == "SendTaxiCallsToNui") {
                    this.callList = JSON.parse(event.data.calls);
                }
            },
            false
        );
        var check = this.playerData.roles.findIndex(item => item == "taxi");
        if (check != -1) {
            this.isTaxi = true;
        } else {
            this.isTaxi = false;
        }
        
    },
}

</script>

<style scoped>

</style>