<template>
    <section class="fullpage">
        <div class="shop shop_food">
            <div class="header">
                <div class="logo"></div>
                <div class="main_menu">

                    <button v-for="(item, index) in allFoodListData" :key="'menu_'+index" class="btn" :class="{active: page == index}" @click="changePage(index)">{{item.category}}</button>

                    <!-- <button class="btn" :class="{active: page == 0}" @click="changePage(0)">Еда</button>
                    <button class="btn" :class="{active: page == 1}" @click="changePage(1)">Вода</button>
                    <button class="btn" :class="{active: page == 2}" @click="changePage(2)">Алкоголь</button>
                    <button class="btn" :class="{active: page == 3}" @click="changePage(3)">Мясо</button>
                    <button class="btn" :class="{active: page == 4}" @click="changePage(4)">Табачные изделия</button>
                    <button class="btn" :class="{active: page == 5}" @click="changePage(5)">Товары для дома</button> -->


                </div>
            </div>
            <div class="body">


                <transition name="list" v-for="(el, index) in allFoodListData" :key="'category' + index">
                    <div class="page" v-show="page == index">
                        <div class="item" v-for="(item, index2) in el.items" :key="'food'+index2">
                            <div class="image" :style="'background-image: url('+ siteName +item.Image + ');'">
                            </div>
                            <h5 class="name">{{item.Name}}</h5>
                            <p class="description">{{item.Description}}</p>
                            <h5 class="price">${{item.Price}}</h5>
                            <button class="btn btn-block btn-add" @click="addToCart(item)">В корзину</button>
                        </div>
                    </div>
                </transition>

                <transition name="list">
                    <div class="page cart" v-show="page == 10">
                        <h3 class="noitems" v-show="cartList.length == 0">Корзина пуста</h3>
                        <div class="item_cart" v-for="(item, index) in cartList" :key="'cart' + index">
                            <div class="name">{{item.Name}}</div>
                            <div class="price">${{item.Price}}/шт.</div>
                            <div class="count">
                                <button class="btn-minus" @click="minusCount(index)"></button>
                                {{item.Count}}
                                <button class="btn-plus" @click="plusCount(index)"></button>
                            </div>
                            <div class="remove">
                                <button class="btn btn-remove" @click="remove(index)">Убрать</button>
                            </div>
                        </div>
                    </div>
                </transition>

            </div>
            <div class="footer">
                <div class="cart_block" v-show="page != 10">
                    <h4><span>${{fullPrice}}</span> ({{count}}шт.)</h4>
                    <button class="btn btn-block btn-cart" @click="changePage(10)">Корзина</button>
                </div>
                <div class="cart_block" v-show="page == 10">
                    <h4><span>${{fullPrice}}</span> ({{count}}шт.)</h4>
                    <button class="btn btn-block btn-cart" @click="openBuy()">Купить</button>
                </div>

            </div>

            <transition name="list">
                <div class="buy_window" v-show="buyWindow == true">
                    <div class="logo"></div>
                    <button class="btn btn-close" @click="closeBuy()">Закрыть</button>

                    <div class="buy_window_inner">
                        <h2>Выберите способ оплаты</h2>
                        <div class="left_buy">
                            <h3>Наличными</h3>
                            <div class="image"></div>
                            <button class="btn btn-block" @click="buyByCash()">Оплатить наличными</button>
                        </div>
                        <div class="right_buy">
                            <h3>Банковской картой</h3>
                            <div class="image"></div>
                            <button class="btn btn-block" @click="buyByCard()">Оплатить картой</button>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </section>
</template>

<script>

import axios from "axios";
export default {
    name: "ShopFood",
    components: {
    },
    props: {
        
    },
    data() {
        return {
            page: 0,
            allFoodListData: [],
            allFoodList: [],
            count: 0,
            cartList: [],
            fullPrice: 0,
            buyWindow: false,
            currentBusinesId: 0,
            siteName: "https://storage.googleapis.com/eclipse-rp/archive/",
            foodTypes: [
                "Еда",
                "Ключ",
                "Проп",
                "Document",
                "BluePrint",
                "Resource",
                "UsableItem",
                "Item",
                "Weapon",
                "Ammo",
                "AdminProp",
                "Water",
                "Alcohol",
                "Electronics",
                "Insect",
                "Zombie",
                "Loot",
                "Car",
                "Meat",
                "VegAndFruit",
                "BerAndMush",
                "Generator",
                "NewsBoard",
                "Radiation",
                "CarLift",
            ]
        }
    },
    methods: {
        changePage(index) {
            this.page = index;
        },
        addToCart(item) {
            var result = this.cartList.indexOf(item);
            if (result == -1) {
                item.Count = 1;
                this.cartList.push(item);
            } else {
                this.cartList[result].Count += 1;
            }
            this.calculate();
        },
        calculate() {
            this.fullPrice = 0;
            this.count = 0;
            
            this.cartList.forEach(item => {
                this.fullPrice += (item.Price * item.Count);
                this.count += item.Count;

            })
        },
        plusCount(index) {
            this.cartList[index].Count += 1;
            this.calculate();
        },
        minusCount(index) {
            console.log('test')
            this.cartList[index].Count -= 1;
            if (this.cartList[index].Count == 0) {
                this.cartList.splice(index, 1);
            }
            this.calculate();
        },

        remove(index) {
            this.cartList.splice(index, 1);
            this.calculate();
        },
        openBuy() {
            if (this.cartList.length > 0) {
                this.buyWindow = true;
            }
        },
        closeBuy() {
            this.buyWindow = false;
        },
        buyByCash() {
            console.log(this.cartList)
            // ПокупОчка, надо проверить всё успешно или нет, а потом уже запускать дальнейший код
            axios.post('https://eclipse/BuyProducts', {
                    Products: this.cartList,
                    BusinessId: this.currentBusinesId
            }).then(() => { return; }).catch(function () { console.log() });
            console.log('Оплата наличными');
            this.buyWindow = false;
            this.cartList = [];
            this.calculate();
        },
        buyByCard() {
            console.log(this.cartList)
            axios.post('https://eclipse/BuyProducts', {
                    Products: this.cartList,
                    BusinessId: this.currentBusinesId
            }).then(() => { return; }).catch(function () { console.log() });
            // ПокупОчка, надо проверить всё успешно или нет, а потом уже запускать дальнейший код
            console.log('Оплата картой');
            this.buyWindow = false;
            //this.cartList = [];
            this.calculate();
        },
        groupByKey(xs, key) {
            return xs.reduce(function(rv, x) {
                (rv[x[key]] = rv[x[key]] || []).push(x);
                return rv;
            }, {});
        },
        showName(index) {
            if (this.foodTypes[index]) {
                return this.foodTypes[index];
            } else {
                return "не найдено"
            }
        },
        testData() {
        }
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadShopItems") {
                this.currentBusinesId = event.data.BusinessId;
                this.allFoodListData = event.data.allFoodList;
                console.log(JSON.stringify(this.allFoodListData))
            }
        },false);

        // this.testData();
    },
   
}
</script>