<template>
    <section class="playerHud">
        <Documents :playerData="playerData" />
        
        <div class="stats_bar d-none" :style="'opacity: '+ Opacity">
            <div class="current_characteristics">
                <div class="icon_group icon_health">
                    <i class="custom_icon custom_icon_health"></i>
                    {{playerStatData.Health}}
                </div>
                <div class="icon_group icon_armor">
                    <i class="custom_icon custom_icon_armor"></i>
                    {{playerStatData.Armor}}
                </div>
                <div class="icon_group icon_thirst">
                    <i class="custom_icon custom_icon_thirst"></i>
                    {{playerStatData.Thirst}}
                </div>
                <div class="icon_group icon_hunger">
                    <i class="custom_icon custom_icon_hunger"></i>
                    {{playerStatData.Hunger}}
                </div>
                <div class="icon_group icon_cigarette">
                    <i class="custom_icon custom_icon_cigarette"></i>
                    {{playerStatData.Cigarette}}
                </div>
            </div>
            <div class="current_cashmoney">
                ${{getPrice(playerStatData.Cash)}}
            </div>
            <div class="current_gametime">
                {{currentHour}} : {{currentMinutes}}
            </div>
        </div>

        <div class="stats_classic d-flex" v-if="playerData.playerPadData.ShowHud == true">
            <b-progress class="bg-food flex-fill" max="100" >
                <b-progress-bar class="" :value="currentHunger"></b-progress-bar>
            </b-progress>
            <b-progress class="bg-water flex-fill" max="100" >
                <b-progress-bar class="" :value="currentThirst"></b-progress-bar>
            </b-progress>
            <b-progress class="bg-smoke flex-fill" max="100" >
                <b-progress-bar class="" :value="currentCigarette"></b-progress-bar>
            </b-progress>
        </div>

        <div class="tips player_tips" v-if="playerData.playerPadData.ShowTips == true">
            <div class="tip" v-for="(item,index) in tips" :key="index+ 'tip'">
                <button class="button-design">{{item.Button}}</button>
                <span>{{item.Name}}</span>
            </div>
        </div>
    </section>
</template>

<script>
import Documents from './other/documents.vue'
require('@/assets/css/hud.css')

export default {
    name: 'PlayerHud',
    components: {
        Documents
    },
    props: {
        playerData: Object,
    },
     data() {
        return {
            playerStatData: this.playerData.playerStatData,
            Opacity: 1,
            red: "red",

            tips: [
                {
                    Name: "Взаимодействие",
                    Button: "E"
                },
                {
                    Name: "Телефон",
                    Button: "M"
                },
                {
                    Name: "Сброс анимаций",
                    Button: "F1"
                },
                {
                    Name: "Анимации",
                    Button: "F2"
                },
                {
                    Name: "Админка",
                    Button: "F3"
                },
                {
                    Name: "Планшет",
                    Button: "F4"
                },
                {
                    Name: "Мышь",
                    Button: "~"
                },
                {
                    Name: "Инвентарь",
                    Button: "I"
                },
            ],
            carTips: [
                {
                    Name: "Выйти",
                    Button: "F"
                },
                {
                    Name: "Свет",
                    Button: "G"
                }
            ]
        }
    },
    computed: {
        currentHour() {
            return ('0' + this.playerStatData.Hours).slice(-2);
        },
        currentMinutes() {
            return ('0' + this.playerStatData.Minutes).slice(-2);
        },
        currentHunger() {
            return this.playerStatData.Hunger;
        },
        currentThirst() {
            var thirst = this.playerStatData.Thirst;
            return thirst;
        },
        currentCigarette() {
            var cigarette = this.playerStatData.Cigarette;
            return cigarette;
        }
    },
    methods: {
        getPrice(numb) {
            var elem = String(numb);
            var result = elem;
            return result.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        },
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if(event.data.type == "LoadPlayerData") {
                this.playerStatData = JSON.parse(event.data.playerStatData);
            }
        },false);
    },
}

</script>

