<template>
    <section class="page">
        <div class="page" v-show="page==0">
            <div class="card">
                <h3 class="mb-3">Управление бизнесами</h3>
                <div class="row">
                    <div class="col-4 pr-1">
                        <button class="btn btn-primary btn-block" @click="changePage(1)">Список бизнесов</button>

                    </div>
                    <div class="col-4 pl-2 pr-2">
                        <button class="btn btn-primary btn-block" @click="changePage(2)">Задания</button>
                    </div>
                    <div class="col-4 pl-1">
                        <button class="btn btn-primary btn-block" @click="changePage(3)">Заявки</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="page data_pages" v-show="page==1">
            <div class="card scrolled">
                <div class="row">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(0)">Назад</button>
                    </div>
                    <div class="col-9 filters">
                        <div class="search_bar d-flex justify-content-end">
                            <input placeholder="поиск" class="form-control search" v-model="searchFieldModel" @input="searchBusiness()">
                        </div>
                    </div>
                </div>
                <div class="data_element" v-show="item.show" v-for="(item, index) in businessList" :key="'business_'+ index" @click="showFullBusiness(index)">
                    <div style="width: 70%" class="">
                        {{item.Name}}
                    </div>
                    <div style="width: 20%" class="">
                        {{item.BusinessType}}
                    </div>
                    <div style="width: 10%" class="">
                        {{item.OwnerId}}
                    </div>
                </div>
            </div>
        </div>

        <div class="page data_pages" v-show="page==2">
            <div class="card scrolled">
                <div class="row">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(0)">Назад</button>
                    </div>
                    <div class="col-9 filters">
                        <div class="search_bar d-flex justify-content-end">
                            <input placeholder="поиск" class="form-control search" v-model="searchFieldModel" @input="searchBusiness()">
                        </div>
                    </div>
                </div>
                <div class="data_element" v-show="item.show" :class="'elemestatus_'+item.Status" v-for="(item, index) in businessMissionsList" :key="'bsn_mis_'+ index" @click="showFullMission(item.Id)">
                    <div style="width: 10%" class="">
                        {{item.Id}}
                    </div>
                    <div style="width: 90%" class="">
                        {{item.Name}}
                    </div>
                </div>
            </div>
        </div>

        <div class="page data_pages" v-show="page==3">
            <div class="card scrolled">
                <div class="row">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(0)">Назад</button>
                    </div>
                    <div class="col-9 filters">
                        <div class="search_bar d-flex justify-content-end">
                            <input placeholder="поиск" class="form-control search" v-model="searchFieldModel" @input="searchBusiness()">
                        </div>
                    </div>
                </div>
                <div class="data_element" v-show="item.show" :class="'elemestatus_'+item.Status" v-for="(item, index) in businessRequestList" :key="'bsn_req_'+ index" @click="showFullRequest(item.Id)">
                    <div style="width: 10%" class="">
                        {{item.Id}}
                    </div>
                    <div style="width: 60%" class="">
                        {{item.Name}}
                    </div>
                    <div style="width: 30%" class="text-right">
                        {{getDate(item.Date)}}
                    </div>
                </div>
            </div>
        </div>

        <div class="page" v-show="page==100">
            <div class="card scrolled">
                <div class="row mb-3">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(1)">Назад</button>
                    </div>
                    <div class="col-9 d-flex align-items-center justify-content-end">
                        <h3 class="copying playername"><b>Бизнес:</b> {{activeBusiness.Name}}</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-8">
                        <p class="mb-0 copying"><b>Тип бизнеса:</b> {{activeBusiness.BusinessType}}</p>
                        <p class="mb-0 copying"><b>Id владельца:</b> {{activeBusiness.OwnerId}}</p>
                        <p class="mb-0 copying"><b>Координаты:</b> {{activeBusiness.Position}}</p>
                        <p class="mb-0 copying">
                            <b>Сотрудники: </b>
                            <span v-for="(employee, index) in activeBusiness.Employees" :key="activeBusiness.OwnerId + '_role' + index">({{employee.Id}}) {{employee.Name}}<span v-if="index < activeBusiness.Employees.length - 1">, </span></span>
                            <button class="ml-1 btn btn-outline-primary btn-esm" @click="editPlayerRoles()">Изменить</button>
                        </p>
                    </div>
                    <div class="col-4">
                        <button class="btn btn-primary btn-block" @click="adminCallCommand(0)">ТП в бизнес</button>
                    </div>
                </div>

                <h3 class="mt-3 mb-2">Задания:</h3>
                <div class="data_element d-flex" v-for="(mission, index) in activeBusiness.Missions" :key="index + 'tasks'" @click="showFullMission(mission.Id)">
                    <div style="width: 10%;"> Id: {{mission.Id}} </div>
                    <div style="width: 35%;"> {{mission.Name}} </div>
                    <div style="width: 35%;"> {{mission.Type}} </div>
                    <div class="text-right" style="width: 20%;"> Активный </div>
                </div>

                <h3 class="mt-3 mb-2">Заявки:</h3>
                <div class="data_element d-flex" v-for="(request, index) in activeBusiness.Requests" :key="index + 'request'" @click="showFullRequest(request.Id)">
                    <div style="width: 10%;"> Id: {{request.Id}} </div>
                    <div style="width: 35%;"> {{request.Name}} </div>
                    <div style="width: 35%;"> {{request.Type}} </div>
                    <div class="text-right" style="width: 20%;"> Активный </div>
                </div>

            </div>
        </div>

        <div class="page" v-show="page==200">
            <div class="card scrolled">
                <div class="row mb-3">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(2)">Назад</button>
                    </div>
                    <div class="col-9 d-flex align-items-center justify-content-end">
                        <h3 class="copying playername"><b>Миссия ID </b>{{activeMission.Id}}</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-8">
                        <p class="mb-0 copying"><b>Описание:</b> {{activeMission.Description}}</p>
                        <p class="mb-0 copying"><b>Id бизнеса:</b> {{activeMission.BusinessId}}</p>
                        <p class="mb-0 copying"><b>Название бизнеса:</b> {{activeMission.BusinessName}}</p>
                        <p class="mb-0 copying"><b>Тип:</b> {{activeMission.Type}}</p>
                        <p class="mb-0 copying"><b>Награда:</b> {{activeMission.Cost}}$</p>
                        
                    </div>
                    <div class="col-4">
                        <button class="btn btn-primary btn-block" @click="returnToBusiness(activeMission.BusinessId)">К бизнесу</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="page" v-show="page==300">
            <div class="card scrolled">
                <div class="row mb-3">
                    <div class="col-3">
                        <button class="btn btn-primary btn-block" @click="changePage(3)">Назад</button>
                    </div>
                    <div class="col-9 d-flex align-items-center justify-content-end">
                        <h3 class="copying playername"><b>Запрос ID </b>{{activeRequest.Id}}</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-8">
                        <p class="mb-0 copying"><b>Описание:</b> {{activeRequest.Description}}</p>
                        <p class="mb-0 copying"><b>Id бизнеса:</b> {{activeRequest.BusinessId}}</p>
                        <p class="mb-0 copying"><b>Название бизнеса:</b> {{activeRequest.BusinessName}}</p>
                    </div>
                    <div class="col-4">
                        <button class="btn btn-primary btn-block" @click="returnToBusiness(activeRequest.BusinessId)">К бизнесу</button>
                    </div>
                </div>
            </div>
        </div>


        <div class="card popup_window" v-show="showEditEmployeesWindow">
            <div class="popup_window_inner fps">
                <div class="d-flex justify-content-between">
                    <h3 class="">Сотрудники: <b>{{activeBusiness.Name}}</b></h3>
                    <button class="btn btn-secondary pr-3 pl-3" @click="showEditEmployeesWindow = false">Закрыть</button>
                </div>
                <div class="row" v-for="(employee, index) in activeBusiness.Employees" :key="'empl_window_' + index">
                    <div class="col-4 player_role mb-1">
                        {{employee.Name}}
                    </div>
                    <div class="col-4">
                        <button class="btn btn-outline-secondary btn-esm" @click="removeEmployeeFromBusiness(index)">Уволить</button>
                    </div>
                </div>
            </div>
        </div>

        

    </section>
</template>

<script>

export default {
    name: "BusinessPanel",
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 0,
            businessList: [],
            businessMissionsList: [],
            businessRequestList: [],

            activeBusiness: {},
            searchFieldModel: "",
            showEditEmployeesWindow: false,

            activeMission: {},
            searchMissionModel: "",

            activeRequest: {},
            searchRequestModel: "",


        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
                this.closeAllModals();
            }
        },
        showFullBusiness(numb) {
            this.activeBusiness = this.businessList[numb];
            this.changePage(100);
        },
        closeAllModals() {
            this.showEditEmployeesWindow = false;
        },
        editPlayerRoles() {
            this.showEditEmployeesWindow = true;
        },
        searchBusiness() {
            var searchText = this.searchFieldModel;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.businessList.forEach((element) => {
                    var string = element.Name
                        + element.OwnerId
                        + element.BusinessType
                    var result = string.search(exp);
                    if (result == -1) {
                        element.show = false;
                    } else {
                        element.show = true;
                    }
                });
            } else {
                this.showAllArray(this.businessList);
            }
        },
        showAllArray(array) {
            array.forEach(element => {
                element.show = true;
            });
        },
        adminCallCommand(command) {
            switch (command) {
                case 0:
                    // Тп в бизнес
                    break;
                default:
                    break;
            }
        },
        removeEmployeeFromBusiness(index) {
            index
            // номер сотрудника

            this.activeBusiness
            // выбранный бизнес
        },
        generateLists() {
            this.businessList.forEach(business => {
                business.Missions.forEach(mission => {
                    mission.show = true;
                    this.businessMissionsList.push(mission);
                });
                business.Requests.forEach(request => {
                    request.show = true;
                    this.businessRequestList.push(request);
                });
            });
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        showFullRequest(id) {
            this.activeRequest = this.businessRequestList.find(item => item.Id == id);
            this.changePage(300);

        },
        showFullMission(id) {
            this.activeMission = this.businessMissionsList.find(item => item.Id == id);
            this.changePage(200);
        },
        returnToBusiness(id) {
            let index = this.businessList.findIndex(item => item.Id == id)
            this.showFullBusiness(index);
        },

        testData() {
            this.businessList = [
                {
                    Name: "Аммуниция в палетобей",
                    BusinessType: "WeaponShop",
                    Position: "-453.44 351.1123 21.24",
                    StockData: [
                        {
                            Name: "Пистолеты",
                            Count: "20"
                        }
                    ],
                    OwnerId: "0",
                    Id: 11,
                    Bank: "0",
                    TaxPercent: "5",
                    Pirce: "1000",
                    Employees: [
                        {
                            Id: "0",
                            Name: "Alex Alexandro",
                        }
                    ],
                    Missions: [
                        {
                            "Id": 0,
                            "Name": "Дальнобойщик",
                            "Type": "Standart",
                            "Cost": "6000",
                            "ExpTime": "Date",
                            "Route": "11",
                            "PerformingId": "1",
                            "BusinessId": "11",
                            "BusinessName": "Аммуниция Vinewood",
                            "Description": "Ваша задача доставить большой груз из точки А в точку B. Чем быстрее будет выполнена задача, тем больше награду вы получите.",
                        },
                        {
                            "Id": 1,
                            "Name": "Дальнобойщик",
                            "Type": "Standart",
                            "Cost": "6000",
                            "ExpTime": "Date",
                            "Route": "11",
                            "PerformingId": "3",
                            "BusinessId": "11",
                            "BusinessName": "Аммуниция Vinewood",
                            "Description": "Ваша задача доставить большой груз из точки А в точку B. Чем быстрее будет выполнена задача, тем больше награду вы получите.",
                        }
                    ],
                    Requests: [
                        {
                            "Id": "0",
                            "Name": "Требуется помощь бизнесу!",
                            "Description": "Срочно требуется 100 калашей и всё такое",
                            "IsReady": "0",
                            "BusinessId": "11",
                            "BusinessName": "Аммуниция Vinewood",
                            "OwnerId": "1",
                            "OwerName": "Douglas Prod",
                            "Date": "Sat Oct 22 2022 16:55:42 GMT+0300 (Москва, стандартное время)"
                        }
                    ],
                    show: false
                }
            ];
            this.showAllArray(this.businessList);
            this.generateLists()
        },
    },
    mounted() {
        

        this.testData();
    }
}
</script>
