<template>
    <div class="app_fullscreen content_page app_mechanic">

        <button class="app_close" @click="closeApp()"></button>

        <transition name="transition_03">
            <div class="content_page_l2" v-show="page==1">
                <div class="lsc_logo">

                </div>
                <h4 class="app_name">LS Customs</h4>
                <div class="main_menu">
                    <button class="btn btn_mech" @click="changePage(2)">Вызвать механика</button>
                    <button v-if="isMechanic" class="btn btn_mech" @click="changePage(4)">Список вызовов</button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page==2">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back white">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Вызов механика</h4>
                    <p>Введите описание</p>
                    <textarea type="text" rows="5" maxlength="140" v-model="message"></textarea>
                    <button class="btn btn_mech" @click="sendTicket()">Отправить</button>
                </div>
                    
                <div class="control">
                    
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page == 3">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back white">Назад</button>
                </div>
                <div class="content mt-3">
                    <p>Меахник успешно вызван</p>
                </div>
                    
                <div class="control">
                    
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page == 4">
                <div class="contact_heading">
                    <button @click="changePage(1)" class="blue_button_back white">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name">Список вызовов</h4>
                    <div class="mechanic_call_list scrolled">
                        <div class="mechanic_call_item" v-for="(item, index) in callList" :key="'mechacall_' + index" :class="{free: !item.IsFree}">
                            <div class="d-flex justify-content-between">
                                <span class="time">Id: {{item.Id}}</span>
                                <span class="time">{{item.Time}}</span>
                            </div>
                            <h5>{{item.PlayerName}} {{item.PlayerPhone}}</h5>
                            <h6><span>Дистанция</span>: {{item.MetresToClient}}m.</h6>
                            <button class="btn btn_mech" @click="readmore(index)">Подробнее</button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 active_call" v-show="page == 5">
                <div class="contact_heading">
                    <button @click="changePage(4)" class="blue_button_back white">Назад</button>
                </div>
                <div class="content">
                    <h4 class="app_name mt-0" ><span>Вызов</span> №{{activeCall.Id}}</h4>
                    <h5><span>Время: </span>{{activeCall.Time}}</h5>
                    <h5><span>Имя: </span>{{activeCall.PlayerName}}</h5>
                    <h5><span>Телефон: </span>{{activeCall.PlayerPhone}}</h5>
                    <h5><span>Дистаниция: </span>{{activeCall.MetresToClient}}<span class="units">м.</span></h5>
                    <p><b>Адрес маршрута</b></p>
                    <h6>{{activeCall.ClientZoneText}}</h6>
                    <h6>{{activeCall.ClientStreetText}}</h6>
                    <p><b>Сообщение</b></p>
                    <h6>{{activeCall.message}}</h6>
                </div>

                <div class="control">
                    <button class="btn btn_mech" @click="takeCall()" v-if="activeCall.IsFree">Принять вызов</button>
                    <button class="btn btn_mech" @click="showRoute()" v-if="activeCall.IsFree">Показать маршрут</button>
                    <button class="btn btn_mech" @click="hideRoute()" v-if="activeCall.IsFree">Скрыть маршрут</button>
                    <button class="btn btn_mech" @click="cancelRoute()" v-if="!activeCall.IsFree && activeCall.DriverPhone == playerData.phone">Отменить вызов</button>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'App_mechanic',
    components: {
        
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            amount: "",
            phoneNumber: "",
            isMechanic: true,
            message: "",
            activeCall: {},
            callList: [
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    MetresToClient: '1200',
                    IsFree: true,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 0,
                    message: "kek hi"
                },
                {
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    MetresToClient: '1200',
                    IsFree: false,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 0,
                    message: "kek hi"
                },{
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    MetresToClient: '1200',
                    IsFree: false,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 0,
                    message: "kek hi"
                },{
                    PlayerName: "Jessie Pinkman",
                    PlayerPhone: "400-4567",
                    ClientZoneText: "Район Пушкина",
                    ClientStreetText: "Улица Колотушкина",
                    MetresToClient: '1200',
                    IsFree: false,
                    Time: '2021-04-26 16:18:30',
                    DriverPhone: '000-0000',
                    Id: 0,
                    message: "kek hi"
                }
            ]
        }
    },
    methods: {
        closeApp() {
            this.$parent.page = 2;
            this.page = 1;
        },
        changePage(page) {
            this.page = page;
        },
        sendTicket() {
            this.changePage(3);
            // Отправляем на бэк
             axios.post('https://eclipse_phone/MechanicRequest', {
                PlayerName: this.$parent.playerData.name,
                PlayerPhone: this.$parent.playerPhone,
                message: this.message
            }).then(() => { return; }).catch(function () { console.log() });  
            this.message = "";
        },
        readmore(index) {
            this.activeCall = this.callList[index];
            this.changePage(5);
        },
        takeCall() {
            this.changePage(1);
            axios.post('https://eclipse_phone/TakeMechanicCall', {
                DriverPhone: this.playerData.phone,
                PlayerPhone: this.activeCall.PlayerPhone,
                Position: this.activeCall.ClientPosition,
                Id: this.activeCall.Id
            }).then(() => { return; }).catch(function () { console.log() });  
            // Принимаем вызов this.activeCall - тут все данные вызова
        },
        showRoute() {
           axios.post('https://eclipse_phone/ShowMechanicRoute', {
                Position: this.activeCall.ClientPosition,
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        hideRoute() {
            // скрываем маршрут   
            axios.post('https://eclipse_phone/HideRoute', {
                
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        cancelRoute() {
            // отменяем маршрут
            this.changePage(1);
             axios.post('https://eclipse_phone/DeclineMechanicCall', {
                DriverPhone: this.playerData.phone,
                PlayerPhone: this.activeCall.PlayerPhone,
                Id: this.activeCall.Id 
            }).then(() => { return; }).catch(function () { console.log() });  
        },
        getWork(job) {
            if(job == "mechanic") {
                this.isMechanic = true;
            }
        }
    },
    mounted() {
        window.addEventListener(
            "message",
            (event) => {
                if(event.data.type == "SetPlayerJob") {
                    this.getWork(event.data.job)
                }
                else if(event.data.type == "SendMechanicCallsToNui") {
                    this.callList = JSON.parse(event.data.calls);
                }
            },
            false
        );
    }
}
</script>