<template>
    <transition name="transition_02">
        <div class="dialog dialog_drugs" v-show="showThis">
            <div class="dialog_inner scrolled" id="messageBlock">
                <transition-group name="list">
                    <div class="dialog_message_outer" :class="{left: !message.Player}" v-for="(message, index) in dialogRenderArray" :key="'message' + index">
                        <div class="dialog_message">
                            <h5>{{message.Author}}:</h5>
                            <p>{{message.Text}}</p>
                        </div>
                    </div>
                </transition-group>
            </div>
            <transition name="list">
                <div class="dialog_buttons" v-show="!onAnimation">
                    <button :disabled="onAnimation" class="btn btn-primary mx-1" v-for="(button, index) in activeMessage.Answers" :key="'btn_' + index" @click="runCommand(button.Command, button.Text, button.FullText, button.Id, button.Price)">{{button.Text}}</button>
                </div>
            </transition>
        </div>
    </transition>
</template>

