    },
    methods: {
        runCommand(command, text, fulltext, id, price) {

            if (command == "GoTo") {
                var message = {};
                message.Author = "Вы";
                if (fulltext) {
                    message.Text = fulltext;
                } else {
                    message.Text = text;
                }
                message.Player = true;
                this.dialogRenderArray.push(message);
                this.scrollToEnd();
                this.nextMove(id)
            }
            if (command == "StartMission") {
                var Mission = {};
                Mission.Id = id;
                Mission.Name = command;
                console.log('it start mission')
                axios.post('https://eclipse/StartMission', {
                    MissionName: id,
                    Price: price
                }).then(() => { return; }).catch(function () { console.log() });
                //this.exitDialog();
            }
            if (command == "Exit") {
                this.exitDialog();
            }

            
        },
        startMove() {
            this.dialogRenderArray = [];
            this.activeMessage = this.messages[0];
            this.dialogRenderArray.push(this.activeMessage)
        },
        nextMove(id) {
            this.onAnimation = true;
            setTimeout(() => {
                this.activeMessage = this.messages.find(item => item.Id == id);
                this.dialogRenderArray.push(this.activeMessage);
                this.scrollToEnd();
                this.onAnimation = false;
            }, this.fadeTime);
        },
        scrollToEnd() {
            var scrollElement = this.$el.querySelector("#messageBlock");
            this.$nextTick(() => {
                scrollElement.scrollTop = scrollElement.scrollHeight;
            });
        },
        exitDialog() {
            this.showThis = false;
            setTimeout(() => {
                axios.post('https://eclipse/CloseInformatorDialog', {
                    }).then(() => { return; }).catch(function () { console.log() });
                this.$parent.showDialog = "";
            }, this.fadeTime);
        
        }
    },
    mounted() {
        // this.showThis = true;
        // this.startMove();
        window.addEventListener("message", (event) => {
            if (event.data.type == "SendInformatorAnswer") {
                this.nextMove(event.data.answer);
            }
            else if(event.data.type == "LoadDialogData") {
                this.messages = event.data.messages;
                this.showThis = true;
                this.startMove();
            }
        },false);
    }
}
