<template>
    <section class="band" v-if="gang != 0">
        <div class="gang" :class="{gang_families: gang==1, gang_ballas: gang==2}" >
            <div class="person"></div>
            <div class="content">
                <div class="logo"></div>
                <div class="top">
                    <h2>The Families</h2>
                    <div class="menu_top">
                        <div class="button" :class="{active: page == 1}" @click="changePage(1)">Банда</div>
                        <div class="button" :class="{active: page == 2}" @click="changePage(2)">Участники</div>
                        <div class="button" :class="{active: page == 3}" @click="changePage(3)">Управление</div>
                    </div>
                </div>

                <div class="page" v-if="page == 1">
                    <h3>Банда</h3>
                    <p>The Families love sports almost as much as they love banging. Flashy, arrogant, competitive, and aggressive; they are life-long bitter enemies of the Ballas.</p>
                </div>

                <div class="page" v-if="page == 2">
                    <h3>Участники</h3>
                    <div class="player_list">
                        <div class="player_item" v-for="(player, index) in playerList" :key="'pl_'+ index">
                            <div class="name">{{player.Name}} {{player.Surname}}</div>
                            <div class="phone">{{player.Phone}}</div>
                            <div class="rank">
                                <div class="left">{{getRank(player.Rank)}}</div>
                                <div class="right">
                                    <button class="btn up" :disabled="player.Rank >= ranks.length - 1" @click="upPlayerRank(player)"></button>
                                    <button class="btn down" :disabled="player.Rank == 0" @click="downPlayerRank(player)"></button>
                                </div>
                            </div>
                            <button class="btn kick" @click="kickPlayerFromGang(player)">Выгнать</button>
                        </div>
                        <button class="btn btn-block invite" @click="showModalWindowA()">Пригласить участника</button>
                    </div>
                </div>

                <div class="page" v-if="page == 3">
                    <h3>Управление</h3>
                    <div class="rank_list">
                        <div class="rank_item" v-for="(item, index) in newRanks" :key="'rank_' + index">
                            <div class="name">
                                Ранг {{index + 1}}
                            </div>
                            <div class="input">
                                <input type="text" class="form-control" v-model="newRanks[index]">
                            </div>
                            <div class="delete">
                                <button class="btn btn-delete btn-block" @click="removeRank(index)">Удалить</button>
                            </div>
                        </div>
                        <button class="btn btn-block" @click="addNewRank()">Добавить ранг</button>
                        <button class="btn btn-block btn-info" @click="saveChanges()">Сохранить все изменения</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal modal_gang" v-if="showModalA">
            <div class="modal_inner">
                <h5 class="py-0 mb-3">Пригласить участника</h5>
                <input class="form-control" placeholder="Веедите номер телефона" type="text" v-model="addNewPlayerPhoneModel">
                <div class="controls">
                    <button class="btn btn-success mr-3" @click="inviteNewPlayer()">Пригласить</button>
                    <button class="btn btn-danger" @click="showModalA = false">Закрыть</button>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
require('@/assets/css/gangs.css')
import axios from 'axios';
export default {
    name: "BandComponent",
    props: {
        playerData: Object,
    },
    data() {
        return {
            gang: 1,
            page: 1,
            playerList: [
                {
                    Name: "Douglas",
                    Surname: "Prod",
                    Id: 1,
                    Phone: 8005555,
                    Rank: 4,
                },
                {
                    Name: "Admino",
                    Surname: "Servero",
                    Id: 2,
                    Phone: 8005552,
                    Rank: 0,
                }
            ],
            ranks: [
                "Assosiate",
                "Soldier",
                "Caporegime",
                "Underboss",
                "Consigliere",
                "Boss",
            ],
            newRanks: [],
            showModalA: false,
            addNewPlayerPhoneModel: "",
        }
    },
    methods: {
        changePage(numb) {
            if (this.page!=numb) {
                this.page = numb;
                this.newRanks = this.ranks.slice(0);
            }
        },
        getRank(numb) {
            let rank = this.ranks[numb];
            if (!rank) rank = "не найдено"
            return rank;
        },
        upPlayerRank(player) {
            let maxRank = this.ranks.length;
            if (player.Rank < maxRank - 1)
            player.Rank += 1;
            // повышаем ранг на бэке
            axios.post(`https://eclipse/UpRank`, {
                phone: player.Phone,
                rank: player.Rank,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
        },
        downPlayerRank(player) {
            if (player.Rank > 0)
            player.Rank -= 1;
            // понижаем ранг на бэке
            axios.post(`https://eclipse/DownRank`, {
                phone: player.Phone,
                rank: player.Rank,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
        },
        kickPlayerFromGang(player) {
            console.log('kick player ', player)
            player
            axios.post(`https://eclipse/kickPlayer`, {
                phone: player.Phone,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
            // удаляем игрока на бэке
        },
        showModalWindowA() {
            this.showModalA = true;
            this.addNewPlayerPhoneModel = "";
        },
        inviteNewPlayer() {
            this.addNewPlayerPhoneModel
            // Номер приглашаемого игрока
            console.log(this.addNewPlayerPhoneModel)
            axios.post(`https://eclipse/inviteToBand`, {
                phone: this.addNewPlayerPhoneModel,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
            this.showModalA = false;
        },
        addNewRank() {
            let size = this.newRanks.length
            let newsize = size + 1
            this.$set(this.newRanks, size, 'Ранг ' + newsize);
        },
        removeRank(index) {
            this.newRanks.splice(index, 1);
        },
        saveChanges() {
            this.ranks = this.newRanks.slice(0);
            console.log(this.ranks)
            axios.post(`https://eclipse/UpdateRanks`, {
                ranks: this.ranks,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
            // Надо перезаписать ранги на бэке
        }
    },
    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "OpenBand") {
                console.log(event.data.playerList)
                this.playerList = event.data.playerList;
                
            }
            if (event.data.type == "SaveRanks") {
                this.ranks = JSON.parse(event.data.ranks);
            }
        }, false);
    }
}
</script>