<template>
    <transition name="list">
        <div v-if="showThis" class="item_changingbag">

                <div class="page" v-if="page == 0">
                    <h3>Сумка с одеждой</h3>
                    <button class="btn btn-secondary btn-block btn-close" v-for="(item, index) in clothSets" :key="'set_'+index" @click="chooseSet(index)">{{item.Name}}</button>
                    <button class="btn btn-primary btn-block btn-close" @click="changePage(1)" v-if="clothSets.length < maxSize">Новый комплект</button>
                    <button class="btn btn-danger btn-block btn-close" @click="closeThisItem()">Закрыть сумку</button>
                </div>

                <div class="page" v-if="page == 1">
                    <h3>Новый комплект</h3>
                    <div class="form-group">
                                <label class="st_label">Название комплекта:</label>
                                <input type="text" class="form-control" v-model="newSetName">
                            </div>
                    <button class="btn btn-success btn-block btn-close" @click="saveNewSet()">Сохранить</button>
                    <button class="btn btn-primary btn-block btn-close" @click="changePage(0)">Назад</button>
                </div>

                <div class="page" v-if="page == 3">
                    <h3>{{activeItem.Name}}</h3>
                    <button class="btn btn-secondary btn-block btn-close" @click="wearSet()">Надеть</button>
                    <button class="btn btn-danger btn-block btn-close" @click="changePage(4)">Удалить</button>
                    <button class="btn btn-primary btn-block btn-close" @click="changePage(0)">Назад</button>
                </div>

                <div class="page" v-if="page == 4">
                    <h4>Вы уверены в удалении?</h4>
                    <h5>{{activeItem.Name}}</h5>
                    <button class="btn btn-danger btn-block btn-close" @click="deleteSet()">Удалить</button>
                    <button class="btn btn-primary btn-block btn-close" @click="changePage(3)">Назад</button>
                </div>

        </div>
    </transition>
</template>

<script>
import axios from "axios";
export default {
    name: 'changingBag',
    props: {
        playerData: Object,
    },
    data() {
        return {
            showThis: false,
            page: 0,

            clothSets: [
                
            ],
            maxSize: 5,
            activeItem: undefined,
            newSetName: "",
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
            }
            this.newSetName = "";
        },
        closeThisItem() {
            axios.post('https://eclipse/Tilda', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.showThis = false;
        },
        doCommand(e) {
            if(e.keyCode == 27 && this.showThis) {
                this.closeThisItem();
            }
        },
        saveNewSet() {
            if (3 <= this.newSetName.length && this.newSetName.length <= 20) {
                let Name = this.newSetName
                axios.post('https://eclipse/SaveNewSet', {
                    Name: Name
                }).then(() => { return; }).catch(function () { console.log() });
                this.changePage(0);
            } else {
                // высираем "Название должно содержать от 3 до 20 символов"
            }
           
        },
        chooseSet(index) {
            this.activeItem = this.clothSets[index];
            this.changePage(3);
        },
        wearSet() {
            // надеваем this.activeItem
            // высираем "комплект 1 успешно надет, всё такое"
            this.changePage(0);
            axios.post('https://eclipse/WearSet', {
                    Id: this.activeItem.Id
                }).then(() => { return; }).catch(function () { console.log() });
        },
        rewriteSet() {
            
        },
        deleteSet() {
            // удаляем this.activeItem
            // высираем "успешно удалили комплек 2, всё такое"
            // подгружаем обновленный список комплектов
            this.changePage(0);
            axios.post('https://eclipse/DeleteSet', {
                    Id: this.activeItem.Id
                }).then(() => { return; }).catch(function () { console.log() });
        },
        addNewSet() {

        }
    },
    created() {
        window.addEventListener("keydown", this.doCommand);
    },
    destroyed() {
        window.removeEventListener("keydown", this.doCommand);
    },
    mounted() {
        window.addEventListener( "message", (event) =>
        {
            if (event.data.type == "ShowClothBag") {
                this.showThis = true;
            }
            else if(event.data.type == "LoadClothBagData") {
                this.clothSets = event.data.clothSets;
            }
        }, false);
    }
}

</script>