<template>
    <div class="app_fullscreen content_page app_darkchat">
        <transition name="transition_03">
            <div class="content_page_l2 page_1" v-show="page==1">
                <div class="contact_heading">
                    <button @click="closeApp" class="blue_button_back white">Выйти</button>
                </div>
                <div class="dark_loading">
                    <div class="loader"></div>
                    
                </div>
                <h4 class="text-center">Подключение к серверам</h4>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_2" v-show="page==2">
                <div class="contact_heading">
                    <button @click="closeApp" class="blue_button_back white">Выйти</button>
                </div>
                <div class="content">
                    <button class="btn" @click="changePage(3)">Кодированный чат</button>
                    <button class="btn" @click="changePage(4)">Анонимная лента</button>
                    <button class="btn" @click="changePage(5)">Помощь</button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_3" v-show="page==3">
                <div class="contact_heading">
                    <button @click="changePage(2)" class="blue_button_back white">Назад</button>
                    <h4 class="app_name">Ваш код: {{playerDarkData.code}}</h4>
                </div>
                <div class="content">
                    <div class="dark_message" v-for="(message, index) in playerDarkData.messages" :key="'message'+index">
                        
                        <div class="message_author" :class="{to_me: message.to == playerDarkData.code}">
                            <span :class="{author_me: message.from == playerDarkData.code}">{{message.from}}</span> к <span :class="{author_me: message.to == playerDarkData.code}">{{message.to}}</span>
                        </div>
                        <div class="message_text">
                            <p v-if="!message.coordinate">{{message.text}}</p>
                            <button v-if="message.coordinate" class="mark" @click="showCoordinates(message.coordinate)">Метка на карте <b-icon icon="geo-alt-fill" aria-hidden="true"></b-icon></button>
                        </div>
                    </div>
                </div>
                <div class="control">
                    <textarea type="text" rows="3" maxlength="200" v-model="newMessage.text" placeholder="Сообщение"></textarea>
                    <div class="width100 d-flex justify-content-between">
                        <input type="text" placeholder="Код клиента" v-model="newMessage.to">
                        <button class="btn" @click="sendMessage()">Отправить</button>
                        <button class="btn" @click="sendCoordinates()"><b-icon icon="geo-alt-fill" aria-hidden="true"></b-icon></button>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_4" v-show="page==4">
                <div class="contact_heading">
                    <button @click="changePage(2)" class="blue_button_back white">Назад</button>
                    <h4 class="app_name">Лента</h4>
                </div>

                <div class="content">
                    <div class="dark_message" v-for="(message, index) in playerDarkData.anonmessages" :key="'message'+index">
                        
                        <div class="message_author text-right">
                            <span class="author_me" v-if="message.from == playerDarkData.code">{{message.from}}</span>
                            <span v-if="message.from != playerDarkData.code">Аноним</span>
                        </div>
                        <div class="message_text">
                            <p v-if="!message.coordinate">{{message.text}}</p>
                            <button v-if="message.coordinate" class="mark" @click="showCoordinates(message.coordinate)">Метка на карте <b-icon icon="geo-alt-fill" aria-hidden="true"></b-icon></button>
                        </div>
                    </div>
                </div>

                <div class="control">
                    <textarea type="text" rows="3" maxlength="200" v-model="anonMessage.text" placeholder="Сообщение"></textarea>
                    <div class="width100 d-flex justify-content-center">
                        <button class="btn" @click="sendAnonMessage()">Отправить</button>
                        <button class="btn" @click="sendAnonCoordinates()"><b-icon icon="geo-alt-fill" aria-hidden="true"></b-icon></button>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="content_page_l2 page_4" v-show="page==5">
                <div class="contact_heading">
                    <button @click="changePage(2)" class="blue_button_back white">Назад</button>
                    <h4 class="app_name">Помощь</h4>
                </div>
                <p>Кодированный чат: введите код собеседника, он получит ваше сообщение. Также вы получите все сообщения, отправленные на ваш код.</p>
                <p>Анонимная лента: общий чат, где никто не увидит имя автора. Полная анонимность!</p>
            </div>
        </transition>


    </div>
</template>

<script>

import axios from 'axios';

export default {
    name: 'App_darkchat',
    props: {
    },
    data() {
        return {
            page: 1,
            newMessage: {"text": "", "to": ""},
            anonMessage: {"text": ""},
            playerDarkData: {
                "code": null,
                "messages": [
                    // {"from": 91240, "to": 435556, "text": "Нужна срочно информацию по оружию. Желательно Ак-47. Подойдет и оружие местного производства."},
                ],
                "anonmessages": [
                    // {"from": 12315, 'text': "Анонимная лента, привет"},
                ]
            },

        }
    },
    mounted() {
        window.addEventListener(
        "message",
        (event) => {
            if (event.data.type == "SendDarkNetId") {
                this.playerDarkData.code = event.data.code;
            }
            else if(event.data.type == "SetCodedMessages") {
                this.playerDarkData.messages = JSON.parse(event.data.data);
                this.sortByDate(this.playerDarkData.messages);
            }
            else if(event.data.type == "SendAnonChatMessage") {
                this.playerDarkData.anonmessages = JSON.parse(event.data.data)
                this.sortByDate(this.playerDarkData.anonmessages)
            }
            else if(event.data.type == "SendCodedMessageToNui") {
                this.playerDarkData.messages.unshift(JSON.parse(event.data.data))
            }
        },
            false
        );
    },
    methods: {
        closeApp() {
            this.page = 1;
            this.$parent.page = 2;
            this.$parent.whiteColor = true;
        },
        changePage(page) {
            this.page = page;
        },
        startLoad() {
            setTimeout(() => {
                this.page = 2;
            }, 1500);
        },
        sortByDate(array) {
            array.sort(function (a, b) {
                var dateA = new Date(a.date),
                dateB = new Date(b.date);
                return dateB - dateA;
            });
        },
        sortById() {
            this.playerDarkData.anonmessages.sort(function(a, b){
                return b.id - a.id
            });
        },
        sendMessage() {
            if (this.newMessage.text.length > 2 && this.newMessage.text.to != 0) {
                this.newMessage.from = this.playerDarkData.code;
                this.playerDarkData.messages.unshift(this.newMessage);
                axios.post('https://eclipse/sendSecretChatMessage', {
                    from: this.newMessage.from,
                    to: this.newMessage.to,
                    text: this.newMessage.text
                }).then(() => { return; }).catch(function () { console.log() });
                this.newMessage = {};
            }
        },
        sendAnonMessage() {
            if (this.anonMessage.text.length > 2) {
                this.anonMessage.from = this.playerDarkData.code;
                this.playerDarkData.anonmessages.unshift(this.anonMessage);
                axios.post('https://eclipse/sendAnonMessage', {
                    from: this.anonMessage.from,
                    text: this.anonMessage.text
                }).then(() => { return; }).catch(function () { console.log() });
                this.anonMessage = {};
            }
        },
        sendCoordinates() {
            this.newMessage.from = this.playerDarkData.code;
            this.newMessage.coordinate = true;
            this.playerDarkData.messages.unshift(this.newMessage);
            axios.post('https://eclipse/sendSecretChatMessage', {
                    from: this.newMessage.from,
                    to: this.newMessage.to,
                    isCoordinate: true,
                }).then(() => { return; }).catch(function () { console.log() });
            this.newMessage = {};
        },
        sendAnonCoordinates() {
            this.anonMessage.from = this.playerDarkData.code;
                this.anonMessage.coordinate = "";
                this.playerDarkData.anonmessages.unshift(this.anonMessage);
                axios.post('https://eclipse/sendAnonMessage', {
                    from: this.anonMessage.from,
                    text: this.anonMessage.text,
                    isCoordinate: true,
                }).then(() => { return; }).catch(function () { console.log() });
            this.anonMessage = {};
        },
        showCoordinates(coordinate) {
            console.log(coordinate)
            axios.post('https://eclipse/SetGpsCoordinate', {
                    Coordinate: coordinate
                }).then(() => { return; }).catch(function () { console.log() });
        },
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    }
}
</script>

<style scoped>
.loader {
    width: 6em;
    height: 6em;
    font-size: 25px;
    box-sizing: border-box;
    border-top: 0.3em solid hotpink;
    border-radius: 50%;
    position: relative;
    animation: rotating 2s ease-in-out infinite;
    --direction: 1;
}

.loader::before,
.loader::after {
    content: '';
    position: absolute;
    width: inherit;
    height: inherit;
    border-radius: 50%;
    box-sizing: border-box;
    top: -0.2em;
}

.loader::before {
    border-top: 0.3em solid dodgerblue;
    transform: rotate(120deg);
}

.loader::after {
    border-top: 0.3em solid gold;
    transform: rotate(240deg);
}

.loader span {
    position: absolute;
    color: white;
    width: inherit;
    height: inherit;
    text-align: center;
    line-height: 10em;
    font-family: sans-serif;
    animation: rotating 2s linear infinite;
    --direction: -1;
}

@keyframes rotating {
    50% {
        transform: rotate(calc(180deg * var(--direction)));
    }

    100% {
        transform: rotate(calc(360deg * var(--direction)));
    }
}

</style>