<template>
    <div class="content_outer postman">
        <div class="main_content">
            <h2 class="text-center">Профессия курьера</h2>
            <p class="justify">Начни карьеру в самой перспективной отрасли <b>Лос-Сантоса</b>. Только тебе решать, какой стиль доставки ты выберешь: неторопливая черепаха или яростный кролик?</p>
            <p class="justify">Не спи! Это <b>гонка на выживание</b>, с тобой соперничают самые одичавшие мигранты из мексики, готовые перегрызть глотку за каждые 10 песо.</p>
            <p class="justify">Твоя задача проста: доставить груз из точки <b>А</b>, в точку <b>Б</b>. Однако не забывай: чем опаснее груз, тем больше награда!</p>
            <p v-if="playerData.sex">Готов стать <b>мужчиной?</b></p>
            <p v-else>Готова стать <b>женщиной</b>?</p>
            <p>Тогда не раздумывая хватай груз и доставь его на место. Помни: не открывай груз, не показывай копам и самое главное: удачи! Бабки мы переведем тебе на счёт. <b>ВПЕРЕД!</b></p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()">Стать курьером</button>
                <button v-else class="btn btn-primary" @click="stopJob()">Закончить рабочий день</button>
                <button class="btn btn-secondary" @click="exit()">Выход</button>
            </div>
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobPostman',
    props: {
        playerData: Object
    },
    components: {
        
    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/GoPostalStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/GoPostalStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>