                                    </div>
                                    <div class="app_icon">
                                        <button class="app app_button_dark_chat" @click="runApp('19')" :class="{active: anim == 19}"></button>
                                        <span>Dark chat</span>
                                    </div>
                                    <div class="app_icon">
                                        <button class="app app_button_yellow" @click="runApp('20')" :class="{active: anim == 20}"></button>
                                        <span>Dynasty8 exchange</span>
                                    </div>
                                    
                                    <div class="app_icon">
                                        <button class="app app_button_youtube" @click="runApp('21')" :class="{active: anim == 21}"></button>
                                        <span>Youtube</span>
                                    </div>
                                    <div class="app_icon">
                                        <button class="app app_button_radio" @click="runApp('22')" :class="{active: anim == 22}"></button>
                                        <span>Радио</span>
                                    </div> -->
                                    <div class="app_icon">
