<script>
require('@/assets/css/player_cad.css')
import axios from "axios";
import AppSettings from '../cad/cadApps/Settings.vue'
import AppGoverment from '../cad/cadApps/Goverment.vue'
import AppMessage from '../cad/cadApps/AppMessage.vue'
import AppCalendar from '../cad/cadApps/AppCalendar.vue'
import AppWeather from '../cad/cadApps/AppWeather.vue'
import AppCalculator from '../cad/cadApps/AppCalculator.vue'
import AppGlobe from './cadApps/AppGlobe.vue'
import AppBleeter from '../cad/cadApps/AppBleeter.vue'
import AppDarkChat from '../cad/cadApps/AppDarkChat.vue'
import AppYellowPages from '../cad/cadApps/AppYellowPages.vue'
import AppYoutube from '../cad/cadApps/AppYoutube.vue'
import AppRadio from '../cad/cadApps/AppRadio.vue'
import AppBusiness from '../cad/cadApps/AppBusiness.vue'
import AppHelp from '../cad/cadApps/AppHelp.vue'
import AppBrowser from '../cad/cadApps/AppBrowser.vue'
import AppMail from '../cad/cadApps/AppMail.vue'
import AppCasino from '../cad/cadApps/AppCasino.vue'
import AppRealestate from '../cad/cadApps/AppRealEstate.vue'


export default {
    name: "PlayerCad",
    components: {
        AppSettings,
        AppGoverment,
        AppMessage,
        AppCalendar,
        AppWeather,
        AppCalculator,
        AppGlobe,
        AppBleeter,
        AppDarkChat,
        AppYellowPages,
        AppYoutube,
        AppRadio,
        AppBusiness,
        AppHelp,
        AppBrowser,
        AppMail,
        AppCasino,
        AppRealestate
    },
    props: {
        playerData: Object,
    },
    data() {
        return {
            show: true,
            logged: 0,
            currentTime: {
                Hours: 14,
                Minutes: 30,
            },
            MonthList: [
                "Января",
                "Февраля",
                "Марта",
                "Апреля",
                "Мая",
                "Июня",
                "Июля",
                "Августа",
                "Сентября",
                "Октября",
                "Ноября",
               " Декабря"
            ],
            WeekList: [
                "Понедельник",
                "Вторник",
                "Среда",
                "Четверг",
                "Пятница",
                "Суббота",
                "Воскресенье"
            ],

            anim: 0,
            page: 0,
            colorblack: false,
            showLeftPart: true,

        }
    },
    computed: {
        appWeek() {
            var today = new Date();
            var week = today.getDay();
            if (week == 0) {
                week = 7;
            }
            var text = this.WeekList[week - 1].substring(0, 5);
            return text;
        },
        appFullweek() {
            var today = new Date();
            var week = today.getDay();
            if (week == 0) {
                week = 7;
            }
            var text = this.WeekList[week - 1];
            return text;
        },
        appDay() {
            var today = new Date();
            var date = today.getDate();
            return date;
        },
        appMonth() {
            var today = new Date();
            var date = today.getMonth();
            return this.MonthList[date];
        }
    },
    methods: {
        login() {
            this.logged = 1;
            setTimeout(() => {
                this.logged = 2;
            }, 2000);
        },
        runApp(index) {
            if (this.page != index) {
                this.anim = index;
                this.colorblack = false;
                setTimeout(() => {
                    this.anim = 0;
                    this.page = index;
                    if (this.page == 0) {
                        this.showLeftPart = true;
                    } else {
                        this.showLeftPart = false;
                    }
                    if (this.page == 34) {
                        this.$refs.casinoApp.resetPage();
                    }
                }, 100);
            }

            if(index == 31) {
                 axios.post('https://eclipse/GetBusinessCharacterData', {
                
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        makeBlack() {
            setTimeout(() => {
                this.colorblack = true;
            }, 100);
        },
        closeAllApps() {
            this.page = 1;
            this.colorblack = false;
            this.showLeftPart = true;
        }
        
    }
}
</script>

