                MakeupColorIndex: this.MakeupColorIndex,
            });
        },
        updateBlush() {
            axios.post('https://eclipse/updateBlush', {
                BlushIndex: this.BlushIndex,
                BlushOpacity: this.BlushOpacity,
                BlishColorIndex: this.BlushColorIndex,
