                HairSecondColorIndex: this.HairSecondColorIndex,
                HightLightIndex: this.HightLightIndex, // Добавлено
                OverlayHairIndex: this.OverlayHairIndex, // Добавленно
                Price: this.malehairstyles[this.HairIndex].Price
