            });
        },
        doCommand(e) {
            if (e.keyCode == 27) {
                this.showPage = ""
                axios.post('https://eclipse/CloseBarbershop', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        toggleCameraClick(state) {
            if(state == true) {
            
                this.mouseState = true;
                this.mouseX = 0;
                this.mouseY = 0;
            }
            else if(state == false) {
                this.mouseState = false;
            }
        },
        scrollBlock(state) {
            if(state == true) {
                this.blockScroll = true;

            }
            else if(state == false) {
                this.blockScroll = false;
                this.mouseState = false;
            }
        },
        wheelMove($event) {
            if(!this.blockScroll) {
                axios.post('https://eclipse/Mouse', {
                    DeltaY: $event.deltaY,
                    CtrlPressed: this.isControlPressed
                }).then(() => { return; }).catch(function (error) { console.log(error) });
                
            }
        },
        pressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = true;
            }
        },
        unpressCtrl(e) {
            if (e.keyCode == 17) {
                this.isControlPressed = false;
            }
        },
        toggleCameraMove($event) {
            if(this.mouseState == true && !this.blockScroll) {
                if(this.mouseX == 0 && this.mouseY == 0) {
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY;
                    return;
                }
                if($event.clientX || $event.clientY) {
                    var xPosition = $event.clientX - this.mouseX;
                    var yPositiuon = $event.clientY - this.mouseY;
                    this.mouseX = $event.clientX;
                    this.mouseY = $event.clientY; 
                    axios.post('https://eclipse/X', {
                    Xposition: xPosition,
                    Yposition: yPositiuon
                }).then(() => { return; }).catch(function (error) { console.log(error) });
                }
            }
        },
        changePlayerAnimation(numb) {
            this.activePlayerAnim = numb;
            axios.post(`https://eclipse/changePlayerAnimation`, {
                Anim: this.activePlayerAnim,
            }).then(() => { return; }).catch(function (error) { console.log(error) });
        }
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
        window.addEventListener('keydown', this.pressCtrl);
        window.addEventListener('keyup', this.unpressCtrl);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
        window.removeEventListener('keydown', this.pressCtrl);
        window.removeEventListener('keyup', this.unpressCtrl);
    },
    mounted() {
        this.isMale = this.playerData.sex;
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadBarberShopData") {
                var characterData = JSON.parse(event.data.characterData);
                this.eyeScale = characterData.eyeScale
                this.eyebrowHeight = characterData.eyebrowHeight
                this.eyebrowDepth = characterData.eyebrowDepth
                this.noseWidth = characterData.noseWidth
                this.noseHeight = characterData.noseHeight
                this.noseDepth = characterData.noseDepth
                this.noseTipLength = characterData.noseTipLength
                this.noseTipHeight = characterData.noseTipHeight
                this.noseBroke = characterData.noseBroke
                this.lipThickness = characterData.lipThickness
                this.HairOpacity = characterData.HairOpacity
                this.Hair2Opacity = characterData.Hair2Opacity
                this.EyeBrowsOpacity = characterData.EyeBrowsOpacity
                this.BeardOpacity = characterData.BeardOpacity
