<template>
    <div class="jobs">
        <JobElectric :playerData="playerData" v-if="showJob=='Electric'" />
        <JobPostman :playerData="playerData" v-if="showJob=='Postman'" />
        <JobCabman :playerData="playerData" v-if="showJob=='Cabman'" />
        <JobTrucker :playerData="playerData" v-if="showJob=='Trucker'" />
        <JobLawnmower :playerData="playerData" v-if="showJob=='Lawnmower'" />
        <JobCollector :playerData="playerData" v-if="showJob=='Collector'" />
        <JobBusdriver :playerData="playerData" v-if="showJob=='BusDriver'" />
        <JobLoader :playerData="playerData" v-if="showJob=='Loader'" />
        <PoliceJob :playerData="playerData" v-if="showJob=='Policeman'" />
        <PoliceBadge v-if="showPoliceBadge" />
        <EmsJob :playerData="playerData" v-if="showJob=='Doctor'" />

        <!-- 
            Лицензия, которую надо получить в мэрии
            Магазины продуктов
            Кафе
        -->

        <BusinessInfo :playerData="playerData" v-if="showBusinessInfo" />
        <BusinessHud :businessData="businessData" v-if="showBusinessHud" />
        <EstateInfo :playerData="playerData" v-if="showEstateInfo" />
        <Elevator v-if="showElevator" />


    </div>
</template>

<script>
require('@/assets/css/jobs.css')
import JobElectric from './JobElectrician.vue'
import JobPostman from './JobPostman.vue'
import JobCabman from './JobCabman.vue'
import JobTrucker from './JobTrucker.vue'
import JobLawnmower from './JobLawnmower.vue'
import JobCollector from './JobCollector.vue'
import JobBusdriver from './JobBusdriver.vue'
import JobLoader from './JobLoader.vue'
import PoliceJob from './police/PoliceJob.vue'
import PoliceBadge from './police/PoliceBadge.vue'
import EmsJob from './ems/JobEms.vue'
import BusinessInfo from './BusinessInfo.vue'
import BusinessHud from './Businesshud.vue'
import EstateInfo from './estate/EstateInfo.vue'
import Elevator from './estate/Elevator.vue'

import axios from "axios";

export default {
    name: 'JobsComponent',
    props: {
        playerData: Object,
    },
    components: {
        JobElectric,
        JobPostman,
        JobCabman,
        JobTrucker,
        JobLawnmower,
        JobCollector,
        JobBusdriver,
        JobLoader,
        PoliceJob,
        PoliceBadge,
        EmsJob,
        BusinessInfo,
        BusinessHud,
        EstateInfo,
        Elevator
    },
    data() {
        return {
            showJob: "",
            jobActive: false,
            showPoliceBadge: true,
            showBusinessInfo: false,
            showBusinessHud: false,
            businessData: {
                Name: "Перевоз груза",
                Description: "",
                Cost: "250",
            },
            showEstateInfo: false,
            showElevator: false,
        }
    },
    methods: {
        doCommand(e) {
            if (e.keyCode == 27 & this.showJob != "") {
                this.showJob = ""
                axios.post('https://eclipse/Tilda', {}).then(() => { return; }).catch(function () { console.log() });
            }
        },
    },
    mounted() {
        window.addEventListener(
        "message",
        (event) => {
            if (event.data.type == "LoadJobData") {
            this.showJob = event.data.name;
            }
            else if(event.data.type == "ActivateJob") {
                this.jobActive = true
            }
            else if(event.data.type == "DeactivateJob") {
                this.jobActive = false
            }
            else if(event.data.type == "ShowBusinessInfo") {
                this.showBusinessInfo = true;
            }
            else if(event.data.type == "CloseBusinessInfo") {
                this.showBusinessInfo = false;
            }
            else if(event.data.type == "ShowBusinessMissionHud") {
                this.businessData = event.data.businessData;
                this.showBusinessHud = true;
            }
            else if(event.data.type == "CloseBusinessMissionHud") {
                this.showBusinessHud = false;
            }
            else if(event.data.type == "ShowEstateInfo") {
                this.showEstateInfo = true;
            }
            else if(event.data.type == "CloseEstateInfo") {
                this.showEstateInfo = false;
            }
            else if(event.data.type == "ShowElevator") {
                this.showElevator = true;
            }
            else if(event.data.type == "CloseElevator") {
                this.showElevator = false;
            }
        },

        false
        );
    },
    created() {
        window.addEventListener('keydown', this.doCommand);
    },
    destroyed() {
        window.removeEventListener('keydown', this.doCommand);
    },
}

</script>