<template>
    <section class="hacker_game">
        <div class="board">
            <div class="board_top">
                <div class="header">
                    <h2 class="main_title">CONNECTING TO THE HOST</h2>
                    <h3 class="sec_title">COMPROMISING GLOBAL SECURITY ONE SLIP AT A TIME</h3>
                    <div class="title_image"></div>
                    <b-icon icon="reception4" class="recep_icon"></b-icon>
                </div>
                <h2 class="ip" :class="{win: gameWin}">{{ip}}</h2>
                <h4 class="timer">{{timerMinutes}}:{{timerSeconds}}:{{timerMiliseconds}}</h4>
            </div>
            <div class="board_center align-items-center flex-column" v-if="!gameStart && !gameWin">
                <h4 class="text-center gamestart">You have {{timeToWin}} seconds to hack the console.</h4>
                <button class="start_hack" @click="startGame()">START</button>
            </div>
            <div class="board_center" v-if="!gameWin">
                <div class="g_cell" :class="{active: index == playerAnswer[0] || index == playerAnswer[1] || index == playerAnswer[2] || index == playerAnswer[3]}" v-for="(item, index) in numbersArray" :key="index + 'cell'">
                    {{item}}
                </div>
            </div>
            <div class="board_center align-items-center flex-column" v-if="gameWin">
                <h4 class="text-center win">Successfully connected to the host.</h4>
                <button class="start_hack" @click="startGame()">RESTART</button>
            </div>
            <div class="board_center align-items-center flex-column" v-if="gameLost && !gameWin">
                <h4 class="text-center lose">Time's up. The alarm went off!</h4>
                <button class="start_hack" @click="startGame()">RESTART</button>
            </div>
        </div>
        <div class="controls p-1" style="position: absolute; right: 0; top: 0;">
            <button class="" @click="startGame()">Начать игру</button>
            <button class="" @click="stopGame()">Остановить игру</button>
        </div>
    </section>
</template>

<script>
require('@/assets/css/games/hacker.css')

export default {
    name: "HackerGame",
    props: {
        
    },
    components: {
        
    },
    data() {
        return {
            ip: "00.00.00.00",
            timer: null,
            currentTime: 0,
            timer2: null,
            timer2Time: 1000,
            numbersArray: [],
            answerNumb: [99, 99, 99, 99],
            playerAnswer: [0, 1, 2, 3],
            gameStart: false,
            gameWin: false,
            gameLost: false,

            timeToWin: 120,
        }
    },
    methods: {
        setTimer(timer) {
            this.currentTime = timer;
        },
        startTimer() {
            this.timer = setInterval(() => {
                this.currentTime--
                if (this.currentTime < 0) {
                    this.lostGame();
                }
            }, 1000);
            this.timer2 = setInterval(() => {
                this.timer2Time -= 5;
                if (this.timer2Time < 0) {
                    this.timer2Time = 1000;
                }
            }, 5);
        },
        stopTimer() {
            this.currentTime = 0;
            this.timer2Time = "000";
            clearTimeout(this.timer);
            clearTimeout(this.timer2);
        },
        renderArray() {
            for (let index = 0; index < 80; index++) {
                var value = ('0' + Math.floor(Math.random() * 99)).slice(-2);
                this.$set(this.numbersArray, index, value);
            }
            this.answerNumb[0] = this.getRandomInt(11, 75);
            this.answerNumb[1] = this.answerNumb[0] + 1;
            this.answerNumb[2] = this.answerNumb[0] + 2;
            this.answerNumb[3] = this.answerNumb[0] + 3;
            this.ip = this.numbersArray[this.answerNumb[0]]+ '.' + this.numbersArray[this.answerNumb[1]]+ '.' + this.numbersArray[this.answerNumb[2]]+ '.' + this.numbersArray[this.answerNumb[3]];
        },
        getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min)) + min;
        },
        startArrayTimer(){
            setInterval(function() {
                if (this.gameStart) {
                    this.changeArray();
                }
            }.bind(this), 2500);
        },
        changeArray() {
            var firstElem = this.numbersArray[0];
            for (let index = 0; index < 79; index++) {
                var newValue = this.numbersArray[index + 1];
                this.$set(this.numbersArray, index, newValue);
            }
            this.$set(this.numbersArray, 79, firstElem);
            this.answerNumb.forEach((item, index) => {
                var NewValue = parseInt(item) - 1;
                if (NewValue < 0) {
                    NewValue = 79;
                }
                this.answerNumb[index] = NewValue;
            })
            this.checkGame();
        },
        stopGame() {
            this.gameStart = false;
            this.gameWin = false;
            this.numbersArray = [];
            this.ip = "00.00.00.00";
            this.setTimer(0);
            this.stopTimer();
        },
        startGame(){
            this.stopGame();
            this.renderArray();
            this.setTimer(this.timeToWin);
            this.startTimer();
            this.playerAnswer = [0, 1, 2, 3],
            this.gameStart = true;
            this.gameWin = false;
        },
        keypress(e){
            if (!this.gameStart) {
                return
            }
            // UP
            if (e.keyCode == 83 || e.keyCode == 40) {
                this.playerAnswer.forEach((item, index) => {
                    var newValue = item + 10;
                    if (newValue > 79) {
                        newValue -= 80;
                    }
                    this.$set(this.playerAnswer, index, newValue);
                });
            }
            // DOWN
            if (e.keyCode == 87 || e.keyCode == 38) {
                this.playerAnswer.forEach((item, index) => {
                    var newValue = item - 10;
                    if (newValue < 0) {
                        newValue += 80;
                    }
                    this.$set(this.playerAnswer, index, newValue);
                });
            }
            // LEFT
            if (e.keyCode == 65 || e.keyCode == 37) {
                this.playerAnswer.forEach((item, index) => {
                    var newValue = item - 1;
                    if ((newValue + 1) % 10 == 0) {
                        newValue = Math.trunc((newValue + 1) / 10) * 10 + 9;
                    }
                    this.$set(this.playerAnswer, index, newValue);
                });
            }
            // RIGHT
            if (e.keyCode == 68 || e.keyCode == 39) {
                this.playerAnswer.forEach((item, index) => {
                    var newValue = item + 1;
                    if ((newValue) % 10 == 0) {
                        newValue = Math.trunc(newValue / 10) * 10 - 10;
                    }
                    this.$set(this.playerAnswer, index, newValue);
                });
            }
            this.checkGame()
        },
        checkGame() {
            var win = true;
            if (parseInt(this.playerAnswer[0]) != parseInt(this.answerNumb)) {
                win = false;
            }
            if (parseInt(this.playerAnswer[1]) != parseInt(this.answerNumb) + 1) {
                win = false;
            }
            if (parseInt(this.playerAnswer[2]) != parseInt(this.answerNumb) + 2) {
                win = false;
            }
            if (parseInt(this.playerAnswer[3]) != parseInt(this.answerNumb) + 3) {
                win = false;
            }
            if (win) {
                this.winGame();
            }
        },
        winGame() {
            this.gameStart = false;
            this.gameWin = true;
            this.stopTimer();
        },
        lostGame() {
            this.numbersArray = [];
            this.stopTimer();
            this.gameWin = false;
            this.gameLost = true;
            
        }
    },
    computed: {
        timerMinutes() {
            return ('0' + Math.trunc(this.currentTime / 60)).slice(-2);
        },
        timerSeconds() {
            return ('0' + (this.currentTime - (Math.trunc(this.currentTime / 60) * 60))).slice(-2);
        },
        timerMiliseconds() {
            return ('000' + this.timer2Time).slice(-3);
        }
    },
    mounted() {
        this.startArrayTimer();
        document.addEventListener('keydown', (event) => {
            this.keypress(event);
        });
    },
    destroyed() {
        this.stopTimer()
    },
}
</script>
