<template>
    <div class="policejob">
        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 0">
                <h2 class="text-center">Полицейский департамент LSPD</h2>
                <p>Ты — один из <b>лучших</b> людей этого штата. В твоих руках — вся сила и мощь пенитенциарной системы Америки. Не  подведи своего напарника и не опозорь наш департамент. Не дай этим недоноскам на улице даже подумать о нарушении закона.</p>
                <p>И всегда помни о <b>правиле Миранды</b>: всё-таки будет глубо отпустить преступника, если ты ему не зачитал его права или он был в этот момент без сознания, так как хотел выкрасть твой ствол.</p>
                <p>У тебя в руках самое лучшее оружие — <b>закон</b>, не стесняйся им пользоваться. Весь список законов доступен в диспетчерском планшете, думаю ты разберешься.</p>
                <div class="menu">
                    <button class="btn btn-primary" @click="startDuty()" v-if="!playerOnDuty">Начать смену</button>
                    <button class="btn btn-primary" @click="exitDuty()" v-if="playerOnDuty">Выйти со смены</button>
                    <button class="btn btn-primary" @click="openCad()">Открыть КАД</button>
                    <button class="btn btn-primary" @click="changePage(1)">Информация</button>
                    <button class="btn btn-secondary" @click="exit()">Выход</button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 1">
                <h2 class="text-center">Информация</h2>
                <div class="player_info" v-if="policeDataExist">
                    <h4><b>Имя офицера:</b> {{playerData.name}}</h4>
                    <h4><b>Номер телефона:</b> {{playerData.phone}}</h4>
                    <h4><b>Звание:</b> {{playerData.policeData.rank}}</h4>
                    <h4><b>Позывной:</b> {{playerData.policeData.callsign}}</h4>
                    <h4><b>На службе:</b> 0 дней</h4>
                </div>
                <div class="menu">
                    <button class="btn btn-secondary" @click="changePage(0)">Назад</button>
                </div>
            </div>
        </transition>
        <div class="police_girl"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'PoliceJob',
    props: {
        playerData: Object
    },
    components: {
        
    },
    data() {
        return {
            playerOnDuty: false,
            policeDataExist: false,
            page: 0
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
            }
        },
        startDuty() {
            this.playerOnDuty = true;
            axios.post('https://eclipse/PoliceOnDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('выйти на смену');
        },
        exitDuty() {
            axios.post('https://eclipse/PoliceOffDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            this.playerOnDuty = false;
            console.log('выйти со смены');
        },
        exit() {
            axios.post('https://eclipse/ExitPolicRec', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('выход');
        },
        openCad() {
            this.$parent.showJob = "";
            //this.$parent.$parent.showPage = "PoliceCad";
            axios.post('https://eclipse/OpenCad', {
            }).then(() => { return; }).catch(function () { console.log() });
            
            // Может надо сделать через бэк, хз
        }
    },
    mounted() {
         window.addEventListener("message",(event) => {
            if (event.data.type == "PoliceData") {
            //    this.playerData.name = event.data.name
            //    this.playerData.phone = event.data.phone
            //    this.playerOnDuty = event.data.duty
            }
        }, false);
        if (this.playerData.policeData) {
            this.policeDataExist = true;
        }
        if (this.playerData.policeData && this.playerData.policeData.onDuty != 0) {
            this.playerOnDuty = true;
        } else {
            this.playerOnDuty = false;
        }

    }
}

</script>